import { Pipe, PipeTransform } from '@angular/core';
import moment from 'moment';
import { PsCommonSettings } from '../../psServices/models/ps-common.settings';
import { CommonUtils } from './../../psServices/models/common-utils';


@Pipe({
  name: 'psDateFormat'
})
export class PsDateFormatPipe implements PipeTransform {

  /* private omniPull: OmniPullService;
  constructor() {
    this.omniPull = CommonUtils.injectionHandler(OmniPullService);
  } */
  transform(date, format, momentInputFormat?: moment.MomentFormatSpecification, basedOnLang?: boolean) {
    if (basedOnLang === undefined) {
      basedOnLang = true;
    }
    if (CommonUtils.isMobile()) {
      // Modified by Richie for 1192538
      let dateVal: string, dateMoment: moment.Moment;

      let mobileDate = moment(date, momentInputFormat || "YYYY-MM-DDTHH:mm").toDate(); //Edited by ghada, tp#1427702 for considering the sent date format in case of mobile
      if (!CommonUtils.isValidDate(mobileDate)) {
        mobileDate = new Date(date);
      }
      dateMoment = moment(mobileDate);
      if (dateMoment.isValid()) {
        dateVal = dateMoment.format(format);
      } else {
        dateVal = date;
      }
      // End Richie
      return dateVal;
    } else {
      if (CommonUtils.isDate(date, momentInputFormat) && date) { //Edited by ghada, tp#1427702 for considering the sent date format in case it get sent with the params
        if (basedOnLang && PsCommonSettings.activeLanguge) {
          if (momentInputFormat) {
            return moment(date, momentInputFormat).locale(PsCommonSettings.activeLanguge.toLocaleLowerCase()).format(format);
          } else {
            return moment(date).locale(PsCommonSettings.activeLanguge.toLocaleLowerCase()).format(format);
          }
        } else {
          if (momentInputFormat) {
            return moment(date, momentInputFormat).format(format);
          } else {
            return moment(date).format(format);
          }
        }
      } else {
        return date;
      }
    }
  }

  parse(date, format, validateDateFormat?: boolean) {
    if (validateDateFormat === undefined) {
      validateDateFormat = false;
    }
    if (CommonUtils.isDateObject(date)) {
      return date;
    } else {
      if (CommonUtils.isMobile()) {
        let dateObj;
        const momentDate = moment(date, format).toDate();
        if (CommonUtils.isValidDate(momentDate)) {
          dateObj = momentDate;
        } else {
          dateObj = new Date(date);
        }

        if (validateDateFormat) {
          return dateObj;
        } else {
          // in case the value of the date was set as 'dd/mm/yyyy' then if we return the value in this format, it will set the dd as mm and vice versa. 
          // so everytime we enter and leave the date field the month and days will change places in case the days were less than or equal to 12
          let valInMobileFormat = date;
          if (String(date).indexOf('T') === -1) {
            const dateMoment = moment(dateObj);
            if (dateMoment.isValid()) {
              valInMobileFormat = dateMoment.format('YYYY-MM-DDTHH:mmZ');
            }
          }
          return valInMobileFormat;
        }
      } else {
        return moment(date, format).toDate();
      }
    }
  }


}
