import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { AppVersion } from '@ionic-native/app-version/ngx';
import { TranslateService } from '@ngx-translate/core';
import { Deploy } from 'plugins/cordova-plugin-ionic/dist/ngx';
import { OmniCommonService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-common.service';
import { IOptionsAlert } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { CheckAppService } from '../../psServices/checkApp/checkApp.service';
import { PsSecurityService } from '../../psServices/checkApp/ps-security.service';
import { ConstantCommon } from '../../psServices/models/common-constant';
import { CommonUtils } from '../../psServices/models/common-utils';
import { PsApplicationSettings } from '../../psServices/models/ps-app-settings';
import { IOptionsAlertButton } from '../../psServices/models/ps-common-interface';
import { PsCommonSettings } from '../../psServices/models/ps-common.settings';
import { PsCommonService } from '../../psServices/ps-common/ps-common.service';
import { LoggerService } from '../../psServices/logger/logger.service';
import * as CryptoJS from 'crypto-js';

@Component({
  selector: 'ps-splash-screen',
  templateUrl: './ps-splash-screen.page.html',
  styleUrls: ['./ps-splash-screen.page.scss'],
})
export class PsSplashScreenPage implements OnInit {

  @Output() public loadComplete: EventEmitter<any> = new EventEmitter<any>();

  loadingKey = 'loading_key';
  progress = 0;
  updating = true;
  loaderIcon;
  bankLogo;
  constructor(
    private common: PsCommonService,
    private omniCommon: OmniCommonService,
    private deploy: Deploy,
    private checkAppService: CheckAppService,
    private translate: TranslateService,
    private appVersion: AppVersion,
    private psSecurity: PsSecurityService,
    private logger: LoggerService
  ) {
  }

  async ngOnInit() {
    this.loaderIcon = CommonUtils.getCssVariableValue('--ps-loader-image-name') && CommonUtils.getCssVariableValue('--ps-loader-image-name').includes('base64') ?
      CommonUtils.getCssVariableValue('--ps-loader-image-name') : PsApplicationSettings.CLIENT_ASSETS_CONFIG.IMGS_FOLDER_URL + CommonUtils.getCssVariableValue('--ps-loader-image-name');

    this.bankLogo = PsApplicationSettings.CLIENT_ASSETS_CONFIG.IMGS_FOLDER_URL + CommonUtils.getCssVariableValue('--ps-splash-screen-logo-name');
    const labelsObj = this.omniCommon.labelKeysTransList.get(PsCommonSettings.activeLanguge.toLocaleLowerCase());
    if (labelsObj) {
      this.common.language.addAndUseTranstlation(labelsObj.lang, labelsObj.labels);
    }
  }

  async checkAvailableUpdate(data): Promise<string> {
    return new Promise<string>(async (resolve, reject) => {
      if (this.common.isNativeMobile()) {
        // get latest version (for now will read it from commonsettings)
        // const latestVer = PsCommonSettings.latestAppVerNb; // major.minor.store.live.assets
        // GI: Run deploy.checkForUpdate() method in parallel with returnAppversiondetails Service 
        this.checkAppflowforUpdate().then((res) => {
          if (res && res !== 'pendingRequest') {
            resolve(res);
          }
        }).catch(async (err) => {
          this.common.logger.error(err);
          const errMsg = await this.omniCommon.checkLabelTrans('error_while_updating_app_key');
          reject(errMsg);
        });
        // Added by Richie to log the time of of the specific action
        if (PsApplicationSettings.MAIN_CONFIG.TIME_LOG_ENABLED) {
          PsCommonSettings.timeLogs.push('inside ps-splash-screen, before returnAppVersion() with start time: ' + (new Date()).toISOString());
        }
        // end Richie
        const res = await this.common.returnAppVersion().catch(async error => {
          const errMsg = await this.omniCommon.checkLabelTrans('error_while_updating_app_key');
          reject(errMsg);
        });
        if (res) {
            // means we have successfully retrieved the version
            const appVerParts = CommonUtils.returnVersionParts(PsCommonSettings.appVersionNumber);
            if (appVerParts) {
              // Added by Richie to log the time of of the specific action
              if (PsApplicationSettings.MAIN_CONFIG.TIME_LOG_ENABLED) {
                PsCommonSettings.timeLogs.push('inside ps-splash-screen, before returnMandatoryVer() with start time: ' + (new Date()).toISOString());
              }
              // end Richie
              let integrityToken = '';
              let frontendUniqueValue = '';
              let userAgent = '';
              try {
                if (CommonUtils.isAndroid()) {
                  userAgent = CommonUtils.cacheParameterIfNotCached("userAgent", null, {
                    methodToCall() {
                      let val = this.class.checkAppService.returnNativeUserAgent();
                      return val;
                    },
                    class: this
                  });
                  frontendUniqueValue = this.generateUniqueValue(16);
                  const hashedNonce = this.generateNonce(data.backendUniqueValue, frontendUniqueValue);
                  integrityToken = await this.psSecurity.rootDeviceDetection(data.projectNumber, hashedNonce);
                  if (PsApplicationSettings.MAIN_CONFIG.LOG_LEVEL_ALL) {
                    this.omniCommon.presentInfoAlert(`integrityToken: ${integrityToken}`, { title: 'Tip' });
                  }
                }
              } catch (error) {
                this.common.logger.error(error.message);
                const errMsg = await this.omniCommon.checkLabelTrans('GySGcfU2NVCQo55c');
                reject(errMsg);
              }
              const resultObj = await this.returnMandatoryVer(integrityToken, frontendUniqueValue, data.udid, userAgent).catch(async error => {
                this.common.logger.error(error);
                if (CommonUtils.isAndroid() && error.data && error.data.outputCode === -3
                  && error.data.outputNotification === 'Security Error'
                  && error.data.outputType === 'SEC_ERR') {
                  this.logger.log('Your device is not compatible!!!');
                  this.psSecurity.handleStopApp();
                } else {
                  const errMsg = await this.omniCommon.checkLabelTrans('error_while_updating_app_key');
                  reject(errMsg);
                }
              });
              PsCommonSettings.dbUpdateRequestWasSent = true;
              if (resultObj) {
                // Added by Richie to log the time of of the specific action
                if (PsApplicationSettings.MAIN_CONFIG.TIME_LOG_ENABLED) {
                  PsCommonSettings.timeLogs.push('inside ps-splash-screen, after returnMandatoryVer() and before checkAntiTampering() with end time: ' + (new Date()).toISOString());
                }
                // end Richie
                // Added by Richie for #BUG 1287746  in order to check application for anti-tampering
                // check application for anti-tampering
                this.psSecurity.checkAntiTampering(resultObj[ConstantCommon.ANTI_TAMPERING_OBJECT]);
                // End Richie
                if (resultObj[ConstantCommon.SAFETY_NET_OBJ]) {
                  // Added by Richie to log the time of of the specific action
                  if (PsApplicationSettings.MAIN_CONFIG.TIME_LOG_ENABLED) {
                    PsCommonSettings.timeLogs.push('inside ps-splash-screen, before safetyNetChecker() with start time: ' + (new Date()).toISOString());
                  }
                  // end Richie
                }

                //check for application update
                const latestVer = resultObj[ConstantCommon.APP_DB_VERSION];// Added by Richie for #BUG 1287746 to maintain the old behavior
                if (latestVer) {
                  // get the latest version splits
                  const latestVerParts = CommonUtils.returnVersionParts(latestVer);
                  if (latestVerParts) {
                    // for the case if the app was updated from the store and the database was not updated then stop the app
                    // checking on version.extension is added by Richie for TP# 1135618
                    if (latestVerParts.major < appVerParts.major || latestVerParts.minor < appVerParts.minor || latestVerParts.extension < appVerParts.extension || latestVerParts.store < appVerParts.store) {
                      // const msg = this.common.translate('application_is_under_maintenance_key');
                      // const msg = await this.checkLabelTrans('application_is_under_maintenance_key');
                      // const alertOptions: IOptionsAlert = { title: 'warning_key', message: msg, noButtons: true };
                      // CommonUtils.presentInfoAlert(null, alertOptions);
                      // resolve('stop');
                      // return;
                      resolve('noUpdate');
                    }
                    // checking on version.extension is added by Richie for TP# 1135618
                    if (latestVerParts.major > appVerParts.major || latestVerParts.minor > appVerParts.minor || latestVerParts.extension > appVerParts.extension || latestVerParts.store > appVerParts.store) {
                      PsCommonSettings.mandatoryUpdate = '1';
                      PsCommonSettings.dbUpdateAvailable = true;
                      this.commonUpdateChecking().then((res) => {
                        if (res && res !== 'pendingRequest') {
                          resolve(res);
                        }
                      }).catch(async (err) => {
                        this.common.logger.error(err);
                        const errMsg = await this.omniCommon.checkLabelTrans('error_while_updating_app_key');
                        reject(errMsg);
                      });
                    } else if (latestVerParts.live !== appVerParts.live || latestVerParts.assets !== appVerParts.assets) {
                      PsCommonSettings.dbUpdateAvailable = true;
                      this.commonUpdateChecking().then((res) => {
                        if (res && res !== 'pendingRequest') {
                          resolve(res);
                        }
                      }).catch(async (err) => {
                        this.common.logger.error(err);
                        const errMsg = await this.omniCommon.checkLabelTrans('error_while_updating_app_key');
                        reject(errMsg);
                      });
                    } else {
                      resolve('noUpdate');
                    }
                    // GI: #DN: End 1349742 
                  } else {
                    const errMsg = await this.omniCommon.checkLabelTrans('error_while_updating_app_key');
                    reject(errMsg);
                  }
                }
              }
            } else {
              const errMsg = await this.omniCommon.checkLabelTrans('error_while_updating_app_key');
              reject(errMsg);
            }
        } else {
          const errMsg = await this.omniCommon.checkLabelTrans('error_while_updating_app_key');
          reject(errMsg);
        }
      } else {
        resolve('success');
      }
    });
  }

  private generateUniqueValue(length) {
    const array = new Uint8Array(length / 2);
    window.crypto.getRandomValues(array);
    return Array.from(array, byte => ('0' + byte.toString(16)).slice(-2)).join('');
  }

  private generateNonce(backendUniqueValue, frontendUniqueValue) {
    return CryptoJS.SHA256(backendUniqueValue + frontendUniqueValue).toString(CryptoJS.enc.Hex);
  }

  /**
   * GI: #1349742 - run returnVersionDetails in parallel with appflow update request
   * this method will check if we have an update in appflow
   */
  async checkAppflowforUpdate() {
    // Added by Richie to log the time of of the specific action
    if (PsApplicationSettings.MAIN_CONFIG.TIME_LOG_ENABLED) {
      PsCommonSettings.timeLogs.push('inside ps-splash-screen, before checkForUpdate() with start time: ' + (new Date()).toISOString());
    }
    return new Promise<string>(async (resolve, reject) => {
      const update = await this.deploy.checkForUpdate();
      // Added by Richie to log the time of of the specific action
      if (PsApplicationSettings.MAIN_CONFIG.TIME_LOG_ENABLED) {
        PsCommonSettings.timeLogs.push('inside ps-splash-screen, after checkForUpdate() with start time: ' + (new Date()).toISOString());
      }
      PsCommonSettings.appflowUpdateRequestWasSent = true;
      PsCommonSettings.appflowUpdateAvailable = update.available;
      this.commonUpdateChecking().then((res) => {
        resolve(res);
      }).catch((err) => {
        reject(err)
      });
    })
  }

  /**
   *  GI: #1349742 - run returnVersionDetails in parallel with appflow update request
   *  this method will check if we have an update in appflow || database update
   */
  async commonUpdateChecking() {
    return new Promise<string>(async (resolve, reject) => {
      if (PsCommonSettings.mandatoryUpdate == '1' || (PsCommonSettings.appflowUpdateRequestWasSent && PsCommonSettings.dbUpdateRequestWasSent)) {
        if (PsCommonSettings.dbUpdateAvailable && (PsCommonSettings.appflowUpdateAvailable || PsCommonSettings.mandatoryUpdate == '1')) {
          const msg = this.common.translate('store_update_required_key');
          // this.common.logger.warn(warningMsg);
          const alertOptions: IOptionsAlert = { title: 'update_key', message: msg, buttonsArray: [] };
          const updatetBtnOption: IOptionsAlertButton = {
            group: null,
            labelKey: 'update_key',
            handler: async () => {
              this.common.modalCtrl.dismiss();
              if (PsCommonSettings.mandatoryUpdate === '1') {
                // const packageName = await this.appVersion.getPackageName();
                // this.market.open(packageName);
                resolve('storeUpdate');
                return;
              } else {
                await this.performManualUpdate();
              }
              resolve('success');
            }
          };
          alertOptions.buttonsArray.push(updatetBtnOption);
          if ('0' === PsCommonSettings.mandatoryUpdate) {
            // alertOptions.message = this.common.translate('update_available_key');
            alertOptions.message = await this.omniCommon.checkLabelTrans('update_available_key');
            // add the cancel the button
            const cancelBtnOption: IOptionsAlertButton = {
              group: null,
              labelKey: 'cancel_key',
              handler: async () => {
                this.common.modalCtrl.dismiss();
                resolve('noUpdate');
              }
            };
            alertOptions.buttonsArray.push(cancelBtnOption);
          }
          CommonUtils.presentInfoAlert(null, alertOptions);
        } else {
          resolve('noUpdate');
        }
      } else {
        resolve('pendingRequest');
      }
    });
  }

  async performManualUpdate() {
    try {
      this.common.logger.log('Manual Update Started');
      this.loadingKey = 'updating_key';
      // Added by Richie to log the time of of the specific action
      if (PsApplicationSettings.MAIN_CONFIG.TIME_LOG_ENABLED) {
        PsCommonSettings.timeLogs.push('inside ps-splash-screen, before deploy.downloadUpdate with start time: ' + (new Date()).toISOString());
      }
      // end Richie
      await this.deploy.downloadUpdate((downProgress) => {
        if (this.loadingKey !== 'downloading_key') {
          this.loadingKey = 'downloading_key';
        }
        this.progress = downProgress;
      });
      // Added by Richie to log the time of of the specific action
      if (PsApplicationSettings.MAIN_CONFIG.TIME_LOG_ENABLED) {
        PsCommonSettings.timeLogs.push('inside ps-splash-screen, after deploy.downloadUpdate and before deploy.extractUpdate with end time: ' + (new Date()).toISOString());
      }
      // end Richie
      await this.deploy.extractUpdate((extractProgress) => {
        if (this.loadingKey !== 'extracting_key') {
          this.loadingKey = 'extracting_key';
        }
        this.progress = extractProgress;
      });
      // Added by Richie to log the time of of the specific action
      if (PsApplicationSettings.MAIN_CONFIG.TIME_LOG_ENABLED) {
        PsCommonSettings.timeLogs.push('inside ps-splash-screen, after deploy.extractUpdate and before deploy.reloadApp with end time: ' + (new Date()).toISOString());
      }
      // end Richie
      this.loadingKey = 'reloading_key';
      await this.deploy.reloadApp();
      this.common.logger.log('Manual Update Finished');
    } catch (err) {
      this.common.logger.error(err);
      this.loadingKey = 'Connection Error Occured During Update, Please Restart Your Application.';
      this.common.modalCtrl.dismiss();
    }
  }
  async performAutomaticUpdate(): Promise<string> {
    return new Promise<string>(async (resolve, reject) => {
      try {
        const currentVersion = await this.deploy.getCurrentVersion();
        // const config: IDeployConfig = {channel: 'Richie'};
        // await this.deploy.configure(config);
        this.common.logger.log('Automatic Update Started');
        this.loadingKey = 'updating_key';
        const resp = await this.deploy.sync({ updateMethod: 'auto' }, percentDone => {
          this.progress = percentDone;
        });
        if (!currentVersion || currentVersion.versionId !== resp.versionId) {
          // We found an update, and are in process of redirecting you since you put auto!
          this.common.logger.log('Automatic Update finished');
          resolve('success');
        } else {
          // No update available
          resolve('noUpdate');
        }
      } catch (err) {
        this.common.logger.error(err);
        reject('error');
      }
    });
  }
  private async returnMandatoryVer(integrityToken: string, frontendUniqueValue: string, udid: string, userAgent: any): Promise<any> {
    return new Promise<any>(async (resolve, reject) => {
      const versionParam = {
        compCode: PsApplicationSettings.MAIN_CONFIG.COMP_CODE,
        appId: PsApplicationSettings.MAIN_CONFIG.APP_ID,
        channelId: this.omniCommon.returnChnlID(),
        integrityToken,
        frontendUniqueValue,
        udid,
        userAgent
      };
      this.common.http.commonRequestAjax(PsCommonSettings.serviceUrl.returnVersionDetails, versionParam).then(res => {
        if (res && res.data) {
          // Added by Richie for #BUG 1287746
          let response = {};
          if (res.data.commonResponseList) {
            const commonResponseList = res.data.commonResponseList;
            const obj = {};
            obj[ConstantCommon.ANTI_TAMPERING_AES_KEY] = commonResponseList[ConstantCommon.ANTI_TAMPERING_AES_KEY];
            obj[ConstantCommon.ANTI_TAMPERING_AES_IV] = commonResponseList[ConstantCommon.ANTI_TAMPERING_AES_IV];
            response[ConstantCommon.ANTI_TAMPERING_OBJECT] = obj;

            response[ConstantCommon.SAFETY_NET_OBJ] = {};
            response[ConstantCommon.SAFETY_NET_OBJ][ConstantCommon.SAFETY_NET_NONCE] = commonResponseList[ConstantCommon.SAFETY_NET_NONCE];
            response[ConstantCommon.SAFETY_NET_OBJ][ConstantCommon.SAFETY_NET_API_KEY] = commonResponseList[ConstantCommon.SAFETY_NET_API_KEY];
          }
          if (res.data.gridModel && res.data.gridModel[0] && res.data.gridModel[0].webVersion) {
            response[ConstantCommon.APP_DB_VERSION] = res.data.gridModel[0].webVersion;
          }
          if (res.data.decodeIntegrityTokenResponse) {
            response[ConstantCommon.DECODE_INTEGRITY_TOKEN] = res.data.decodeIntegrityTokenResponse;
          }
          resolve(response);
          // End Richie
        } else {
          if (PsApplicationSettings.MAIN_CONFIG.LOG_LEVEL_ALL) {
            this.omniCommon.presentInfoAlert(`returnMandatoryVer() function return null`, { title: 'Tip' });
          }
          resolve(null);
        }
      }).catch((err) => {
        this.common.logger.error(err);
        reject(err);
      });
    });
  }

}
