import { Component, Input, OnInit } from '@angular/core';
import { IUpdateUserProfileRequest } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-interfaces';
import { OmniCommonService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-common.service';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { PsSelectDropdownComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-field-select/ps-select-dropdown/ps-select-dropdown.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IPsSelect } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { IOptionsPsDropdownTheme } from './ps-dropdown-theme.component.interfaces';


@Component({
  selector: 'ps-dropdown-theme',
  templateUrl: './ps-dropdown-theme.component.html'
})
export class PsDropdownThemeComponent extends PsSelectDropdownComponent implements OnInit {

  @Input() options: IOptionsPsDropdownTheme;
  defaultSelectOptions: IOptionsPsDropdownTheme = {
    listOfOptions: [],
  };
  allowedThemes;
  defaultThemeCode;
  temporaryThemeCode;
  prelogin: any;
  postlogin: any;
  bankDefaultThemeCode: any;
  constructor(commonProv: PsCommonService,
    private omniPull: OmniPullService, private omniCommon: OmniCommonService, loggerP: LoggerService,
    public navService: PsNavigatorService) {
    super(commonProv, loggerP);
  }

  ngOnInit() {
    this.commonProv.copyObject(this.defaultSelectOptions, this.options, true);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.defaultSelectOptions.fcName], 0);

    this.allowedThemes = this.common.session.getValueOf(ConstantCommon.preLoginResponse)[ConstantCommon.ALLOWED_THEMES];
    this.defaultSelectOptions.listOfOptions = [];
    if (this.allowedThemes) {
      this.allowedThemes.forEach((item) => {
        const theme: IPsSelect = {
          itemValue: item.themeCode,
          description: item.nameDesc,
          selectedObj: item
        };
        this.defaultSelectOptions.listOfOptions.push(theme);
      });

      this.prelogin = this.common.session.getValueOf(ConstantCommon.preLoginResponse);
      this.postlogin = this.common.session.getValueOf(ConstantCommon.USERINFO);
      this.bankDefaultThemeCode = this.allowedThemes.filter(b => b.themeId == this.prelogin.parameters[ConstantCommon.DEFAULT_THEME]?.parameterVal)[0]?.themeCode;
      this.defaultThemeCode = this.postlogin?.omniUserVO && this.postlogin?.omniUserVO[ConstantCommon.THEME_CODE] ? this.postlogin.omniUserVO[ConstantCommon.THEME_CODE] :
        this.bankDefaultThemeCode;
    }
  }
  temporaryThemeSwitching(selectedTheme) {
    if (selectedTheme.itemValue != this.defaultThemeCode) {
      this.defaultThemeCode = this.postlogin?.omniUserVO && this.postlogin?.omniUserVO[ConstantCommon.THEME_CODE] ? this.postlogin.omniUserVO[ConstantCommon.THEME_CODE] :
        this.bankDefaultThemeCode;
      this.temporaryThemeCode = selectedTheme.itemValue;
      this.applyChoosenTheme(selectedTheme.itemValue);
      this.confirmationPopup();
    }

  }

  applyChoosenTheme(selectedThemeCode) {
    this.common.applyChoosenTheme(selectedThemeCode,
      this.omniPull.omniCommon.session.getValueOf(ConstantCommon.preLoginResponse)[ConstantCommon.ALLOWED_THEMES])
  }
  async discardSelectedTheme() {
    this.applyChoosenTheme(this.defaultThemeCode);
    super.writeValue(this.defaultThemeCode);
  };

  async saveSelectedTheme() {
    this.defaultThemeCode = this.temporaryThemeCode;
    const paramData: IUpdateUserProfileRequest = {
      themeCode: this.temporaryThemeCode
    };
    const updateThemeResult = await this.omniPull.updateUserProfile(paramData).catch(error => {
      this.logger.error('Error! while updating user theme in PsDropdownTheme : ', error);
    });
    if (updateThemeResult) {
      localStorage.setItem('defaultTheme', this.temporaryThemeCode);
      let userInfo = this.common.session.getValueOf(ConstantCommon.USERINFO);
      userInfo.omniUserVO[ConstantCommon.THEME_CODE] = this.temporaryThemeCode;
      this.omniPull.omniCommon.session.append(ConstantCommon.USERINFO, userInfo);
      // once theme is updated we need to close any screen already opened as it could be affected by the selected theme 
      // and should be refreshed
      this.navService.navigateToMain();
    }
  };

  confirmationPopup() {
    return new Promise<any>((resolve, reject) => {
      {
        CommonUtils.presentInfoAlert(this.commonProv.translate('theme_switch_confirm_msg?_key'),
          {
            buttonsArray: [
              {
                group: this.options.group,
                type: 'reset',
                labelKey: 'cancel_key',
                handler: () => {
                  this.discardSelectedTheme();
                  CommonUtils.dismissAllModals();
                  reject(false);
                }
              },
              {
                group: this.options.group,
                labelKey: 'ok_key',
                type: 'submit',
                handler: () => {
                  this.saveSelectedTheme();
                  CommonUtils.dismissAllModals();
                }
              }
            ]
          }
        );
      }
    });
  }


}
