  protected setCustIconOptions() {
    this.required = ConstantCommon.DEFAULT_MANDATORY === 1 ? true : false;
    this.custIconOptions.fieldNameId = this.options.fcName ? this.options.fcName : this.id;
    this.custIconOptions.fieldNameDesc = this.options.labelKey ? this.options.labelKey : null;
    this.custIconOptions.availableCustomization.DEFAULT_VALUE = true;
    this.custIconOptions.availableCustomization.SERVICE_MAPPING = true;
    this.custIconOptions.formGroup = this.options.group;
    this.custIconOptions.rowIndex = this.options.rowIndex;// Added by Richie to handle indexed fields
    // Added by Richie for #TP 1456907
    if (this.options.availableBusinessCust) {
      this.busCustIconOptions.fieldNameId = this.custIconOptions.fieldNameId;
      this.busCustIconOptions.fieldNameDesc = this.custIconOptions.fieldNameDesc;
      this.busCustIconOptions.formGroup = this.custIconOptions.formGroup;
    }
    // End Richie
  }

  ngOnDestroy() {
    if (this.mandatorySubscription) {
      this.mandatorySubscription.unsubscribe();
    }
    if (this.maxLengthSubscription) {
      this.maxLengthSubscription.unsubscribe();
    }
    if (this.maxValueSubscription) {
      this.maxValueSubscription.unsubscribe();
    }
    if (this.minLengthSubscription) {
      this.minLengthSubscription.unsubscribe();
    }
    if (this.minValueSubscription) {
      this.minValueSubscription.unsubscribe();
    }
    if (this.minValueDateSubscription) {
      this.minValueDateSubscription.unsubscribe();
    }
    if (this.maxValueDateBehaviorSubject) {
      this.maxValueDateBehaviorSubject.unsubscribe();
    }
    if (this.labelKeySubscription) {
      this.labelKeySubscription.unsubscribe();
    }
    if (this.defaultValueSubscription) {
      this.defaultValueSubscription.unsubscribe();
    }
    if (this.patternSubscription) {
      this.patternSubscription.unsubscribe();
    }
    if (this.patternErrorSubscription) {
      this.patternErrorSubscription.unsubscribe();
    }
    if (this.placeholderSubscription) {
      this.placeholderSubscription.unsubscribe();
    }
    //Added by Richie for AZDB-71
    if (this.callServiceMapSubscription) {
      this.callServiceMapSubscription.unsubscribe();
    }
    //end Richie
    //Added by Richie for #BUG 1570554
    if (this.options?.group?.controls && this.options.group.controls[this.options.fcName] && this.options.group.controls[this.options.fcName][ConstantCommon.CONTROLLER_VALIDATION_SERVICE_ERR_HANDLER]) {
      this.options.group.controls[this.options.fcName][ConstantCommon.CONTROLLER_VALIDATION_SERVICE_ERR_HANDLER].unsubscribe();
    }
    //End Richie
  }
  // Added by Richie for #BUG 1340366; used in ag-grid to retrieve the value upon refreshing the cells
  public getValue(): any {
    const val = this.commonProv.valFromNestedObj(this.options.fcName, this.data);
    return val;
  }
