  /**
   * function used to call the on change event of components.
   *
   * @author ClarkRouhana
   * TP #1217612
   */
  dynCompInitBase(params: any, item?: any) {
    if (params.onPsChange !== undefined) {
      this.onPsChange.subscribe((res) => {
        res.rowIndex = item.rowIndex;
        res.rowNode = item;
        params.onPsChange(res);
      });
    }
  }

  /**
   * function used to switch any field component from preview mode to edit mode.
   *
   * @author ClarkRouhana
   * TP #1217612
   */
  get previewMode() {
    const forcePreview = this.options?.group?.controls[this.options.fcName] ? this.options?.group?.controls[this.options.fcName][ConstantCommon.CONTROLLER_FORCE_PREVIEW] : false;
    return super.previewMode || forcePreview;
  }
