import { AfterViewInit, Component, ComponentFactoryResolver, EventEmitter, Input, OnInit, Output, ViewChild, ViewContainerRef } from '@angular/core';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { PsBaseFieldComponent } from '../ps-base-field.component';
import { LoggerService } from './../../../../psServices/logger/logger.service';
import { ComponentOptions, IOptionsPsDynamicComponentLoaderExposed } from './../../../../psServices/models/ps-common-interface';
import { PsCommonService } from './../../../../psServices/ps-common/ps-common.service';
import { CommonUtils } from '../../../../psServices/models/common-utils';
import { CommonCustUtils } from '../../../../customization/psServices/util/common-cust-utils';

/**
 * @author Aftab.Ali
 * @since 31/12/2019
 *
 * <p> PsDynamicComponentLoaderComponent </p>
 */
@Component({
  selector: 'ps-dynamic-component-loader',
  templateUrl: './ps-dynamic-component-loader.component.html',
  styleUrls: ['./ps-dynamic-component-loader.component.scss'],
})
export class PsDynamicComponentLoaderComponent extends PsBaseFieldComponent implements OnInit, AfterViewInit {

  @Input() options: IOptionsPsDynamicComponentLoaderExposed = {};
  @ViewChild('dynamicComponent', { static: true, read: ViewContainerRef }) dynamicComponentRef;
  // eslint-disable-next-line @angular-eslint/no-output-on-prefix
  @Output() public onClickCard = new EventEmitter<any>();
  @Output() public onClick = new EventEmitter<any>();
  @Output() public onPsCompChange = new EventEmitter<any>();
  @Output() public compLoaded = new EventEmitter<any>();
  @Output() public compInit = new EventEmitter<any>();
  loaderHost: any;
  refer: any;

  constructor(
    commonService: PsCommonService,
    logger: LoggerService,
    private componentFactoryResolver?: ComponentFactoryResolver
  ) {
    super(commonService, logger);
  }

  async ngOnInit() {
    super.ngOnInit();



    //Added by Mustafa for #BUG 1570520, used for dynamic labels
    const componentValue = this.commonProv.valFromNestedObj(this.options.componentOptions?.fcName, this.options.componentOptions?.group.controls['formData'].value);

    // AZDB-4958 - in case the component is not in preview mode the flag for pending status should not be set so that the value will be taken after user set the value in the field
    if (this.options.componentOptions?.forcePreviewMode) {
      this.options.componentOptions.pendingStatus = componentValue ? false : true;
    }
    const isVisible = this.commonProv.getElementValidations(this.options.componentOptions?.fcName?.slice(0, this.options.componentOptions.fcName.lastIndexOf('-')));
    if (this.options.componentOptions?.shortcutLabel?.type === ConstantCommon.CARD_LABEL_TYPE
      && this.options.mappingId
      && !componentValue
      && isVisible.IS_VISIBLE !== 0) {
      const formData = {};
      CommonUtils.copyObject(formData, this.options.group.controls.formData.value);
      CommonUtils.removeIndexedAttributes(formData, this.options.itemCard.rowIndex);
      const customizationRequest: any = {
        mappingId: this.options.mappingId,
        commonParametersList: { executePWSServiceFlag: '1', ...this.options.itemCard, ...formData }
      };
      CommonCustUtils.dynamicLabels(this.common, customizationRequest).then((result: any) => {
        if (result && result.data) {
          const value = result.data[this.options.componentOptions.fcName.slice(0, this.options.componentOptions.fcName.lastIndexOf('-'))];
          this.options.componentOptions.pendingStatus = false;
          this.setValueToFormData(this.options.componentOptions.fcName, value);
        } else if (result && result.errorMsg) {
          this.options.componentOptions.pendingStatus = false;
          this.errorDynamicLabelValue(result.errorMsg);
          this.logger.error('Error ! while calling common service for dynamic labels ', result.errorMsg);
        }
      }, (error) => {
        this.options.componentOptions.pendingStatus = false;
        this.errorDynamicLabelValue(error);
        this.logger.error('Error ! while calling common service for dynamic labels ', error);
      });
    }
    //End Mustafa
  }

  ngAfterViewInit() {
    if (this.options && this.options.component) {
      const factory = this.componentFactoryResolver.resolveComponentFactory(this.options.component);
      const ref = this.dynamicComponentRef.createComponent(factory);
      if (this.options.multiOutlet ? this.options.multiOutlet : false) {
        this.multiOutletOptions(ref.instance);
      } else {
        if (this.options.componentOptions !== undefined) {
          (ref.instance as ComponentOptions).options = this.options.componentOptions;
        } else {
          (ref.instance as ComponentOptions).options = this.options;
        }
        ref.instance.id = this.id + '_dynCompntLoader';
        ref.instance.onClickCard = this.onClickCard;
        ref.instance.onClick = this.onClick;
        ref.instance.onPsChange = this.onPsChange;
        ref.instance.onPsInit = this.compInit;
      }
      ref.changeDetectorRef.detectChanges();
      this.compLoaded.emit(true);
    }
  }

  errorDynamicLabelValue(msg) {
    CommonUtils.presentInfoAlert(msg,
      {
        buttonsArray: [
          {
            group: this.options.group,
            type: 'submit',
            labelKey: 'ok_key',
            handler: () => {
              CommonUtils.dismissAllModals();
            }
          }
        ]
      }
    );
  }

  setValueToFormData(fcName, value) {
    value = value !== undefined ? value : '-';
    this.options.group.controls[fcName]?.setValue(value, { emitEvent: false });
    this.commonProv.setValInsideNestedObj(fcName, value, this.options.group.controls['formData']?.value);
  }

  multiOutletOptions(instance) {
    if (this.options.multiOutletOptions.outletType === ConstantCommon.OUTLET_TYPE_COMPONENT) {
      this.options.group.addControl(this.options.multiOutletOptions.segmentName, instance.visaFormGroup);
    } else {
      instance.stepperOptions = {
        ...this.options.multiOutletOptions.stepperOptions,
        ...instance.stepperOptions
      };
      instance.fromMultiLoader = this.options.multiOutletOptions.fromMultiLoader;
      instance.nav.navParam = this.options.multiOutletOptions.navParams;
      this.options.group.addControl(this.options.multiOutletOptions.segmentName, instance.stepperOptions.group);
    }
    this.refer = instance;
  }

  preCallFunction(params?) {
    params = {};
    if (this.options.group.contains(this.options.multiOutletOptions.segmentName)) {
      if (this.options.multiOutletOptions.outletType === ConstantCommon.OUTLET_TYPE_PAGE) {
        if (this.refer.stepperOptions.submitOptions.preCallFunction != null) {
          this.refer.stepperOptions.submitOptions.preCallFunction.func(params);
        }
      }
    }
  }

}
