import { Component, Input, OnInit } from '@angular/core';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IOptionsPsDateTimeOnly, IOptionsPsKeyinDate } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { PsKeyinDateComponent } from '../ps-keyin-date.component';
import { Events } from 'src/app/commonSRC/psServices/Event/event.service';

@Component({
  selector: 'ps-date-time-only',
  templateUrl: './ps-date-time-only.component.html',
  styleUrls: ['./ps-date-time-only.component.scss'],
})
export class PsDateTimeOnlyComponent extends PsKeyinDateComponent implements OnInit {
  @Input() public options: IOptionsPsDateTimeOnly;
  dateOptions: IOptionsPsKeyinDate = {
    isTimeComponent: true,
    setValAsFormat: true,
  };

  constructor(public commonProv: PsCommonService, loggerP: LoggerService, events: Events) {
    super(commonProv, loggerP, events);
  }

  ngOnInit() {

    this.dateOptions.mode = this.options.mode ? this.options.mode : ConstantCommon.DEFAULT_12_HOUR_FORMAT;

    if (this.dateOptions.mode === ConstantCommon.DEFAULT_12_HOUR_FORMAT) {
      this.dateOptions.displayFormat = ConstantCommon.DEFAULT_12_HOUR_DISPLAY_FORMAT,
        this.dateOptions.pickerFormat = this.dateOptions.displayFormat;
    }
    if (this.dateOptions.mode === ConstantCommon.DEFAULT_24_HOUR_FORMAT) {
      this.dateOptions.displayFormat = ConstantCommon.DEFAULT_24_HOUR_DISPLAY_FORMAT,
        this.dateOptions.pickerFormat = this.dateOptions.displayFormat;
    }
    this.commonProv.copyObject(this.dateOptions, this.options, false);

  }

  dateChanged(value) {
    this.onPsChange.emit(value);
  }

}
