import { Component, Input, OnInit } from '@angular/core';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { PsLabelComponent } from './../../../../commonSRC/psComponents/ps-base/ps-base-field/ps-field-label/ps-label/ps-label.component';
import { ConstantCommon } from './../../../../commonSRC/psServices/models/common-constant';
import { IOptionsPsLabel } from './../../../../commonSRC/psServices/models/ps-common-interface';
import { OmniCommonService } from './../../../psServices/omni-common/omni-common.service';
import { IOptionsPsLabelWelcomeExposed } from './ps-label-welcome.component.interfaces';
import { TranslateService } from '@ngx-translate/core';
import { ILoginResponse } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-interfaces';



@Component({
  selector: 'ps-label-welcome',
  templateUrl: './ps-label-welcome.component.html',
  styleUrls: ['./ps-label-welcome.component.scss']
})
export class PsLabelWelcomeComponent extends PsLabelComponent implements OnInit {
  @Input() options: IOptionsPsLabelWelcomeExposed;
  nickname = '';
  userinfo;
  userInformation: ILoginResponse;
  
  labelOptions: IOptionsPsLabel = {
    labelKey: 'welcome_key' // set the key for translation.
  };
  userWelcomeOptions: IOptionsPsLabel = {
    labelKey: 'welcome_key'
  };
   cifNameOptions: IOptionsPsLabel = {
    fcName: 'cifName'
  };
  constructor(
    common: PsCommonService,
    public logger: LoggerService,
    public omniCommon: OmniCommonService,
    private translate: TranslateService
  ) {
    super(common, logger);
  }

  ngOnInit() {
    this.userinfo = this.commonProv?.session?.getValueOf(ConstantCommon.USERINFO)
    this.userInformation = this.common.session.getValueOf(ConstantCommon.USERINFO)
    this.commonProv.copyObject(this.options, this.userLabelOptions, false, true);
    this.commonProv.copyObject(this.options, this.labelOptions, false, true);
    try {
      this.labelOptions.labelKey = this.omniCommon.common.translate(this.labelOptions.labelKey);
    } catch (e) {
      this.omniCommon.common.logger.error(e);
    }
  }
  //Added by ghada, tp1351022 for fixng the issue of nickname update is not reflected under welcome section
  get userLabelOptions(): IOptionsPsLabel {

    if (this.userinfo) {
      //AZDB-4547 Makki - Fix new users blank screen after logging in      
      try {
        this.nickname = this.commonProv?.session?.getValueOf(CommonBussinessConstant.USER_NICKNAME) || this.userinfo.personalizationCO?.nickname || (this.translate.currentLang.toLocaleLowerCase() == 'ar' ? this.userinfo.customerInfoCO?.shortNameArabic : this.userinfo.customerInfoCO?.shortNameEnglish);
        //AZDB-4752 Saeed- fix for AGIBSI240157 - 192300 - Welcome message is showing as corporate name instead of name for corporate user
        if(PsCommonBusinessSettings.isAgent || PsCommonBusinessSettings.isCorporate)//not retail
        {
          // AZDB-7291 MEGHA- Show correct AR/EN name for agent/corporate users instead of default EN.
          this.nickname = this.translate.currentLang.toLowerCase() === 'ar' ? this.userinfo.omniUserAppVO?.ARABIC_NAME || this.nickname : this.userinfo.omniUserAppVO?.NAME || this.nickname;
          this.cifNameOptions.labelKey = this.userInformation.customerInfoCO?.shortName;

        }
      }
      catch (error) {
        this.logger.error(error);
      }

    }
    return {
      translate: false,
      labelKey: this.nickname
    }
  };
}
