import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { PsTemplateBasePage } from '../ps-template-base/ps-template-base.page';
import { IOptionsPsInputSearchHTMLExposed } from '../../psServices/models/ps-common-interface';


@Component({
  selector: 'ps-template-list',
  templateUrl: './ps-template-list.template.html'
})

/// Not a page template, just used as a common wrapper and a base class for any list of container card components 

export class PsTemplateListPage extends PsTemplateBasePage implements OnInit {
  @ViewChild('templateList', { read: ElementRef, static: false }) templateList: ElementRef;
  searchOptions: IOptionsPsInputSearchHTMLExposed = {
    contentReference: 'container-lookup-option',
    selectors:"ion-label,span:not(:has(ps-label,ion-label),.highlight)",
    actionType:"filter"
  };
  constructor() {
    super();
  }
  ngOnInit() {
    this.init();
  }
  init() {
    super.init();
  }
  ngAfterViewInit() {
    this.searchOptions.templateRef = this.templateList;
  }


}
