import { Injectable } from '@angular/core';
import { AES256 } from '@awesome-cordova-plugins/aes-256/ngx';
import * as forge from 'node-forge';
import { pki } from 'node-forge';
import { LoggerService } from '../logger/logger.service';
import { CommonUtils } from '../models/common-utils';
import {
  IGenKeyPair,

  IKeyGeneratorResult, IPublicPrivateKey
} from '../models/hybridkey-interface';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';



@Injectable({
  providedIn: 'root'
})
export class HybridKeyService {

  constructor(private logger: LoggerService, private aes256: AES256) {
  }

  private x_k01V_Y = '';
  private z_i02_vA = '';
  /** Generate Keys ********************************************************************************************************/

  public genkeypair(): Promise<IGenKeyPair> {
    const rsa = forge.pki.rsa;
    const d: Date = new Date();
    return new Promise<IGenKeyPair>((resolve, reject) => {
      rsa.generateKeyPair({ bits: 1024, e: 0x10001, workers: -1 }, (err, keypair) => {
        // keypair.privateKey, keypair.publicKey
        if (!!err) {
          this.logger.error(err);
        }
        resolve({ k: keypair, d1: d, d2: new Date() });
      });
    });
  }

  public returnReadableKeys(publicKey, privateKey): IPublicPrivateKey {
    const publicK = forge.pki.publicKeyToPem(publicKey);
    const privateK = forge.pki.privateKeyToPem(privateKey);
    const returnedPair: IPublicPrivateKey =
    {
      publicKey: publicK,
      privateKey: privateK
    }
    return returnedPair;
  }

  private jh34djao_1u5pqml() {
    if (this.x_k01V_Y == '' || this.z_i02_vA == '' || this.x_k01V_Y == undefined || this.z_i02_vA == undefined) {
      if (!CommonUtils.isNativeMobile() && (ConstantCommon.X_k01V_Y != undefined || ConstantCommon.Z_i02_vA != undefined)) {
        this.x_k01V_Y = ConstantCommon.X_k01V_Y;
        this.z_i02_vA = ConstantCommon.Z_i02_vA;
      } else {
        const commonService: PsCommonService = CommonUtils.injectionHandler(PsCommonService);
        if (commonService && commonService.session) {
          const aP201VYx = commonService.session.getValueOf(ConstantCommon.x_k01V_Y);
          const hLn67B0j = commonService.session.getValueOf(ConstantCommon.z_i02_vA);
          if (aP201VYx != undefined && hLn67B0j != undefined) {
            this.x_k01V_Y = aP201VYx;
            this.z_i02_vA = hLn67B0j;
          }
        }
      }
    }
  }
  /** Encryption methods ********************************************************************************************************/

  public encryptMessage(message, pKey): IKeyGeneratorResult {

    const key = this.randomString(16);/* forge.random.getBytesSync(16) */;
    const iv = this.randomString(16);
    const result: IKeyGeneratorResult = {};
    const encryptedKey = this.encryptKey(key, pKey);
    const encryptedIv = iv;

    const cipher = forge.cipher.createCipher('AES-CBC', new forge.util.ByteStringBuffer(key));
    cipher.start({
      iv: iv
    });
    cipher.update(forge.util.createBuffer(JSON.stringify(message), 'utf8'));
    cipher.finish();

    const encrypted = forge.util.encode64(cipher.output.getBytes());
    result.key = encryptedKey;
    result.message = encrypted;
    result.iv = encryptedIv;

    return result;
  }


  public encryptKey(key, pKey): string {

    if (pKey == null || typeof pKey === 'undefined') {
      this.logger.error('public key was empty in sesion')
    }
    const pubKey = forge.pki.publicKeyFromPem(pKey) as pki.rsa.PublicKey;
    const buffer = forge.util.createBuffer(key, 'raw');
    const bytes = buffer.getBytes();
    const encrypted = pubKey.encrypt(bytes, 'RSA-OAEP', {
      md: forge.md.sha256.create(),
      mgf1: {
        md: forge.md.sha1.create()
      }
    });
    const b64Encoded = forge.util.encode64(encrypted);
    return b64Encoded;
  }


  public encryptTransportMessage(messageObj, encodeB64 = false) {
    this.jh34djao_1u5pqml();
    const key = forge.util.decode64(this.x_k01V_Y);
    const iv = forge.util.decode64(this.z_i02_vA);
    let messageString = JSON.stringify(messageObj);
    // Added by Nabil/Richie for #BUG 1281686 in order to maintain the old behavior for the encrypt/decrypt of the session variables
    if (encodeB64) {
      messageString = btoa(messageString);
    }
    // End Nabil/Richie
    const cipher = forge.cipher.createCipher('AES-CBC', new forge.util.ByteStringBuffer(key));
    cipher.start({
      iv: iv
    });
    cipher.update(forge.util.createBuffer(JSON.stringify(messageString), 'utf8'));
    cipher.finish();

    const encrypted = forge.util.encode64(cipher.output.getBytes());

    return { pathParam: encrypted };

  }

  /** Decryption methods ********************************************************************************************************/

  public decryptKey(keyToBeDecrypted, pKey) {
    let decrypted;
    const privateKey = forge.pki.privateKeyFromPem(pKey) as pki.rsa.PrivateKey;
    decrypted = privateKey.decrypt(forge.util.decode64(keyToBeDecrypted), 'RSA-OAEP', {
      md: forge.md.sha256.create(),
      mgf1: {
        md: forge.md.sha256.create()
      }
    });
    return decrypted;
  }


  async nativeDecrypt(encryptedMessage, pKey?) {
    if (CommonUtils.isNativeMobile()) {
      this.jh34djao_1u5pqml();
      let key = forge.util.decode64(this.x_k01V_Y);
      let iv = forge.util.decode64(this.z_i02_vA);
      let message = encryptedMessage;
      if (encryptedMessage.key && pKey) {
        key = encryptedMessage.key;
        iv = encryptedMessage.iv;
        key = this.decryptKey(key, pKey);
        message = encryptedMessage.pathParam;
      }
      let aes256Res: any = await this.aes256.decrypt(key, iv, message).catch((error: any) => alert(error));
      return aes256Res;
    }
  }

  public decryptMessage(key: string, iv: string, encryptedMessage: string): string {

    let output: string;
    try {
      const decipher = forge.cipher.createDecipher('AES-CBC', key);
      decipher.start({ iv: iv });
      decipher.update(forge.util.createBuffer(forge.util.decode64(encryptedMessage), 'raw'));
      const success = decipher.finish();
      if (success) {
        output = decipher.output.getBytes();
      } else {
        output = '';
        throw new Error('message decryptions failed ! ');
      }
    } catch (error) {
      this.logger.error(error);
    }
    const decryptedMessage = forge.util.decodeUtf8(output);
    //this.logger.log(' HybridKey decryptedMessage ' + decryptedMessage);
    return decryptedMessage;
  }


  decryptResponse(retData, pKey): string {
    let decryptedMessage: string = '';
    const key = retData.key;
    const iv = retData.iv;
    const message = retData.pathParam;
    const decryptedKey = this.decryptKey(key, pKey);
    const decryptedIv = iv;
    decryptedMessage = this.decryptMessage(decryptedKey, decryptedIv, message);
    decryptedMessage = JSON.parse(decryptedMessage);
    return decryptedMessage;
  }

  public decryptTransportMessage(encryptedMessage) {
    this.jh34djao_1u5pqml();
    const key = forge.util.decode64(this.x_k01V_Y);
    const iv = forge.util.decode64(this.z_i02_vA);
    return this.decryptMessage(key, iv, encryptedMessage);
  }

  /** Utility methods ********************************************************************************************************/

  public randomString(length) {
    const chars = '0123456789abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ!@#$%^&*';
    let result = '';
    for (let i = length; i > 0; --i) {
      result += chars[Math.floor(Math.random() * chars.length)]
    }
    return result;
  }

  public hashcode(s) {
    var h = 0, l = s.length, i = 0;
    if (l > 0)
      while (i < l)
        h = (h << 5) - h + s.charCodeAt(i++) | 0;
    return h;
  }

  public encryptSessionMessage(iv: string, key: string, messageObj: any) {
    let messageString = JSON.stringify(messageObj);
    const cipher = forge.cipher.createCipher('AES-CBC', new forge.util.ByteStringBuffer(key));
    cipher.start({
      iv: iv
    });
    cipher.update(forge.util.createBuffer(JSON.stringify(messageString), 'utf8'));
    cipher.finish();

    const encrypted = forge.util.encode64(cipher.output.getBytes());
    return encrypted;
  }

  public decryptSessionMessage(iv: string, key: string, encryptedMessage: string) {
    let decryptedMessage = this.decryptMessage(key, iv, encryptedMessage);
    // remove "\\" and \\"" from the beginning and the end, for example '"\\"IndlYl8xOWQ3OTMwYWEyZmZkMmQyYzBhODUxMzExZDBmNzAwN182OTVjNDIwN2Y5ZDg4MGNhNGM4ZmJmYTJkOTE0MjdjZmM3NWE0MDAxNTIzOTg0YjUxZTQ5MGQwMGQ5NzU2NTFlIg==\\""'
    decryptedMessage = decryptedMessage.replace(/(\\"")$/, '');
    decryptedMessage = decryptedMessage.replace(/^("\\")/, '');
    return decryptedMessage;
  }
}
