import { Component, ElementRef, EventEmitter, Input, OnInit, Output, ViewChild, AfterViewInit } from '@angular/core';
import { PsBaseFieldComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-base-field.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { PsApplicationSettings } from 'src/app/commonSRC/psServices/models/ps-app-settings';
import { IOptionsPsKeyinInput } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { CommonUtils } from './../../../commonSRC/psServices/models/common-utils';
import { PsCommonService } from './../../../commonSRC/psServices/ps-common/ps-common.service';
import { IPsCaptchaOptions } from './ps-captcha.component.interface';


@Component({
  selector: 'ps-captcha',
  templateUrl: './ps-captcha.component.html',
  styleUrls: ['./ps-captcha.component.scss'],
})
export class PsCaptchaComponent extends PsBaseFieldComponent implements OnInit, AfterViewInit {
  @Input() options: IPsCaptchaOptions;
  @Output() onCaptchaResolved = new EventEmitter<any>();
  siteKey: string;
  checkCaptcha = false;
  @ViewChild('recaptcha', {read: ElementRef}) recaptchaElement: ElementRef;
  constructor(private commonService: PsCommonService,  public loggerP: LoggerService) { 
    super(commonService, loggerP);
    
  }

  ngOnInit() {  
    super.ngOnInit();
   
    this.custIconOptions.component = PsCaptchaComponent;
    this.custIconOptions.componentOptions = this.options;
    this.custIconOptions.availableCustomization.KEY_LABEL_ID = false;
    this.custIconOptions.availableCustomization.IS_MANDATORY = true;
    this.custIconOptions.availableCustomization.SERVICE_MAPPING = false;
    this.custIconOptions.availableCustomization.DEFAULT_VALUE = false;
    this.custIconOptions.availableCustomization.IS_READONLY = false;
    this.siteKey = this.commonService.isNativeMobile() ? PsApplicationSettings.GOOGLE_API_KEYS.RECAPTCHA_NATIVE_KEY : PsApplicationSettings.GOOGLE_API_KEYS.RECAPTCHA_KEY;
  }
  ngAfterViewInit(){
    this.addRecaptchaScript();
  }

  renderReCaptch() {
    const captchaFieldCus = this.commonProv.getElementValidations(this.options.fcName);
    if(captchaFieldCus.IS_VISIBLE !== 0){//15052023 added by Salah to only render the captcha if no hidden customization is set by the admin 
      window.grecaptcha.render(this.recaptchaElement.nativeElement, {
        sitekey: this.siteKey,
        callback: (response) => {
          this.onResolve(response);
        },
      });
    }
  }
  addRecaptchaScript() {
    window.grecaptchaCallback = () => {
      this.renderReCaptch();
      this.checkCaptcha = true;
    };
    CommonUtils.addJSScript('https://www.google.com/recaptcha/api.js?hl=' + PsCommonSettings.activeLanguge.toLowerCase() + '&onload=grecaptchaCallback&amp;render=explicit',
      {
        id: 'recaptcha-jssdk',
        async: true,
        checkSRCIfPresent: true,
        removeIDIfPresent: false
      }
    );
  }

  checkCaptchaFunction() {
    const selector = document.getElementsByClassName('g-recaptcha-bubble-arrow');
    if (selector && selector.length > 0) {
      selector[0].parentElement.classList.add('ps-custom-captcha');
      this.checkCaptcha = false;
    }
  }

  onResolve(data) {
    if(data !== undefined && data !== null){
      this.options.group.get(this.options.fcName).setValue(true);
      this.onCaptchaResolved.emit(data);
    }else{
      this.options.group.get(this.options.fcName).setValue(null);      
    }
  }
}
