import { Component, ElementRef, EventEmitter, HostBinding, Input, OnInit, Output, ViewChild } from '@angular/core';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { PsBaseFieldComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-base-field.component';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { PsApplicationSettings } from 'src/app/commonSRC/psServices/models/ps-app-settings';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';

import { ConstantCommon } from '../../../../commonSRC/psServices/models/common-constant';
import { IOptionsPsLabel, IPageCommon } from '../../../../commonSRC/psServices/models/ps-common-interface';
import { ILoginResponse, IbusinessProfilesCO } from '../../../../pages/omni-login/omni-login.interfaces';
import { IOptionsPsMenuExposed, IPageBussiness } from './ps-complex-menu.component.interfaces';





/*
 * @author Heba.Hassan
 * @since 17/11/2019
 *
 * ps-complex-menu - .
 */
@Component({
    selector: 'ps-complex-menu',
    templateUrl: './ps-complex-menu.component.html',
    styleUrls: ['./ps-complex-menu.component.scss'],
})


export class PsComplexMenuComponent extends PsBaseFieldComponent implements OnInit {

    @Output("parentFun") parentFun: EventEmitter<any> = new EventEmitter();
    mainMenuPages: IPageCommon[];
    quickLinksMenuPages: IPageCommon[];
    pagesChild: IPageCommon[];
    userInformation: ILoginResponse;
    openedPage: IPageBussiness;
    businessProfileQuicklinkMap: any[] = [];
    headerLabelOptions: IOptionsPsLabel = {};
    @Input() public options: IOptionsPsMenuExposed = {};

    quickLinksList: IbusinessProfilesCO[] = [];
    isSubMenu: boolean = false;
    loinResponse: IbusinessProfilesCO[];
    allMenu: Array<IPageCommon> = [];
    @HostBinding('class') class = this.isTreeView ? 'is-tree-view' : 'is-widget-view';
    @ViewChild('widgetMenu', { read: ElementRef, static: false }) widgetMenu: ElementRef;
    levelOfWigets = 0;
    get isTreeView(): boolean {
        return PsApplicationSettings.CLIENT_ASSETS_CONFIG.UI_CONFIGURATION.MENU_TREE != null ?
            PsApplicationSettings.CLIENT_ASSETS_CONFIG.UI_CONFIGURATION.MENU_TREE : (this.commonProv.isWeb() || this.commonProv.isWebLayout()) ? true : false;
        ;
    }
    constructor(public commonProv: PsCommonService, private navService: PsNavigatorService,
        public omniPull: OmniPullService,) {
        super(commonProv, commonProv.logger);
    }


    prepareMenu(list: IbusinessProfilesCO[], pages: IPageCommon[], hasParents: boolean = false, onlyAppend?: boolean) {
        if (this.userInformation && this.userInformation.businessProfiles.length > 0) {
            if (this.options.parentMenuOperId > 0 && hasParents == false) {
                const parent = list.filter(item => item.operVO.OPER_ID === this.options.parentMenuOperId).shift();
                list = parent.children;
            }
            for (let i = 0; i < list.length; i++) {
                const row = list[i];
                if (!onlyAppend && this.canAddOper(row)) {
                    let page: IPageCommon;
                    page = {
                        title: row.operVO.OPER_NAME ? row.operVO.OPER_NAME : row.operVO.DESCRIPTION,
                        icon: row.operAppChnlVO.ICON_NAME,
                        fallBackIcon: row.operAppChnlVO.FALL_BACK_ICON_NAME,
                        component: row.operAppChnlVO.PAGE_HREF != null ? row.operAppChnlVO.PAGE_HREF : ConstantCommon.HASH_INPUT,
                        children: [],
                        hasParent: hasParents,
                        operID: row.operVO.OPER_ID,
                        collapse: true,
                        hide: true,
                        param: row.operAppChnlVO.PARAM !== undefined ? JSON.parse(row.operAppChnlVO.PARAM) : row.operAppChnlVO.PARAM,
                        isMaker: row.businessProfileOperVO ? (row.businessProfileOperVO.MAKER_YN == 'Y' ? true : false) : false,
                        isChecker: row.businessProfileOperVO ? (row.businessProfileOperVO.CHECKER_YN == 'Y' ? true : false) : false
                    };
                    if (row.children && row.children.length > 0) {
                        page.isChildLeaf = true;
                        row.children.forEach(element => {
                            if (this.canAddOper(element)) {
                                page.isParent = true
                                delete page.isChildLeaf;
                            }
                        });
                    } else {
                        page.isChildLeaf = true;
                    }

                    //Commented by ghada, as such check will prevent the acknowledgement of data issue such having duplicate operations in the menu
                    // const isDuplicateOperation = this.hasDuplicateOperation(this.pages, page.operID);
                    // if (!isDuplicateOperation) {
                    //     pages.push(page);
                    //     this.allMenu.push(page);
                    // }

                    pages.push(page);
                    this.allMenu.push(page);

                    // check if menu has childs build recursively the sub-menus
                    if (row.children != null) {
                        let index = (pages.length);
                        index = index - 1;
                        this.pagesChild = pages[index].children;
                        this.prepareMenu(row.children, this.pagesChild, true);
                    }
                    // push the final children menu built recursively to the main menu list
                    let index = this.pages.length;
                    index = index - 1;
                    this.pages.concat(pages);
                } else {
                    if (row.children != null) {
                        this.prepareMenu(row.children, this.pagesChild, true, true);
                    }
                }
            }
        }
    }

    // private hasDuplicateOperation(pages: IPageCommon[], operID: any): boolean {
    //     if (!pages) {
    //         return false;
    //     }
    //     for (const page of pages) {
    //         if (page.operID === operID) {
    //             return true;
    //         }
    //         if (page.children && this.hasDuplicateOperation(page.children, operID)) {
    //             return true;
    //         }
    //     }
    //     return false;
    // }

    canAddOper(page) {
        return (page.operVO.BUSINESS_PROFILE_YN !== 'N' && page.operAppChnlVO.CATEG == CommonBussinessConstant.MENU_OPER)
    }

    getQuickLinks(businessProfileList: IbusinessProfilesCO[]) {
        this.quickLinksList = this.commonProv.session.getValueOf(CommonBussinessConstant.QUICK_LINKS_MENUS);
        if (this.quickLinksList && this.quickLinksList.length > 0) {
            //fetching the quicklinkoperation deatils from businessProfileList filtering based on oper_id
            this.quickLinksList = this.quickLinksList && this.quickLinksList.length > 0 ? this.quickLinksList : this.userInformation.quickLinks;
        } else {
            //fetching the default quicklinkoperation defined in ps-config.json
            this.quickLinksList = PsApplicationSettings.MAIN_CONFIG.DEFAULT_QUICK_LINKS_MENU ? PsApplicationSettings.MAIN_CONFIG.DEFAULT_QUICK_LINKS_MENU : [];
        }
        this.businessProfileQuicklinkMap = this.quickLinksList;
        let quicklinkArrayList: IbusinessProfilesCO[] = [];
        if (this.businessProfileQuicklinkMap && this.businessProfileQuicklinkMap.length > 0) {
            for (let i = 0; i < this.businessProfileQuicklinkMap.length; i++) {
                const row = this.businessProfileQuicklinkMap[i];
                let result;
                if (row.operId) {
                    result = businessProfileList.find((obj) => obj.operVO.OPER_ID === row.operId);
                }
                else {
                    result = businessProfileList.find((obj) => obj.operVO.OPER_ID === row);
                }
                if (result !== undefined) {
                    result.operAppChnlVO.FALL_BACK_ICON_NAME = result.operAppChnlVO.ICON_NAME;
                    result.operAppChnlVO.ICON_NAME = 'quick_menu_' + result.operAppChnlVO.ICON_NAME;
                    quicklinkArrayList.push(result);
                }
            }
        }
        return quicklinkArrayList;
    }
    get pages() {
        if (this.options.quicklinkMenu) {
            return this.omniPull.omniCommon.quickLinksMenuPages;
        }
        return this.mainMenuPages;
    }

    ngOnInit() {
        this.userInformation = this.commonProv.session.getValueOf(ConstantCommon.USERINFO);
        if (this.userInformation && this.userInformation.businessProfiles.length > 0) {

            if (this.options && this.options.quicklinkMenu) {

                let quickLinks = this.getQuickLinks(this.userInformation.businessProfiles);
                if (!this.omniPull.omniCommon.quickLinksMenuPages) {
                    this.omniPull.omniCommon.quickLinksMenuPages = [];
                }
                this.prepareMenu(quickLinks, this.omniPull.omniCommon.quickLinksMenuPages);
            }
            else {
                if (!this.mainMenuPages) {
                    this.mainMenuPages = [];
                }
                const businessProfileMap = CommonUtils.prepareBusinessProfileMap(this.userInformation.businessProfiles, []);
                this.commonProv.session.append(ConstantCommon.BUSINESS_PROFILE_MAP, businessProfileMap);
                this.prepareMenu(this.userInformation.businessProfiles, this.mainMenuPages);
            }
            this.pages.forEach((ipage) => {
                ipage.isRoot = true;
                ipage.isParent = false;
                ipage.hide = false;
            });
        }
        if (!this.options.quicklinkMenu) {
            this.headerLabelOptions.labelKey = 'full_menu_key';
        }
        else {
            this.headerLabelOptions.labelKey = 'my_quick_links_key';
        }
    }


    prepareSelected(page: IPageBussiness, ipage: IPageCommon) {
        if ((page.isRoot || page.isParent) && (page.operID === ipage.operID)) {
            page.isSelected = !page.isSelected;
        } else if ((page.isChildLeaf || page.isParent) && (page.operID === ipage.operID || ((ipage.isParent || ipage.isRoot) && page.parent && page.parent.operID === ipage.operID))) {
            ipage.isSelected = true;
        } else {
            ipage.isSelected = false;
        }
        if (ipage.isParent || ipage.isRoot) {
            ipage.children.forEach((child) => {
                this.prepareSelected(page, child);
            });
        }

    }

    expandOrCollapseTree(event: Event, page: IPageBussiness) {

        this.pages.forEach((iPage) => {
            this.prepareSelected(page, iPage);
        });

        this.openedPage = page;
        if (page.children && page.children.length > 0) {
            this.allMenu.forEach((iPage) => {
                if (iPage.operID !== page.operID) {
                    if (page.isRoot) {
                        iPage.collapse = true;
                    }
                    if (page.isParent && iPage.isParent) {
                        iPage.collapse = true;
                    }
                    if (page.children.includes(iPage)) {
                        iPage.parent = page;
                    }
                }

            });
            page.collapse = !page.collapse;
        } else {
            page.isSelected = true;
            this.openPage(page);
        }
        return false;
    }

    expandOrCollapseWidget(event: Event, page: IPageBussiness) {
        if (this.widgetMenu) {
            this.widgetMenu.nativeElement.scrollTop = 0;
        }
        if (page.children && page.children.length > 0 && page.isChildLeaf != true) {

            //if sub menu not enabled (old behavior) or sub menu flag is enabled and we send the parent menu oper id 
            //and sub menu page already opened=> open sub menu in same page by expanding the existing menu
            //  scenario 1: if sub menu not enabled 
            //  scenario 2: sub menu enabled and we click on parent menu inside sub menu page
            if (!PsApplicationSettings.CLIENT_ASSETS_CONFIG.UI_CONFIGURATION?.SUB_MENU_PAGE ||
                (PsApplicationSettings.CLIENT_ASSETS_CONFIG.UI_CONFIGURATION?.SUB_MENU_PAGE &&
                    this.commonProv.activePage.value.component == 'sub-menu' && this.options.parentMenuOperId > 0)) {
                this.allMenu.forEach((iPage) => {
                    if (page.children.includes(iPage)) {
                        iPage.collapse = false;
                        iPage.hide = false;
                        iPage.parent = page;
                    } else {
                        iPage.collapse = true;
                        iPage.hide = true;
                    }
                });
                this.openedPage = page;
                this.levelOfWigets++;
                this.isSubMenu = true;
            } else {
                //if sub menu flag is enabled and parent menu oper id not sent
                //scenario 1: if sub menu enabled 
                page.component = 'sub-menu';
                page.param = {
                    parentMenuOperId: page.operID
                }
                //if sub menu already opened, close the page and reopen with new oper
                //senario 1: open parent menu from hamburger menu and sub menu already opened
                if (this.commonProv.activePage.value.component == 'sub-menu') {
                    this.navService.navigateBackward().then(res => { this.openPage(page) });
                } else {
                    //senario 1: open parent menu from hamburger menu and sub menu not opened
                    this.openPage(page);
                }
                this.isSubMenu = false;
            }
        } else {
            this.openPage(page);
        }
        return false;
    }

    openPage(page: IPageBussiness) {

        this.navService.openPage(page);
        this.parentFun.emit(page);
    }

    changeView() {
        if (this.isTreeView) {
            document.getElementsByTagName('ps-complex-menu')[0].classList.remove('is-widget-view');
            document.getElementsByTagName('ps-complex-menu')[0].classList.add('is-tree-view');
        } else {
            document.getElementsByTagName('ps-complex-menu')[0].classList.remove('is-tree-view');
            document.getElementsByTagName('ps-complex-menu')[0].classList.add('is-widget-view');
        }
    }
    returnOptions(item: IPageCommon, siblings) {
        if (item) {
            if (!item.children) {
                item.children = [];
            }
            item.siblings = [...siblings];
            item.actionMenuOptionsItem = {
                labelKey: item.title,
                menuIconOptions: { iconName: item.icon, fallBackIconName: item.fallBackIcon },
                expandCollapseIconOptions: {
                    iconName: item.children.length > 0 ? !item.collapse ? 'remove' : 'arrow' : item.children.length === 0 && item.hasParent ? '' : ''
                }
            };
            return item.actionMenuOptionsItem;
        } else {
            return {};
        }
    }

    goBack(root?: boolean) {
        if (this.widgetMenu) {
            this.widgetMenu.nativeElement.scrollTop = 0;
        }
        if (this.openedPage) {
            // this.findNestedItemInArray(this.pages, this.openedPage.parent.operID, 'operID', 'children');
            this.allMenu.forEach((iPage) => {
                iPage.collapse = true;
                iPage.hide = true;
            });
            if (this.openedPage.siblings && !root) {
                this.openedPage.siblings.forEach((iPage) => {
                    iPage.collapse = false;
                    iPage.hide = false;
                });
            }
            this.openedPage = root ? undefined : this.openedPage.parent;
            this.levelOfWigets--;
            if (root) {
                this.allMenu.forEach((iPage) => {
                    if (iPage.isRoot) {
                        iPage.collapse = false;
                        iPage.hide = false;
                    }
                });
                this.levelOfWigets = 0;
            }
        }
    }

    navigateToRoot() {
        this.goBack(true)
    }

    collabseHeaderMenu() {
        this.options.displayMenuHeader = !this.options.displayMenuHeader;
    }
}
