import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { distinctUntilChanged } from 'rxjs/operators';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { IOptionsPsBiometricButton } from '../../../../../psServices/models/ps-common-interface';
import { PsCommonService } from '../../../../../psServices/ps-common/ps-common.service';
import { PsActionButtonComponent } from '../ps-action-button.component';


@Component({
  selector: 'ps-button-biometric',
  templateUrl: './ps-button-biometric.component.html',
})
export class PsButtonBiometricComponent extends PsActionButtonComponent implements OnInit {

  @Output() public authenticateBiometric: EventEmitter<any> = new EventEmitter<any>();
  @Input() options: IOptionsPsBiometricButton;

  constructor(private commonProv: PsCommonService, private route: ActivatedRoute, public navService?: PsNavigatorService) {
    super(commonProv, commonProv.logger);
  }

  ngOnInit() {

  }

  logInByBiometric() {
    this.commonProv.checkBiometricAuthentication(this.options.biometricOptions).then(bioResult => {
      /*Mofified by ghada, tp1578210 
      - using pipe(distinctUntilChanged()) in order to subscribe to observable only for distinct change to eliminate unnecessary  services calls resulted in security error 
      - changing the emitter to separate between button onClick event and the authentication of the biometric which is needed from this emit line
      */
      this.commonProv.biometricAuthResult.pipe(distinctUntilChanged()).subscribe(bioResultObs => {
        if (bioResult === ConstantCommon.BIOMETRIC_AUTH_SUCCESS || bioResultObs === ConstantCommon.BIOMETRIC_AUTH_SUCCESS) {
          this.authenticateBiometric.emit({ isAuthenticated: true });
        }
      });
    });
  }

}
