import { Component, ComponentFactoryResolver, ElementRef, OnInit, Type, ViewChild } from '@angular/core';
import { CallNumber } from '@ionic-native/call-number/ngx';
import { EmailComposer } from '@ionic-native/email-composer/ngx';
import { MenuController } from '@ionic/angular';
import { PsComplexMenuReachComponent } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-menu-reach/ps-complex-menu-reach.component';
import { IOptionsPsMenuExposed, IPageBussiness } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-menu/ps-complex-menu.component.interfaces';
import { OmniCommonService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-common.service';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { ConstantCommon } from '../../psServices/models/common-constant';
import { IOptionsPsActionIconExposed, IOptionsPsButtonFabList, IOptionsPsDynamicComponentLoaderExposed, IOptionsPsLabel, IPageCommon } from '../../psServices/models/ps-common-interface';
import { PsCommonSettings } from '../../psServices/models/ps-common.settings';
import { PsNotificationsService } from '../../psServices/notifications/ps-notifications.service';
import { PsTemplateLandingMainPage } from '../ps-template-landing-main/ps-template-landing-main.page';
import { PsCommonService } from './../../psServices/ps-common/ps-common.service';



@Component({
  selector: 'ps-template-landing-hamburger',
  templateUrl: './ps-template-landing-hamburger.template.html',
})
export class PsTemplateLandingHamburgerPage extends PsTemplateLandingMainPage implements OnInit {
  public dynamicComponentOptions: IOptionsPsDynamicComponentLoaderExposed = {};

  quickMenuHeader: IOptionsPsMenuExposed = { quicklinkMenu: true };
  helpLabelOptions: IOptionsPsLabel = { labelKey: 'help_key' };
  @ViewChild('sideNav') sideNav: ElementRef<HTMLElement>;
  closeIcon: IOptionsPsActionIconExposed = {
    iconName: 'close'
  }; menuIcon: IOptionsPsActionIconExposed = {
    iconName: 'menu'
  };
  settingsIcon: IOptionsPsActionIconExposed = {
    iconName: 'settings'
  };
  loadDynPage?: boolean;
  collapsed = true;
  public dynaComponent: Type<any>;
  fabListOptions: IOptionsPsButtonFabList = {
    group: this.options.group,
    mainProperties: {
      iconName: 'landing-fab-button',
      group: this.options.group
    }
  };
  menuReach: PsComplexMenuReachComponent
  constructor(public menu: MenuController, public commonProv: PsCommonService, public omniCommon?: OmniCommonService, private omniPull?: OmniPullService, public psNotificationsService?: PsNotificationsService,
    public callNumber?: CallNumber, private emailComposer?: EmailComposer, public componentFactoryResolver?: ComponentFactoryResolver) {
    super(omniCommon, componentFactoryResolver);
  }

  ngOnInit() {
    this.init();
    if (this.commonProv.isMobile()) {//added by ghada, AZDB-1252 for adding the fabList for landingType 4 in mobile vew only
      this.menuReach = new PsComplexMenuReachComponent(this.omniPull, this.commonProv, this.psNotificationsService, this.callNumber, this.emailComposer);
      const menuReachFabList = this.menuReach.fabListOptions;
      this.fabListOptions.startFabList = menuReachFabList.startFabList;

      this.menuReach.onClick.subscribe(result => {
        if (result.clearComponent) {
          this.onClearComponent()
        } else {
          this.changeContent(result);
        }
      });
    }
  }

  openCloseNav(isPage?:IPageBussiness) {
    this.collapsed = !this.collapsed;
    // Added By OmarTalaat incase user clicked on page from main menu and reach menu is open we need to clear the 
    // reach menu component and flip back to main router outler to show the page that user clicked from menu
    if (isPage) {
      this.onClearComponent()
    }
    if (this.collapsed) {
      this.closeNav();
    } else {
      if (this.commonProv.isMobileLayout()) {
        this.sideNav.nativeElement.style.height = "100%";
      } else {
        this.sideNav.nativeElement.style.width = "25%";
      }
    }
  }

  closeNav() {
    if (!this.collapsed) {
      this.collapsed = true;
    }
    if (this.commonProv.isMobileLayout()) {
      this.sideNav.nativeElement.style.height = "0%";
    } else {
      this.sideNav.nativeElement.style.width = "0%";
    }
  }
  openProfile() {
    PsCommonSettings.oper_ID = ConstantCommon.PROFILE_OPER_ID;
    const page: IPageCommon = {
      operID: ConstantCommon.PROFILE_OPER_ID,
      title: 'profile_key'
    };
    this.navService.openPage(page);
  }
  ngAfterViewInit() {
    if (!this.commonProv.isMobileLayout() && this.navService.isUserLoggedIn) {
      this.openCloseNav();
    }
  }

  goBack(component?) {
    this.changeContent(component ? component : this.previousComponent);
    this.onClearComponent();
  }
}
