import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IOptionsPsComplexSegment, IOptionsPsContainerSegment } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { PsBaseFieldComponent } from '../ps-base-field.component';

@Component({
  selector: 'ps-complex-segment',
  templateUrl: './ps-complex-segment.component.html'
})
export class PsComplexSegmentComponent extends PsBaseFieldComponent implements OnInit {

  @Input() options: IOptionsPsComplexSegment;
  @Output() public complexSegmentButtonClicked: EventEmitter<any> = new EventEmitter<any>();
  segmentContainerOptions: IOptionsPsContainerSegment = {};
  constructor(public commonProv: PsCommonService, public loggerP: LoggerService) {
    super(commonProv, loggerP);
  }

  ngOnInit() {
    this.custIconOptions.component = PsComplexSegmentComponent;
    this.custIconOptions.componentOptions = this.options;
    // TP#1334175 - Get the fcName and Group from the main object in case not defined under segment specific
    this.options.segmentOptions.fcName = this.options.segmentOptions.fcName ? this.options.segmentOptions.fcName : this.options.fcName;
    this.options.segmentOptions.group = this.options.segmentOptions.group ? this.options.segmentOptions.group : this.options.group;
    //remove empty segments if exist
    this.options.segmentOptions.segmentList = this.options.segmentOptions.segmentList.filter(b => CommonUtils.isNotEmptyObject(b));
    super.ngOnInit();
    for (const segment of this.options.segmentOptions.segmentList) {
      this.applyCustomization(this.options.segmentOptions.fcName + '_' + segment.itemValue);
      //added by Marina; jiraAZDB-5173; 
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.options.segmentOptions.fcName + '_' + segment.itemValue], 0);
    }
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.options.segmentOptions.fcName], 0);
    this.populateContainerSegmentOptions();
  }

  private populateContainerSegmentOptions() {
    this.commonProv.copyObject(this.segmentContainerOptions, this.options.segmentContainerOptions, false, false);
    this.segmentContainerOptions.custOptionsMap = this.custOptionsMap;
    this.segmentContainerOptions.fieldName = this.options.segmentOptions.fcName;
    this.segmentContainerOptions.segments = this.options.segmentOptions.segmentList;
    //Modified for Bug 1327710
    this.checkDefaultSegment();
  }

  //set default segment
  //check if default segment return in VO or as default segment and not hidden, set 
  //else, set first segment in segmentList not hidden 
  //TP 1327710
  checkDefaultSegment() {
    const valFromVo = this.options.segmentOptions.group?.controls[ConstantCommon.CONTROLLER_FORM_DATA]?.value[this.options.segmentOptions.fcName];
    // Modified by Marina TP #1430697; sysParamScreenList added to retrieve screen customizations in order to be able to read segment's default value from it
    const fieldCust = this.commonProv.getElementValidations(this.options.segmentOptions.fcName);
    const defaultSegment = valFromVo || (fieldCust?.DEFAULT_VALUE && JSON.parse(fieldCust.DEFAULT_VALUE)?.value) || this.options.segmentOptions.defaultSegment?.itemValue;

    if (defaultSegment && this.returnContentVisible(defaultSegment.toString())) {
      this.segmentContainerOptions.selectedSegment = defaultSegment.toString();
    } else {
      let index = 0;
      while (index < this.segmentContainerOptions.segments.length) {
        if (this.returnContentVisible(this.segmentContainerOptions.segments[index].itemValue)) {
          this.options.segmentOptions.defaultSegment = this.segmentContainerOptions.segments[index];
          this.segmentContainerOptions.selectedSegment = this.options.segmentOptions.defaultSegment.itemValue.toString();
          break;
        } else {
          index++;
        }
      }
    }
  }

  public onClickSegment(ev) {
    if (ev) {
      this.segmentContainerOptions = {
        numberOfSegments: this.options.segmentContainerOptions.numberOfSegments,
        namesofSegments: this.options.segmentContainerOptions.namesofSegments,
        segments: this.options.segmentOptions.segmentList,
        selectedSegment: ev,
        custOptionsMap: this.custOptionsMap,
        fieldName: this.options.segmentOptions.fcName
      }
      this.complexSegmentButtonClicked.emit(ev);
    }
  }

  //Modified for bug 1327710 to return if specific segement is visible
  public returnContentVisible(segment?: any) {
    if (this.custMode) {
      return true;
    } else if (segment) {
      return this.custOptionsMap && this.custOptionsMap.get(this.options.segmentOptions.fcName + '_' + segment).visible;
    } else {
      const visible = false;
      if (this.custOptionsMap && this.options.segmentOptions) {
        for (const segment of this.options.segmentOptions.segmentList) {
          const tempVisible = this.custOptionsMap.get(this.options.segmentOptions.fcName + '_' + segment.itemValue).visible
          if (tempVisible) {
            return true;
          }
        }
      }
      return visible;
    }

  }

}
