  /** Added by ghada, AZDB-8520
 * Checks if the user should be blocked from entering an amount
 * when the minimum limit is greater than the smallest value of all limits.
 * Returns an object with the block status and the labelKey of the smallest limit.
 */
  isUserBlockedByMinLimit() {
    const minLimit = this.limitsService.getTotalOfLimitsPerType(this.arrayOfDefinedLimits, LimitsTypes.min);
    if (!this.arrayOfDefinedLimits || this.arrayOfDefinedLimits.length === 0) {
      return { blocked: false };
    }

    // Find the smallest limit and its label
    let smallestLimitValue = Number.POSITIVE_INFINITY;
    let smallestLimitLabel: string | undefined = undefined;

    this.arrayOfDefinedLimits.forEach(limit => {
      if (typeof limit.total === 'number' && !isNaN(limit.total) && limit.total < smallestLimitValue) {
        smallestLimitValue = limit.total;
        smallestLimitLabel = limit.labelKey;
      }
    });

    if (minLimit.length === 0 || smallestLimitValue === Number.POSITIVE_INFINITY) {
      return { blocked: false };
    }

    const minLimitValue = Math.max(...minLimit);
    const blocked = minLimitValue > smallestLimitValue;

    return { blocked, smallestLimitLabel };
  }

