import { Component, Input, OnInit } from '@angular/core';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { PsActionButtonComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-action/ps-action-button/ps-action-button.component';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { IOptionsPsLogoutButtonExposed } from './ps-button-logout.component.interfaces';


@Component({
  selector: 'ps-button-logout',
  templateUrl: './ps-button-logout.component.html',
  styleUrls: ['./ps-button-logout.component.scss'],
})
export class PsButtonLogoutComponent extends PsActionButtonComponent implements OnInit {

  @Input() options: IOptionsPsLogoutButtonExposed;
  defalutOptions: IOptionsPsLogoutButtonExposed;
  sessionId: any;
  userId: any;
  userInfo: any;
  constructor(private navService?: PsNavigatorService, private omniPull?: OmniPullService) {
    super(omniPull.omniCommon.common, omniPull.omniCommon.common.logger);
    this.sessionId = this.omniPull.omniCommon.session.getValueOf(ConstantCommon.SESSION_ID);
    this.userInfo = this.omniPull.omniCommon.session.getValueOf(ConstantCommon.USERINFO);
    this.userId = this.userInfo.ocUserId;
  }

  ngOnInit() {
    this.defalutOptions = {
      iconName: 'log-out',
      psClass: 'ps-button-cancel ps-button-logout',
      labelKey: 'logout_key',
      group: this.options.group
    }
  }

  //Modified by ghade US#1323351, for opening the log report when it's enabled in business profiles in ODAM
  async onButtonClicked(event: Event) {
    const activityLogRepEnabled = this.userInfo.businessProfiles.some((i) => i.operVO.OPER_ID === CommonBussinessConstant.ACTIVITY_LOG_OPER_ID_LOGOUT && i.operVO.BUSINESS_PROFILE_YN == 'Y');
    this.common.presentLoading();

    //Modified by ghada, tp1592133 moving the process of opening activity rep inside logout to stop unnecessary navigation to login screen if the report is to open on logout 
    this.omniPull.omniCommon.logout(0, activityLogRepEnabled, this.omniPull).then(() => {
      this.onClick.emit(event);
      this.omniPull.omniCommon.common.session.remove(CommonBussinessConstant.QUICK_LINKS_MENUS);
      this.omniPull.omniCommon.quickLinksMenuPages = [];
      this.common.dismissLoading();
    });
  }
}
