import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { PsActionHyperlinkComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-action/ps-action-hyperlink/ps-action-hyperlink.component';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { IOptionsPsHyperlinkProfileimageInlineLabelExposed } from './ps-hyperlink-profileimage-inline-label.component.interface';

@Component({
  selector: 'ps-hyperlink-profileimage-inline-label',
  templateUrl: './ps-hyperlink-profileimage-inline-label.component.html',
  styleUrls: ['./ps-hyperlink-profileimage-inline-label.component.scss'],
})
export class PsHyperlinkProfileimageInlineLabelComponent extends PsActionHyperlinkComponent implements OnInit {
  @Output("parentFun") parentFun: EventEmitter<any> = new EventEmitter();
  @Input() options: IOptionsPsHyperlinkProfileimageInlineLabelExposed = {
    actionImageOptions: {
      psClass: 'profile-pic-user',
    },
    labelOptions: {
      labelKey: 'edit_key'
    },
    route: 'profile',
    labelKey: 'profile_key',
    operId: ConstantCommon.PROFILE_OPER_ID //*Added by ghada tp#1264414 for fixing customization issue on refresh profile screen
  }
  userProfileImage: string;
  constructor(public commonService: PsCommonService, public nav: PsNavigatorService) {
    super(commonService, nav);
  }

  ngOnInit() {
    if (!this.options.actionImageOptions.imageBase64Url) {
      this.options.actionImageOptions.imageName = CommonBussinessConstant.DEFAULT_IMAGE;
    }
  }
  editProfile(event) {
    super.handleClickEvent(event);
    this.parentFun.emit()
  }
}
