import { Component, Input, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { IntegrationConstants } from 'src/app/commonBussinessSRC/psServices/integration/ps-integration-constants';
import { JBPMTask } from 'src/app/commonBussinessSRC/psServices/integration/ps-integration-interfaces';
import { PsBaseFieldComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-base-field.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IOptionsPsActionIconExposed } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';

@Component({
  selector: 'ps-jbpm-task-details',
  templateUrl: './ps-jbpm-task-details.component.html',
})
export class PsJbpmTaskDetailsComponent extends PsBaseFieldComponent implements OnInit {

  @Input() options: JBPMTask;

  public ACTION_INFO = IntegrationConstants.JBPM.ACTION.ACTION_INFO;

  public ACTION_COMMENT = IntegrationConstants.JBPM.ACTION.ACTION_COMMENT;

  public ACTION_SUSPEND = IntegrationConstants.JBPM.ACTION.ACTION_SUSPEND;

  ObjectReference = Object;

  closeIcon: IOptionsPsActionIconExposed = {
    iconName: 'close'
  };
  panelOptions = {
    labelKey: 'task_details_key',
    expanded: true
  };

  constructor(public commonProv: PsCommonService, loggerP: LoggerService, public modalController: ModalController) {
    super(commonProv, loggerP);
  }

  ngOnInit() {
    this.logger.log(this.options);
  }

  dismissPopover() {
    this.modalController.dismiss();
  }

}
