import { Component, Input, OnInit } from '@angular/core';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IOptionsPsFieldQrGenerationExposed } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { PsBaseFieldComponent } from '../ps-base-field.component';

@Component({
  selector: 'ps-field-qr-generator',
  templateUrl: './ps-field-qr-generator.component.html',
  styleUrls: ['./ps-field-qr-generator.component.scss'],
})
export class PsFieldQrGeneratorComponent extends PsBaseFieldComponent implements OnInit {

  @Input() options: IOptionsPsFieldQrGenerationExposed;

  constructor(private commonS: PsCommonService, public loggerP: LoggerService) {
    super(commonS, loggerP);
  }

  ngOnInit() {
    super.init();
    this.custIconOptions.availableCustomization.IS_READONLY = false;
    this.custIconOptions.availableCustomization.IS_MANDATORY = false;
    this.custIconOptions.availableCustomization.DEFAULT_VALUE = false;
    this.custIconOptions.availableCustomization.SERVICE_MAPPING = false;
  }

}
