/**
 * @author Aftab.Ali
 * @since 09/12/2019
 * <p> PsContainerLookupOptionComponent </p>
 */
@Component({
  selector: 'ps-container-lookup-option',
  templateUrl: './ps-container-lookup-option.component.html',
  styleUrls: ['./ps-container-lookup-option.component.scss'],
})
export class PsContainerLookupOptionComponent extends PsBaseFieldComponent implements OnInit, OnChanges, AfterViewInit {

  @Input() options: IOptionsPsContainerLookupOptionComponentExposed = {};
  @Input() itemCard: any;
  @Output() clickedCard: EventEmitter<any> = new EventEmitter<any>();
  @Output() logoClicked: EventEmitter<any> = new EventEmitter<any>();
  @Output() onCardSelectionChange: EventEmitter<any> = new EventEmitter<any>();
  @Output() afterViewInit: EventEmitter<any> = new EventEmitter<any>();
  // @Output() refreshList: EventEmitter<any> = new EventEmitter<any>();
  public templateOptions: IMapKeyValue[] = [];
  public selectedItem: any = {};
  public actionDetailsOptions: IActionDetailsOptions = {};
  public editMode = false;
  public showFebCloseIcon = false;
  public showDocuments = true;
  public hasReports = false;
  public collapsed = true;
  public showTotal;
  public balanceOptions: IOptionsPsBalanceExposed = {};
  public currencyImageOptions: IOptionsPsActionImage = {};
  public currencyImage: string;
  public dynamicEditOptions: IOptionsDynamicEditExposed = {};
  public gotoActionIcon: IOptionsPsActionIcon[] = [];
  public titleOptions: IOptionsPsLabel = {};
  public actionHyperlinkOptions: IOptionsPsActionHyperlink = {};
  public statusOptions: IOptionsPsLabel = {};
  public currencyISO: string;
  public showBalance = false;
  public itemStatusClass = '';
  public recordAttachementOptions: IOptionsPsRecordAttachmentComponentExposed = {};
  public showMoreOptions: IOptionsPsActionIcon = {};
  public showLessOptions: IOptionsPsActionIcon = {};
  public documentsOptions: IOptionsPsActionIcon = {};
  inputLabelOptions: IOptionsPsKeyinInput;
  public checkBoxOptions: IOptionsPsSelectCheckbox;
  public editIconOptions: IOptionsPsActionIcon = {
    iconName: 'create'
  };
  public submitIconOptions: IOptionsPsActionIcon = {
    iconName: 'checkmark',
    labelOptions: {
      labelKey: 'save_key'
    }
  };

  public actionOptionList: IActionDetailsOptions[] = [];

  public isIOS: boolean;
  public loaderOptions: IOptionsPsActionIcon = {};
  detailsDisplayed = false;
  reqParamsLabelKey: any;
  @ViewChild('moreItems', { static: false }) moreItems: Element;
  public dynaComponent: Type<any>;
  public dynamicComponentParam; any;
  @ViewChild('actionIcon', { static: false }) actionIcon: PsActionIconComponent;
  hasButtonPrepared = false;
  addShortcutOptions: IOptionsPsAddIconCustomization = {};
  addLabelOptions: IOptionsPsAddIconCustomization = {};
  customizationMode = false;
  landingType: number;
  sysParamScreenDispList = this.common.initialScreenDisplayParams.get(PsCommonSettings.oper_ID);
  matCardCssSelectors: string;
  statementsList: IActionDetailsOptions[];

  labelsOptionList: any[] = [];
  actionLabels: any[] = [];
  custObj: any = {};
  totalLabelsNb = 0;
  operationDetails: any = {};

  /**
   * getter to get the row index of a card
   *
   * @author ClarkRouhana
   * TP #1217612
   */
  get rowIndexClass() {
    return this.itemCard?.rowIndex || this.itemCard?.rowIndex === 0 ? '-' + this.itemCard?.rowIndex : ''
  }

  constructor(
    private omniCommonService: OmniCommonService,
    commonService: PsCommonService,
    logger: LoggerService,
    public navService?: PsNavigatorService,
    private platform?: Platform,
    private componentFactoryResolver?: ComponentFactoryResolver,
    private dynamicButtonService?: DynamicButtonsService,
    public events?: Events,
  ) {
    super(commonService, logger);
  }

  async ngOnInit() {
    if (!this.options.listOfStatementOptions && this.options.pageData?.operID) {
      this.options.listOfStatementOptions = [{
        operId: this.options.pageData?.operID,
        title: this.options.pageData?.title,
        redirectUrl: this.options.statementOptions?.redirectUrl,
        buttonIcon: this.options.statementOptions?.buttonIcon
      }]
    }
    this.landingType = PsApplicationSettings?.CLIENT_ASSETS_CONFIG.UI_CONFIGURATION.LANDING_UI_TYPE;
    this.showTotal = this.options.showInitialCardValues ? this.options.showInitialCardValues : PsCommonSettings.showInitialCardValues;
    this.isIOS = this.platform.is('ios');
    this.options.group = this.options.formGroup;

    /*
     * @author Mustafa
     * TP #1338932
    */
    this.actionLabels = this.dynamicButtonService.returnOperCardLabels();
    this.custObj = { ...this.itemCard, ...this.actionLabels?.map(e => e.iconName).reduce((acc, curr) => (acc[curr] = '', acc), {}) }
    delete this.custObj.rowIndex;
    this.addShortcutOptions.formGroup = this.options.formGroup;
    this.addShortcutOptions.type = ConstantCommon.CARD_BUTTON_TYPE;
    this.addLabelOptions.formGroup = this.options.formGroup;
    this.addLabelOptions.type = ConstantCommon.CARD_LABEL_TYPE;
    this.addLabelOptions.namesList = this.addShortcutOptions.namesList = this.custObj;

    super.ngOnInit();
    this.statementsList = this.options.listOfStatementOptions;
    this.editMode = false;
    this.options.showItemPopUp = false;
    this.options.showSelectedCard = true;
    // this.options.formGroup = new FormGroup({});

    // Add css classes to the card dynamically, based on the card type, segment
    this.matCardCssSelectors = this.options.matCardCssSelectors;

    this.checkBoxOptions = {
      group: this.options.formGroup,
      fcName: 'checkboxSelection' + this.rowIndexClass
    }

    this.operationDetails = this.common.getOperationDetails(PsCommonSettings.oper_ID);

    this.loadBalanceMap();
    this.loadLabelsValueMap();
    this.gotoActionIcon = this.options.listOfStatementOptions?.map(statement => {
      return {
        iconName: statement.buttonIcon,
        allowCust: true,
        labelOptions: {
          labelKey: statement.title
        }
      }
    })

    this.showMoreOptions = {
      iconName: 'arrow-down',
      labelOptions: {
        labelKey: 'show_more_key'
      }
    };
    this.showLessOptions = {
      iconName: 'arrow-up',
      labelOptions: {
        labelKey: 'show_less_key'
      }
    };

    this.documentsOptions = {
      iconName: 'document',
      labelOptions: {
        labelKey: 'documents_key',
      },
      allowCust: true,
      group: this.options.group,

    };

    this.loaderOptions = {
      iconName: 'loading',
      psClass: 'align-loader'
    };

    this.dynamicEditOptions = {
      itemCard: this.itemCard,
      group: this.options.formGroup,
      isEditable: this.options.isEditable,
      actionUrl: this.options.editActionUrl,
      requestMap: this.options.requestMap,
      editRequestMap: this.options.editRequestMap,
      translate: this.options.translateSubTitle
    };

    if (this.itemCard && this.itemCard.nickName) {
      const reqMap: Map<string, string> = new Map(this.options.requestMap);
      reqMap.set('oldNickName', 'nickName');
      const editReqMap: Map<string, string> = new Map(this.options.editRequestMap);
      editReqMap.set('newNickName', 'nickName');
      this.dynamicEditOptions.valueMap = this.itemCard.nickName;
      this.dynamicEditOptions.fcName = 'nickName';
      this.dynamicEditOptions.newValueFcname = 'nickName';
      this.dynamicEditOptions.requestMap = reqMap;
      this.dynamicEditOptions.editRequestMap = editReqMap;
    } else {
      if (this.templateOptions[1]) {
        this.dynamicEditOptions.valueMap = this.itemCard[this.templateOptions[1].value];
        this.dynamicEditOptions.fcName = this.templateOptions[1].value;
        this.dynamicEditOptions.newValueFcname = this.templateOptions[1].value;
      }
    }

    setTimeout(() => {
      if (this.itemCard && (this.itemCard[this.options.currencyDescription] || this.itemCard[this.options.currencyFlag])) {
        this.currencyISO = this.itemCard[this.options.currencyDescription] || this.itemCard[this.options.currencyFlag];
        this.currencyImage = this.itemCard[this.options.currencyFlag].toLowerCase() + '.png';
      }
      this.currencyImageOptions = {
        imagePath: PsApplicationSettings.CLIENT_ASSETS_CONFIG.IMGS_FOLDER_URL + 'currencies/',
        imageName: this.currencyImage,
        imageBase64Url: ''
      };
    }, 1);
    if (this.templateOptions && this.templateOptions.length > 0) {
      this.titleOptions.labelKey = this.itemCard[this.templateOptions[0].value];
      this.titleOptions.translate = false;
    }
    this.statusOptions.labelKey = this.itemCard.status;
    this.itemStatusClass = 'status-class-' + (this.itemCard.statusCode ? this.itemCard.statusCode : this.statusOptions.labelKey);
    this.actionOptionList = [];
    if (this.options.actionDetailsOptions !== null && this.options.actionDetailsOptions !== undefined) {
      // This service will get the action buttons with respect to the operation ID
      this.actionOptionList = [];
      this.loadActionDetailsOptions(this.options.actionDetailsOptions);
      const actionButtons = this.dynamicButtonService.returnOperCardButtons();
      if (actionButtons !== undefined && actionButtons.length > 0) {
        this.loadActionDetailsOptions(actionButtons);
      }
    }
    this.reloadOnStepperSubmit();
    this.events.subscribe('custButtonReloaded', (response) => {
      this.actionOptionList = [];
      this.loadActionDetailsOptions(this.options.actionDetailsOptions);
      const actionButtons = this.dynamicButtonService.returnOperCardButtons();
      if (actionButtons !== undefined && actionButtons.length > 0) {
        this.loadActionDetailsOptions(actionButtons);
      }
    });
    //adding the condition on isEdit in order to have the show more opened when adding a new row in the grid
    if (this.itemCard.isEdit || this.custMode) {
      if (this.custMode) {
        this.customizationMode = true;
      }
      this.showMore(); // Added in order to show all the fields under the card in order to be able to define 
    }
    this.hasReports = this.omniCommonService.getParentDocumentsList(PsCommonSettings.oper_ID).length > 0;
  }

  ngAfterViewInit() {
    this.afterViewInit.emit(this.itemCard);
  }

  ngOnChanges(changes: SimpleChanges) {
    // Added by Clark for TP#1217612: reload the action buttons when a button is added or removed or something changed from its properties.
    if (this.options.gridMobile) {
      if (this.actionOptionList.length > 0) {
        if (this.actionOptionList[0].id !== this.options.actionDetailsOptions.filter(action => CommonUtils.isNotEmptyObject(action))[0].id) {
          this.actionOptionList = [];
          this.loadActionDetailsOptions(this.options.actionDetailsOptions);
        }
      }
    }
  }

  reloadOnStepperSubmit() {
    this.events.subscribe('reloadCards', (reload) => {
      if (reload) {
        this.options.reloadFunction.func();
      }
    })
  }

  /**
   * Load action button inside the card
  */
  loadActionDetailsOptions(actionButtons: any[]) {
    //check if action hyperlink or action icon are hidden by customization to be removed from list by applying filter
    //TP #1326482 
    actionButtons
      .filter(b =>
        b && ((b.actionHyperlink ? (this.sysParamScreenDispList?.get(b.id + '_lookup_link') == undefined || this.sysParamScreenDispList?.get(b.id + '_lookup_link').IS_VISIBLE == true) :
          (this.sysParamScreenDispList?.get(b.id) == undefined || this.sysParamScreenDispList?.get(b.id).IS_VISIBLE == true)) || this.custMode))
      .forEach(actionOption => {
        if (actionOption.actionHyperlink) {
          actionOption.type = ActionDetailsOptionTypes.HYPERLINK;
          let hyperLink: IActionDetailsOptions = {};
          this.common.copyObject(hyperLink, actionOption, true, true);
          hyperLink.actionHyperlink = actionOption.actionHyperlink; //added by Marina-jira-8560; to have the same reference actionHyperLink in both objects to be able to read itemCard inside list page and be able to update query params
          hyperLink.actionHyperlink.businessRelatedYN = actionOption.businessRelatedYN;
          hyperLink.actionHyperlink.shortcutButton = actionOption.shortcutButton;
          let params = Object.assign({}, this.itemCard, actionOption.actionHyperlink.navigationOptions ? actionOption.actionHyperlink.navigationOptions.queryParams : {});
          hyperLink.actionHyperlink.navigationOptions = {
            queryParams: { ScreenVO: params }
          };
          if (actionOption.itemCardName) {
            let itemValues = hyperLink.actionHyperlink.navigationOptions.queryParams.ScreenVO;
            hyperLink.actionHyperlink.navigationOptions.queryParams.ScreenVO = {};
            hyperLink.actionHyperlink.navigationOptions.queryParams.ScreenVO[actionOption.itemCardName] = itemValues;
          }
          hyperLink.actionHyperlink.navigationOptions.queryParams = {
            ...hyperLink.actionHyperlink.navigationOptions.queryParams.ScreenVO,
            ScreenVO: hyperLink.actionHyperlink.navigationOptions.queryParams.ScreenVO,
            headerMap: this.options.headerMap,
            labelsValueMap: this.options.labelsValueMap,
            showInitialCardValues: this.showTotal
          }
          hyperLink.actionHyperlink.pageOptions.operId = this.itemCard.operId ? this.itemCard.operId : actionOption.actionHyperlink.pageOptions.operId;
          this.actionOptionList.push(hyperLink);
        } else if (actionOption.actionUrl || actionOption.mappingId || actionOption.commonMethod) {
          actionOption.type = ActionDetailsOptionTypes.ICON;
          actionOption.allowCust = true;
          if (actionOption.label === 'suspend' && (this.itemCard.status !== 'S' && this.itemCard.status !== 'Suspended')) {
            this.actionOptionList.push(actionOption);
          } else if (actionOption.label === 'activate' && (this.itemCard.status !== 'A' && this.itemCard.status !== 'Active')) {
            this.actionOptionList.push(actionOption);
          } else if (actionOption.label !== 'activate' && actionOption.label !== 'suspend') {
            this.actionOptionList.push(actionOption);
          }
        } else if (actionOption.component) {
          actionOption.id = actionOption.id ? actionOption.id : '';
          actionOption.type = ActionDetailsOptionTypes.COMPONENT;
          this.dynaComponent = typeof (actionOption.component) !== 'string' ? actionOption.component : DynamicScreenConstants.DYNAMIC_COMPONENTS_MAP[actionOption.component];
          this.dynamicComponentParam = {
            businessRelatedYN: actionOption.businessRelatedYN,
            group: this.options.group,
            shortcutButton: actionOption.shortcutButton,
            // Pass all action options to the component
            ...actionOption,
            ...actionOption.shortcutButton,
            ...actionOption.componentOptions
          };
          actionOption.dynamicComponentOptions = {
            component: this.dynaComponent,
            componentOptions: this.dynamicComponentParam
          }
          if ((actionOption.component === 'PsIconSocialSharingComponent' && this.commonProv.isNativeMobile()) || PsCommonSettings.custMode) {
            this.actionOptionList.push(actionOption);
          } else if (actionOption.component !== 'PsIconSocialSharingComponent') {
            this.actionOptionList.push(actionOption);
          }
        }
      });
  }

  /**
   *
   * @param item
   */
  public showClickedCard(item: any) {
    if (this.options.isEditable) {
      return;
    }
    this.clickedCard.emit(item);
  }

  //added by Marina for TP #1450292 to handle card logo clicks 
  public handleLogoClicked(card) {
    this.logoClicked.emit(card);

  }
  /**
   * preparing list for template card and content card from the map passed in options
   */
  private loadLabelsValueMap() {
    if (this.options.headerMap && this.options.headerMap.size > 0) {
      Array.from(this.options.headerMap).forEach((headerOption) => {
        const tempOption: IMapKeyValue = {
          key: headerOption[0],
          value: headerOption[1].value,
          isEdit: headerOption[1].isEdit,
          formGroupMap: headerOption[1].formGroupMap,
        };
        if (tempOption.formGroupMap && tempOption.formGroupMap !== undefined) {
          tempOption.formGroupMap.group = this.options.formGroup;
        }
        this.templateOptions.push(tempOption);
      });
    }

    this.labelsOptionList = [];
    this.totalLabelsNb = this.options.labelsValueMap.size;
    this.staticLabels(this.options.labelsValueMap, this.showTotal);
    if (this.actionLabels !== undefined && this.actionLabels.length > 0) {
      this.totalLabelsNb += this.actionLabels.length;
      this.dynamicLabels(this.actionLabels, this.showTotal);
    }
  }

  /**
   *
   * @param item
   * @param actionOptions
   */
  public actionFunc(item: any, actionOptions: IActionDetailsOptions, event) {
    if (this.customizationMode) { return; };
    if (actionOptions && actionOptions.commonMethod && actionOptions.commonMethod !== '') {
      this.commonProv[actionOptions.commonMethod](item);
    } else if (actionOptions && ((actionOptions.actionUrl && actionOptions.actionUrl !== '') || (actionOptions.mappingId && actionOptions.mappingId !== ''))) {
      let requestparameters: any;
      if (actionOptions.actionUrl !== undefined) {
        requestparameters = {
          actionType: actionOptions.actionType,
          commonParametersList: item,
          operId: actionOptions.operId
        };
        if (item && item.dataSaveId) {
          requestparameters.dataSaveId = item.dataSaveId;
        }
      } else if (actionOptions.mappingId !== undefined) {
        requestparameters = {
          mappingId: actionOptions.mappingId,
          commonParametersList: {
            actionType: actionOptions.actionType,
            operId: actionOptions.operId,
            ...item
          }
        };
        if (item && item.dataSaveId) {
          requestparameters.commonParametersList.dataSaveId = item.dataSaveId;
        }
      }

      // ACTION_REASON_INPUT
      // ACTION_REASON_WARNING

      if (actionOptions.alertType) {
        if (actionOptions.alertType === ConstantCommon.ACTION_REASON_WARNING) {
          this.confirmationPopup(requestparameters, actionOptions, item);
        } else if (actionOptions.alertType === ConstantCommon.ACTION_REASON_INPUT) {
          this.reasonPopup(requestparameters, actionOptions, item);
        } else if (actionOptions.alertType === ConstantCommon.ACTION_TYPE_QR) {
          this.commonProv.generateQRCode(actionOptions.qrData || requestparameters.commonParametersList);
        } else {
          this.confirmationPopup(requestparameters, actionOptions, item);
        }

      } else {
        this.actionGenericFunction(requestparameters, actionOptions, item);
      }
    }
  }
  public filterFunc(item: any, actionOptions: IActionDetailsOptions): any {
    // send specific parameters and not all item data
    const requestMap = {};
    actionOptions.filterParamValues.forEach(element => {
      requestMap[element] = item[element];
    });
    return requestMap;
  }

  /**
   * will send the edited values and additional values from the selected item
   */
  public submit() {

    const requestParams = this.options.formGroup.value;
    this.actionDetailsOptions.rquestParam.forEach(param => {
      const requestObj = {
        [param]: this.selectedItem[param]
      };
      this.commonProv.copyObject(requestParams, requestObj, false);
    });

    if (this.actionDetailsOptions && this.actionDetailsOptions.actionUrl) {
      this.dynamicButtonService.prepareCommonReqAjax(this.actionDetailsOptions.actionUrl, requestParams).then(result => {
        this.logger.log('success', result);
      }).catch(
        error => this.logger.error(error)
      );
    }
  }

  public showMore() {
    this.staticLabels(this.options.labelsValueMap, this.totalLabelsNb);
    if (this.actionLabels !== undefined && this.actionLabels.length > 0) {
      this.dynamicLabels(this.actionLabels, this.totalLabelsNb);
    }

    this.collapsed = !this.collapsed;
    this.expandCollapseClassList();

    // Restrict to call for one time only on card expend
    if (!this.collapsed && !this.detailsDisplayed) {
      this.detailsDisplayed = true;
      if (this.options.detailServiceUrl !== undefined && this.options.detailServiceUrl.length > 0 && !this.customizationMode) {
        this.options.detailServiceUrl.forEach(async (service, index) => {
          this.reqParamsLabelKey = {};

          // check and prepare request params that passes from the business component
          if (service.paramsKeyLabels !== undefined) {
            this.reqParamsLabelKey = this.prepareReqParamKeyLabel(service.paramsKeyLabels);
          }
          if (service.url !== undefined) {

            // Calling ajax request 
            this.dynamicButtonService.prepareCommonReqAjax(service.url, this.reqParamsLabelKey).then((result) => {
              if (result !== undefined) {

                // Bind the updated response value to the itemCard as defined as responsekeylabel in business component.
                const matchResponse = service.responseKeyLabel;
                this.labelsOptionList.forEach(param => {
                  matchResponse.forEach(response => {
                    if (response.value === param.key && param.inputLabelOptions.shortcutLabel.custData.businessRelatedYN === 'Y') {
                      this.itemCard[param.key] = result[response.key];
                      if (param.showMappingOnNoValue && !this.itemCard[param.key]) {
                        param.labelVisibility = false;
                      } else {
                        param.labelVisibility = true;
                      }
                      this.setValueToFormData(param.inputLabelOptions.fcName, this.itemCard[param.key]);
                    }
                  });
                  param.inputLabelOptions.pendingStatus = false;
                });
              }
            }), (error) => {
              this.logger.error('Error ! while calling omni service for static labels ', error);
            }
          }
        });
      }
    }
  }

  public showLess() {
    this.common.hideElementWhenAnimationEnds(this.moreItems, 'card-action-buttons-hide').then(() => {
      this.collapsed = !this.collapsed;
      this.expandCollapseClassList();
    });
  }

  public openDocuments() {
    let title: string = CommonUtils.translate(PsCommonSettings.pageName);
    if (!title.includes("Documents")) {
      title = title + " Documents";
    }
    this.navService.openPage(
      {
        component: 'documents',
        title: title,
        icon: 'document',
        operID: CommonBussinessConstant.DOCUMENTS_OPER,
        param: { options: this.options, itemCard: this.itemCard, fromOper: PsCommonSettings.oper_ID, fromOperName: CommonUtils.translate(PsCommonSettings.pageName) }
      });
  }

  private loadBalanceMap() {
    if (this.options.balanceMapping && this.options.balanceMapping.length > 0) {
      this.balanceOptions.labelBalance1 = this.options.balanceMapping[0].key;
      this.balanceOptions.labelBalance2 = this.options.balanceMapping[1].key;
      this.balanceOptions.balanceValue1 = this.itemCard[this.options.balanceMapping[0].value];
      this.balanceOptions.balanceValue2 = this.itemCard[this.options.balanceMapping[1].value];
      this.balanceOptions.balanceFcNames =this.options.balanceFcNames;
      if (this.balanceOptions.balanceValue1 !== null && this.balanceOptions.balanceValue1 !== undefined && this.balanceOptions.balanceValue2 !== null && this.balanceOptions.balanceValue2 !== undefined) {
        this.showBalance = true;
      }
    }
  }

  switchToEditMode() {

  }
  expandCollapseClassList() {
    //select last tag name if we have multiple pages on top of each other
    if (!this.collapsed) {
      document.getElementsByTagName('ps-template-list')[document.getElementsByTagName('ps-template-list').length - 1]?.classList.add('list-card-expanded');
    } else {
      document.getElementsByTagName('ps-template-list')[document.getElementsByTagName('ps-template-list').length - 1]?.classList.remove('list-card-expanded');
    }
  }
  /**
   *
   * @param statmenetOptions
   */

  public gotoStatement(statmenetOptions: IActionDetailsOptions) {
    if (statmenetOptions && statmenetOptions.redirectUrl && statmenetOptions.redirectUrl !== '') {
      const reportParametersList = this.itemCard as Map<string, string>;
      const navigationExtras: NavigationExtras = {
        queryParams: { ...reportParametersList }
      };
      const page: IPageCommon = {
        operID: statmenetOptions.operId,
        title: statmenetOptions.title,
        icon: 'report',
        component: statmenetOptions.redirectUrl.split('/').length > 1 ? statmenetOptions.redirectUrl.split('/')[1] : statmenetOptions.redirectUrl,
        param: navigationExtras.queryParams
      };
      this.navService.openPage(page);
    }
  }


  get previewValOptions(): IOptionsPsFieldLabel {
    return {
      labelKey: this.fcValueDesc,
      psClass: 'ps-value-preview',
      previewMode: true
    };
  }

  get fcValueDesc(): string {
    const value: any = this.titleOptions.labelKey;
    return value;
  }


  /**
   *
   * @param requestparameters
   * @param actionOptions
   * @param item
   */
  private reasonPopup(requestparameters: any, actionOptions: IActionDetailsOptions, item: any) {
    const postCall = actionOptions.postCallFunction;
    actionOptions.postCallFunction = undefined;
    return new Promise<any>((resolve, reject) => {
      {
        {
          const alertController: IOptionsPsComplexAlertController = {
            cssClass: 'ps-button-save',
            header: actionOptions.alertLabel,
            inputs: [
              {
                type: 'keyin',
                options: {
                  placeHolder: actionOptions.alertLabel,
                  fcName: actionOptions.fcName,
                  group: this.dynamicEditOptions.group
                }
              }
            ],
            buttons: [
              {
                role: 'cancel',
                options: {
                  labelKey: 'cancel_key',
                  psClass: 'ps-drafts-report-cancel-button',
                  group: this.options.formGroup
                },
                handler: () => {
                  this.dynamicEditOptions.group.removeControl(actionOptions.fcName);
                  reject(false);
                }
              }, {
                role: 'cancel',
                options: {
                  // ...actionOptions,
                  labelKey: 'submit_key',
                  iconName: 'paper-plane',
                  psClass: 'ps-drafts-report-submit-button',
                  group: this.options.formGroup
                },
                handler: () => {
                  let requestParams: any;
                  requestParams = {
                    ...this.dynamicEditOptions.group.value,

                    ...requestparameters,
                    commonParametersList: {
                      ...this.dynamicEditOptions.group.value,
                      ...requestparameters.commonParametersList
                    }
                  };
                  if (actionOptions.mappingId !== undefined) {
                    if (actionOptions.actionUrl !== undefined) {
                      requestParams = {
                        mappingId: actionOptions.mappingId,
                        ...requestParams
                      };
                    }
                  }
                  const items: any = {
                    ...this.dynamicEditOptions.group.value,
                    ...item
                  };
                  this.dynamicEditOptions.group.removeControl(actionOptions.fcName);
                  actionOptions.postCallFunction = postCall;
                  this.actionGenericFunction(requestParams, actionOptions, items);
                  CommonUtils.dismissAllModals();
                }
              }
            ]
          };
          this.commonProv.presentPsAlert(alertController, 'ps-input-reason');
        }
      }
    });
  }


  /**
   *
   * @param requestparameters
   * @param actionOptions
   * @param item
   */
  private confirmationPopup(requestparameters: any, actionOptions: IActionDetailsOptions, item: any) {
    return new Promise<any>((resolve, reject) => {
      {
        let buttonsObject: Array<IOptionsAlertButton> = [
          {
            group: this.options.group,
            type: 'reset',
            labelKey: 'cancel_key',
            handler: () => {
              CommonUtils.dismissAllModals();
              reject(false);
            }
          },
          {
            group: this.options.group,
            labelKey: 'ok_key',
            type: 'submit',
            handler: () => {
              this.actionGenericFunction(requestparameters, actionOptions, item, 'checkboxConfirm');
              CommonUtils.dismissAllModals();
            }
          }
        ];
        if (actionOptions.alertStyle === ConstantCommon.ACTION_ALERT_WARNING) {
          CommonUtils.presentFailureAlert(actionOptions.alertLabel !== undefined ? this.commonProv.translate(actionOptions.alertLabel) : this.commonProv.translate('are_you_sure_?_key'), { buttonsArray: buttonsObject });
        } else { // INFO ALERT
          CommonUtils.presentInfoAlert(actionOptions.alertLabel !== undefined ? this.commonProv.translate(actionOptions.alertLabel) : this.commonProv.translate('are_you_sure_?_key'), { buttonsArray: buttonsObject });
        }
      }
    });
  }


  /**
   *
   * @param requestparameters
   * @param actionOptions
   * @param item
   */
  private actionGenericFunction(requestparameters: any, actionOptions: IActionDetailsOptions, item: any, confirmCheckBox?: string) {
    CommonUtils.presentLoading();
    requestparameters = {
      ...requestparameters,
      isActionButton: true,
    };
    if (actionOptions.detailServiceCallOnAction) {
      if (actionOptions.detailServiceUrl !== undefined) {
        this.dynamicButtonService.prepareCommonReqAjax(actionOptions.detailServiceUrl, requestparameters).then((result) => {
          if (result !== undefined) {
            result = this.commonProv.prepareResult(result);
            if (confirmCheckBox) {
              result[confirmCheckBox] = true;
              requestparameters[confirmCheckBox] = true;
            }
            requestparameters.commonParametersList = Object.assign(requestparameters.commonParametersList, result);
            this.callGenericActionFunction(requestparameters, actionOptions, item);
          }
        });
      }
    } else {
      this.callGenericActionFunction(requestparameters, actionOptions, item);
    }
  }
  prepareReqParamKeyLabel(paramsKeyLabels) {
    paramsKeyLabels.forEach(param => {
      if (param.value !== undefined) {
        this.reqParamsLabelKey[param.key] = this.itemCard[param.value];
      } else if (param.staticValue !== undefined) {
        this.reqParamsLabelKey[param.key] = param.staticValue;
      }
    });
    return this.reqParamsLabelKey
  }

  /*
    * @author Mustafa
    * TP #1338932
  */
  private staticLabels(actionLabels: any, totalRecords: number) {
    const startIndex = this.labelsOptionList.length;
    const actionLabelsArr = Array.from(actionLabels);
    for (let i = startIndex; i < actionLabelsArr.length && this.labelsOptionList.length < totalRecords; i++) {
      const labelOption = actionLabelsArr[i];
      let labelVisibility = true;
      if (labelOption[1].showMappingOnNoValue && CommonUtils.isEmptyString(this.itemCard[labelOption[1].key])) {
        labelVisibility = false;
      }
      //fcName edited by Marina jira-AZDB-887; to avoid overriding label data when we have multiple same lookup on the screen
      const contentOption: labelPreview = {
        inputLabelOptions: {
          labelKey: labelOption[0] ? this.commonProv.translate(labelOption[0]) : '',
          fcName: this.options.fcName ? this.options.fcName + '-' + labelOption[1].key + '_lookup' + this.rowIndexClass : labelOption[1].key + '_lookup' + this.rowIndexClass,
          group: this.options.formGroup,
          shortcutLabel: { type: ConstantCommon.CARD_LABEL_TYPE, custData: { businessRelatedYN: 'Y' } },
          forcePreviewMode: true,
          rowIndex: this.itemCard.rowIndex,
          prevModeNotPrevStep: true,
          pendingStatus: this.itemCard[labelOption[1].key] ? false : true,
          screenTypeDesc: this.operationDetails ? this.operationDetails.TYPE_DESC : undefined
        },
        isEdit: labelOption[1].isEdit,
        type: labelOption[1].type,
        value: this.itemCard[labelOption[1].key] ? this.itemCard[labelOption[1].key] : undefined,
        key: labelOption[1].key,
        showMappingOnNoValue: labelOption[1].showMappingOnNoValue,
        labelVisibility,
      };
      contentOption.dynamicLoaderOptions = labelOption[1].dynamicLoaderOptions ? labelOption[1].dynamicLoaderOptions
        : {
          component: PsKeyinInputComponent,
          componentOptions: contentOption.inputLabelOptions,
          group: this.options.formGroup,
        }
      this.setValueToFormData(contentOption.inputLabelOptions.fcName, contentOption.value);
      this.labelsOptionList.push(contentOption);
    }
  }

  /*
    * @author Mustafa
    * TP #1338932
  */
  private async dynamicLabels(actionLabels: any[], totalRecords: number) {
    let startIndex = this.labelsOptionList.length - this.options.labelsValueMap.size;
    for (let i = startIndex; i < actionLabels.length && this.labelsOptionList.length < totalRecords; i++) {
      const label = actionLabels[i];
      const contentOption: labelPreview = {
        inputLabelOptions: {
          labelKey: this.commonProv.translate(label.keyLabelDesc),
          fcName: label.iconName + this.rowIndexClass,
          group: this.options.formGroup,
          shortcutLabel: { type: ConstantCommon.CARD_LABEL_TYPE, custData: label, namesList: this.custObj, formGroup: this.options.formGroup },
          forcePreviewMode: true,
          rowIndex: this.itemCard.rowIndex,
          prevModeNotPrevStep: true,
          pendingStatus: this.custMode ? false : true,
          screenTypeDesc: this.operationDetails ? this.operationDetails.TYPE_DESC : undefined
        },
        labelVisibility: true
      };

      contentOption.dynamicLoaderOptions = {
        component: PsKeyinInputComponent,
        componentOptions: contentOption.inputLabelOptions,
        group: this.options.formGroup,
        mappingId: label.mappingId,
        itemCard: this.itemCard
      }

      this.labelsOptionList.push(contentOption);
    }
  }

  callGenericActionFunction(requestparameters: any, actionOptions: IActionDetailsOptions, item: any) {
    if (actionOptions.requestParamKeyVal) {
      this.common.copyObject(requestparameters, actionOptions.requestParamKeyVal, false)
    }
    if (actionOptions.actionUrl !== undefined) {
      this.dynamicButtonService.prepareCommonReqAjax(actionOptions.actionUrl, requestparameters).then(result => {
        if (result.outputType === ConstantCommon.OUT_PUT_TYPE_SUCCESS) {
          if (this.options.itemList && this.options.itemList.length > 0) {
            this.options.itemList = this.options.itemList.filter(itemVal => itemVal !== item);
            this.options.reloadFunction.func();
          }
        } else {
          CommonUtils.presentFailureAlert(result.outputNotification, { autoHide: true });
        }
        CommonUtils.dismissLoading();
      }).catch(error => {
        this.logger.error(error);
        CommonUtils.dismissLoading();
      });
    } else if (actionOptions.mappingId !== undefined) {
      this.common.commonServiceCallForCustomization(requestparameters).then(result => { 
        const resultObj: any =   result;    
        if(resultObj.data.outputType === ConstantCommon.OUT_PUT_TYPE_SUCCESS || resultObj.data.outputType === "SC"){
          CommonUtils.presentSuccessAlert(resultObj.data.outputNotification, { autoHide: true });
	        //AZDB-8846 AIBIRQI250160 - 204612 - when submit a request added by dynamic action button, theres no success or failure msg showing
          if (this.options.reloadFunction !== undefined) {
           this.options.reloadFunction.func();
          }
        } else{
          CommonUtils.presentFailureAlert(resultObj.data.outputNotification, { autoHide: true });
        }
        this.common.dismissLoading();
      }).catch(error => {
        CommonUtils.presentFailureAlert(error.data.outputNotification, { autoHide: true });
        this.common.dismissLoading();
        if (error.data.outputType !== 'S') {
          const resError = { msgKey: error.data.outputNotification };
        }
        this.logger.error('Error ! while calling common service for customization ', error);
      });
    }
  }

  /**
   * function used to set the value to the controller.
   *
   * @author ClarkRouhana
   * TP #1217612
   */
  setValueToFormData(fcName, value, innerValueForTypeSelect?) {
    value = value !== undefined ? value : '-';
    this.options.formGroup.controls[fcName]?.setValue(value, { emitEvent: false });
    if (CommonUtils.isNotNull(innerValueForTypeSelect)) {
      value = innerValueForTypeSelect;
    }
    this.commonProv.setValInsideNestedObj(fcName, value, this.options.formGroup.controls['formData']?.value);
  }

  /**
   * function used to build the cards dynamically. It runs on init of every dynamic component loader.
   *
   * @author ClarkRouhana
   * TP #1217612
   */
  onDynamicComponentInit(event, content) {
    if (this.options.gridMobile) {
      event.currentComponent.dynCompInitBase(content.dynamicLoaderOptions.componentOptions, this.itemCard);

      let innerValueForTypeSelect; // used for grid
      let fcName = event.fcName;
      let forcePreviewControl = content.dynamicLoaderOptions.componentOptions.group.controls[fcName][ConstantCommon.CONTROLLER_FORCE_PREVIEW];
      content.dynamicLoaderOptions.componentOptions.group.controls[fcName][ConstantCommon.CONTROLLER_FORCE_PREVIEW] = CommonUtils.isNotNull(forcePreviewControl) ? forcePreviewControl : content.dynamicLoaderOptions.componentForcePreview;

      let value = this.commonProv.valFromNestedObj(fcName.split('-')[0], this.itemCard.data);


      this.labelsOptionList.forEach((card) => {
        if (card.dynamicLoaderOptions.componentOptions.fcName === fcName) {
          if (card.type === 'date') {
            if (value !== '' && value !== undefined && value !== null) {
              value = new PsDateFormatPipe().transform(new Date(value == '' ? new Date() : value), this.options.dateFormat);
            }
          } else if (card.type === 'select') {
            const control = this.options.formGroup.controls[fcName];
            let innerValue = control !== undefined ? control['innerValue'] !== undefined ? control['innerValue'] : undefined : undefined;
            if (innerValue === undefined) {
              const selectedOpt = card.dynamicLoaderOptions.componentOptions.listOfOptions.filter((opt: IPsSelect) => { return opt.itemValue === value; });
              if (selectedOpt && selectedOpt.length > 0) {
                innerValue = selectedOpt[0];
                control['innerValue'] = innerValue;
                innerValueForTypeSelect = innerValue;
              }
            }
            card.dynamicLoaderOptions.componentOptions.selectedItem = value;
            value = innerValue !== undefined ? innerValue['description'] : value;
          }
        }
      })

      if (!content.isEdit) {
        this.common.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [CommonUtils.returnNameWithoutIndex(fcName, this.itemCard?.rowIndex)], 1);
      }
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [CommonUtils.returnNameWithoutIndex(this.checkBoxOptions.fcName, this.itemCard?.rowIndex)], 0);

      this.setValueToFormData(fcName, value, innerValueForTypeSelect);
    }
  }

  /**
   * function used to emit the click of the dynamic action button.
   *
   * @author ClarkRouhana
   * TP #1217612
   */
  onDynamicLoaderClick(event, actionButtonOptions) {
    event['rowNode'] = this.itemCard;

    if (actionButtonOptions.onPsClick !== undefined) {
      actionButtonOptions.onPsClick(event);
    }
  }

  /**
   * function called on long-press of a card. In Case of single-selection, the event will be emitted immediately; otherwise in case of multi-selection, the checkbox will be checked then it trigger onCardCheckboxSelection() to emit the event
   *
   * @author ClarkRouhana
   * TP #1217612
   */
  onCardLongPressSelection(event) {
    if (!this.itemCard.selected) {
      if (this.options.checkboxSelection && !this.showHideCheckboxes(this.itemCard)) {
        this.options.formGroup.controls[this.checkBoxOptions.fcName].setValue(true);
      } else if (!this.options.checkboxSelection) {
        document.querySelector('.selected-container-lookup')?.classList.remove('selected-container-lookup');
        document.getElementsByClassName('container-lookup' + this.rowIndexClass)[0].classList.add('selected-container-lookup');
        this.onCardSelectionChange.emit(this.itemCard);
      }
    }
  }

  /**
   * function called on checkbox value changed so that it emits the event.
   *
   * @author ClarkRouhana
   * TP #1217612
   */
  onCardCheckboxSelection(event) {
    this.itemCard.selected = event.newValue;
    this.showHideCheckboxes(this.itemCard);
    let card = {
      ...this.itemCard, onSelectCheckboxChange: event
    };
    this.onCardSelectionChange.emit(card);
  }

  /**
   * function used to show or hide the checkboxes depending on at least of one card selection
   *
   * @author ClarkRouhana
   * TP #1217612
   */
  showHideCheckboxes(itemCard) {
    let display = false;

    if (itemCard.selected) {
      document.getElementsByClassName('container-lookup' + this.rowIndexClass)[0].classList.add('selected-container-lookup');
    } else {
      document.getElementsByClassName('container-lookup' + this.rowIndexClass)[0].classList.remove('selected-container-lookup');
    }

    if (document.querySelector('.selected-container-lookup')) {
      display = true;
    }

    document.querySelectorAll('#card-selection-checkbox').forEach((checkbox: HTMLElement) => {
      checkbox.style.display = display ? 'flex' : 'none';
    });

    return display;
  }

}



