import { Directive, ElementRef, Host, HostListener, Input, OnInit, Optional, SkipSelf } from '@angular/core';
import { AbstractControl, FormControl, FormGroupDirective } from '@angular/forms';
import Inputmask from 'inputmask';
import { ConstantCommon } from '../../psServices/models/common-constant';
import { IPsMaskOptions } from '../../psServices/models/ps-common-interface';


@Directive({
  selector: '[psInputMask]'
})
export class PsInputMaskDirective implements OnInit {
  @Input() psInputMask: IPsMaskOptions;
  @Input() formControlName: string;
  private inputController: AbstractControl;

  /**
   * Event focusin in directive
   * @author Richard Zourob
   * @constant {string} value
   * remove the number format while focus on the input
   */
  @HostListener("focusin", ["$event"])
  inputOnfocus(event: any): void {
    if (event.target.value !== '.' && this.psInputMask?.alias && this.psInputMask?.alias === 'currency') {
      if (event.target.value != null && event.target.value != "") {
        const val = event.target.value;
        const value = parseFloat(val.toString().replace(new RegExp(ConstantCommon.THOUSANDS_SEPARATOR, 'g'), ''));
        if (!isNaN(value))
          event.target.value = value;
        else
          event.target.value = null;
      }
    }
    //document.body.firstElementChild.setAttribute("tabIndex" , "1");
  }


  /**
   * Event focusout in directive
   * reformat the number while blur from the input
   */
  @HostListener("focusout", ["$event"])
  onBlur(event: any): void {
    const val = event.target.value;
    // Condition Added by Richie in order not to use the masker in case not provided
    if (val && this.psInputMask) {
      if (val == '.') {
        event.preventDefault();
        event.stopPropagation();
      }
      const value = this.returnValue(val, event);
      event.target.value = value;
    }
  }

  /**
   * Event keypress in directive
   * handle the decimal fraction length
   */
  @HostListener('keypress', ['$event'])
  inputpress(event: any): void {
    var value = event.target.value;
    if (value && this.psInputMask) {
      if (value == "NaN") {
        event.target.value = null;
      }

      var decimalPointPosition = value.indexOf(".");
      if (!((event.charCode === 46) || (event.charCode > 47 && event.charCode <= 57))) {
        event.preventDefault();
        event.stopPropagation();
      }

      else if (decimalPointPosition >= 0) {
        if (event.charCode === 46) {
          event.preventDefault();
          event.stopPropagation();
        }
        else {
          var decimalCount = value.substring(decimalPointPosition + 1).length;
          if ((decimalCount == this.psInputMask.digits && event.target.selectionStart > decimalPointPosition)) {
            event.preventDefault();
            event.stopPropagation();
          }
        }
      }
    }
  }

  constructor(
    @Optional() @Host() @SkipSelf()
    private controlContainer: FormGroupDirective,
    private elementRef: ElementRef
  ) { }

  ngOnInit(): void {
    // Condition Added by Richie in order not to use the masker in case not provided
    if (this.psInputMask) {
      if (this.controlContainer && this.controlContainer.control) {
        if (this.formControlName) {
          this.inputController = this.controlContainer.control.controls[this.formControlName];
        } else {
        }
      } else {
      }
      this.initialValue();
    }
  }

  initialValue(): void {
    const initVal = this.elementRef.nativeElement.value;
    const value: string = this.returnValue(initVal);
    this.setValueInFormControl(value);
  }

  returnValue(value: string, event?: any): string {
    if (value && this.psInputMask) {
      const formattedVal: string = Inputmask.format(value, this.psInputMask);
      if (this.inputController) {
        this.inputController[ConstantCommon.CONTROLLER_VALUE_CHANGED] = true;
      }
      // Added by Richie for #BUG 1192327
      const splittedVal = value.toString().split('.');
      if (splittedVal && splittedVal.length === 2 && splittedVal[0] === '' && (splittedVal[1] === '' || parseInt(splittedVal[1], 10) === 0)) {
        this.inputController.setValue('', { emitEvent: false });
        return '';
      }
      // this case we don't format the number if we erase a number from the end so no need to reformat and display unessecary zeros
      if (event && (event.code === 'Backspace' || event.key === 'Backspace' || event.keyCode === 229 || event.keyCode === 8 || event.key === 'Delete' || event.code === 'Delete') && value.indexOf('.') !== -1 && parseFloat(formattedVal.toString().replace(new RegExp(ConstantCommon.THOUSANDS_SEPARATOR, 'g'), ''))
        === parseFloat(value.toString().replace(new RegExp(ConstantCommon.THOUSANDS_SEPARATOR, 'g'), ''))
        && (value.split('.').length === 2) && value.split('.')[1].length <= this.psInputMask.digits
        && value.split('.')[0] === formattedVal.split('.')[0]) {
        return value;
      } else {
        if (this.psInputMask.alias === 'amount' && splittedVal.length === 2 && splittedVal[1].length < 2) {
          return value; // Allow user to enter the second digit without formatting
        }
        return formattedVal;
      }
    } else {
      return value;
    }
  }
  /**
   * The verification of form
   * @author Richard Zourob
   * @example <caption>this.verifyFormControl()</caption>
   * @returns {boolean} return a boolean value
   */
  verifyFormControl(): boolean {
    if (this.inputController instanceof FormControl) {
      return false;
    } else {
      return false;
    }
  }

  /**
   * Set Value em FormControl
   * @author Richard Zourob
   * @example <caption>this.setValueInFormControl(string)</caption>
   */
  setValueInFormControl(value: string): void {
    if (!this.verifyFormControl()) {
      this.elementRef.nativeElement.value = value;
      return;
    }

    // emitEvent: false added by Richie in order to prevent calling the change event upon pressing each key
    this.inputController.setValue(value, {
      emitEvent: false,
      // emitModelToViewChange: false,
      emitViewToModelChange: false
    });
    this.inputController.updateValueAndValidity({ emitEvent: false });
  }

}
