import { Injectable } from '@angular/core';
import { IOperDef } from 'src/app/pages/omni-login/omni-login.interfaces';
import { Events } from '../Event/event.service';
import { HttpService } from '../http/http.service';
import { LoggerService } from '../logger/logger.service';
import { ConstantCommon } from '../models/common-constant';
import { PsCommonSettings } from '../models/ps-common.settings';
import { SessionService } from '../session/session.service';
import { CommonCustUtils } from './../../customization/psServices/util/common-cust-utils';




@Injectable({ providedIn: 'root' })
export class DynamicButtonsService {
    public currentOperId: any;
    public actionButtonsMap: Map<number, any[]> = new Map<number, any[]>();
    public actionLabelsMap: Map<number, any[]> = new Map<number, any[]>();
    loadButtons = true;
    constructor(public logger: LoggerService, private http: HttpService, private session: SessionService, public events?: Events) {
    }

    public async getActionButtons(params) {
        this.events.subscribe('submitShortcuts', (response) => {
            if (this.loadButtons) {
                this.loadButtons = false;
                this.actionButtonsMap.clear();
                this.getActionButtons({ operId: this.currentOperId !== undefined ? this.currentOperId : PsCommonSettings.oper_ID, buttonType: 'dynamicActionButton' }).then(() => {
                    this.events.publish('custButtonReloaded', true);
                });
                setTimeout(() => {
                    this.loadButtons = true;
                }, 2000);
            }
        });
        this.currentOperId = params.operId;
        if (this.actionButtonsMap.get(this.currentOperId) !== undefined) {
            return new Promise<any>((resolve, reject) => {
                resolve(this.actionButtonsMap.get(this.currentOperId));
            });
        } else {
            return new Promise<any>((resolve, reject) => {
                params[ConstantCommon.CALLED_FROM] = CommonCustUtils.returnCalledFrom();
                this.http.commonRequestAjax(PsCommonSettings.serviceUrl.actionButtons, params).then((result) => {
                    if (result && result.data && result.data.gridModel && result.data.gridModel.length > 0) {
                        if (params.buttonType === 'dynamicActionButton') {
                            result.data.gridModel = this.prepareActionButtons(result.data.gridModel)
                        }
                        this.actionButtonsMap.set(this.currentOperId, result.data.gridModel);
                        resolve(result.data.gridModel);
                    } else {
                        // added so that empty reponses can be added to the map as well so that the service is not always called when actionButtons for the particular oper is required
                        this.actionButtonsMap.set(this.currentOperId, []);
                        resolve(null);
                    }
                }).catch((err) => {
                    this.logger.error('Error: While fetching Action Buttons :', err);
                    reject(err);
                });
            });
        }
    }

    public fillActionButtons(gridModel: any, buttonType: string, currentOperId: number) {
        if (gridModel && gridModel.length > 0) {
            if (buttonType === 'dynamicActionButton') {
                gridModel = this.prepareActionButtons(gridModel)
            }
            this.actionButtonsMap.set(currentOperId, gridModel);
        } else {
            // added so that empty reponses can be added to the map as well so that the service is not always called when actionButtons for the particular oper is required
            this.actionButtonsMap.set(currentOperId, []);
        }
    }

    returnOperCardButtons(): any[] {
        return this.actionButtonsMap.get(PsCommonSettings.oper_ID).map((actionBtn =>JSON.parse(JSON.stringify(actionBtn))));
    }

    fillActionLabels(gridModel: any, currentOperId: number) {
        if (gridModel && gridModel.length > 0) {
            this.actionLabelsMap.set(currentOperId, gridModel);
        } else {
            this.actionLabelsMap.set(currentOperId, []);
        }
    }

    returnOperCardLabels(): any[] {
        return this.actionLabelsMap.get(PsCommonSettings.oper_ID);
    }

    // Prepare the action buttons to make it compatible with the already written logic for displaying static buttons defined in business component
    prepareActionButtons(actionButtonArr) {
        let actionButtons = [];
        let routeDetail;
        actionButtonArr.forEach((button) => {
            let buttonObj = {};
            let buttonParams = null;
            if (button.parameters && this.IsJsonString(button.parameters)) {
                buttonParams = JSON.parse(button.parameters);
            }

            buttonObj['businessRelatedYN'] = button.businessRelatedYN;
            buttonObj['cardButtonsId'] = button.cardButtonsId;
            buttonObj['cardActionsId'] = button.cardActionsId;
            buttonObj['shortcutButton'] = button;
            buttonObj['id'] = String(button.keyLabelDesc).toLowerCase();
            if (button.actionType === 'S') {
                buttonObj['actionType'] = buttonParams !== null && buttonParams.actionType ? buttonParams.actionType : button.actionType;
                buttonObj['actionUrl'] = button.actionUrl;
                buttonObj['mappingId'] = button.mappingId;
                buttonObj['iconName'] = button.iconName;
                buttonObj['label'] = button.keyLabelDesc;
                buttonObj['alertLabel'] = button.alertkeyLabelDesc;
                buttonObj['labelOptions'] = {
                    labelKey: button.keyLabelDesc
                }
                if (button.alertType === 'W') {
                    buttonObj['alertStyle'] = ConstantCommon.ACTION_ALERT_WARNING;
                }
                buttonObj['alertType'] = button.alertType === 'P' ? ConstantCommon.ACTION_REASON_INPUT : button.alertType === 'C' ? ConstantCommon.ACTION_REASON_WARNING : ConstantCommon.ACTION_REASON_WARNING;
                buttonObj['reloadList'] = true;
            }
            if (button.actionType === 'H') {
                routeDetail = this.getPageByOperId(button.actionOper);
                if (routeDetail !== undefined || PsCommonSettings.custMode) {
                    buttonObj['label'] = button.keyLabelDesc;
                    buttonObj['actionHyperlink'] = {
                        iconOptions: {
                            iconName: button.iconName,
                            labelOptions: {
                                labelKey: button.keyLabelDesc
                            }
                        },
                        route: PsCommonSettings.custMode ? '' : routeDetail.PAGE_HREF,
                        navigationOptions: {
                            queryParams: buttonParams
                        },
                        pageOptions: {
                            iconName: button.iconName,
                            operId: JSON.parse(button.actionOper),
                            title: button.keyLabelDesc
                        }
                    }
                }
            }
            if (button.actionType === 'C') {
                buttonObj['label'] = button.keyLabelDesc;
                buttonObj['component'] = button.componentName;
                buttonObj['componentOptions'] = buttonParams;
            }
            if (button.alertInputName !== undefined) {
                buttonObj['fcName'] = button.alertInputName;
            }
            if (buttonObj['alertType'] !== undefined && button.alertInputName === undefined) {
                buttonObj['fcName'] = 'alertInputName';
            }
            if (button.additionalParam === 'Y') {
                buttonObj['preCallFunction'] = this.prepareCallFunction(button);
            }
            if (button.actionType === 'H' && (routeDetail !== undefined || PsCommonSettings.custMode)) {
                actionButtons.push(buttonObj);
            } else if (button.actionType !== 'H') {
                actionButtons.push(buttonObj);
            }
        });
        return actionButtons;
    }
    getPageByOperId(operId) {
        const pages = this.session.getValueOf(ConstantCommon.BUSINESS_PROFILE_MAP) as Array<IOperDef>;
        if (pages) {
            const activePage = pages.find((p) => {
                return p.OPER_ID == operId;
            });
            return activePage;
        }
        return null;
    }
    prepareCallFunction(actionButtonDetail) {
        return {
            func() {
                return new Promise<any>((resolve, reject) => {
                    resolve(this.executionClass.preparePreCallFunction(actionButtonDetail));
                });
            },
            executionClass: this,
            params: []
        }
    }

    preparePreCallFunction(actionButtonDetail) {
        this.prepareCommonReqAjax(actionButtonDetail.addParamServiceUrl, actionButtonDetail.navigationOptions.queryParams).then((result) => {
            if (result !== undefined) {
                actionButtonDetail.navigationOptions.queryParams = {
                    ScreenVO: { result, ...actionButtonDetail.navigationOptions.queryParams },
                    ...actionButtonDetail.navigationOptions.queryParams
                }
            }
        }), (error) => {
            // this.showLoader = false;
        }
    }
    // Common request ajax method to avoid calling bussiness component services
    public async prepareCommonReqAjax(url, params): Promise<any> {
        params['apiCode'] = -1;
        return new Promise<any>((resolve, reject) => {
            this.http.commonRequestAjax(url, params
            ).then
                ((result) => {
                    const list: Array<any> = result.data.gridModel as Array<any>;
                    if (result.data !== undefined && list === undefined) {
                        resolve(result.data);
                    }
                    if (list && list.length > 0) {
                        const data = list[0];
                        resolve(data);
                    }
                }).catch((error) => {
                    this.logger.error('Error: ' + error);
                    reject(undefined);
                });
        })

    }
    IsJsonString(str) {
        try {
            JSON.parse(str);
        } catch (e) {
            return false;
        }
        return true;
    }

}
