export class HttpService {
  private myHeaders = new HttpHeaders();
  public activeConnectionMap: any = {};
  public isNoRequestPendingBS: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(null);

  constructor(public http: HttpClient, private session: SessionService, private events: Events, private logger: LoggerService, private psNetworkService: PsNetworkService, private hybridkey: HybridKeyService, private requestCache: PsRequestCacheService) { }

  sendRequest(url: string, theParams?: any, timeOut?: number): Promise<any> {
    return new Promise<any>((resolve, reject) => {
      let timestamp = theParams["URequestID"];

      let newUrl = PsApplicationSettings.MAIN_CONFIG.SERVICE_URL + url;
      if (PsApplicationSettings.MAIN_CONFIG.ENABLE_REQUEST_ID && !environment.production) {
        newUrl = newUrl + "?URequestID=" + timestamp
      }
      this.logger.log(newUrl);
      // let _callStack: Array<string> = new Array<string>();
      // (new Error().stack).split("at").filter((x) => { return x.includes("webpackJsonp") }).forEach((str) => { let sp = str.replace(/ *\([^)]*\) */g, "").split("."); _callStack.push(`at  ${sp[sp.length - 1].trim() + '()'}  in ${sp[sp.length - 2].trim() + '.Class'}`) });
      // this.logger.log("%c Stack Call:", 'background: #5E35B1; color: #FFF', _callStack);
      const sub = this.http.request('POST', newUrl, {
        body: theParams ? theParams : {}, // set as empty json in case it was null by Richie for #BUG 818945
        headers: this.myHeaders,
        observe: 'response',
        reportProgress: true,
        responseType: 'json'
      }).pipe(timeout(timeOut)).subscribe(async res => {
        if (res && res.hasOwnProperty('ok') && res.ok) {
          try {//added by ghada for tp#1270405 to fix unhandeled excpetions issue
            let returnedResponse: any = res.body;
            let finalResponse: any = res.body;
            if (returnedResponse.wasReadFromCache) {
              this.logger.log(new Date() + `%c Decrypted From Cache ` + timestamp + ` : [${res.url.split('/').pop()}]`, 'background: #f24eda; color: #FFF', finalResponse);
              resolve({
                success: true,
                error: null,
                data: res.body,
              });
              return res.body;
            }
            const enableEcryption = CommonUtils.cacheParameterIfNotCached(PsCommonSettings.ENCRYPT_PARAMS, this.session);

            /* nabil feghali - OMNI common security  */
            if (finalResponse && finalResponse.hasOwnProperty('pathResp')) {
              let decryptedPathParam;
              if (CommonUtils.isNativeMobile() && CommonUtils.isAndroid()) {
                decryptedPathParam = await this.hybridkey.nativeDecrypt(finalResponse.pathResp);
              } else {
                decryptedPathParam = this.hybridkey.decryptTransportMessage(finalResponse.pathResp);
              }
              finalResponse = JSON.parse(decryptedPathParam);
              if (theParams[PsCommonSettings.CUSTOM_TIMEOUT_REQUEST_TOAST]) {
                finalResponse[PsCommonSettings.CUSTOM_TIMEOUT_REQUEST_TOAST] = theParams[PsCommonSettings.CUSTOM_TIMEOUT_REQUEST_TOAST];
              }
            }
            /* nabil feghali - OMNI common security - end  */

            if (enableEcryption && finalResponse && finalResponse.hasOwnProperty('pathParam') && finalResponse.hasOwnProperty('key') && finalResponse.hasOwnProperty('iv')) {
              this.logger.log(new Date() + ` %c Response ` + timestamp + ` : [${res.url.split('/').pop()}]`, 'background: #36f746; color: #000', res);
              const retData: IHTTPResponseResult = res.body;
              if (finalResponse) {
                try {
                  /* nabil feghali - OMNI common security  */
                  if (CommonUtils.isNativeMobile() && CommonUtils.isAndroid()) {
                    const pKey = this.session.getValueOf(ConstantCommon.PRIVATE_KEY);
                    finalResponse = await this.hybridkey.nativeDecrypt(finalResponse, pKey);
                    finalResponse = JSON.parse(finalResponse);
                  } else {
                    finalResponse = this.decryptResponse(finalResponse);
                  }
                  if (PsApplicationSettings.MAIN_CONFIG.LOG_LEVEL_ALL) {
                    CommonUtils.presentInfoAlert(`${url}: ${finalResponse.outputNotification}`, {title: 'Tip'});
                  }

                  if (url.includes(ConstantCommon.LOGIN_USER_URL)
                    && finalResponse.hasOwnProperty(ConstantCommon.SESSION_ID)
                    && finalResponse[ConstantCommon.SESSION_ID] != null
                    && finalResponse[ConstantCommon.SESSION_ID] != '') {
                    this.session.append(ConstantCommon.SESSION_ID, finalResponse[ConstantCommon.SESSION_ID]);
                  }

                  if (finalResponse.hasOwnProperty(ConstantCommon.SERVER_LOGIN_TOKEN)
                    && finalResponse[ConstantCommon.SERVER_LOGIN_TOKEN] != null
                    && finalResponse[ConstantCommon.SERVER_LOGIN_TOKEN] != '') {
                    this.session.append(ConstantCommon.SERVER_LOGIN_TOKEN, finalResponse[ConstantCommon.SERVER_LOGIN_TOKEN]);
                  }


                  if (finalResponse.hasOwnProperty(ConstantCommon.SERVER_AUTH_TOKEN)
                    && finalResponse[ConstantCommon.SERVER_AUTH_TOKEN] != null
                    && finalResponse[ConstantCommon.SERVER_AUTH_TOKEN] != '') {
                    this.session.append(ConstantCommon.SERVER_AUTH_TOKEN, finalResponse[ConstantCommon.SERVER_AUTH_TOKEN]);
                  }

                  /* nabil feghali - OMNI common security - end  */

                } catch (err) {
                  this.logger.error(retData.key);
                  this.logger.error(this.session.getValueOf(ConstantCommon.PRIVATE_KEY));
                  this.logger.error(url);
                }
                this.logger.log(new Date() + `%c Decrypted ` + timestamp + ` : [${res.url.split('/').pop()}]`, 'background: #f24eda; color: #FFF', finalResponse);
                const validity = this.processResponse(finalResponse, url, theParams);
                if (validity && !validity.isValid) {
                  reject({
                    success: false,
                    error: null,
                    data: finalResponse,
                    suppressMessage: true
                  });
                  return;
                }
                this.cacheRequest(newUrl, finalResponse, returnedResponse);
                resolve({
                  success: true,
                  error: null,
                  data: finalResponse,
                });
              } else {
                reject({
                  success: false,
                  error: null,
                  data: finalResponse,
                  suppressMessage: true
                });
              }
            } else {
              if (PsApplicationSettings.MAIN_CONFIG.LOG_LEVEL_ALL) {
                CommonUtils.presentInfoAlert(`${url}: ${finalResponse.outputNotification}`, {title: 'Tip'});
              }
              const result = this.checkStatus(finalResponse);
              this.logger.log(new Date() + ` %c Response ` + timestamp + ` : [${res.url.split('/').pop()}]`, 'background: #36f746; color: #000', res);
              const validity = this.processResponse(finalResponse, url, theParams);
              this.handleSecurityError(finalResponse, url, theParams, timeOut).then((secResult) => {
                if (secResult === false) {
                  if (validity && !validity.isValid) {
                    reject({
                      success: false,
                      error: null,
                      data: finalResponse,
                      suppressMessage: true
                    });
                    return;
                  }
                  if (result.success) {
                    this.cacheRequest(newUrl, finalResponse, returnedResponse);
                    resolve({
                      success: true,
                      error: null,
                      data: finalResponse
                    });
                  } else {
                    resolve({
                      success: false,
                      error: null,
                      data: finalResponse
                    });
                  }
                } else {
                  resolve(secResult);
                }
              }).catch((err) => {
                reject(err);
              });

            }
          } catch (e) {
            this.logger.error('something went wrong in sendRequest/http.service', e);
          }
        }
      },
        (err) => {
          if (PsCommonSettings.isCertificateExpired) {
            CommonUtils.returnExcludedURLs().forEach(element => {
              if (!url.includes(element)) {
                return;
              }
            });
          }
          let timestamp = theParams["URequestID"];
          if (err && err.error) {
            if (err.error === 'anotherSession') {
              this.events.publish('user:endedByOtherActiveSession');
              return;
            }
          }
          if (err instanceof HttpErrorResponse) {
            switch ((err as HttpErrorResponse).status) {
              case 500:
                this.logger.error('%c [' + String.fromCharCode(9888) + ']' + ' Something Went Wrong On The Server!', 'background: #eeff05; color: #000');
                break;
              // Commented temporarly until fixing the case to have specific error code handling when restarting the server
              // if (this.session.getValueOf(ConstantCommon.USER_IS_LOGGED_IN)) {
              //   this.events.publish("Navigator:logout");
              // }
              default:
                throwError(new HttpErrorResponse(err));

            }
            this.logger.error('Server error please contact administrator, ' + err.message);
            this.logger.error('%c ERROR --------------------------------------------------------', 'background: #222; color: #FF5959');
            this.logger.error(err);
            this.logger.error('%c END ----------------------------------------------------------', 'background: #222; color: #FF5959');
            resolve({
              success: false,
              error: 'Server error please contact administrator, ' + err.message,
              data: null
            }
            );
          } else if (err instanceof TimeoutError) {
            sub.unsubscribe();
            this.logger.error(new Date() + " " + CommonUtils.translate('request_timeout_key') + " " + url + " URequestID=" + timestamp);
            reject(CommonUtils.translate('request_timeout_key'));
            return false;
          } else {
            this.logger.error('[' + String.fromCharCode(9888) + ']', 'Server is down or your cors extension need reload', err);
            resolve({
              success: false,
              error: 'Unknown Error',
              data: null
            });
          }

        });

      // commented by Richie and replaced with request.pipe(timeout(timeOut)) since the timeout section was triggered in case of cust war even if the response was returned successfully
      // if (timeOut) {
      //   setTimeout(() => {
      //     sub.unsubscribe();
      //     reject(CommonUtils.translate('request_timeout_key'));
      //     return false;
      //   }, timeOut);
      // }
    });

  }
  public async commonRequestAjax(url: string, theParams?: any): Promise<any> {
    const tempTimestamp = Date.now();
    let randomNumber = Math.floor((Math.random() * 999999) + 1);
    let timestamp = tempTimestamp + randomNumber;

    // Added by Richie to log the time of each request
    if (PsApplicationSettings.MAIN_CONFIG.TIME_LOG_ENABLED) {
      PsCommonSettings.timeLogs.push('request for ' + url + ' with start time: ' + (new Date()).toISOString());
    }
    // end Richie
    if (CommonUtils.isEmptyObject(theParams)) {
      theParams = {};
    }
    const connectionStatus: ConnectionStatus = await this.psNetworkService.handleNetworkNotifications();
    if (connectionStatus === 0) {
      return;
    }
    if (PsApplicationSettings.MAIN_CONFIG.ENABLE_REQUEST_ID && !environment.production) {
      theParams["URequestID"] = timestamp;
    }
    return new Promise<any>((resolve, reject) => {
      if (this.activeConnectionMap[PsCommonSettings.oper_ID] >= 0) {
        this.activeConnectionMap[PsCommonSettings.oper_ID]++
      } else {
        this.activeConnectionMap[PsCommonSettings.oper_ID] = 1;
      }

      /* GI: *M094115032022* Moved the below from send request method to here in order to have a final theParams in order to check correctly upon caching; #1347894  */
      let timeOut = CommonUtils.getCurrentRequestTimeOut(url, theParams);

      if (PsCommonSettings.requestTimeOut < timeOut) {
        let interval = CommonUtils.executeWithTimeOut(null, timeOut / 1000, CommonUtils.translate('this_might_take_a_while_key'));
        theParams[PsCommonSettings.CUSTOM_TIMEOUT_REQUEST_TOAST] = interval;
      }

      /* End GI */
      /* GI: Added the below for #1347894*/
      const requestURLIsDefined = PsApplicationSettings.MAIN_CONFIG.REQUESTS_TO_CACHE[url];
      if (requestURLIsDefined) {
        const EXCLUDED_OPERS: Array<any> = requestURLIsDefined.EXCLUDED_OPERS ? requestURLIsDefined.EXCLUDED_OPERS : [];
        if (EXCLUDED_OPERS.length == 0 || !(EXCLUDED_OPERS.includes(PsCommonSettings.oper_ID))) {
          theParams["requestToBeCached"] = true;
        }
      }
      /* End GI */

      /* Jira #5913 Makki: Clean The payload from parameters like (outputNotification) as per OSRV team   request in mentioned jira issue*/
      theParams = this.cleanParametersFromKeywords(theParams, PsCommonSettings.parametersToRemove);
      /* End of Jira #5913*/
      const runRequest = () => {
        return this.sendRequest(url, theParams, timeOut).then((response) => {
          this.decrementPendingRequest(theParams, response);
          if (PsApplicationSettings.MAIN_CONFIG.TIME_LOG_ENABLED) {
            PsCommonSettings.timeLogs.push('request for ' + url + ' with cached response: end time: ' + (new Date()).toISOString());
          }
          resolve(response);
        }).catch((error) => {
          this.decrementPendingRequest(theParams, error);
          if (error && error.type !== 'U' && !(error.type && error.outputCode) && !error.suppressMessage) {
            CommonUtils.presentFatalAlert(error);
          }
          CommonUtils.dismissLoading();
          reject(error);
        });
      };
      const isDeviceComaptible = this.events.returnChannelBsValue('device:compatibility');
      let isWhitelisted = CommonUtils.returnRootValidationWhitelist().some(element => url.includes(element));

      if (!CommonUtils.isAndroid() || (isDeviceComaptible || isWhitelisted)) {
        return runRequest();
      }
        // Otherwise, wait for validation to finish
        this.events.getEvent$('device:compatibility').pipe(
          tap(val => {
            if (val === null) {
              this.logger.log('request for ' + url + 'is waiting for root validation result with start time: ' + (new Date()).toISOString());
              if (PsApplicationSettings.MAIN_CONFIG.LOG_LEVEL_ALL) {
                  CommonUtils.presentInfoAlert(`${url}: is waiting for root detection validation result`, {title: 'Tip'});
              }
            }
          }),
          filter(val => val !== null),
          take(1)
        ).subscribe(isAllowed => {
          if (!isAllowed) {
            reject(new Error('Rooted device detected'));
            return;
          }
          runRequest();
        });
  });
  }

  /* Jira #5913 Makki: Recursively check every Array,Object and remove the required keywords from it    */
  private cleanParametersFromKeywords(obj: any, keywords: string[]): any {
    if (Array.isArray(obj)) {
      return obj.map(item => this.cleanParametersFromKeywords(item, keywords));
    } else if (obj && typeof obj === 'object') {
      for (const key of Object.keys(obj)) {
        if (keywords.some(keyword => key.includes(keyword))) {
          delete obj[key];
        } else {
          obj[key] = this.cleanParametersFromKeywords(obj[key], keywords);
        }
      }
    }
    return obj;
  }


  // ARagab : This func used to call any third party api [like : prayers api]
  public httpGet(url: string, theParams?: any): Promise<any> {
    return new Promise<any>((resolve, reject) => {
      this.http.get(url, theParams).subscribe(res => {
        resolve(res);
      },
        (err) => {
          reject(err);
          this.logger.error(err);
        });
    });
  }
  private processResponse(finalResponse: any, url: string, theParams?: any): any {
    // gilbertAndary removed the below in order to be compatible with latest code 11-11-2019
    // if (!url.includes('loginProcess') && (!url.includes('logoutUser'))) {
    const sessionId = this.session.getValueOf(ConstantCommon.SESSION_ID);
    const publicKey = this.session.getValueOf(ConstantCommon.PUBLIC_KEY);
    const ENCRYPT_PARAMS = CommonUtils.cacheParameterIfNotCached(PsCommonSettings.ENCRYPT_PARAMS, this.session);
    //AZDB-9537 AIBIRQI240102 - clone from AZDB-4642 override alerts --Mahmoud Awad
    let options: IOptionsAlert ={};
    if (finalResponse && finalResponse.hasOwnProperty(ConstantCommon.frontEndPublicKeyToSend) && ENCRYPT_PARAMS) {

      if (publicKey && String(finalResponse[ConstantCommon.frontEndPublicKeyToSend]) !== String(this.hybridkey.hashcode(publicKey))) {
        this.events.publish('user:endedByOtherActiveSession');
        return {
          showMessage: false,
          isValid: false
        };
      }
    }
    if (finalResponse && finalResponse.hasOwnProperty(ConstantCommon.SESSION_ID) && finalResponse[ConstantCommon.SESSION_ID] != '') {
      if (sessionId && String(finalResponse[ConstantCommon.SESSION_ID]) !== String(sessionId)) {
        this.events.publish('user:endedByOtherActiveSession');
        return {
          showMessage: false,
          isValid: false
        };
      }
    }
    if (finalResponse && finalResponse.entity) {
      let entityObj = null;
      try {
        entityObj = JSON.parse(finalResponse.entity);
      }
      catch (e) {
        this.logger.error(e);
      }
      // outputCode -3 means security error
      if (entityObj != null && entityObj.outputCode === -3 && entityObj.outputType == 'SEC_ERR') {
        return {
          showMessage: false,
          isValid: false
        };
      }
    }

    if ((url.includes(PsCommonBusinessSettings.serviceUrl.login) || url.includes(PsCommonSettings.serviceUrl.authenticate))
      && finalResponse && finalResponse.outputType
      && (((finalResponse.outputType).toString()).toLowerCase() === 'e' || ((finalResponse.outputType).toString()).toLowerCase() === 'f')) {

      this.session.clearUserSession();

    }

    //AZDB-4642 override alerts --Mahmoud Awad
    if(theParams.overrideAlerts){
      //override the alert
      const alert = theParams.overrideAlerts.find(s=>s.code == finalResponse.outputCode);
      if (alert){
        options = alert.optionsAlert;
        if(options.message){
          finalResponse.outputNotification = options.message;
        }
      }
    }
    
    return this.checkOutputType(finalResponse,options);
  }

  private handleSecurityError(finalResponse, url, params, timeOut) {
    return new Promise<any>((resolve, reject) => {
      /*nabil feghali : device registration*/
      let bypass = false;
      if ((finalResponse && finalResponse.entity) || (typeof finalResponse === 'object' && finalResponse.outputCode && finalResponse.errorCode)) {
        let entityObj = null;
        if (typeof finalResponse === 'object' && finalResponse.outputCode && finalResponse.errorCode) {
          entityObj = finalResponse;
        }
        else {
          try {
            entityObj = JSON.parse(finalResponse.entity);
          }
          catch (e) {
            this.logger.error(e);
          }
        }
        /*nabil feghali keycloak refresh token, errorCode === -23 means refresh login token and entityObj.errorCode === -24 means refresh auth token*/
        if (entityObj != null && entityObj.outputCode === -3 && (entityObj.errorCode === -23 || entityObj.errorCode === -24 || entityObj.errorCode === -27 || entityObj.errorCode === -29)) {
          this.refreshToken(entityObj, url, params, timeOut).then((result) => {
            resolve(result);
          }).catch((error) => {
            reject(error)
          });
        } else {
          if (entityObj != null) {
            if (entityObj.outputCode === -3) {
              if (entityObj.errorCode === -3 || entityObj.errorCode === -20) {
                this.events.publish('user:endedByOtherActiveSession', (entityObj.errorCode));
              }
              /*nabil feghali : device registration*/
              // -21 = Device Inactive while session was active and trying to call a service after login. Or New Device trying to call a service after login without registration
              // -22 = Device Blocked, for ex it has been stolen
              //checking on url.includes('logoutUser') to avoid getting infinite loop
              else if ((entityObj.errorCode === -22 || (entityObj.errorCode === -21 && !url.includes('logoutUser')))) {
                this.events.publish('user:deviceRegistrationErrorHandler', (entityObj.errorCode));
              }
              // -7 = another session exception, this occure when the session timeout appear and we call logout but at the same time the user is forced logged out
              else if (entityObj != null && entityObj.outputCode === -3 && (entityObj.errorCode === -7 /* && PsCommonSettings.isLoggedIn*/)) {
                this.events.publish('user:endedByOtherActiveSession', (entityObj.errorCode));
              }
              /*nabil feghali : for all other security errors we will show the below message based on Ali recommendation to show error message for such cases instead of keeping the system idle*/
              else {
                CommonUtils.presentFailureAlert(CommonUtils.translate('something_went_wrong_key'));
              }
            }
          }
          resolve(bypass);
        }
      } else {
        resolve(bypass);
      }
    });
  }

  checkOutputType(finalResponse, options?: IOptionsAlert) {
    if (finalResponse && finalResponse.outputType) {
      let alertOptions = finalResponse.actionType?.toLowerCase() === ConstantCommon.ACTION_TYPE_SUBMIT ? {customImageOperID: finalResponse.operId} : {};
      if(options!=null)
        options.customImageOperID=finalResponse.actionType?.toLowerCase() === ConstantCommon.ACTION_TYPE_SUBMIT ?  finalResponse.operId : options.customImageOperID;
      else options=alertOptions;
      if ((((finalResponse.outputType).toString()).toLowerCase() !== ConstantCommon.OUTPUT_TYPE_S) && (((finalResponse.outputType).toString()).toLowerCase() !== ConstantCommon.OUTPUT_TYPE_I) && (((finalResponse.outputType).toString()).toLowerCase() !== ConstantCommon.OUTPUT_TYPE_SC)) {
        if (((finalResponse.outputType).toString()).toLowerCase() === ConstantCommon.OUTPUT_TYPE_E) {
          CommonUtils.presentFatalAlert(finalResponse.outputNotification, options);
        } else if (((finalResponse.outputType).toString()).toLowerCase() === ConstantCommon.OUTPUT_TYPE_F) {
          CommonUtils.presentFailureAlert(finalResponse.outputNotification, options);
        }
        return {
          showMessage: false,
          isValid: false
        };
      } else {
        if (((finalResponse.outputType).toString()).toLowerCase() === ConstantCommon.OUTPUT_TYPE_I) {
          CommonUtils.presentInfoAlert(finalResponse.outputNotification,options);
        } else if (((finalResponse.outputType).toString()).toLowerCase() === ConstantCommon.OUTPUT_TYPE_S && finalResponse.outputCode > 0) {
          CommonUtils.presentSuccessAlert(finalResponse.outputNotification, { autoHide: true,...options });
        }
        return {
          isValid: true
        };
      }
    }
    return {
      isValid: true
    };
  }

  /**
   * function used to send request with file content
   * @author RichardZourob
   * @param url
   * @param theParams
   */
  public fileRequest(url: string, theParams?: any): Observable<HttpEvent<{}>> {
    const req = new HttpRequest('POST', PsApplicationSettings.MAIN_CONFIG.SERVICE_URL + url, theParams,
      {
        headers: this.myHeaders,
        reportProgress: true,
        responseType: 'text'
      });

    return this.http.request(req);
  }

  checkStatus(retData) {
    if (retData) {
      if (retData.hasOwnProperty('errorDesc') || retData.hasOwnProperty('errorCode')) {
        this.logger.warn('%c [' + String.fromCharCode(9888) + ']' + ' Something Went Wrong On The Server!', 'background: #eeff05; color: #000', retData.hasOwnProperty('errorDesc') ? retData.errorDesc : '', retData.hasOwnProperty('errorCode') ? retData.errorCode : '');
        return ({
          success: false,
          error: null,
          data: retData
        });
      } else {
        return ({
          success: true,
          error: null,
          data: retData
        });
      }
    } else {
      this.logger.warn('%c [' + String.fromCharCode(9888) + ']' + 'Either An error in enc/dec or your are not handling correctly your service! the body was returned as null', 'background: #eeff05; color: #000');
      return ({
        success: true,
        error: null,
        data: retData
      });
    }
  }

  decryptResponse(retData): string {

    /* nabil feghali - OMNI common security  */
    const pKey = this.session.getValueOf(ConstantCommon.PRIVATE_KEY);
    return this.hybridkey.decryptResponse(retData, pKey);
  }

  decrementPendingRequest(theParams, response) {
    if (theParams[PsCommonSettings.CUSTOM_TIMEOUT_REQUEST_TOAST]) {
      CommonUtils.dismissAllToast(theParams[PsCommonSettings.CUSTOM_TIMEOUT_REQUEST_TOAST]);
    }
    this.activeConnectionMap[PsCommonSettings.oper_ID]--;
    if (this.activeConnectionMap[PsCommonSettings.oper_ID] == 0) {
      this.isNoRequestPendingBS.next(true);
    }
  }

  /**
   * GI: this method will replace login token and refresh token with new tokens.
   * @param entityObj 
   * @param url 
   * @param params 
   * @param timeOut 
   */
  refreshToken(entityObj, url, params, timeOut): Promise<any> {
    return new Promise<any>((resolve, reject) => {
      let refreshURL = PsCommonSettings.serviceUrl.refreshToken;
      let refreshParam = {};
      let commonParametersList = {};
      let loginToken = this.session.getValueOf(ConstantCommon.SERVER_LOGIN_TOKEN);
      let authToken = this.session.getValueOf(ConstantCommon.SERVER_AUTH_TOKEN);
      commonParametersList['tokenType'] = (entityObj.errorCode == -23 || entityObj.errorCode == -27) ? 'loginToken' : 'authToken';
      commonParametersList['access_token'] = (entityObj.errorCode == -23 || entityObj.errorCode == -27) ? loginToken.token : authToken.token;
      commonParametersList['refresh_token'] = (entityObj.errorCode == -23 || entityObj.errorCode == -27) ? loginToken.refreshToken : authToken.refreshToken;
      refreshParam['commonParametersList'] = commonParametersList;
      this.commonRequestAjax(refreshURL, refreshParam).then((authResult) => {

        if (authResult.data.outputCode === -25 && authResult.data.errorCode === - 1) {
          this.events.publish('user:endedBysessionTermination');
          resolve(authResult);
        }
        else {
          if (authResult && authResult.data && authResult.data.serverLoginToken) {
            loginToken.token = authResult.data.serverLoginToken.token;
            loginToken.refreshToken = authResult.data.serverLoginToken.refreshToken;
          }
          this.session.append(ConstantCommon.SERVER_LOGIN_TOKEN, loginToken);
          this.commonRequestAjax(url, params).then((result) => {
            resolve(result);
          }).catch((error) => {
            reject(error);
          });
        }
      }).catch((error) => {
        reject(error);
      });
    });
  }
  cacheRequest(newUrl, finalResponse, returnedResponse) {
    if (returnedResponse.plainRequest && returnedResponse.plainRequest.requestToBeCached) {
      // delete returnedResponse.plainRequest.requestToBeCached;
      this.requestCache.permanentCache(newUrl, finalResponse, returnedResponse.plainRequest);
    }
  }

  
}
