import { AfterViewInit, Component, ElementRef, Input, ViewChild } from '@angular/core';
import * as Highcharts from "highcharts";
import Exporting from 'highcharts/modules/exporting';
import { fromEvent, Observable, Subscription } from 'rxjs';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IFileFormat, IOmniCommonFileRequest, IOptionsPsActionChartsComponent, IOptionsPsActionImage, IOptionsPsCharts } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';

@Component({
	selector: 'ps-action-charts',
	templateUrl: './ps-action-charts.component.html'
})
export class PsActionChartsComponent implements AfterViewInit {

	@Input() options: IOptionsPsActionChartsComponent = {};

	@ViewChild('chart', { read: ElementRef, static: false }) chartElement: ElementRef;
	@ViewChild('container', { read: ElementRef, static: false }) containerElement: ElementRef;
	chart: Highcharts.Chart;
	resizeObservable: Observable<Event>;
	resizeSubscription: Subscription;
	noReportDataFound;
	public dataWasLoaded = false;
	actionImageOptions: IOptionsPsActionImage = {
		imageName: CommonUtils.getCssVariableValue('--ps-loader-image-name')
	  };
	chartResizeSubscription: Subscription;
	constructor(private commonService: PsCommonService) { }

	async ngAfterViewInit() {
		if (this.options.chartType) {

			const reportRequest: IOmniCommonFileRequest = {
				operId: this.options ? this.options.operId : this.options.reportId || 1,
				progRef: this.options ? this.options.progRef : '',	
				reportFormat: IFileFormat.JSON,
				reportType: ConstantCommon.REPORT_TYPE,
				reportParametersList: this.options.reportParametersList
			};

			this.commonService.returnReportDetails(reportRequest).then(result => {
				if (result) {
					this.commonService.dismissLoading();
					if(result['data'] && result['data']['outputType'].toString().toLowerCase() !== 's'){
						this.noReportDataFound = true;
					}
					if (result['data']['base64Data']) {
						let jsonReportData = JSON.parse(atob(result['data']['base64Data']));
						let serviceData = this.parseJsonReportData(jsonReportData);

						const chartOptions = this.parseDataCommonFunction(serviceData, {});

						if (this.options.chartType == 'BC') {
							this.parseBarData(serviceData, chartOptions);
						}
						if (this.options.chartType == 'PC') {
							this.parsePieData(serviceData, chartOptions);
						}
						if (this.options.chartType == 'DC') {
							this.parseDonutData(serviceData, chartOptions);
						}
						if (this.options.chartType == 'LC') {
							this.parseLineData(serviceData, chartOptions);
						}
						if (this.options.chartType == 'AC') {
							this.parseAreaData(serviceData, chartOptions);
						}

						if (this.options.chartType == 'packedbubble') {
							this.parseBubbleData(serviceData, chartOptions);
						}
						Exporting(Highcharts);
						this.dataWasLoaded = true;
						this.chart = Highcharts.chart(this.chartElement.nativeElement, chartOptions, () => {
							this.resize();
						});

						this.resize();
					} else {
						this.dataWasLoaded = true;
						this.noReportDataFound = true;
					}


				}
			}).catch((error) => {
				this.commonService.dismissLoading();
				this.dataWasLoaded = true;
				this.noReportDataFound = true
				this.commonService.logger.log(error);
			});
		}


	}


	ngOnInit() {
		this.resizeObservable = fromEvent(window, 'resize');
		this.chartResizeSubscription = this.commonService.chartResizeSubject.subscribe(evt => {
    	    this.resize();
       })
		this.resizeSubscription = this.resizeObservable.subscribe(evt => {
			this.resize();
		});
	}

	resize() {
		if (this.chart && this.chartElement) {
			const containerWidth = this.containerElement.nativeElement.offsetWidth;
			// const containerHeight = this.chartElement.nativeElement.offsetHeight;
			const chartPadding = CommonUtils.getCssVariableValue('--ps-chart-padding');
			const result = 'calc(' + containerWidth + 'px - ' + chartPadding + ')px';
			CommonUtils.changeCssVariable('--ps-chart-width', result);
			this.chart.reflow();
		}
	}

	ngOnDestroy() {
		if (this.resizeSubscription) {
			this.resizeSubscription.unsubscribe();
		}
	}


	parseDataCommonFunction(data: IOptionsPsCharts, chartOption: Highcharts.Options): Highcharts.Options {

		Object.assign(chartOption, data['CHART']);			
		
		Object.assign(chartOption, {
						exporting: { enabled: false },
						credits: { enabled: false }
				});

	    const chartType = ['DC', 'PC'].includes(this.options.chartType) ? 'pie' : this.options.chartType === 'LC' ? 'line' : '';

		chartOption.chart = {
			type: chartType,
			options3d: {
				enabled: true,
				alpha: 45
			}
		};
		return chartOption;
	}



	parseBarData(data: IOptionsPsCharts, chartOption: Highcharts.Options): Highcharts.Options {
		this.parseBarLineAreaData(data, chartOption);
		chartOption.plotOptions = { bar: { dataLabels: { enabled: true } } };
		CommonUtils.setValInsideNestedObj('xAxis.categories', data.xAxis, chartOption);
		return chartOption;
	}


	parseLineData(data: IOptionsPsCharts, chartOption: Highcharts.Options) {
		this.parseBarLineAreaData(data, chartOption);
		chartOption.plotOptions = {
			series: {
				label: {
					connectorAllowed: false
				}
			}
		}
	}

	parseAreaData(data: IOptionsPsCharts, chartOption: Highcharts.Options): Highcharts.Options {
		this.parseBarLineAreaData(data, chartOption);
		chartOption.plotOptions = {
			area: {
				marker: {
					enabled: false,
					symbol: 'circle',
					radius: 2,
					states: {
						hover: {
							enabled: true
						}
					}
				}
			}
		}
		// CommonUtils.setValInsideNestedObj('xAxis.categories', data.xAxis, chartOption);
		return chartOption;
	}

	parseBarLineAreaData(data: IOptionsPsCharts, chartOption: Highcharts.Options) {
		if (chartOption.chart.type == 'line') {
			let categories = [];
			let seriesMap = {};
			let chartOptionSeries = [];
			if (data['SERIES']['DATA']) {
				for (const objectIndex in data['SERIES']['DATA']) {
					var serieObj = data['SERIES']['DATA'][objectIndex];
					if (!categories.includes(serieObj.x)) {
						categories.push(serieObj.x);
					}
					var seriesName = serieObj.name;
					var seriesY = serieObj.y;
					if (seriesName in seriesMap) {
						seriesMap[seriesName].push(Math.abs(seriesY));
					}
					else {
						seriesMap[seriesName] = [];
						seriesMap[seriesName].push(Math.abs(seriesY));
					}
				}

				chartOptionSeries = [];
				Object.keys(seriesMap).forEach(key => {
					const valueArray = seriesMap[key];
					chartOptionSeries.push({
						name: key,
						data: valueArray
					});
				});
			}
			chartOption.series = chartOptionSeries;
			chartOption.xAxis = { 'categories': categories };

		}
		else {
			chartOption.series = [];
			const series = data.data;
			Object.keys(series).forEach(key => {
				const value = series[key];
				chartOption.series.push({
					type: undefined,
					name: key,
					data: value
				});
			});
		}
	}


	parseBubbleData(data: IOptionsPsCharts, chartOption: Highcharts.Options) {
		chartOption.series = [];
		const series = data.data;
		let mainData = [];
		let result: any = {};
		const finalResult: any = [];
		Object.keys(series).forEach((key, index) => {
			const value = series[key];
			mainData = [];
			result = {};
			// const total = value.reduce((a, b) => a + b);
			result.name = key;
			for (let i = 0; i < value.length; i++) {
				const name = data.xAxis[i];
				mainData.push({
					name: name,
					value: value[i]
				});
			}
			result.data = mainData;
			finalResult.push(result);
		});
		chartOption.series.push(
			finalResult
		);

		chartOption.plotOptions = {
			packedbubble: {
				minSize: '30%',
				maxSize: '120%',
				layoutAlgorithm: {
					splitSeries: "false",
					gravitationalConstant: 0.02
				},
				dataLabels: {
					enabled: true,
					format: '{point.name}',
					filter: {
						property: 'y',
						operator: '>',
						value: 250
					},
					style: {
						color: 'black',
						textOutline: 'none',
						fontWeight: 'normal'
					}
				}
			}
		}
	}


	parseDonutData(data: IOptionsPsCharts, chartOption: Highcharts.Options): Highcharts.Options {

		let returnedChartOption = this.parsePieData(data, chartOption);
	    returnedChartOption.plotOptions.pie.innerSize = '60%';
		return returnedChartOption;
	}

	parsePieData(data: IOptionsPsCharts, chartOption: Highcharts.Options): Highcharts.Options {
		CommonUtils.copyObject(chartOption.plotOptions.pie, {
		 allowPointSelect: true,
		 cursor: 'pointer',
		 dataLabels: { enabled: true },
		 showInLegend: true
		  }, false, true);
		
		chartOption.series = [data['SERIES']];
		const rawData = Object.values(data['SERIES']['DATA']) as any[];
		const processedData = rawData.map(point => ({
		 ...point,
		 y: Math.abs(point.y)
		}));
  
	   CommonUtils.copyObject(chartOption.series[0], {
		colorByPoint: true,
		type: undefined,
		data: processedData
	   }, false, true);
	  
	   return chartOption;
	  }

	  parseJsonReportData(obj: any): any {
		const result: any = { SERIES: { DATA: [] } };
		let tempSeriesData = {};
	
		// Helper function to convert boolean-like strings
		const parseBoolean = (value: string): boolean | string => {
			if (value === 'boolean:true') return true;
			if (value === 'boolean:false') return false;
			return value;
		};
	
		for (const entry of Object.values(obj)) {
			const objectPath = entry["HOST"];
			const value = parseBoolean(entry["PORT"]);
			const parts = objectPath.split('#');
	
			if (parts[0] === 'SERIES' && parts[1] === 'DATA') {
				tempSeriesData[parts[2]] = value;
			} else if (objectPath === 'new_line' && Object.keys(tempSeriesData).length > 0) {
				result.SERIES.DATA.push(tempSeriesData);
				tempSeriesData = {};
			} else {
				let target = result;
				while (parts.length > 1) {
					const part = parts.shift()!;
					target = target[part] ??= {};
				}
				target[parts[0]] = value;
			}
		}

		return result;
	}


}
