  isAgent: boolean;
  isMaker: boolean;
  isChecker: boolean;
  commonParamServices: Array<string>;
  filteredReport: Array<number>;
  functions?: {
    __SET(variable: IPsCommonBusinessSettings);
    _REFRESH();
  };
  paramExclusionList: {};
  hint: string;
  becomeOurCustomer: string;
  productAndServices: string;
  qiblaDirection: string;
  prayerTime: string;
