import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { MatSelect } from '@angular/material/select';
import { ICellRendererAngularComp } from 'ag-grid-angular';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { PsSelect } from 'src/app/commonSRC/psServices/models/common-type';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { PsApplicationSettings } from 'src/app/commonSRC/psServices/models/ps-app-settings';
import { LoggerService } from '../../../../../psServices/logger/logger.service';
import { IOptionsPsActionIcon, IOptionsPsFieldLabel, IOptionsPsLabelInput, IOptionsPsSelectDropdown, IPsSelect } from '../../../../../psServices/models/ps-common-interface';
import { PsCommonSettings } from '../../../../../psServices/models/ps-common.settings';
import { PsCommonService } from '../../../../../psServices/ps-common/ps-common.service';
import { PsFieldSelectComponent } from '../ps-field-select.component';


@Component({
  selector: 'ps-select-dropdown-base',
  templateUrl: './ps-select-dropdown-base.component.html',
})
export class PsSelectDropdownBaseComponent extends PsFieldSelectComponent implements OnInit, ICellRendererAngularComp {

  @Input() options: IOptionsPsSelectDropdown;
  @Output() public selectClick: EventEmitter<any> = new EventEmitter<any>();

  interface = PsCommonSettings.psSelectDropdownInterface;
  public _selectedItem: IPsSelect = {};
  get labelOptions(): IOptionsPsLabelInput {
    return {
      required: this.required,
      labelKey: this.options.labelKey,
      previewMode: this.previewMode
    };
  }
  valueOptions: IOptionsPsLabelInput = {};
  enableEmptyOpt: boolean;
  wordpressIconOptions: IOptionsPsActionIcon = {
    iconName: 'logo-wordpress',
  };
  buildIconOptions: IOptionsPsActionIcon = {
    iconName: 'build',
  };
  outerIcon = '';
  loaderUrl;
  showExtraInfo = false;
  emptyArrayOptions: PsSelect = [{ itemValue: null, description: '' }];
  @ViewChild('matSelect', { static: false }) matSelect: MatSelect;
  @Output() public onServiceDataChange: EventEmitter<any> = new EventEmitter<any>();

  public get selectData(): Array<IPsSelect> {
    if (this.options && this.options.listOfOptions && this.options.listOfOptions.length > 0) {
      // Added by Richie for #TP 1210739
      const firstOption = this.options.listOfOptions[0];
      // Added by Richie to handle fields with rowIndex
      if (firstOption && firstOption.selectedObj) {
        const fcName = CommonUtils.returnNameWithoutIndex(this.options.fcName, this.options.rowIndex)
        // End Richie
        const prefixName = fcName + '.selectedObj.';
        const extraOptionDetails = CommonUtils.extractPropertyNames(firstOption.selectedObj, prefixName);
        if (extraOptionDetails && extraOptionDetails.length > 0) {
          this.options.group.controls[this.options.fcName][ConstantCommon.CONTROLLER_EXTRA_OPTION_DETAILS] = extraOptionDetails;
        }
      }
      // End Richie
      if (this.enableEmptyOpt) {
        return [...this.emptyArrayOptions, ...this.options.listOfOptions];
      } else {
        return this.options.listOfOptions as IPsSelect[];
      }
    } else {
      return new Array<IPsSelect>();
    }
  }

  get previewValOptions(): IOptionsPsFieldLabel {
    return {
      labelKey: this.fcValueDesc,
      psClass: 'ps-value-preview',
      previewMode: true
    };
  }
  constructor(commonProv: PsCommonService, loggerP: LoggerService) {
    super(commonProv, loggerP);
  }

  ngOnInit() {
    this.init();
  }

  init() {
    this.custIconOptions.component = PsSelectDropdownBaseComponent;
    this.commonProv.addComponentValidator(this.options.group, this.options.fcName, this.common.prepareValidation(false, null, null, null, null, null, null, null, [this.commonProv.custOnlyObjectAllowed()]));
    super.init();
    this.loaderUrl = PsApplicationSettings.CLIENT_ASSETS_CONFIG.SVG_URL + 'loading.svg';
    if (this.options.asyncURL && this.options.callAsyncOnInit && this.visible) {
      const serviceRequestObj: any = {
        nbRec: this.numberOfRowsToShow,
        recToskip: 0,
        ...this.options.serviceRequestObj
      };
      this.loadAsyncUrl(serviceRequestObj, false).then((data) => { });
    }
    if (this.options.labelKey !== undefined) {
      this.labelOptions.labelKey = this.options.labelKey;
    } else {
      // this.labelOptions.labelKey = this.options.placeHolder;
    }
    //  this.options.isMultiple = true;
    // adding the checking PsCommonSettings.enableEmptyOpt here for the case if the dropdown is mandatory then still the empty option is added since the flag this.enableEmptyOpt was set to true from this property on the constructor
    if (PsCommonSettings.enableEmptyOpt && !this.required && (this.enableEmptyOpt !== false)) {
      this.enableEmptyOpt = true;
    }

    if (this.options && (this.options.iconOptions || this.options.imageOptions)) {
      // this.itemOptions = { ...this.options };
      this.itemOptions = CommonUtils.copyObject(this.itemOptions, this.options);
    }

    /* if (this.options.selectDefaultItem !== undefined) {
      this.selectedItem = this.options.selectDefaultItem;
    } */
    this.labelOptions.required = this.required;
  }
  loadAsyncUrl(parameterRequest: any, triggeredByScroll: boolean) {
    if (triggeredByScroll) {
      parameterRequest.recToskip = this.options.listOfOptions.length;
    }
    return this.commonProv.commonSelectDropdownRequest(this.options.asyncURL, parameterRequest).then(data => {
      if (this.options.mapResponseProperties) {
        if (data.gridModel) {
          this.options.listOfOptions = this.options.listOfOptions ? this.options.listOfOptions : [];
          for (let i = 0; i < data.gridModel.length; i++) {
            let option: IPsSelect = {
              itemValue: data.gridModel[i][this.options.mapResponseProperties.itemValue],
              description: data.gridModel[i][this.options.mapResponseProperties.description],
              selectedObj: data.gridModel[i],
              iconName: data.gridModel[i][this.options.mapResponseProperties.iconName] ? data.gridModel[i][this.options.mapResponseProperties.iconName] : (this.options.mapResponseProperties.imageName ? null : ConstantCommon.DEFAULT_ICON_NAME),
              imageName: data.gridModel[i][this.options.mapResponseProperties.imageName] ? data.gridModel[i][this.options.mapResponseProperties.imageName] : this.options.mapResponseProperties.fallBackImageName,
              fallBackImageName: this.options.mapResponseProperties.fallBackImageName ? this.options.mapResponseProperties.fallBackImageName : null,
              additionalDescList: [],
              additionActionsList: []
            };
            if (this.options.mapResponseProperties.additionalDescList && this.options.mapResponseProperties.additionalDescList.length > 0) {
              for (let eachAddedDesc of this.options.mapResponseProperties.additionalDescList) {
                let updatedValue;
                // if we are adding multiple values from multiple prop names in 1 label
                if (eachAddedDesc.propNames && eachAddedDesc.propNames.length > 1) {
                  for (let eachPropName of eachAddedDesc.propNames) {
                    updatedValue += data.gridModel[i][eachPropName] + ' ';
                  }
                  option.additionalDescList.push(updatedValue);
                } else if (eachAddedDesc.propNames && eachAddedDesc.propNames.length === 1) {
                  const value = data.gridModel[i][eachAddedDesc.propNames[0]];
                  // if we have a mask to be applied on the value before display
                  if (value && eachAddedDesc.maskOptions) {
                    updatedValue = CommonUtils.applyMaskFormat(+value, eachAddedDesc.maskOptions);
                  } else {
                    updatedValue = value;
                  }
                  //if we are adding a static string to value 
                  if (eachAddedDesc.concatString) {
                    updatedValue += ' ' + this.common.translate(eachAddedDesc.concatString);
                  }
                  option.additionalDescList.push(updatedValue);
                }
              }
            }
            if (this.options.mapResponseProperties.additionalActionList && this.options.mapResponseProperties.additionalActionList.length > 0) {
              option.additionActionsList.push(...this.options.mapResponseProperties.additionalActionList);
            }
            this.options.listOfOptions.push(option);
          }
        } else {
          this.options.listOfOptions = [];
        }
      } else {
        this.options.listOfOptions = data.gridModel;
      }
      this.options.totalNbRec = data.totalNbRec;
      data.triggeredByScroll = false;
      this.onServiceDataChange.emit(data);
    });
  }


  onSelectChange(event) {
    const item = this.selectData.filter((item) => item.itemValue === event.value);
    super.writeValue(item[0]);
    this.selectedItem = item[0];
    this.outerIcon = item[0].iconUrl;
    this.options.placeHolder = '';
    this.onPsChange.emit(item[0]);
    this.showExtraInfo = true;
  }

  onSelectOpened() {
    this.showExtraInfo = true;
    if (this.options.forcePanelClose) {
      this.matSelect.close();
    }
  }


  get fcValueDesc(): string {
    const value: any = this.fcValue;
    // modified by Richie for #BUG 766112
    if (value) {
      const selectedOption = this.selectData.find(item => item.itemValue === value.itemValue);
      if (selectedOption) {
        return selectedOption.description;
      } else {
        return value;
      }
    }
  }

  get selectedItem(): any {
    const obj = super.returnValue(true);
    if (obj instanceof Object) {
      this.outerIcon = obj.iconUrl;
      // this.options.placeHolder = '';
      return obj.itemValue;
    } else if (obj !== undefined) {
      const selectItem = this.selectData.filter((item) => item.itemValue === obj || String(item.itemValue) === String(obj));
      if (selectItem.length > 0) {
        this.outerIcon = selectItem[0].iconUrl;
        // this.options.placeHolder = '';
        this.showExtraInfo = true;
        /**
         * #BUG 1077331
         * in case the dropdown was set to a value thus the innerValue will be the selected object from the list.
         * thus if for any reason we set the value of the dropdown to the same itemvalue from the vo,
         * then the value in the vo remains as the itemValue and not the selected obj
         * since the selectedValue here & the innerValue will be equal in this case
         * and thus it will not set the selected option again in the VO instead of the itemValue.
         * That's why we need to set the innerValue back to the itemValue that is set in the vo
         * in order to be able to reset it to the selected object inside the vo as it should be.
         */
        if (this.innerValue === selectItem[0]) {
          this.innerValue = obj;
        }
        super.writeValue(selectItem[0]);
      } else {
        return null;
      }
      return obj;
    }
    return obj;
  }

  set selectedItem(value: any) {
    if (value) {
      const selectItem = this.selectData.filter((item) => item.itemValue === value || String(item.itemValue) === String(value));
      if (selectItem.length > 0) {
        super.writeValue(selectItem[0], true);
        this.outerIcon = selectItem[0].iconUrl;
        this._selectedItem = selectItem[0];
        // Commented by Richie for #TP 1082495 since the change event will be called from the subscribe as all other components
        // this.onPsChange.emit(selectItem[0]);
      }
    } else if (this.oldValue) {
      this.outerIcon = null;
      super.writeValue(value, true);
    }
    // this.options.placeHolder = '';
    this.showExtraInfo = true;
  }

  getPlaceHolder() {
    if (this.selectedItem) {
      return '';
    } else {
      return this.options.placeHolder;
    }
  }

  protected setCustIconOptions() {
    this.custIconOptions.componentOptions = this.options;
    this.componentName = 'PsSelectDropdownComponent';
    super.setCustIconOptions();
  }

  onSelectCLick(event) {
    this.selectClick.emit(event);
  }
  agInit(params: any) {
    this.agInitBase(params, this);
    setTimeout(() => {
      this.selectedItem = params.colDef.cellEditorParams.componentOptions.group.controls[params.colDef.cellEditorParams.componentOptions.fcName].value;
    }, 200);
    if (params.colDef.cellEditorParams.onPsChange !== undefined) {
      this.onPsChange.subscribe((res) => {
        res.rowIndex = params.rowIndex;
        params.colDef.cellEditorParams.onPsChange(res);
      });
    }
  }

  refresh(params: any) {
    this.agRefreshBase(params, this);
    setTimeout(() => {
      this.selectedItem = params.colDef.cellEditorParams.componentOptions.group.controls[params.colDef.cellEditorParams.componentOptions.fcName].value;
    }, 200);
    return true;
  }
}
