import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, RouterStateSnapshot, UrlTree } from '@angular/router';
import { Observable } from 'rxjs/internal/Observable';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { CommonUtils } from '../../commonSRC/psServices/models/common-utils';
import { PsCommonSettings } from '../../commonSRC/psServices/models/ps-common.settings';
import { PsNavigatorService } from '../../commonSRC/psServices/navigator/ps-navigator.service';
import { PsCommonService } from './../../commonSRC/psServices/ps-common/ps-common.service';

@Injectable({
  providedIn: 'root'
})
// TP 1332609
// prevent navigation of any routing directly from browser
export class CanActivateRouteGuard implements CanActivate {

  constructor(private navService: PsNavigatorService, private commonService: PsCommonService) { }

  canActivate(activatedRoute: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean | UrlTree | Observable<boolean | UrlTree> | Promise<boolean | UrlTree> {
    const activePageHref = this.navService.activePageList[this.navService.activePageList.length - 1]?.PAGE_HREF;
    if (CommonUtils.isNativeMobile() || ((PsCommonSettings.isLoggedIn && activatedRoute.routeConfig.path == 'home')

      || (PsCommonSettings.oper_ID && this.navService.activePageList[this.navService.activePageList.length - 1].OPER_ID == PsCommonSettings.oper_ID

        && typeof activePageHref === 'string' ? activePageHref : activePageHref[activePageHref.length - 1] == activatedRoute.routeConfig.path))) {

      return true;

    }
    CommonUtils.presentFailureAlert('page_not_found_key', { autoHide: true }).then(() => {
      PsCommonSettings.oper_ID = ConstantCommon.LANDING_OPER_ID;
      this.navService.navigateToMain();
      this.commonService.dismissLoading();
      return;
    });

  }
}