import { Component, Input, OnInit } from '@angular/core';
import { PsDateFormatPipe } from 'src/app/commonSRC/psPipes/ps-date-format/ps-date-format.pipe';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { LoggerService } from '../../../../../psServices/logger/logger.service';
import { IOptionsPsLabel } from '../../../../../psServices/models/ps-common-interface';
import { PsCommonSettings } from '../../../../../psServices/models/ps-common.settings';
import { PsCommonService } from '../../../../../psServices/ps-common/ps-common.service';
import { PsFieldLabelComponent } from '../ps-field-label.component';


@Component({
  selector: 'ps-label-input',
  templateUrl: './ps-label-input.component.html',
  styleUrls: ['./ps-label-input.component.scss'],
})
export class PsLabelInputComponent extends PsFieldLabelComponent implements OnInit {
  @Input() options: IOptionsPsLabel = {
    psClass: 'ps-label-input',
    position: PsCommonSettings.PS_LABEL_INPUT_POSITION
  };


  constructor(public commonProv: PsCommonService, public loggerP: LoggerService) {
    super(commonProv, loggerP);
  }

  ngOnInit() {
    if (this.options && this.options.previewMode != null && this.options.previewMode !== undefined && this.options.previewMode !== true) {
      this.options.previewMode = false;
    }
    this.options.psClass = 'ps-label-input';
  }

  agInit(params: any) {
    this.options = params.colDef.cellEditorParams.componentOptions;
    this.options.translate = false;
    this.ngOnInit();
  }
  refresh(params: any) {
  	// Commented by Richie for #BUG 1340366; in order not to populate the same data on all rows once changing the values on one row.
    // if (params.data[Object.keys(params.data)[0]] == null) {
    // this.getUpdatedvalue(params);
    // }
    return false;
  }

  getUpdatedvalue(params) {
    // params.colDef.cellEditorParams.componentOptions.group.controls[params.colDef.cellEditorParams.componentOptions.fcName].setValue(params.colDef.cellEditorParams.componentOptions.group.controls[params.colDef.cellEditorParams.componentOptions.fcName]._pendingValue);
    // let updatedValue = params.colDef.cellEditorParams.componentOptions.group.controls[params.colDef.cellEditorParams.componentOptions.fcName]._pendingValue;
    const formData = params.colDef.cellEditorParams.componentOptions.group.controls[ConstantCommon.CONTROLLER_FORM_DATA].value;
    let updatedValue = this.common.valFromNestedObj(params.colDef.cellEditorParams.componentOptions.fcName, formData);
    this.options = this.common.copyObject(this.options, params.colDef.cellEditorParams.componentOptions);
    this.options.labelKey = params.colDef.cellEditorParams.type === 'date' ? new PsDateFormatPipe().transform(new Date(updatedValue), params.colDef.cellEditorParams.dateFormat) : params.colDef.cellEditorParams.type === 'select' ? updatedValue.description : updatedValue;
  }
}
