import { Injectable } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { FingerprintAIO } from '@awesome-cordova-plugins/fingerprint-aio/ngx';
import { ModalController } from '@ionic/angular';
import { PsBaseFieldComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-base-field.component';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IOmniRequestBaseObject, IOptionsPsBaseFieldExposed } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { PsAuthenticationMatrixComponent } from '../../psComponents/ps-complex-components/ps-authentication-matrix/ps-authentication-matrix.component';
import { IAuthenticationMatrixResponse } from '../../psComponents/ps-complex-components/ps-authentication-matrix/ps-authentication-matrix.component.interface';
import { CommonBussinessConstant } from '../models/ps-common-bussiness-constant';
import { PsCommonBusinessSettings } from '../models/ps-commonBusiness.settings';
import { PsCommonSettings } from './../../../commonSRC/psServices/models/ps-common.settings';
import { PsCommonService } from './../../../commonSRC/psServices/ps-common/ps-common.service';
import { AuthMatrixViewer, IAuthMatrix } from './../../psComponents/ps-complex-components/ps-authentication-matrix/ps-authentication-matrix.component.interface';




@Injectable({ providedIn: 'root' })
export class AuthenticationMatrixService {

  deviceHaveFingerPrint = false;
  assignedHardToken: boolean;
  assignedSoftToken: boolean;

  constructor(public faio: FingerprintAIO, public common: PsCommonService, private modalController: ModalController, private navService: PsNavigatorService) {
    if (CommonUtils.isNativeMobile()) {
      this.faio.isAvailable().then(result => {
        this.deviceHaveFingerPrint = result;
      }).catch(error => this.common.logger.log(error));
    }
  }

  //edited by Marina TP:#1486207 to prevent sending request to submit URL if athenticationRequiredProcess failed
  //as confirmed by Rakesh on teams that any outputCode other than zero in the response should be treated as failure
  returnAuthenticationsRequired(parameter: IOmniRequestBaseObject): Promise<IAuthenticationMatrixResponse> {
    return new Promise<IAuthenticationMatrixResponse>((resolve) => {
      parameter.apiCode = -1;
      if (CommonUtils.isNativeMobile()) {
        parameter.fingerprintAvailableOnMobile = this.deviceHaveFingerPrint ? true : false
      }

      this.common.http.commonRequestAjax(PsCommonBusinessSettings.serviceUrl.authenticationsRequiredProcess, parameter).then(response => {
        if (response.data) {
          this.assignedHardToken = response.data.assignedHardToken == 1 ? true : false;
          this.assignedSoftToken = response.data.assignedSoftToken == 1 ? true : false;
        }
        if (response.data.outputCode === 0) {
          if (response.data.enableSoftTokenOTPValue && !PsCommonSettings.isLoggedIn && response.data?.operId == ConstantCommon.LOGIN_OPER_ID) {
            this.common.session.append(CommonBussinessConstant.ASSIGNED_SOFT_TOKEN, this.assignedSoftToken);
          }
          resolve(CommonUtils.isNotNull(response.data) ? response.data.gridModel : []);
        }
        else {
          CommonUtils.dismissLoading();
        }
      }).catch(error => this.common.logger.log(error));
    });
  }

  //Edited by Marina for TP #1486207 to be able to verify account number before sending the OTP
  authenticationEnabled(userInfo): Promise<IAuthMatrix> {
    return new Promise<IAuthMatrix>((resolve, reject) => {
      let paramData: IOmniRequestBaseObject = {};
      if (PsCommonSettings.oper_ID === CommonBussinessConstant.ONLINE_REGISTRATION_OPER) {
        paramData = { ...userInfo }
      }
      else {
        paramData.userName = userInfo.userName;
      }
      this.returnAuthenticationsRequired(paramData).then(authenticationResult => {
        if (authenticationResult && (authenticationResult as Array<any>).length > 0) {
          resolve({ success: true, data: authenticationResult });
        } else {
          resolve({ success: false, data: null });
        }
      }).catch(error => {
        this.common.logger.error(error);
        reject(error);
      });
    });
  }

  /*
  * initialize the OTP validation parameters and pass the value to the authentication matrix to show the OTP and handle the validation
  */
  requestOTPValidation(viewedAs: AuthMatrixViewer, mobileNo?: string, config?: IOptionsPsBaseFieldExposed, baseField?: PsBaseFieldComponent) {

    return new Promise<IAuthMatrix>(async (resolve, reject) => {
      let authEnabled;
      const otp: IAuthenticationMatrixResponse[] = [{ authentionTypesEnabled: 'OTP', mobileNo, fieldOptions: config, baseField: baseField }];
      authEnabled = { data: otp, success: true };

      this.showAndManageValidations(authEnabled, viewedAs);

    });

  }

  /*
  *get the authentications and display the required ones
  */
  authenticationMatrixProcess(viewedAs: AuthMatrixViewer, group: FormGroup): Promise<IAuthMatrix> {
    CommonUtils.presentLoading();
    return new Promise<IAuthMatrix>((resolve, reject) => {
      let userInfo: any = {};
      const formValues = group && group.controls && group.controls.formData ? group.controls.formData.value : {};
      userInfo = { ...formValues };
      this.common.session.getStoredValueOf(ConstantCommon.USER_NAME).then(name => {

        const sessionUserInfo = this.common.session.getValueOf(ConstantCommon.USERINFO);

        if (formValues[ConstantCommon.USER_NAME]) {
          if (name && CommonUtils.isMaskedInput(formValues[ConstantCommon.USER_NAME])) {
            userInfo.userName = name;
          }
          else {
            userInfo.userName = formValues[ConstantCommon.USER_NAME];
          }
        }
        else {
          userInfo.userName = name;
        }
        if (PsCommonSettings.isLoggedIn && sessionUserInfo && sessionUserInfo.omniUserVO && sessionUserInfo.omniUserVO.NAME) {
          userInfo.userName = sessionUserInfo.omniUserVO.NAME;
        }
        if (formValues['omniUserCO'] && formValues['omniUserCO'].omniUserVO && formValues['omniUserCO'].omniUserVO.NAME) {
          userInfo.userName = formValues['omniUserCO'].omniUserVO.NAME;
        }
        if (PsCommonSettings.isLoggedIn
          && (userInfo.userCifNo == undefined || userInfo.userCifNo == null || userInfo.userCifNo == '')) {
          if (sessionUserInfo && sessionUserInfo.omniUserVO && sessionUserInfo.omniUserVO.CIF_NO) {
            userInfo.userCifNo = sessionUserInfo.omniUserVO.CIF_NO;
          }
        }

        this.authenticationEnabled(userInfo).then(authEnabled => {
          if (authEnabled.success) {
            this.showAndManageValidations(authEnabled, viewedAs, userInfo).then(result => {
              resolve(result);
            }).catch(error => {
              reject(error);
            });
          } else {
            resolve({ success: true, data: null });
          }
        });
      });
    });
  }

  showAndManageValidations(authEnabled: any, viewedAs: AuthMatrixViewer, userInfo?: any) {
    return new Promise<IAuthMatrix>(async (resolve, reject) => {
      // Added By OmarTalaat ABEOI240006 Wrapping Submit on every request/Transaction
      // Check if there is at least one object with authentionTypesEnabled === 'SoftTokenOTP'
      const assigneeToken = this.common.session.getValueOf(CommonBussinessConstant.ASSIGNED_SOFT_TOKEN);
      const isSoftTokenOTPAuthEnabled = authEnabled.data.some(item => item.authentionTypesEnabled === "SoftTokenOTP");
      if (assigneeToken == 0 && isSoftTokenOTPAuthEnabled) {
        CommonUtils.dismissLoading();
          this.common.isUserAssignedToSoftTokenAction(false,'activate_otp_submit_failure_modal_key').catch(error => {
            reject({ success: false, data: null });
            this.common.logger.log(error)});
      } else {
      const data = authEnabled.data;
      switch (viewedAs) {
        case AuthMatrixViewer.MODAL: {
          const alertID = 'ps-auth-matrix-process-alert';
          this.modalController.dismiss(null, null, alertID);
          let props;
          props = { authenticationMatrixType: data, authenticationMatrixUserInfo: userInfo, assignedHardToken: this.assignedHardToken, assignedSoftToken: this.assignedSoftToken };
          const authModal = await this.modalController.create({
            component: PsAuthenticationMatrixComponent,
            componentProps: props,
            cssClass: 'alert-modal',
            animated: false,
            id: alertID
          });
          await authModal.present().then(() => {
            // resolve();
          }).catch(error => this.common.logger.log(error));
          CommonUtils.dismissLoading(); // Dismiss loading that was created in ps-button-submit
          await authModal.onDidDismiss().then(data => {
            if (data['data']['success'] === true) {
              resolve({ success: true, data });
            } else {
              reject({ success: false, data: null });
            }
          }).catch(error => this.common.logger.log(error));
        }
      }
    }
    });
  }

}
