import { ChangeDetectorRef, Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { LaunchNavigator, LaunchNavigatorOptions } from '@ionic-native/launch-navigator/ngx';
import { Platform } from '@ionic/angular';
import { google } from 'google-maps';
import { Subscription } from 'rxjs';
import { EventEmitterService } from 'src/app/commonSRC/psServices/event-emitter/event-emitter.service';
import { GeolocationService } from 'src/app/commonSRC/psServices/geolocation/geolocation.service';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { PsApplicationSettings } from 'src/app/commonSRC/psServices/models/ps-app-settings';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { LoggerService } from '../../../../../psServices/logger/logger.service';
import { IControlProp, IMapPosition, IOptionsPsActionImageExposed, IOptionsPsContainerForm, IOptionsPsFormMap, IOptionsPsLabelInput } from '../../../../../psServices/models/ps-common-interface';
import { PsCommonService } from '../../../../../psServices/ps-common/ps-common.service';
import { PsBaseContainerComponent } from '../../ps-base-container.component';


declare var google: google;

@Component({
  selector: 'ps-form-map',
  templateUrl: './ps-form-map.component.html',
  styleUrls: ['./ps-form-map.component.scss'],
})
export class PsFormMapComponent extends PsBaseContainerComponent implements OnInit, OnDestroy {

  @Input() options: IOptionsPsFormMap = {};
  @Output() public onMarkerClick: EventEmitter<any> = new EventEmitter<any>();
  formOptions: IOptionsPsContainerForm = {};
  noPlatform: boolean;
  labelOptions: IOptionsPsLabelInput = {
    labelKey: 'google_map_could_not_load_key'
  };
  launchNavigation: LaunchNavigatorOptions = {};
  mapApp: any;
  // map: GoogleMap;

  @ViewChild('googleMapCanvas', { static: false }) mapElement: any;
  @ViewChild('legendWrapper', { static: false }) legendWrapper: any;
  mapWeb: any;
  isDesktop: boolean;
  isApp: boolean;
  languageChangedSubscription: Subscription;
  markersSubscription: Subscription;
  directionSubscription: Subscription;
  markerLength: number;
  userPosition: any;
  nearestBranch: IMapPosition;
  defaultMapCenter: IMapPosition = { lat: 12.4896154, lng: 23.2273462 }; //Added by Marina TP:#1270858; default map center (world center) to be used in case no user position
  setMarkers: any;
  locationMarker: any;
  // googleInstance: google;
  legendWasAdded = false;
  legend: Array<IOptionsPsActionImageExposed> = [];
  constructor(public commonProv: PsCommonService, public loggerP: LoggerService, private launchNavigator: LaunchNavigator,
    private platform: Platform, private cd: ChangeDetectorRef, private eventEmitterService: EventEmitterService,
    private geolocationService: GeolocationService) {
    super(commonProv, loggerP);
  }

  ngOnInit() {
    // await this.platform.ready();
    this.initializeMap();
  }

  subscribeOnLanguageChange() {
    if (!this.languageChangedSubscription) {
      this.languageChangedSubscription = this.commonProv.languageChanged.subscribe(isChanged => {
        if (isChanged && CommonUtils.previousLanguage !== String(PsCommonSettings.activeLanguge).toLocaleLowerCase()) {
          this.initializeMap();
        }
      });
    }
  }

  initializeMap() {
    //Edited by ghada for tp#1304247 for adding region code to implement regions localizations on the map
    CommonUtils.loadMapScript(PsApplicationSettings.GOOGLE_API_KEYS.MAP_WEB_API_KEY, PsApplicationSettings?.MAIN_CONFIG?.CLIENT_COUNTRY_ISO_CODE).then(async () => {
      await this.initializeMapCenter();
      if (!this.commonProv.isWeb()) {
        const isMapAvailable = this.launchNavigator.isAppAvailable(this.launchNavigator.APP.GOOGLE_MAPS);
        if (isMapAvailable) {
          this.mapApp = this.launchNavigator.APP.GOOGLE_MAPS;
        }
      }
      this.subscribeOnLanguageChange();
      this.loadMarkersAndSubscribe();
      this.onDrirectionClick();
      this.addLegend();

    }).catch((error => {
      if (error && error.display) {
        CommonUtils.presentFailureAlert(error.message);
      }
      this.logger.error(error);
    }));
  }

  loadMarkersAndSubscribe() {
    if (!this.markersSubscription) {
      this.markersSubscription = this.options.markers.subscribe(async markers => {
        this.removeMarkers();
        await this.loadMarkersWeb(markers.markersList, markers.markerType);
        if (!this.legendWasAdded) {
          this.addLegend();
        }
      });
    }
  }

  onDrirectionClick() {

    if (!this.directionSubscription) {
      this.directionSubscription = this.eventEmitterService.getDirectionChangeEmitter().subscribe((options) => {
        if (this.commonProv.isWeb()) {
     
          const anchorEl = document.createElement('a');
          anchorEl.href = 'https://www.google.com/maps/dir/?api=1&origin=' + options.cordinates.currentLocationString + '&destination=' + options.cordinates.markerLocationString + '&travelmode=driving';
          anchorEl.target = '_blank';
          anchorEl.click();
        } else {
     
          try {
            this.launchNavigation = {
              start: options.cordinates.currentLocationString,
              app: this.mapApp
            };

            this.launchNavigator.navigate(options.cordinates.markerLocationString, (CommonUtils.isIOS() ? options : this.launchNavigation)).then(success => { //Modified by ghada, AZDB-6408  sending the right options to fix the issue of opening map app on clicking get direction btn
              this.logger.log('Map App opened successfully', success);
          
            }, error => {
              this.logger.error('something went wrong', error)
         
            }
            )
          } catch (err) {
            this.logger.error('something went wrong on click getDirection', err)
       
          }
        }
      });
    }
  }

  //Edited by Marina TP:#1270858. To be able to add any number of controls(buttons) on the map 
  addMapControls(controls: IControlProp[], controlLbl) {
    // Set CSS for the control border.
    for (const control of controls) {
      const controlUI = document.createElement('div');
      controlUI.className = 'my-location-btn-ui' + ' ' + control.className;
      controlUI.id = control.id;
      controlUI.title = control.title;

      // Set CSS for the control interior.
      const controlText = document.createElement('div');
      controlText.className = 'my-location-btn-text';
      controlText.innerHTML = this.commonProv.translate(control.innerHTML || controlLbl);
      controlUI.appendChild(controlText);

      // Setup the click event listeners
      controlUI.addEventListener('click', () => {
        if (this.locationMarker !== undefined) {
          this.locationMarker.setMap(null);
        }
        if (this.userPosition) {
          if (control.positionType == 'myLocation')
            this.handleMyLocationClick();
          else {
            this.handleNearestBranchClick();
          }
        }
        //if Location access was not granted from the beginning
        else {
          this.geolocationService.getUserPosition().then((res) => {
            this.userPosition = { lat: res.coords.latitude, lng: res.coords.longitude };
            this.setUserMarker();
            this.nearestBranch = this.geolocationService.findNearestBranch(this.setMarkers, this.userPosition);
            if (control.positionType == 'myLocation')
              this.handleMyLocationClick();
            else {
              this.handleNearestBranchClick();
            }
          })
        }
      }
      );
      //Add the control to the map 
      this.mapWeb?.controls[google.maps.ControlPosition.BOTTOM_CENTER].push(controlUI);
    }
  }

  //Added by ghada, tp#1535994 to remove existing controls and re-add them on changing map segemts
  removeExistingMapControls() {
    const elements = document.getElementsByClassName('my-location-btn-ui');
    while (elements.length > 0) {
      elements[0].parentNode.removeChild(elements[0]);
    }
    this.mapWeb?.controls[google.maps.ControlPosition.BOTTOM_CENTER]?.clear();
  }

  //recenters the map around user's position upon clicking on my location button
  handleMyLocationClick() {
    this.mapWeb.setOptions({
      center: this.userPosition,
      zoom: PsApplicationSettings.CLIENT_ASSETS_CONFIG.UI_CONFIGURATION.DefaultZoomBranchPercentage,
    });
    this.mapApp.setCenter(this.userPosition);

  }

  //fits the map's bound around user's position and the nearest branch to it upon clicking on nearest branch button
  handleNearestBranchClick() {
    let bounds = new google.maps.LatLngBounds();
    const userPosition = new google.maps.LatLng(this.userPosition.lat, this.userPosition.lng);
    bounds.extend(userPosition);
    bounds.extend(this.nearestBranch);
    this.mapWeb.fitBounds(bounds)
  }

  //Added by Marina TP:#1270858. To handle location permession separatley before initializing google map
  //if location access is permitted load the map with user location and default zoom
  //else load it with default location (world center) and minimum zoom
  initializeMapCenter() {
    return new Promise<any>((resolve) => { //Modified by ghada, AZDB-4472 function have to return a promose in order to await it's call in initializeMap() so we don't proceed with populating map controls and markers before map get initlaized
      this.geolocationService.getCachedCoordinates(false).then((res) => {
        this.userPosition = { lat: res.latitude, lng: res.longitude }
        resolve(true);
      }, (error => {
        resolve(this.loadMapWeb(this.defaultMapCenter, PsCommonSettings.GOOGLE_MAP_CAMERA_MIN_ZOOM));
      }))
    })

  }

  //initializes google maps
  //edited by Marina TP:#1270858 to ensure having map center and zoom before initializing it 
  //and to set user marker only if user's position is available
  loadMapWeb(mapCenter, mapZoom) {
    const googleCanvas = this.mapElement.nativeElement;
    if (googleCanvas) {
      googleCanvas.innerHTML = '';
    }
    const mapOptions: any = {
      center: mapCenter,
      zoom: mapZoom,
      minZoom: PsCommonSettings.GOOGLE_MAP_CAMERA_MIN_ZOOM,
      streetViewControl: false,
      restriction: {
        latLngBounds: {
          north: 85,
          south: -85,
          west: -180,
          east: 180
        }
      }
    };
    this.mapWeb = new google.maps.Map(googleCanvas, mapOptions);
    if (this.userPosition) {
      this.setUserMarker();
    }

    // this.loadMarkersWeb(this.options.markers.value);
    // this.cd.detectChanges();
  }

  setUserMarker() {
    const userMarkerOptions: google.maps.ReadonlyMarkerOptions = {
      position: new google.maps.LatLng(this.userPosition),
      icon: PsApplicationSettings.CLIENT_ASSETS_CONFIG.IMGS_FOLDER_URL + PsCommonSettings.MAP_TYPE_LOCATION_IMG,
      zIndex: 1,
      map: this.mapWeb,
      animation: PsCommonSettings.MAP_MARKER_ANIMATION === 'DROP' ? google.maps.Animation.DROP : google.maps.Animation.BOUNCE,
    };
    const mapUserMarkerWeb = new google.maps.Marker(userMarkerOptions);
    mapUserMarkerWeb.setMap(this.mapWeb);
  }

  loadMarkersWeb(markers: Array<any>, markersType) {
    try {
      if (markers !== undefined && markers.values !== undefined) {
        this.setMarkers = [];
        //Modified by ghada, tp #1542033 for opening the map to the nearest location and nto the end user's
        if (this.userPosition) {
          this.loadMapWeb(this.userPosition, PsApplicationSettings.CLIENT_ASSETS_CONFIG.UI_CONFIGURATION.DefaultZoomBranchPercentage,)
          this.nearestBranch = this.userPosition ? this.geolocationService.findNearestBranch(markers, this.userPosition) : undefined;
          if (this.nearestBranch) {
            this.handleNearestBranchClick();
          }
        }
        markers.forEach((marker, index) => {
          if (index === markers.length - 1) {
            this.commonProv.dismissLoading();
          }
          this.addMarkerWeb(marker);
        });
      }
    } catch (error) {
      this.logger.error(error);
    }
    //Modified by ghada, tp#1535994 to change controls label based on the selected marker type (segemt)
    this.removeExistingMapControls();
    let controlLbl = ''
    if (markersType) {
      controlLbl = 'nearest_' + (markersType === 'all' ? 'location' : markersType) + '_key';
      this.addMapControls(this.options.controls, controlLbl);
    }
  }

  addMarkerWeb(marker: any) {
    const { zIndex } = marker;
    let position: any = {};
    if (marker.position !== undefined) {
      position = marker.position;
    } else if (marker.latitude !== undefined && marker.longitude !== undefined) {
      position.lat = marker.latitude;
      position.lng = marker.longitude;
    }
    if (position.lat !== undefined && position.lng !== undefined) {
      const markerOptions: google.maps.ReadonlyMarkerOptions = {
        position: new google.maps.LatLng(position.lat, position.lng),
        icon: marker.icon,
        zIndex: 1,
        map: this.mapWeb,
        animation: PsCommonSettings.MAP_MARKER_ANIMATION === 'DROP' ? google.maps.Animation.DROP : google.maps.Animation.BOUNCE,
      };
      const mapMarkerWeb = new google.maps.Marker(markerOptions);
      mapMarkerWeb.addListener('click', () => {
        if (document.fullscreen) {
          document.exitFullscreen();
        }
        this.onMarkerClick.emit(marker);
      });
      this.setMarkers.push(mapMarkerWeb);
      // mapMarkerWeb.setMap(this.mapWeb);

    }
  }

  addLegend() {
    if (this.options.legend && this.legendWrapper && this.options.legend.length > 0) {
      const legendWrapper = this.legendWrapper.nativeElement;
      this.options.legend.forEach((type) => {
        const name = type.name;
        const icon = type.icon;

        const image: IOptionsPsActionImageExposed = {
          imageName: icon,
          labelOptions: {
            labelKey: name
          }
        };
        if (image.labelOptions.labelKey === 'Branch') {
          image.labelOptions.psClass = 'legend-branch';
        }
        this.legend.push(image);
        this.legendWasAdded = true;
      });
      this.mapWeb.controls[google.maps.ControlPosition.RIGHT_BOTTOM].push(legendWrapper);
    }
  }

  ngOnDestroy() {
    if (this.languageChangedSubscription) {
      this.languageChangedSubscription.unsubscribe();
    }
    if (this.directionSubscription) {
      this.directionSubscription.unsubscribe();
    }
    if (this.markersSubscription) {
      this.markersSubscription.unsubscribe();
    }

  }

  removeMarkers() {
    if (this.setMarkers !== undefined) {
      // eslint-disable-next-line @typescript-eslint/prefer-for-of
      for (let i = 0; i < this.setMarkers.length; i++) {
        this.setMarkers[i].setMap(null);
      }
    }
  }
}
