import { Component, Input, OnDestroy, OnInit, HostBinding } from '@angular/core';
import { Subscription } from 'rxjs';
import { IOptionsPsMenuExposed } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-menu/ps-complex-menu.component.interfaces';
import { CommonCustUtils } from '../../customization/psServices/util/common-cust-utils';
import { ConstantCommon } from '../../psServices/models/common-constant';
import { CommonUtils } from '../../psServices/models/common-utils';
import { PsApplicationSettings } from '../../psServices/models/ps-app-settings';
import { IOptionsPsActionIcon, IOptionsPsActionIconExposed, IOptionsPsLabel, IOptionsPsLabelHeader, IOptionsTemplateScreen, IPageCommon } from '../../psServices/models/ps-common-interface';
import { PsCommonSettings } from '../../psServices/models/ps-common.settings';
import { PsTemplateBasePage } from '../ps-template-base/ps-template-base.page';


@Component({
  selector: 'ps-template-screen',
  templateUrl: './ps-template-screen.template.html',
  styleUrls: ['./ps-template-screen.template.scss'],
})
export class PsTemplateScreenPage extends PsTemplateBasePage implements OnInit, OnDestroy {

  @Input() screenOptions: IOptionsTemplateScreen;
  landingType;

  public collapseOptions: IOptionsPsActionIcon = {
    iconName: 'arrow-down',
    labelOptions: {
      labelKey: 'show_more_key'
    }
  };

  public expandOptions: IOptionsPsActionIcon = {
    iconName: 'arrow-up',
    labelOptions: {
      labelKey: 'show_less_key'
    }
  };
  helpLabelOptions: IOptionsPsLabel = { labelKey: 'help_key' };
  menuIcon: IOptionsPsActionIconExposed = {
    iconName: 'menu'
  };
  closeIcon: IOptionsPsActionIconExposed = {
    iconName: 'close'
  };
  arrowIconOptios: IOptionsPsActionIcon = {
    iconName: 'arrow-back',
    allowCust: true
  };

  homeOptions: IOptionsPsActionIcon = {
    iconName: 'lock'
  };
  optionsHeader: IOptionsPsLabelHeader = {};
  headerIconOptions: IOptionsPsActionIcon = {};
  private checkViewSubscriptionC: Subscription;
  private activePageSubscription: Subscription;
  isMobile?: boolean;
  quickMenuHeader: IOptionsPsMenuExposed = { quicklinkMenu: true };
  public collapsed = false;

  @HostBinding('class') operId = 'operId_' + PsCommonSettings.oper_ID;

  constructor() {
    super();
  }

  iconShouldBePresent = false;
  iconShouldBeChecked = false;
  hasSubscribed = false;
  ngOnInit() {
    this.landingType = PsApplicationSettings.CLIENT_ASSETS_CONFIG.UI_CONFIGURATION.LANDING_UI_TYPE;
    if (this.screenOptions?.quickMenuCollapsed) {
      this.collapsed = true;
    }
    this.isMobile = this.commonProv.isMobileLayout()
    super.init();
    if (this.navService.isUserLoggedIn) {
      this.homeOptions.iconName = 'menu';
    }

    this.checkViewSubscriptionC = this.commonProv.getCheckViewByOper(
      PsCommonSettings.oper_ID > 0 ? PsCommonSettings.oper_ID : ConstantCommon.DEFAULT_FALLBACK_OPER).subscribe((result) => {
        if (result) {
          if (this.activePageSubscription) {
            this.activePageSubscription.unsubscribe();
          }
          if (result && result.didEnter) {
            this.hasSubscribed = true;
            const page = this.navService.activePage
            if (page) {
              this.updateHeader(page);
            }
          }
          if (result && result.willLeave && this.hasSubscribed) {
            if (this.checkViewSubscriptionC) {
              this.checkViewSubscriptionC.unsubscribe();
            }
          }
        } else {
          this.activePageSubscription = this.commonProv.activePage.subscribe(page => {
            if (page) {
              setTimeout(() => {
                this.updateHeader(page);
              }, 500);
            } else {
              if (this.activePageSubscription) {
                this.activePageSubscription.unsubscribe();
              }
            }
          });
        }
      });
    if ((this.commonProv.isWebLayout() || this.landingType == 4) && this.navService.isUserLoggedIn) {
      this.iconShouldBeChecked = true;
    } else { this.iconShouldBePresent = true; }
  }


  updateHeader(page: IPageCommon) {
    this.optionsHeader.labelKey = "";
    this.headerIconOptions.iconName = null;
    this.optionsHeader.labelKey = page.title;
    this.headerIconOptions.iconName = page.icon;
  }

  openPage() {
    this.navService.navigateToMain();
  }

  //edited by Marina TP:#1586190; to avoid navigating backward in case we need to navigate to other page after back
  onBackIconClicked() {
    this.navService.onCancelClicked().then((navigatedBack) => {  
     if(navigatedBack && this.screenOptions.nextPageOptions){
      this.navService.openPage(this.screenOptions.nextPageOptions);
     }
    })
  }

  ngOnDestroy() {
    if (this.checkViewSubscriptionC) {
      this.checkViewSubscriptionC.unsubscribe();
    }
  }

  get ifHomePage() {
    const oper = PsApplicationSettings.CLIENT_ASSETS_CONFIG.UI_CONFIGURATION.LANDING_PAGE_OPER;
    this.iconShouldBeChecked = false;
    if (CommonUtils.isNotNull(oper)) {
      this.iconShouldBePresent = oper !== PsCommonSettings.oper_ID;
    } else {
      this.iconShouldBePresent = true;
    }
    return this.iconShouldBePresent;
  }

  public showMore() {
    this.collapsed = false;
  }
  public showLess() {
    this.collapsed = true;
  }
}
