// Added by Richie for #BUG 1340366
// these functions are taken from the ag-grid-community.js overridden here in order to supress stop the editing of the row upon clicking outside the editted row.
// these functions should be updated everytime we upgrade the ag-grid library.
RowComp.prototype.stopEditing = function (cancel, supressStopEditing = true) {
  // if (supressStopEditing) { return; }
  if (cancel === void 0) { cancel = false; }
  this.forEachCellComp(function (renderedCell) {
    if (!supressStopEditing) {
      renderedCell.stopEditing(cancel);
    }
  });
  if (!this.editingRow) {
    return;
  }
  if (!cancel) {
    var event_1 = this.createRowEvent(Events.EVENT_ROW_VALUE_CHANGED);
    this.beans.eventService.dispatchEvent(event_1);
  }
  this.setEditingRow(false);
};
GridApi.prototype.stopEditing = function (cancel, supressStopEditing?) {
  if (cancel === void 0) { cancel = false; }
  this.rowRenderer.stopEditing(cancel, supressStopEditing);
};
RowRenderer.prototype.stopEditing = function (cancel, supressStopEditing?) {
  if (cancel === void 0) { cancel = false; }
  this.forEachRowComp(function (key, rowComp) {
    rowComp.stopEditing(cancel, supressStopEditing);
  });
};
// End Richie

