@Injectable({
  providedIn: 'root'
})
export class PsNavigatorService {
  languageChanged;

  // @ViewChild('sideNav') sideNav: ElementRef<HTMLElement>;
  constructor(private navCtrl: NavController, public commonService: PsCommonService, public router: Router,
    public session: SessionService, private events: Events) {
    // added by ghada, AZDB-6133 in order to check for language change to properly reload and show the opened screen
    this.commonService.languageChanged.subscribe(isChanged => {
      if (isChanged && CommonUtils.previousLanguage && CommonUtils.previousLanguage !== String(PsCommonSettings.activeLanguge).toLocaleLowerCase()) {
        this.languageChanged = true;
      }
    });
  }

  private pagesNavigationInfo: BehaviorSubject<IOperDef> = new BehaviorSubject<IOperDef>(null);
  public isLoggedIn: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(null);
  private navParam: any = {};
  activeOpersList = [this.isUserLoggedIn ? ConstantCommon.LANDING_OPER_ID : ConstantCommon.LOGIN_OPER_ID];
  reloadRequired: boolean = false; // AZDB-7828 - Added by Muzammil
  dynamicRoutes: Routes = [];
  activePage: IPageBussiness;
  activePages: Array<IPageBussiness> = [];
  activePageList: Array<IOperDef> = [this.isUserLoggedIn ?
    {
      OPER_ID: ConstantCommon.LANDING_OPER_ID,
      route: ['./home']
    } :
    {
      OPER_ID: ConstantCommon.LOGIN_OPER_ID,
      // salah US #1475765 changed from ./omni
      route: ['./omni-login']
    }];


  private commonNavigationOptions = {
    // skipLocationChange: true , replaceUrl: false
  };
  private forwardAnimation = {
    ...this.commonNavigationOptions,
    animationDirection: 'forward'
  };

  private backwardAnimation = {
    ...this.commonNavigationOptions,
    animationDirection: 'back'
  };

  get isUserLoggedIn() {
    return PsCommonSettings.isLoggedIn;
  }

  public getPagesNavigationInfo(): Observable<IOperDef> {
    return this.pagesNavigationInfo.asObservable();
  }

  public getIsUserLoggedIn(): Observable<boolean> {
    return this.isLoggedIn.asObservable();
  }

  /**
   * a new page is going to be pushed to the stack of the outlet (ion-router-outlet),
   * and that it will show a "forward" animation by default.
   */
  private navigateForward(url, options?: PSNavigationOptions): Promise<any> {
    return new Promise<any>((resolve) => {
      if (PsCommonSettings.oper_ID === (ConstantCommon.LOGIN_OPER_ID || ConstantCommon.LANDING_OPER_ID)) {
        this.activeOpersList = [PsCommonSettings.oper_ID];
        this.navigateToMain().then(() => {
          resolve(true);
        }).catch(() => {
          reject();
        });
      } else {
        const canNavigate = this.commonCheckingPriorNavigation(url, options, this.navigateForward);
        if (!canNavigate) {
          reject();
          return;
        }
        this.activeOpersList.push(PsCommonSettings.oper_ID);
        this.commonService.applyPageCustomization().then(() => {
          if (this.checkIfWeb(url)) {
            return this.navigateWeb(url, options);
          }
          this.navCtrl.navigateForward(url, this.createOptionsAndApplyCommonFlow(url, options, this.forwardAnimation)).then((result) => {
            this.postNavigationCommonFlow();
            resolve(result);
          }).catch((error) => {
            reject(error);
          });
        });
      }
    });
  }


  private commonCheckingPriorNavigation(url, options: PSNavigationOptions, functionToCall?: (...value: any) => boolean | void | { [key: string]: any; }, reloadWithoutPopup?): boolean {
    const previousOperID = this.activeOpersList.slice(-1).pop();
    let previousPageHref: any;
    if (this.activePageList.length > 0) {
      previousPageHref = this.activePageList.slice(-1).pop().route?.length > 1 ?
        this.activePageList.slice(-1).pop().route[1] : (Array.isArray(this.activePageList.slice(-1).pop().route) ? this.activePageList.slice(-1).pop().route[0].replace('./', '') : this.activePageList.slice(-1).pop().route)

    }


    const currentOperID = PsCommonSettings.oper_ID;
    // Start - AZDB-7828 - Added by Muzammil
    if(!this.reloadRequired && this.activeOpersList.includes(currentOperID) && currentOperID === CommonBussinessConstant.WIDGET_BILL_PAYMENTS_OPER_ID && this.activeOpersList.slice(-1).pop() === CommonBussinessConstant.FAV_PAYEE_MANAGMENT_OPER_ID){
      this.reloadRequired = true;
    }
    // End - AZDB-7828 - Added by Muzammil
    if (previousOperID && currentOperID && previousOperID === currentOperID  && previousPageHref == options.psPage?.component) {//Makki: Jira #6292, make psPage attribute optional in case it was called from langaugeChange event during runtime
      CommonUtils.dismissLoading();
      // added by ghada, AZDB-6133 in case mandatory reloading of the current screen without the option of having user permission  
      if (reloadWithoutPopup) {
        this.reloadSameScreen(options);
      } else {
        const alertController: IOptionsPsComplexAlertController = {
          cssClass: 'same-route-popup',
          header: 'reload_screen_key',
          buttons: [
            {
              role: 'cancel',
              options: {
                labelKey: 'cancel_key',
                psClass: 'ps-drafts-report-cancel-button',
                group: new FormGroup({}),
              },
              handler: () => {

              }
            },
            {
              options: {
                labelKey: 'yes_key',
                psClass: 'ps-button-submit',
                group: new FormGroup({}),
              },
              handler: async () => {
                await this.reloadSameScreen(options);
                CommonUtils.dismissAllModals();
              }
            }
          ]
        };
        this.commonService.presentPsAlert(alertController, 'same-route-popup');
      }
      return false;
    }
    return true;
  }


  // added by ghada, AZDB-6133 to separate the logic of reloading the current opned screen in order to use it multiple places without duplicating the code
  private async reloadSameScreen(options: PSNavigationOptions) {
    const oper = PsApplicationSettings.CLIENT_ASSETS_CONFIG.UI_CONFIGURATION.LANDING_PAGE_OPER;
    const currentOperID = PsCommonSettings.oper_ID;
    if (currentOperID === oper) {
      await this.navigateToDummyPage();
      this.navigateToMain();
    } else {
      await this.navigateBackward();
      this.openPage(options.psPage);
    }
  }

  private checkIfWeb(url) {
    const path = this.router.config.find((element) => element.path === url && element.outlet === 'webOutlet');
    if (!path) {
      return false;
    }
    if (this.commonService.isWeb() && path && this.isUserLoggedIn) {
      return true;
    }
    return false;
  }


  private navigateWeb(url, options?: PSNavigationOptions, type?): Promise<any> {
    return new Promise<any>((resolve, rej) => {
      const tempUrl: any = {};
      tempUrl.outlets = {};
      tempUrl.outlets.webOutlet = url;
      if (!type) {
        this.navCtrl.navigateForward([{ outlets: tempUrl.outlets }], this.createOptionsAndApplyCommonFlow(url, options, this.forwardAnimation)).then((result) => {
          this.postNavigationCommonFlow();
          resolve(result);
        }).catch((error) => {
          rej(error);
        });
      }
      if (type === 0) {
        this.activeOpersList = [];
        this.activeOpersList.push(PsCommonSettings.oper_ID);
        this.navCtrl.navigateRoot([{ outlets: tempUrl.outlets }], this.createOptionsAndApplyCommonFlow(url, options, this.forwardAnimation)).then((result) => {
          this.postNavigationCommonFlow();
          resolve(result);
        }).catch((error) => {
          rej(error);
        });
      }
    });
  }

  /**
   * all existing pages in the stack will be removed, and the navigated
   *  page will become the single page in the stack.
   */
  navigateToMain(url?: any, options?: PSNavigationOptions): Promise<any> {
    // Added by Richie to log the time of of the specific action
    if (PsApplicationSettings.MAIN_CONFIG.TIME_LOG_ENABLED) {
      PsCommonSettings.timeLogs.push('in navigate service, navigateToMain with start time: ' + (new Date()).toISOString());
    }
    // end Richie
    if (!url) {
      if (this.isUserLoggedIn) {
        const oper = PsApplicationSettings.CLIENT_ASSETS_CONFIG.UI_CONFIGURATION.LANDING_PAGE_OPER;
        const currentPage: IOperDef = this.commonService.getPageByOperId(oper);

        if (CommonUtils.isNotNull(oper) && currentPage && currentPage.PAGE_HREF && (this.commonService.isWebLayout() || PsApplicationSettings?.CLIENT_ASSETS_CONFIG?.UI_CONFIGURATION?.LANDING_UI_TYPE == 4)) {
          return new Promise<any>(async (resolve, rej) => {
            this.openPage({ operID: oper }, true).then(res => {
              // Added by Richie to log the time of of the specific action
              if (PsApplicationSettings.MAIN_CONFIG.TIME_LOG_ENABLED) {
                PsCommonSettings.timeLogs.push('in navigate service, navigateToMain of logged in user with end time: ' + (new Date()).toISOString());
              }
              // end Richie
              resolve(res);
            }).catch(err => { rej(err); });
          });
        } else {
          url = ['./home'];
          PsCommonSettings.oper_ID = ConstantCommon.LANDING_OPER_ID;
        }
      } else {
        this.commonService.removeOperCust(PsCommonSettings.oper_ID);
        url = ['./omni-login'];
        PsCommonSettings.oper_ID = ConstantCommon.LOGIN_OPER_ID;
      }
    }
    return new Promise<any>(async (resolve, rej) => {
      let custRes = true;
      // Added by Richie to log the time of of the specific action
      if (PsApplicationSettings.MAIN_CONFIG.TIME_LOG_ENABLED) {
        PsCommonSettings.timeLogs.push('in navigate service, navigateToMain: before applyPageCustomization with start time: ' + (new Date()).toISOString());
      }
      // end Richie
      custRes = await this.commonService.applyPageCustomization();
      // Added by Richie to log the time of of the specific action
      if (PsApplicationSettings.MAIN_CONFIG.TIME_LOG_ENABLED) {
        PsCommonSettings.timeLogs.push('in navigate service, navigateToMain: after applyPageCustomization with end time: ' + (new Date()).toISOString());
      }
      // end Richie
      if (!custRes) {
        rej(custRes);
      }
      this.activeOpersList = [];
      this.activeOpersList.push(PsCommonSettings.oper_ID);
      if (custRes) {
        if (url.includes('#')) {
          this.createRoute(url, PsCommonSettings.oper_ID);
          url = PsCommonSettings.oper_ID;
        }
        if (this.checkIfWeb(url)) {
          this.navigateWeb(url, options, 0).then(res => {
            resolve(res);
          }).catch(err => { rej(err); });
        } else {
          options ? options.navigationType = 'main' : options = {
            navigationType: 'main'
          };
          // Added by Richie to log the time of of the specific action
          if (PsApplicationSettings.MAIN_CONFIG.TIME_LOG_ENABLED) {
            PsCommonSettings.timeLogs.push('in navigate service, navigateToMain: before navigateRoot with start time: ' + (new Date()).toISOString());
          }
          // end Richie
          this.navCtrl.navigateRoot(url, this.createOptionsAndApplyCommonFlow(url, options, this.forwardAnimation)).then(res => {
            this.postNavigationCommonFlow();
            // Added by Richie to log the time of of the specific action
            if (PsApplicationSettings.MAIN_CONFIG.TIME_LOG_ENABLED) {
              PsCommonSettings.timeLogs.push('in navigate service, navigateToMain: after navigateRoot with end time: ' + (new Date()).toISOString());
            }
            // end Richie
            resolve(res);
          }).catch(err => {
            rej(err);
          });
        }
      }
    });
  }

  private createRoute(page_href, oper_Id) {
    if (page_href != null && page_href.includes('#')) {
      // folder-name/folder-name.module.ts#ClassNameDeclaredInModuleTS
      let res = page_href.split("#");
      this.dynamicRoutes = [];
      if (!this.router.config.includes(oper_Id.toString())) {
        this.dynamicRoutes.push({ path: oper_Id.toString(), loadChildren: () => import('src/app/pages/' + res[0]).then(m => m[res[1]]) });
        this.pushAllroutes();
      }
    }
  }

  private pushAllroutes() {
    if (this.dynamicRoutes.length > 0) {
      this.router.resetConfig([
        ...this.router.config,
        ...this.dynamicRoutes
      ]);
    }
  }


  private createOptionsAndApplyCommonFlow(url, options: PSNavigationOptions, animation): PSNavigationOptions {
    let newOptions: PSNavigationOptions = {};
    newOptions = this.commonService.copyObject(newOptions, options, true);
    newOptions = newOptions ? { ...newOptions, ...this.commonNavigationOptions, ...animation } :
      {
        ...this.commonNavigationOptions,
        ...animation
      };
    this.navParam = newOptions.queryParams ? newOptions.queryParams : {};
    if (options && options.navigationType) {
      this.navParam.navigationType = options.navigationType
    }
    // apply common flow
    let currentPage: IOperDef = this.commonService.getPageByOperId(PsCommonSettings.oper_ID);
    if (currentPage) {
      currentPage.route = url;
    } else {
      // edited by ahmed regarding TP#1411307 to add the PAGE_HREF property as it not exist
      // in prelogin screens and in admin customization to be checked on in Activate Guard
      currentPage = {
        route: url,
        OPER_ID: PsCommonSettings.oper_ID,
        PAGE_HREF: url
      };
    }
    this.pagesNavigationInfo.next(currentPage);
    if (options && options.navigationType != "backward") {
      this.activePageList.push(currentPage);
    }
    //Edited by ghade, tp #1303345 for fixing the issue of browser preserving its default state as omni-login after logging in
    //by changing browser url only in two cases when: 1-  user is loggedIn and path is 'home' 2- user is loggedOut and path is 'omni -login'
    url = url && url.toLocaleString().split('/')[1] || url;
    const loggedIn = options?.queryParams?.logout || this.isUserLoggedIn;
    newOptions.skipLocationChange = true;
    // end apply common flow
    // reset number of active ajax calls
    try {
      const previousOperID = this.activeOpersList.slice(-1).pop();
      if (this.commonService.http.activeConnectionMap[previousOperID]) {
        this.commonService.http.activeConnectionMap[previousOperID] = 0;
      }
    } catch (error) {
    }
    // added by ghada, AZDB-6133 to fix the issue of having the home screen not being reloaded properly on changing language
    if (this.languageChanged && this.isUserLoggedIn) {
      this.commonCheckingPriorNavigation(url, options, null, true);
      this.languageChanged = false;
    }
    // end reset number of active ajax calls
    return newOptions;

  }


  autoNavigate(navigationHandler: INavigationHandler) {
    if (!window.navigationHandler) {
      window.navigationHandler = navigationHandler;
    }
    const routes = navigationHandler.pageData;
    const currentPage = navigationHandler.currentPage;
    const mainPage = navigationHandler.mainPage;
    window.navigationHandler.currentPage++;

    if ([routes[currentPage].component]) {
      const page = {
        title: routes[currentPage].title ? this.commonService.translate(routes[currentPage].title) : null,
        icon: routes[currentPage].icon ? routes[currentPage].icon : null,
        operID: routes[currentPage].operID
      };
      this.commonService.activePage.next(page);
      this.activePages.push(page);
      PsCommonSettings.oper_ID = routes[currentPage].operID;
      this.navigateForward([routes[currentPage].component]);
    } else {
      // window.navigationHandler = undefined;
    }
  }

  popActivePage() {
    this.activeOpersList.pop();
    this.activePageList.pop();
    this.activePages.pop();
  }

  /**
  * Edited by ghada tp#1272134
  * instead of usingnavCtrl.pop(), navigateBack is better. to take advantage of 'skipChangeLocation' flag 
  * to follow app consistency of preventing URL changes and naming conventions  
  * */
  navigateBackward(): Promise<any> {
    return new Promise<any>((resolve) => {
      this.popActivePage()
      if (this.activeOpersList.length > 0) {

        PsCommonSettings.oper_ID = this.activeOpersList[this.activeOpersList.length - 1];
        const currentPage = this.activePageList[this.activePageList.length - 1];
        /**
        * Added by: MFawzy TP: #1377208
        * setting window.navigationHandler to be undefined to let the autoNavigate to set it with the options sent from preLoginRoutingChecking 
        * */
        if (PsCommonSettings.oper_ID === ConstantCommon.LOGIN_OPER_ID) {
          window.navigationHandler = undefined;
        }
        // End MFawzy
        let url: any = this.commonService.getPageByOperId(PsCommonSettings.oper_ID)?.PAGE_HREF;
        if (!url) {
          url = currentPage.route;
        }
        //Added for dynamic url
        if (url.includes('#')) {
          url = currentPage.route;
        }
        const activeP = this.activePages[this.activePages.length - 1];
        this.commonService.activePage.next(activeP);
        this.activePage = activeP;

        //added by ghada, ABEOI240089 for keeping the parentMenuOperId on navigating backword to submenu to be able to view it's content
        let navigationOptions: any = { ...this.activePage?.param, navigationType: 'backward' }
        if (CommonUtils.isMobile()) {
          navigationOptions.queryParams = { ...navigationOptions.queryParams, ...this.activePage?.param }
        }
        this.navCtrl.navigateBack(url, this.createOptionsAndApplyCommonFlow(url, navigationOptions, this.backwardAnimation)).then((result) => {
          this.postNavigationCommonFlow();
          //this.resetFlippedFlagForReachMenuScreens();//Jira #9053 - Removed the calling to this method to fix the issue and it didn't had an impact on Jira #4511 anymore which was the main reason to add the calling to reset reach menu flip
          resolve(result);
        }).catch((error) => {
          reject(error);
        });
      } else {
        this.navigateToMain().then(result => {
          resolve(result);
        }).catch(error => {
          reject(error);
        });
      }
    });
  }

  private resetFlippedFlagForReachMenuScreens() {
    document.getElementById('main-header')?.classList.remove('flipped');
  }

  getParamKey(key: string) {
    return this.navParam[key];
  }

  getAllParams() {
    return this.navParam != null && this.navParam != undefined ? this.navParam : {};
  }

  onCancelClicked():Promise<any> {
    return new Promise<any>((resolve) => {
     this.commonService.cancelClicked.next(true);
     const loginResponse = this.session.getValueOf(ConstantCommon.USERINFO);
     if (loginResponse === null || loginResponse?.deductChargeRequired !== "true") {
      this.navigateBackward().then(() => {
        resolve(true);
      })
     } else {
      this.events.publish('user:endedBysessionTermination');
      this.commonService.deductCharges.next(false);
    }
  });
  }

  async openPage(page: IPageBussiness, isMain?: boolean) {
    this.commonService.presentLoading();
    const currentPage: IOperDef = this.commonService.getPageByOperId(page.operID);
    this.commonService.http.activeConnectionMap[page.operID] = 0;
    if (currentPage) {
      if ((!page.component) && page.operID) {
        page.component = currentPage.PAGE_HREF.includes('#') ? currentPage.OPER_ID.toString() : currentPage.PAGE_HREF;
        page.icon = currentPage.ICON_NAME;
        page.title = page.title ? page.title : currentPage.OPER_NAME ? currentPage.OPER_NAME : currentPage.DESCRIPTION;
        page.param = page.param ? page.param : currentPage.PARAM;
        page.isMaker = currentPage.MAKER_YN === 'Y';
        page.isChecker = currentPage.CHECKER_YN === 'Y';
      }

      /*
      * added a checking on submission_report_key that is the case of post submit report navigation. Where we need to redirect to the report page  
      * based on component name and not the page-href. As on submit we are redirecting the user to post submit report using same main oper such as 
      * transfer. so the page href of the oper will be here the main screen and not the report page. So we avoid here entering the if where we are
      * changing the component name.
      */
      if (currentPage.OPER_ID != null && currentPage.PAGE_HREF && currentPage.PAGE_HREF.includes('#') && page.title != 'submission_report_key') {
        page.component = currentPage.OPER_ID.toString();
        page.param = page.param ? page.param : typeof currentPage.PARAM === "string" ? JSON.parse(currentPage.PARAM) : currentPage.PARAM;//Jira #7792 - include param for selected operId 
        this.createRoute(currentPage.PAGE_HREF, page.operID);
      }
    }

    if (CommonBussinessConstant.WORKING_CIF_OPER_TO_BE_CHECKED.includes(page.operID) && PsCommonSettings.APP_ID === ConstantCommon.AGENT_APPLICATION_APP_ID) {
      const workingCif = this.commonService.session.getValueOf('WORKINGCIF');
      if (workingCif === undefined) {
        CommonUtils.presentFailureAlert(this.commonService.translate('please_add_working_cif_before_key')).then(() => {
          this.navigateToMain();
          this.commonService.dismissLoading();
        });
        return;
      }
    }


    if (page && page.component && page.component !== ConstantCommon.HASH_INPUT) {
      let isAvailable = false;
      for (const eachRoute of this.router.config) {
        if (eachRoute.path === page.component) {
          isAvailable = true;
          break;
        }
      }
      if (isAvailable === false) {
        this.handleInvalidPageURL();
      }
      // Added by Richie to retrieve the customization records per screen
      PsCommonSettings.oper_ID = page.operID;
      PsCommonSettings.pageName = page.title;
      PsCommonBusinessSettings.isMaker = page.isMaker;
      PsCommonBusinessSettings.isChecker = page.isChecker;
      const userInfo = this.commonService.session.getValueOf(ConstantCommon.USERINFO);
      if ((page.isMaker != true && page.isChecker) || (page.isMaker === true && page.isChecker && page.param != undefined && page.param['ScreenVO']?.makerOcUserID != undefined && userInfo.ocUserId != page.param['ScreenVO'].makerOcUserID)) {
        if (page.param === undefined) {
          page.param = {};
        }
        page.param.readOnlypage = true;
        page.param.showTcAndPreviewInReadonly = true;
      }

      const navigationOptions: PSNavigationOptions = {
        queryParams: page.param
      };
      navigationOptions.psPage = page;
      this.activePage = page;
      this.commonService.activePage.next(page);
      this.activePages.push(page);
      if (isMain) {
        await this.navigateToMain(['./', page.component], navigationOptions)
      } else {
        await this.navigateForward(['./', page.component], navigationOptions);
      }
      this.commonService.dismissLoading(); // Added by Mohammad for #BUG 1430604
      // Start - AZDB-7828 - Added by Muzammil
      if (!isMain && this.reloadRequired) {
          await this.reloadSameScreen(navigationOptions);
          CommonUtils.dismissAllModals();
          this.reloadRequired = false;
      }
      // End- AZDB-7828 - Added by Muzammil
      // End Richie
    } else {
      this.handleInvalidPageURL();
    }
  }


  handleInvalidPageURL() {
    CommonUtils.presentFailureAlert(this.commonService.translate('page_not_found_key'), { autoHide: true }).then(() => {
      PsCommonSettings.oper_ID = ConstantCommon.LANDING_OPER_ID;
      this.navigateToMain(['./home']);
      this.commonService.dismissLoading();
      return;
    });
  }

  private postNavigationCommonFlow() {
    // added in case of common post navigation
  }

  preLoginRoutingChecking(authenticationResponse): IPageBussiness[] {
    const routesArray: IPageBussiness[] = [];
    if (authenticationResponse.firstLogin) {
      routesArray.push({ component: 'terms-and-conditions', title: 'terms_and_conditions_key', icon: 'diamond' });
    }
    if (authenticationResponse.requireChangePassword) {
      routesArray.push({ component: 'change-pass', title: 'change_password_key', icon: 'diamond', operID: CommonBussinessConstant.CHANGE_PASSWORD_OPER });
    }
    if (authenticationResponse.requireChangePIN) {
      routesArray.push({ component: 'change-pin', title: 'change_pin_key', icon: 'diamond', operID: CommonBussinessConstant.CHANGE_PIN_OPER });
    }
    // Commented By OmarTalaat ABEOI240006 Disable Activate softToken-OTP mandatory if user not assignee
    // if (authenticationResponse.assignedSoftToken != 0) {
    //   routesArray.push({ component: 'activate-soft-token-otp', title: 'activate_soft_token_otp_key', icon: 'diamond', operID: CommonBussinessConstant.SOFT_TOKEN_OTP_OPER_ID });
    // }
    this.session.append(ConstantCommon.USERINFO, { ocUserId: authenticationResponse.ocUserId, omniUserVO: { NAME: authenticationResponse.userName } });
    this.session.append(ConstantCommon.APP_ID, authenticationResponse.appId);
    if (routesArray.length > 0) {
      routesArray.push({ component: 'omni', operID: ConstantCommon.LOGIN_OPER_ID });
      this.autoNavigate({ currentPage: 0, mainPage: 'omni', pageData: routesArray, commonParam: { actionType: 'C' } });
    }
    return routesArray;
  }

  async navigateToDummyPage() {
    const page: IPageCommon = {
      title: ConstantCommon.DUMMY_PAGE_TITLE,
      component: ConstantCommon.DUMMY_PAGE_HREF,
      operID: ConstantCommon.DUMMY_PAGE_OPERID
    };
    this.activePages.push(page);
    this.commonService.activePage.next(page);
    await this.navigateToMain(page.component);
  }
}
