import { ChangeDetectorRef, Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { LoggerService } from '../../../../../psServices/logger/logger.service';
import { IOptionsPsIconCustomization, IOptionsPsSelectSegment, IPsSelect } from '../../../../../psServices/models/ps-common-interface';
import { PsFieldSelectComponent } from '../ps-field-select.component';


/**
 * @author Aftab.Ali
 * @since 19/11/2019
 *
 * <p> PsSelectSegmentComponent will show the segment buttons based on the provided list of values.</p>
 */
@Component({
  selector: 'ps-select-segment',
  templateUrl: './ps-select-segment.component.html',
  styleUrls: ['./ps-select-segment.component.scss'],
})
export class PsSelectSegmentComponent extends PsFieldSelectComponent implements OnInit {

  @Input() options: IOptionsPsSelectSegment;
  @Output() public segmentButtonClicked: EventEmitter<any> = new EventEmitter<any>();
  _selectedSegment: string;
  selectedIdx: any;
  defaultSegment: IPsSelect;
  selectedSegmentColor: string;
  multiOutlet: boolean = false;

  // Added by Richie for #BUG 1116960
  get selectedSegment() {
    const valFromVO: string = this.returnValue();
    if (valFromVO && valFromVO.toString() !== this._selectedSegment) {
      this._selectedSegment = valFromVO.toString();
    }
    return this._selectedSegment;
  }

  set selectedSegment(val) {
    this._selectedSegment = val;
    super.writeValue(this._selectedSegment);
  }
  // End Richie

  constructor(
    commonService: PsCommonService,
    logger: LoggerService,
    private cf: ChangeDetectorRef
  ) {
    super(commonService, logger);
  }

  ngOnInit() {
    super.ngOnInit();
    this.multiOutlet = this.options.isMultiOutlet ? this.options.isMultiOutlet : false;
    for (let i = 0; i < this.options.segmentList.length; i++) {
      this.applyCustomization(this.options.fcName + '_' + this.options.segmentList[i].itemValue);
      const custMapVal = this.custOptionsMap.get(this.options.fcName + '_' + this.options.segmentList[i].itemValue);
      if (custMapVal && !this.custMode && custMapVal.labelKey) {
        this.options.segmentList[i].description = custMapVal.labelKey;
      }
    }
    if (this.options.defaultSegment && this.options.defaultSegment.itemValue) {
      this._selectedSegment = this.options.defaultSegment.itemValue.toString();
    }
  }

  segmentChanged() {
    this.cf.detectChanges();
    if (this.options.isMultiOutlet) {
      this.multiOutletCust();
    }
    this.segmentButtonClicked.emit(this._selectedSegment);
  }

  multiOutletCust() {
    for (let i = 0; i < this.options.segmentList.length; i++) { // added for multi outlet
      this.applyCustomization(this.options.fcName + '_' + this.options.segmentList[i].itemValue);
      const custMapVal = this.custOptionsMap.get(this.options.fcName + '_' + this.options.segmentList[i].itemValue);
      if (custMapVal && !this.custMode && custMapVal.labelKey) {
        this.options.segmentList[i].description = custMapVal.labelKey;
      }
    }
  }

  returnSegCustOptions(segment: IPsSelect) {
    const newOptions: IOptionsPsSelectSegment = {
      labelKey: segment.description,
      fcName: this.options.fcName + '_' + segment.itemValue
    }
    // Hisham Omar - TP#1254006 - Adding formGroup to the options as needed to send formData to the field customization screen
    const segmentCustOptions: IOptionsPsIconCustomization = {
      fieldNameId: this.options.fcName + '_' + segment.itemValue,
      fieldNameDesc: segment.description,
      component: PsSelectSegmentComponent,
      formGroup: this.options.group,
      componentOptions: newOptions,
      availableCustomization: {
        IS_VISIBLE: true,
        IS_READONLY: true,
        KEY_LABEL_ID: true
      }
    }
    return segmentCustOptions;
  }

  returnSegCust(segment: IPsSelect) {
    const segmentCust: any = {};
    const custMapVal = this.custOptionsMap.get(this.options.fcName + '_' + segment.itemValue);
    if (this.custOptionsMap && !this.custMode) {
      if (custMapVal === undefined) {
        segmentCust.visible = true;
        segmentCust.disabled = false;
      } else {
        segmentCust.visible = custMapVal.visible;
        segmentCust.disabled = custMapVal.disabled;
      }
    } else {
      segmentCust.visible = true;
      segmentCust.disabled = false;
    }
    return segmentCust;
  }
}
