import { Component, ElementRef, Input, OnInit } from '@angular/core';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IFileUploadObject, IOptionsPsBaseField } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { PsBaseFieldComponent } from '../ps-base-field.component';


@Component({
  selector: 'ps-field-file',
  templateUrl: './ps-field-file.component.html',
})
export class PsFieldFileComponent extends PsBaseFieldComponent implements OnInit {


  @Input() options: IOptionsPsBaseField;
  constructor(
    commonService: PsCommonService,
    logger: LoggerService, myElement?: ElementRef
  ) {
    super(commonService, logger);
  }

  ngOnInit() {
    this.init();
  }

  init() {
    this.options.fcName = this.options.fcName ? this.options.fcName.includes(ConstantCommon.DEFAULT_FILE_UPLOAD_CNAME) ? this.options.fcName : this.options.fcName + '_' + ConstantCommon.DEFAULT_FILE_UPLOAD_CNAME : ConstantCommon.DEFAULT_FILE_UPLOAD_CNAME;
    super.init();
  }
  setFileValController(file: IFileUploadObject[]) {
    if (file) {
      this.options.group.controls[this.options.fcName].setValue(file);
      super.writeValue(file, true);// true Added by Richie to call the validation service in case added through customization.
    }

  }
  resetFileValController() {
    this.options.group.controls[this.options.fcName].setValue('');
    super.writeValue('');

  }

}
