import { ApplicationRef, Component, ComponentFactoryResolver, ComponentRef, ElementRef, EmbeddedViewRef, Injector, Input, OnInit, Renderer2, TemplateRef, ViewChild } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { IOptionsPsActionIconExposed } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { CommonUtils } from '../../psServices/models/common-utils';
import { IOptionsPsTemplateScreenPopup } from './ps-template-screen-popup.interfaces';

@Component({
  selector: 'ps-template-screen-popup',
  templateUrl: './ps-template-screen-popup.html'
})
export class PsTemplateScreenPopup implements OnInit {

  @Input() options: IOptionsPsTemplateScreenPopup = {};
  currentTemplate: TemplateRef<any>;

  @ViewChild('contentWrapper', { read: ElementRef, static: true }) contentWrapper: ElementRef;
  overlayRef: ComponentRef<any>;

  constructor(public modalController: ModalController, private eRef?: ElementRef,
    private componentFactoryResolver?: ComponentFactoryResolver, private appRef?: ApplicationRef,
    private injector?: Injector, private renderer?: Renderer2) {
    // this.viewContainerRef = viewContainerRef;
  }


  closeIcon: IOptionsPsActionIconExposed = {
    iconName: 'close',
    labelOptions: {
      labelKey: '',
    }
  };


  ngOnInit() {
    if (this.options.component && this.contentWrapper) {
      this.overlayRef = this.componentFactoryResolver
        .resolveComponentFactory(this.options.component)
        .create(this.injector);

      this.overlayRef.instance.options = {};

      CommonUtils.copyObject(this.overlayRef.instance.options, this.options.componentOptions, false, false);

      this.appRef.attachView(this.overlayRef.hostView);

      const domElem = (this.overlayRef.hostView as EmbeddedViewRef<any>)
        .rootNodes[0] as HTMLElement;

      this.renderer.appendChild(this.contentWrapper.nativeElement, domElem);
    }
  }

  dismissPopover() {
    this.modalController.dismiss();
  }
}
