import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { PsBaseActionComponent } from '../ps-base-action.component';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IOptionsPsActionVirtualKeyboard } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { KeyboardOptions } from './ps-action-virtual-keyboard.component.interface';
declare var $: any;

@Component({
  selector: 'ps-action-virtual-keyboard',
  templateUrl: './ps-action-virtual-keyboard.component.html',
})
export class PsActionVirtualKeyboardComponent extends PsBaseActionComponent implements OnInit {

  @Input() options: IOptionsPsActionVirtualKeyboard;
  @Output() onVirtualKeyboardClick = new EventEmitter<any>();
  enableVK:boolean = false;
  constructor(private commonService: PsCommonService, loggerP: LoggerService) {
    super(commonService, loggerP);
  }

  async ngOnInit() {
    let options = { iconName: 'keyboard', iconPosition: 'end', allowCust: true };
    CommonUtils.copyObject(this.options, options, false, true, true);
    this.enableVK = !this.options.group.get(this.options.ionInputFcname).disabled && this.options.forceVK
    if (this.options.automaticallyOpen)
      this.initializeVk();
  }

  showVirtualKeyboard() {
    this.initializeVk();
  }

  initializeVk() {
    setTimeout(() => {
      let inputElement = document.getElementById(this.options.inputId) as HTMLInputElement;
      let appLanguage = PsCommonSettings.activeLanguge.toLowerCase();
      let options: KeyboardOptions = {
        type: 'input',
        layout: this.options.vKeyboardOptions?.layout ? this.options.vKeyboardOptions?.layout : this.mapLanguage(appLanguage),
        usePreview: false,
        useWheel: true,
        appendLocally: false,
        stickyShift: true,
        stayOpen: false,
        change: (event: any, keyboard, el: any) => {
          let val;
          val = el.value != '' ? el.value.split('').reverse().join('') : el.value;
          if (event.action === "bksp") {
            val = val.slice(0, -1);
          }
          this.options.group.get(this.options.ionInputFcname).setValue(val);
          this.commonService.setValInsideNestedObj(this.options.ionInputFcname, val, this.options.group.controls.formData.value);
        },
        canceled: () => {
          this.commonService.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, ['vkeyboardIcon_' + this.options.ionInputFcname], 0);
        },
        accepted: () => {
          this.commonService.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, ['vkeyboardIcon_' + this.options.ionInputFcname], 0);
        },
      };
      if (options.layout === 'num') {
        options['css'] = {
          buttonDefault: 'num-buttons'
        };
      }
      this.commonService.copyObject(this.options.vKeyboardOptions, options, true, true, true);
      if (this.options.shuffleKeys && $(inputElement).data('keyboard') == undefined) {
        $(inputElement).keyboard(options).addScramble({
          byRow: true,
          randomizeOnce: false
        });
      } else if ($(inputElement).data('keyboard') == undefined) {
        $(inputElement).keyboard(options);
      }
      let keyboard = $(inputElement).data('keyboard');
      if(!keyboard.isVisible())
        keyboard.reveal();
      let vkRef = document.getElementsByClassName('ui-keyboard')[0] as HTMLInputElement;
      let keyboardPos: any = this.getKeyinInputPosition(vkRef.offsetWidth, vkRef.offsetHeight, vkRef.offsetParent.clientWidth, vkRef.offsetParent.clientHeight);
      Object.assign(vkRef.style, keyboardPos);
      this.commonService.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, ['vkeyboardIcon_' + this.options.ionInputFcname], 1);
    }, 500);
  }

  getKeyinInputPosition(keyboardWidth, keyboardHeight, screenWidth, screenHeight) {
    const elemRect = this.options.parentElementRef.nativeElement.getBoundingClientRect();
    let top = elemRect.top + elemRect.height;
    let currentPosition: any = {
      left: ((screenWidth - keyboardWidth) / 2) + 'px'
    };
    if ((screenHeight - top) > keyboardHeight)
      currentPosition['top'] = top + 'px';
    else
      currentPosition['top'] = (elemRect.top - keyboardHeight) + 'px';
    return currentPosition;
  }

  //to map the application language according to the keyboard layouts
  mapLanguage(lan: string) {
    switch (lan) {
      case 'en':
        return 'ms-US-International';
      case 'fr':
        return 'ms-French';
      case 'ar':
        return 'ms-Arabic (101)';
      case 'ta':
        return 'ms-Tamil';
      case 'si':
        return 'ms-Sinhala';
      default:
        return lan;
    }
  }
}
