import { Injectable } from '@angular/core';
import { environment } from '../../../../environments/environment';
import { ErrorHandlerService } from '../errorhandler/errorhandler.service';
import { CommonUtils } from '../models/common-utils';
import { PsApplicationSettings } from '../models/ps-app-settings';


@Injectable({
  providedIn: 'root'
})
export class LoggerService {

  constructor(private errorHandlerService: ErrorHandlerService) {

  }

  public LOG_NORMAL = 1;
  public LOG_DEBUG = 2;
  public LOG_VERBOSE = 3;
  public LOG_ERROR = 4;

  public log(...message: any[]): void {
    this.displayLogAsAlert(message);
    if (this.enableCrashLiticsDebug()) {
      this.errorHandlerService.handleLoggerServiceMessage(message, this.errorHandlerService.LOG_DEBUG);
    } else if (!environment.production) {
      console.log(...message);
    }
  }

  public warn(...message: any[]): void {
    this.displayLogAsAlert(message);
    if (this.enableCrashLiticsDebug()) {
      this.errorHandlerService.handleLoggerServiceMessage(message, this.errorHandlerService.LOG_WARN);
    } else if (!environment.production) {
      console.warn(...message);
    }
  }

  public info(...message: any[]): void {
    this.displayLogAsAlert(message);
    if (this.enableCrashLiticsDebug()) {
      this.errorHandlerService.handleLoggerServiceMessage(message, this.errorHandlerService.LOG_INFO);
    } else if (!environment.production) {
      console.info(...message);
    }
  }

  public error(...message: any[]): void {
    this.displayLogAsAlert(message);
    if (this.enableCrashLiticsDebug()) {
      this.errorHandlerService.handleLoggerServiceMessage(message, this.errorHandlerService.LOG_ERROR);
    } else if (!environment.production) {
      console.error(...message);
    }
  }

  private displayLogAsAlert(message) {
    // Added by Richie to simplify the debugging of issues upon startup of the app where crashlytics are still not initialized
    if (PsApplicationSettings.MAIN_CONFIG.SHOW_ALERT_ERROR) {
      let err: any = JSON.stringify(message);
      if (err == '[{}]') {
        err = message;
      }
      alert(err);
    }
    // end Richie
  }

  public trace(...message: any[]): void {
    if (this.enableCrashLiticsDebug()) {
      this.errorHandlerService.handleLoggerServiceMessage(message, this.errorHandlerService.LOG_DEBUG);
    } else if (!environment.production) {
      console.trace(...message);
    }
  }

  public async performanceTracing(...message: any[]): Promise<void> {
    if (environment.production && CommonUtils.isNativeMobile()) {
      await this.errorHandlerService.handlePerformanceTracingMessage(message[0]);
    } else if (!environment.production) {
      console.error(...message);
    }
  }

  public clear() {
    console.clear();
  }

  public table(...message: any[]) {
    console.table(...message);
  }

  enableCrashLiticsDebug() {
    return this.errorHandlerService.enableCrashLiticsDebug()
  }

  public logForTesting(...message: any[]): void {
    this.errorHandlerService.handleLoggerServiceMessage(message, this.errorHandlerService.LOG_DEBUG);
  }
}
