  /**
    * function to prevent the controller to set string value in dropdown
    * return null or object
    * #BUG 1130659
    * @author MalakDaher
    */
  public custOnlyObjectAllowed(): ValidatorFn {
    return (control: any): { [key: string]: any } | null => {
      if (control.value && typeof control.value != 'object') {
        if (control.innerValue && typeof control.innerValue === 'object') {
          return null;
        }
        return { onlyObjectAllowed: true }
      }
      return null;
    };
  }

  public custServiceMappingValidator(customizationRequest: any, group: FormGroup): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } | null => {
      if (control.value) {
        this.commonServiceCallForCustomization(customizationRequest).then(result => {
          if (result.outputType === 'S') {
            return null;
          }
        })
          .catch(error => {
            if (error.data.outputType !== 'S') {
              this.logger.error('Error ! while calling common service for customization ', error);
              return { serverError: error.data.outputNotification, valid: false };
            }
          });
      }
      return null;
    };
  }

  /**
   * function to replace the Validators.email() function in order to set the valid email address the same as that in OADM in function OmniCommonMethods.validateEmail()
   * #BUG 789100
   * @author RichardZourob
   */
  public custEmailValidator(): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } | null => {
      if (control.value) {
        const validEmailAddressRegEx = /^[\w!#$%&'*+/=?`{|}~^-]+(?:\.[\w!#$%&'*+/=?`{|}~^-]+)*@(?:[a-zA-Z0-9-]+\.)+[a-zA-Z]{2,63}$/;
        const result = validEmailAddressRegEx.test(control.value);

        if (!result) {
          return { email: true };
        }
      }
      return null;
    };
  }
  /**
   * function to validate the HTML Injection in input fields - Fix for ABE Security issue
   * #JIRA - AZDB-4709
   * @author Muzammil Arif
   */
  public custHtmlInjectionValidator(): ValidatorFn {
    return (control: AbstractControl): ValidationErrors | null => {
      // Regular expression to detect HTML tags
      // const htmlTagPattern = /<\/?[^>]+(>|$)/;
      const htmlTagPattern = ConstantCommon.htmlInjectionRegex;

      // Checking if the control's value matches the pattern
      const hasHtmlTags = htmlTagPattern.test(control.value);

      // If HTML tags are found, return an error object
      return hasHtmlTags ? { 'htmlInjection': true } : null;
    };
  }

