  returnDynamicScreen(request: IDynamicScreenRequest): Promise<any> {
    return new Promise<any>((resolve, reject) => {
      let screenData = this.dynamicScreenMap.get(request.screenId);
      let currLang = PsCommonSettings.activeLanguge;
      if (request.screenId && screenData) {
        if (screenData.data) {
          if (screenData.data.language) {
            //delete saved screen if the language has changed
            if (screenData.data.language.toLowerCase() != currLang.toLowerCase()) {
              this.dynamicScreenMap.delete(request.screenId);
            }
          }
        }
      }
      screenData = this.dynamicScreenMap.get(request.screenId);
      if (!screenData) {
        this.http.commonRequestAjax(PsCommonSettings.serviceUrl.returnDynamicScreen, request).then((result) => {
          if (result && result.success) {
            this.dynamicScreenMap.set(request.screenId, result);
            screenData = this.dynamicScreenMap.get(request.screenId);
            if (screenData.data) {
              //update screen language to current selected language
              if (screenData.data.language) {
                screenData.data.language = currLang;
              }
            }
          }

          resolve(result);
        }).catch((err) => {
          this.logger.error(err);
          reject(err);
        });
      }
      else {
        resolve(screenData);
      }
    });
  }
