  async scanQRFromPhotoLibrary(): Promise<any> {
    let imageData;
    if (this.isNativeMobile()) {
      imageData = await this.camera.getPicture({
        sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
        destinationType: this.camera.DestinationType.DATA_URL,
        encodingType: this.camera.EncodingType.JPEG,
        mediaType: this.camera.MediaType.PICTURE,
        quality: 100
      });
    } else {
      imageData = await this.uploadPhotosToWeb('qr-code');
    }
    if (imageData) {
      const qrData = await this.barcodeScanner.decodeQRFromImage(imageData);
      return qrData;
    } else {
      throw false;
    }
  }

  uploadPhotosToWeb(id) {
    return new Promise((resolve, reject) => {
      const imageUploadElement = document.createElement('input');
      imageUploadElement.name = 'image';
      imageUploadElement.type = 'file';
      imageUploadElement.id = id;
      imageUploadElement.accept = 'image/*';
      imageUploadElement.onchange = (val) => {
        const imagedataValue: any = val.target;
        const fileNamevalue = imagedataValue.files[0].name;
        const ext = fileNamevalue.split('.')[1];
        const acceptFormatExt = ConstantCommon.DEFAULT_ALLOWED_IMG_TYPE;
        if (acceptFormatExt && acceptFormatExt.length > 0) {
          if (acceptFormatExt.indexOf(ext.toLowerCase()) == -1) {
            CommonUtils.presentInfoAlert(this.translate('image_files_only_allowed_key'), { autoHide: true });
            reject(false);
          } else {
            this.getBase64(val.target['files'][0]).then((base64Data) => {
              resolve(base64Data);
            });
          }
        }
      };
      imageUploadElement.click();
    });
  }

  getBase64(file) {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => {
        // this.base64UrlWeb = reader.result.toString();
        let encoded = reader.result.toString().replace(/^data:(.*,)?/, '');
        if ((encoded.length % 4) > 0) {
          encoded += '='.repeat(4 - (encoded.length % 4));
        }
        resolve(encoded);
      };
      reader.onerror = error => reject(error);
    });
  }

