  updateScreenExpressionsMap(dependencies: any[], myObservable: BehaviorSubject<any>) {
    if (dependencies.length > 0) {
      const operationMapEntity = this.screenExpressionsMap.get(PsCommonSettings.oper_ID);
      if (!operationMapEntity || operationMapEntity.size === 0) {
        this.screenExpressionsMap.set(PsCommonSettings.oper_ID, new Map<string, BehaviorSubject<any>[]>());
      }
      for (let eachDep of dependencies) {
        const fieldEntity = this.screenExpressionsMap.get(PsCommonSettings.oper_ID).get(eachDep);
        if (fieldEntity) {
          this.screenExpressionsMap.get(PsCommonSettings.oper_ID).get(eachDep).push(myObservable);
        } else {
          this.screenExpressionsMap.get(PsCommonSettings.oper_ID).set(eachDep, [myObservable]);
        }
      }
    }
  }

  returnExpressionResult(formData: any, expr: string, rowIndex?: number, dependencies?: any[], formGroup?: FormGroup): Promise<any> {
    return new Promise<any>(async (resolve, reject) => {
      // Added by Richie in order to get the data upon first initialization (for the case when there's and expression based on screen variable and that variable already has a value set then we need to evaluate the expression)
      if (formGroup && CommonUtils.isEmptyObject(formData)) {
        formData = this.returnFormVO(formGroup);
      }
      if (formData == null) {
        formData = this.returnFormVO(this.getFormGroupByOper());
        if (CommonUtils.isNotEmptyObject(formData) && CommonUtils.isNotEmptyObject(dependencies) && !formData.includes(Object.keys(dependencies))) {
          formData = dependencies.reduce((res, dependency) => {
            res[dependency] = null;
            return res;
          }, {});
        }
      }
      // End Richie 

      if (CommonUtils.isNotEmptyObject(formData)) {//modified by Richie to cater the cases when the formData is empty then no need to call the execute expression.
        // Added by Richie in order to remove indexed fields in case we have the attribute rowIndex sent.
        let data = {};
        if (rowIndex || rowIndex === 0) {
          this.copyObject(data, formData);
          CommonUtils.removeIndexedAttributes(data, rowIndex);
        } else {
          data = formData;
        }
        // End Richie
        let params = {
          commonParametersList: {},
          expression: expr
        };
        // Added by Richie for #TP 1338932
        const expressionVars = this.returnExpressionFields();
        if (expressionVars) {
          params['fcNamesAndsessionVarsList'] = expressionVars[ConstantCommon.CUSTOMIZATION_EXPR_VARIABLES];
          if (expressionVars[ConstantCommon.SESSION_CUSTOMIZATION_EXPR_VARIABLES]) {
            const sessionParams = this.session.getFilteredSessionObject(expressionVars[ConstantCommon.SESSION_CUSTOMIZATION_EXPR_VARIABLES]);
            // Added by MFawzy for tp#1487591 - to add the default values of storage cust vars under commonParametersList
            const sessionAndStorageVars = await this.session.getListOfSessionAndStorageVars();
            CommonUtils.copyObject(sessionParams, sessionAndStorageVars, true, true);
            // End MFawzy
            this.copyObject(params['commonParametersList'], sessionParams, false, true);
          }
          if (expressionVars[ConstantCommon.SCREEN_CUSTOMIZATION_EXPR_VARIABLES]) {
            const screenData = {};
            for (const screenVar of expressionVars[ConstantCommon.SCREEN_CUSTOMIZATION_EXPR_VARIABLES]) {
              this.setValInsideNestedObj(screenVar, this.valFromNestedObj(screenVar, data), screenData);
            }
            this.copyObject(params['commonParametersList'], screenData, false, true);
          }

        }
        // End Richie
        this.http.commonRequestAjax(PsCommonSettings.serviceUrl.returnExpressionResult, params).then(result => {
          if (result && result.data && result.data.exprResult !== undefined) {
            resolve(result.data.exprResult);
          } else {
            reject();
          }
        }).catch(error => {
          this.logger.log(error);
          reject(error);
        });
      } else {
        reject();
      }
    });
  }

  //Added by Ghada for TP #1189405
  /**
   * this method is to save photos(canvas) to gallery, in case of native mobile, Canvas2ImageService will be used
   */
  saveCanvasToGallery(canvas) {
    if (canvas) {
      if (this.isNativeMobile()) {
        this.canvas2Image.savePhoto(canvas).then(result => {
          if (result) {
            CommonUtils.presentSuccessAlert(CommonUtils.translate('photo_saved_successfully_key'));
          }
        }).catch(err => {
          CommonUtils.presentFailureAlert(CommonUtils.translate('error_on_saving_photo_to_gallery_please_try_again_key'));
          this.logger.error('error on saving photo/canvas to gallery in Canvas2ImageService', err)
        });
      } else {
        const imageData = canvas.toDataURL().split(',')[1];
        this.downloadFile(imageData, 'qr-code', 'png');
      }
    }
  }

