  /**
   * this method is to check if the device has biometric Authentication
   */
  isbiometricAvaliable() {
    return new Promise<any>((resolve, reject) => {
      this.faio.isAvailable().then(result => {
        if (result === 'biometric' || result === 'finger' || result === 'face') {
          resolve(true);
        } else {
          reject(false);
        }
      }).catch(err => {
        this.logger.error('error on checking for biometric authentication availability', err)
        reject(false);
      })
    });
  }


