  /**
   * Function to read file from directory
   * @param dirPath 
   * @param fileName 
   * @returns 
   */
  readFile(dirPath: string, fileName: string) {
    return new Promise<any>((resolve, reject) => {
      let directory = this.file.applicationDirectory + dirPath;
      this.file.resolveDirectoryUrl(directory).then(dir => this.file.getFile(dir, fileName, null).then(fileEntry => fileEntry.file(file => {
        let reader = new FileReader()
        reader.onloadend = (evt) => {
          let data = reader.result as String
          data = data.substring(data.indexOf(',') + 1);
          resolve(data)
        }
        reader.readAsDataURL(file)
      })).catch((e) => {
        this.logger.error(e);
        reject(e);
      })).catch((e) => {
        this.logger.error(e);
        reject(e);
      });
    });
  }


