  /**
   * function used to set the business customizations retrieved for all fields in a certain operation.
   * if the map already contains record of the specified operation then it will override it with the new records sent to be set.
   * 
   * @param businessCustMap a map of business customizations to be set. the map consists of fcName of the fields and the business customizations value for each field.
   * @param operIdParam the oper id related to the business customizations to be set to. Note: if not defined, then it will take the current operId of the opened screen.
   * @author RichardZourob
   */
  setBusinessCustOperMap(businessCustMap: Map<string, any>, operIdParam?: number) {
    const tempBusinessCustMap = new Map<string, any>();
    let operId = PsCommonSettings.oper_ID;
    if (operIdParam) {
      operId = operIdParam;
    }
    if (operId === null || operId === undefined) {
      this.logger.error("Error in setBusinessCustOperMap(): operId is not defined.");
      return;
    }
    businessCustMap.forEach((value, key) => {
      tempBusinessCustMap.set(key, value);
    });
    this.businessCustOperMap.set(operId, tempBusinessCustMap);
  }

  /**
   * function that returns the list of business customizations either related to a certain oper id (if provided as parameter) or the whole map if not specific operId is provided.
   * @param operId (optional) the oper id to retrieve the business customization list related to it. 
   * @returns the business customization map either by specific oper id or for all opers.
   * @author RichardZourob
   */
  returnBusinessCustOperMap(operId?: number) {
    if (operId) {
      return this.businessCustOperMap.get(operId);
    }
    return this.businessCustOperMap;
  }

  /**
   * 
   * @param fcName form controller name of the field or the id of the components that do not have a name.
   * @param operId (optional) the operid from which the record should be fetched from the map. Note: if not provided then will consider the current oper id of the opened screen.
   * @returns the business customization record of the related field or null if not found.
   * @author RichardZourob
   */
  returnFieldBusinessCust(fcName: string, operId?: number) {
    const tempOperId = operId ? operId : PsCommonSettings.oper_ID;
    const operBusCust = this.businessCustOperMap.get(tempOperId);
    if (operBusCust) {
      return operBusCust.get(fcName);
    } else {
      return null;
    }
  }
  // End Richie

  // Added by Richie For #BUG 1488584
  /**
   * function that retrieves the customization variables used in expressions for  a specific operation 
   * and returns those variables splitted between session and screen variables.
   * 
   * @param operId opional operation id by default it takes the current operation
   * @returns a JSON Object {ConstantCommon.SESSION_CUSTOMIZATION_EXPR_VARIABLES: array of all session variables used in customization expressions in the specified operid,
   *                         ConstantCommon.SCREEN_CUSTOMIZATION_EXPR_VARIABLES: array of all screen variables used in customization expressions in the specified operid}
   * or returns null in case no variables are used.
   * 
   * @author RichardZourob
   */
  returnExpressionFields(operId?: number) {
    const tempOperId = operId ? operId : PsCommonSettings.oper_ID;
    const operCust = this.initialScreenDisplayParams.get(tempOperId);
    if (operCust) {
      const expressionVars = <string[]>operCust.get(ConstantCommon.CUSTOMIZATION_EXPR_VARIABLES);
      if (expressionVars) {
        const result = {};
        result[ConstantCommon.CUSTOMIZATION_EXPR_VARIABLES] = expressionVars.map(el => { return '[' + el + ']' }).toString();
        result[ConstantCommon.SESSION_CUSTOMIZATION_EXPR_VARIABLES] = expressionVars.filter(exp => { return exp.indexOf(ConstantCommon.SESSION_VAR_PREFIX) > -1; }).map((el) => { return el.slice(ConstantCommon.SESSION_VAR_PREFIX.length) });
        result[ConstantCommon.SCREEN_CUSTOMIZATION_EXPR_VARIABLES] = expressionVars.filter(exp => { return exp.indexOf(ConstantCommon.SCREEN_VAR_PREFIX) > -1; }).map((el) => { return el.slice(ConstantCommon.SCREEN_VAR_PREFIX.length) });
        return result;
      } else {
        return null;
      }
    } else {
      return null;
    }
  }
  // End Richie 

  /**
   * function that checks if the value is an object (value of a dropdown or radio or upload file) then
   * extract from that object the displayable value that we should display in messages or on screen for the user.
   * @param value the value you want to extract data from or return as it.
   * @param listOfOptions 
   * @returns a string value that can be displayed for end user.
   * @author RichardZourob
   * # BUG 1579154
   */
  returnDisplayValue(value: any, listOfOptions?: any[]) {
    let stringValue = '';
    if (CommonUtils.isEmptyObject(value)) {
      return null;
    }
    if (listOfOptions && listOfOptions.length > 0) {
      //case of a dropdown or radio so the value will be an object of itemValue & Description! and thus we should return the Description.
      // stringValue = listOfOptions.filter((option) => { return option.itemValue === value })[0].description;
      if (typeof value === 'string') {
        stringValue = listOfOptions.filter((option) => { return option.itemValue === value })[0].description;
      } else if (typeof value === 'object') {
        stringValue = listOfOptions.filter((option) => { return option.itemValue === value.itemValue })[0].description;
      }
    } else if (value && typeof value === 'object' && CommonUtils.isNotEmptyArray(value) && CommonUtils.isNotEmptyString(value[0].fileName)) {
      //case of a file upload then the value will be an array of files so we should return the file names concatenated.
      for (let i = 0; i < value.length; i++) {
        if (CommonUtils.isNotEmptyString(stringValue)) {
          stringValue += '; ';
        }
        stringValue += value[i].fileName;
      }
    } else {
      stringValue = value;
    }
    return stringValue;
  }

