  isValidDateFormat(dateString) {
    if (dateString) {
      // Extract the date part (YYYY-MM-DD) from the string
      const datePart = dateString.split('T')[0];
      // regular expression that matches strings with all capital letters, containing at least 'YY', 'MM', and 'DD', and allowing '-' or '/'
      const pattern = /^(?=.*YY)(?=.*MM)(?=.*DD)[A-Z\-\/]+$/;
      // Check if the date part string matches the pattern
      return pattern.test(datePart);
    }
  }

