import { Component, Input, Output, EventEmitter, OnChanges, OnDestroy, OnInit, SimpleChanges } from '@angular/core';
import { OmniPullService } from './../../../../../commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { LoggerService } from './../../../../psServices/logger/logger.service';
import { DynScreenBtnTypes, IDynamicField, IDynamicScreenRequest, IOptionsPsActionIcon, IOptionsPsContainerDynamicScreen } from './../../../../psServices/models/ps-common-interface';
import { PsCommonSettings } from './../../../../psServices/models/ps-common.settings';
import { PsCommonService } from './../../../../psServices/ps-common/ps-common.service';
import { PsBaseContainerComponent } from './../ps-base-container.component';
import { DynamicScreenConstants } from './dynamic-screen-constants';
@Component({
    selector: 'ps-container-dynamic-screen',
    templateUrl: './ps-container-dynamic-screen.component.html'
})
export class PsContainerDynamicScreenComponent extends PsBaseContainerComponent implements OnInit, OnChanges, OnDestroy {
    @Input() options: IOptionsPsContainerDynamicScreen = {};
    @Output() onLoadComplete = new EventEmitter<any>();
    showDynamicComponents = false;
    listDynCompOptions: Array<IDynamicField> = [];
    defaultFormat: any = 'MM/DD/YYYY';
    loadingOptions: IOptionsPsActionIcon = {
        iconName: 'loading'
    };

    //AZDB-1880 Define a variable to store the old DYNAMIC_SCREEN_ID
    private oldDynamicScreenId: any;
    //AZDB-1880 Define a variable to store the old DYNAMIC_SCREEN_FCNAME to remove it in case select new dynamic element
    private oldDynamicScreenFcName: any;

    constructor(commonProv: PsCommonService, loggerP: LoggerService, private omniPull: OmniPullService) {
        super(commonProv, loggerP);
    }

    ngOnInit() {
       
    }

    ngOnDestroy() {
        this.onLoadComplete.unsubscribe();
    }

    //AZDB-1880 using ngOnChanges() to always listen for DynamicScreenId when value changes
    ngOnChanges(changes: SimpleChanges): void {
        if (changes.options && changes.options.currentValue.dynamicScreenId &&
            this.oldDynamicScreenId !== changes.options.currentValue.dynamicScreenId) {
            if (this.options && this.options.dynamicScreenId) {
                this.oldDynamicScreenId = changes.options.currentValue.dynamicScreenId;
                const parameters: IDynamicScreenRequest = {
                    screenId: this.options.dynamicScreenId
                }
                this.commonProv.returnDynamicScreen(parameters).then(result => {
                    if (result && result.success) {
                        this.prepareDynamicFields(result.data.formMap.mainFormGrp);
                    }
                });
            }
        }
    }

   async prepareDynamicFields(dynScreenDataObj) {
        if (this.oldDynamicScreenFcName) {
            this.listDynCompOptions = [];
            delete this.options.group.controls['formData'].value[this.oldDynamicScreenFcName];
        }
        const result = await this.omniPull.getParamValOf('DefaultDateFormat').catch(err => this.logger.log(err));
        if (result && result.DefaultDateFormat) {
            this.defaultFormat = result.DefaultDateFormat;
        }
        const dynRowMapRes = dynScreenDataObj.rowsMap;
        // tslint:disable-next-line: forin
        for (const row in dynRowMapRes) {
            const dynRowObjRes = dynRowMapRes[row];
            const dynColMapRes = dynRowObjRes.colMap;
            // tslint:disable-next-line: forin
            for (const col in dynColMapRes) {
                const custDynFieldOptions: IDynamicField = {};
                const dynObjCur = dynColMapRes[col];
                this.oldDynamicScreenFcName = dynObjCur.fcName;
                const compOptions: any = {};
                compOptions.group = this.options.group;
                compOptions.fcName = dynObjCur.fcName;
                compOptions.labelKey = dynObjCur.labelKey;
                switch (dynObjCur.type) {
                    case 'textfield': {
                        compOptions.type = dynObjCur.inputType;
                        break;
                    }
                    case 'label': {
                        compOptions.type = dynObjCur.labelKey;
                        break;
                    }

                    case 'comboBox': {
                        compOptions.listOfOptions = JSON.parse(dynObjCur.optionList);
                        break;
                    }
                    case 'datePicker': {
                        const pickerFormatIsValid = this.commonProv.isValidDateFormat(dynObjCur.pickerFormat);
                        compOptions.pickerFormat = pickerFormatIsValid ? dynObjCur.pickerFormat : this.defaultFormat;
                        compOptions.displayFormat = this.defaultFormat;
                        break;
                    }
                    case 'radioButton': {
                        compOptions.listOfOptions = JSON.parse(dynObjCur.optionList);
                        break;
                    }
                    case 'button': {
                        compOptions.buttonScrType = dynObjCur.buttonScrType;
                        compOptions.buttonScript = dynObjCur.buttonScript;
                        break;
                    }
                }
                custDynFieldOptions.component = DynamicScreenConstants.DYNAMIC_COMPONENTS_MAP[dynObjCur.type];
                custDynFieldOptions.id = dynObjCur.psId;
                custDynFieldOptions.componentOptions = compOptions;
                this.listDynCompOptions.push(custDynFieldOptions);
            }
        }
        this.showDynamicComponents = true;
        //TP#1319153 - Decrement the pending dynamic screens under this oper
        this.common.decrementDynamicLoadersCount(PsCommonSettings.oper_ID);
        //Jira #6440 - Broadcast the new loaded dyn components
        this.onLoadComplete.emit(this.listDynCompOptions);
    }

    // this function is called when the dynamic component triggers a click event like the action button.
    onClickEvent(event) {
        // (buttonOptions) this means that this event is called from a dynamic button
        if (event.buttonOptions) {
            if (event.buttonOptions.buttonScrType === DynScreenBtnTypes.scriptButton && event.buttonOptions.buttonScript) {
                eval(event.buttonOptions.buttonScript);
            }
            // here will come all other button src types cases later on.
        }
    }

}