
import { ChangeDetectorRef, Component, Input, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { CalendarEvent, CalendarEventTimesChangedEvent, CalendarView } from 'angular-calendar';
import { isSameDay, isSameMonth } from 'date-fns';
import { Subject } from 'rxjs';
import { PsBaseFieldComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-base-field.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IOptionsPsButtonStandard, IOptionsPsComplexCalendarExposed, IPsCalendarEvent, IPsOptionsEventClicked } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';

@Component({
  selector: 'ps-complex-calendar',
  templateUrl: './ps-complex-calendar.component.html',
})
export class PsComplexCalendarComponent extends PsBaseFieldComponent implements OnInit {

  @Input() options: IOptionsPsComplexCalendarExposed;

  constructor(public commonServices: PsCommonService, logger: LoggerService, private cdRef?: ChangeDetectorRef) {
    super(commonServices, logger);
  }

  buttonsGroup: FormGroup = new FormGroup({});
  CalendarView = CalendarView;
  view: CalendarView = CalendarView.Month;
  viewDate: Date = new Date();
  refresh: Subject<any> = new Subject();

  activeDayIsOpen: boolean = true;

  _PsCommonSettings = PsCommonSettings;


  previousOptions: IOptionsPsButtonStandard = {
    group: this.buttonsGroup,
    labelKey: 'previous_key',
    psClass: 'ps-button-back  ps-calendar-previous',
  }
  todayOptions: IOptionsPsButtonStandard = {
    group: this.buttonsGroup,
    labelKey: 'today_key'
  }
  nextOptions: IOptionsPsButtonStandard = {
    group: this.buttonsGroup,
    labelKey: 'next_key',
    psClass: 'ps-button-next ps-calendar-next',
  }



  monthOptions: IOptionsPsButtonStandard = {
    group: this.buttonsGroup,
    labelKey: 'month_key',
    psClass: 'ps-button-submit ps-calendar-month',
  }


  weekOptions: IOptionsPsButtonStandard = {
    group: this.buttonsGroup,
    labelKey: 'week_key',
    psClass: 'ps-button-submit ps-calendar-week',
  }



  dayOptions: IOptionsPsButtonStandard = {
    group: this.buttonsGroup,
    labelKey: 'day_key',
    psClass: 'ps-button-submit ps-calendar-day',
  }


  currentDescription;

  dayClicked({ date, events }: { date: Date; events: CalendarEvent[] }): void {
    if (isSameMonth(date, this.viewDate)) {
      if (
        (isSameDay(this.viewDate, date) && this.activeDayIsOpen === true) ||
        events.length === 0
      ) {
        this.activeDayIsOpen = false;
      } else {
        this.activeDayIsOpen = true;
      }
      this.viewDate = date;
    }
  }

  eventTimesChanged({
    event,
    newStart,
    newEnd,
  }: CalendarEventTimesChangedEvent): void {
    this.options.events = this.options.events.map((iEvent) => {
      if (iEvent === event) {
        return {
          ...event,
          start: newStart,
          end: newEnd,
        };
      }
      return iEvent;
    });
    // this.handleEvent('Dropped or resized', event);
  }

  eventClicked(event: IPsOptionsEventClicked): void {
    if (event && event.event && event.event.onClick) {
      event.event.onClick(event.event);
      this.compWriteValue(event.event);
      this.onPsChange.emit(event.event);
    }
  }


  setView(view: CalendarView) {
    this.view = view;
  }

  closeOpenMonthViewDay() {
    this.activeDayIsOpen = false;
  }

  ngOnInit() {
    super.init();
    this.init();
  }
  init() {
    this.itemOptions = CommonUtils.copyObject(this.itemOptions, this.options);
    this.itemOptions.hideImageAndIconIfNotPresent = true;
  }

  getCurrentValue() {
    const value = super.returnValue();
    const _value = this.options.group.controls[this.options.fcName] ? this.options.group.controls[this.options.fcName].value : null;
    if (_value != value) {
      this.setValueFCVal(value);
    }
    return value;
  }

  compWriteValue(value: IPsCalendarEvent) {
    this.setValueFCVal(value);
    super.writeValue(value);
  }

  setValueFCVal(value: IPsCalendarEvent) {
    this.options.group.controls[this.options.fcName].setValue(value);
    this.options.events.forEach(element => {
      element.cssClass = ''
    });
    if (value) {
      value.cssClass = 'item-is-selected'
    }
  }
}
