import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { IOptionsPsButtonStandard, IOptionsPsLabel } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';

@Component({
    selector: 'ps-header-footer',
    templateUrl: './ps-header-footer.component.html'
})

export class PsHeaderFooterComponent implements OnInit {
    @Output() public onClickSave = new EventEmitter<any>();
    @Output() public onClickConfirmReset = new EventEmitter<any>();
    @Input() labelsOptions: IOptionsPsLabel;
    @Input() confirmResetBtnShow: any;

    operIdOptions: IOptionsPsLabel = {
        labelKey: 'operid_key'
    };
    operIdValueOptions: IOptionsPsLabel = {
        labelKey: String(PsCommonSettings.oper_ID)
    };
    pageNameOptions: IOptionsPsLabel = {
        labelKey: 'page_name_key'
    };
    pageNameValueOptions: IOptionsPsLabel = {
        labelKey: PsCommonSettings.pageName
    };

    fieldNameOptions: IOptionsPsLabel = {
        labelKey: 'name_key'
    };
    resetCustOptions: IOptionsPsButtonStandard = {
        labelKey: 'reset_key',
        allowCust: false,
        group: null
    };

    cancelCustOptions: IOptionsPsButtonStandard = {
        labelKey: 'cancel_key',
        allowCust: false,
        group: null
    };
    saveShortcutOptions: IOptionsPsButtonStandard = {
        labelKey: 'save_key',
        allowCust: false,
        group: null
    };

    constructor(private modalCtrl: ModalController) { }

    ngOnInit() {

    }

    onSave() {
        this.onClickSave.emit();
    }

    confirmReset() {
        this.onClickConfirmReset.emit();
    }

    cancel() {
        this.modalCtrl.dismiss();
    }
}