import { Component, Input, OnInit } from '@angular/core';
import { IOptionsPsLabel } from './../../psServices/models/ps-common-interface';

@Component({
    selector: 'ps-lov-trans-popover',
    templateUrl: './ps-lov-trans-popover.component.html'
})
export class PsLovTransPopoverComponent implements OnInit {

    @Input() options: any[] = [];
    headerLabelKeys: Map<string, string> = new Map<string, string>();
    labelOptionsList: IOptionsPsLabel[] = [];
    columnWidth = {};
    constructor() {

    }

    ngOnInit() {
        this.headerLabelKeys.set('AR', 'arabic_key');
        this.headerLabelKeys.set('EN', 'english_key');
        this.headerLabelKeys.set('FR', 'french_key');
        this.headerLabelKeys.set('FA', 'farisi_key');
        this.headerLabelKeys.set('TK', 'turkey_key');
        this.headerLabelKeys.set('RU', 'russian_key');
        this.headerLabelKeys.set('TA', 'tamil_key');
        this.headerLabelKeys.set('SI', 'sinhala_key');

        this.columnWidth = { width: (100 / this.options[0].lovTransList.length) + '%' };
    }

    headerLabelOptions(language) {
        const labelOptions: IOptionsPsLabel = {
            labelKey: this.headerLabelKeys.get(language)
        };
        return labelOptions;
    }


}
