import { Component, Input, OnInit, Output, EventEmitter, isDevMode } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { PsDraftsReportComponent } from 'src/app/commonBussinessSRC/psComponents/ps-drafts-report/ps-drafts-report.component';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IOptionsPsActionIcon, IOptionsPsActionImage, IoptionsPsContainerWidgetCard, IOptionsPsDynamicComponentLoader, IOptionsPsLabel } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsContainerReportViewerComponent } from '../ps-container-report-viewer/ps-container-report-viewer.component';
import { PsActionChartsComponent } from '../../ps-base-action/ps-action-charts/ps-action-charts.component';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';

@Component({
  selector: 'ps-container-widget-card',
  templateUrl: './ps-container-widget-card.component.html',

})
export class PsContainerWidgetCardComponent implements OnInit {
  @Input() options:IoptionsPsContainerWidgetCard = {}
  @Output() onRemoveWidget = new EventEmitter();
  public titleOptions: IOptionsPsLabel = {};
  public subTitleOptions: IOptionsPsLabel = {};  
  todoReportTypes = ['DR','P','PA'];
  JasperReportType:'JR';
  widgetContent:IOptionsPsDynamicComponentLoader;
  showViewAllIcon:boolean = false
  isModal:boolean = false
  modalId = 'dashboard-widget-container-modal'

  constructor(private commonProv:PsCommonService,private navService:PsNavigatorService) { }
  removeWidgetIcon: IOptionsPsActionIcon = {
    iconName: 'close-square',
    allowCust: true
  };
  expandWidgetIcon: IOptionsPsActionIcon = {
    iconName: 'expand-top-right',
    allowCust: true  
  };
  viewAllIconOptions: IOptionsPsActionIcon = {
    iconName: 'arrow-circle-right',
    labelOptions:{
      labelKey:'view_all_key'
    },
    allowCust: true
  };

  actionImageOptions: IOptionsPsActionImage = {
    imageName: CommonUtils.getCssVariableValue('--ps-loader-image-name')
  };

  ngOnInit() {
   if (this.options) {
    const { widgetHeaderName, widgetTitle, linkedOperation,isModal } = this.options; 
    if (widgetHeaderName) {
     this.titleOptions = {
      labelKey: widgetHeaderName,
     };
    }
    if (widgetTitle) {
     this.subTitleOptions = {
      labelKey: widgetTitle,
     };
    }
    this.showViewAllIcon = !!linkedOperation;
    this.isModal = isModal
    this.widgetContent = this.loadWidgetContent();
    }
  }
  loadWidgetContent(): IOptionsPsDynamicComponentLoader {
    let dynamicComponent: IOptionsPsDynamicComponentLoader | null = null;
    if (this.todoReportTypes.includes(this.options.widgetReportType)) {
      dynamicComponent = {
        component: PsDraftsReportComponent,
        componentOptions: {
          filterByOper: false,
          openedFromLanding: true,
          loadDataFor: this.options.widgetReportType,
        },
      };
    } else if (this.options.widgetReportType === 'L') {
      dynamicComponent = {
        component: PsContainerReportViewerComponent,
        componentOptions: {
          showReport: true,
          operId:PsCommonSettings.oper_ID,
          progRef:this.options.widgetReportId,
          reportParametersList: {},
        },
      };
    } else {
      dynamicComponent = {
        component: PsActionChartsComponent,
        componentOptions: {
          chartType: this.options.widgetReportType,
          operId: PsCommonSettings.oper_ID,
          progRef:this.options.widgetReportId,
          reportParametersList: {},
        },
      };
    }
  
    return dynamicComponent!;
  }


  async expandWidget(){
    CommonUtils.presentWidgetModal(this.options)
  }
  
  dismissModal(){
    CommonUtils.dismissAllModals(null,null,this.modalId)
  }

  handleCloseBtn(){
    if(this.options?.isModal){
      this.dismissModal();
    }else
    this.onRemoveWidget.emit(this.options.widgetCode)
  }

  navigateToScreen(){
   this.dismissModal();
   if (this.commonProv.hasAccessOnOperation(this.options.linkedOperation)){
      this.navService.openPage({operID:this.options.linkedOperation});
    }else{
      CommonUtils.presentInfoAlert(this.commonProv.translate('no_screen_access_key'));
    }
  }
  
}
