import { AfterViewInit, Component, ElementRef, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { ToastController } from '@ionic/angular';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { LoggerService } from './../../../../../../psServices/logger/logger.service';
import { IOptionsPsInputSearchHTMLExposed, IOptionsPsKeyinInput } from './../../../../../../psServices/models/ps-common-interface';
import { PsCommonService } from './../../../../../../psServices/ps-common/ps-common.service';
import { PsKeyinInputComponent } from './../ps-keyin-input.component';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';


@Component({
  selector: 'ps-input-search-html',
  templateUrl: './ps-input-search-html.component.html',
  styleUrls: ['./ps-input-search-html.component.scss'],
})
export class PsInputSearchHtmlComponent extends PsKeyinInputComponent implements OnInit, AfterViewInit {

  @Input() public options: IOptionsPsInputSearchHTMLExposed;
  @ViewChild('searchInput', { read: ElementRef, static: false }) searchInput: ElementRef;
  @Output() onNoMatchesFound = new EventEmitter<number[]>();
  searchGroup = new FormGroup({});
  defaultConf: IOptionsPsKeyinInput = {
    iconOptions: {
      iconName: 'search'
    },
    placeHolder: 'search_data_here_key',
    type: 'text',
    group: this.searchGroup,
    fcName: 'searchHtml'
  };
  word;
  constructor(commonProv: PsCommonService, loggerP: LoggerService, private toastController: ToastController) {
    super(commonProv, loggerP);
  }

  ngOnInit() {
    super.init();
    this.options.actionType = this.options.actionType ? this.options.actionType :'search';
    this.commonProv.copyObject(this.defaultConf, this.options, false, false);
  }

  //TP 1331467
  // remove default min length if not defined from customization and remove mandatory in all cases
  ngAfterViewInit() {
    const initialCust = this.commonProv.getInitialState(this.defaultConf.fcName);
    if (!initialCust.MIN_LENGTH) {
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MINLENGTH, [this.defaultConf.fcName], 0);
    }
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.defaultConf.fcName], 0);
  }




  onCharEntered(data: any): void {
    if (!CommonUtils.isNotNull(this.searchInput)) return;
    const element = this.searchInput.nativeElement.querySelector('*[psFcName]');
    this.defaultConf.group.updateValueAndValidity({ onlySelf: false, emitEvent: true });
    this.word = element.value || data.target.value;

    const templateRef = this.getTemplateRef();
    let contentReferences = this.getContentReferences(templateRef);

    if (this.options.actionType === 'filter' && templateRef) {
      templateRef.classList.remove('ps-container-lookup-option-list-empty');
    }

    const matchFound = this.highlightContentReferences(contentReferences, templateRef);

    if (!matchFound && this.word) {
      this.handleNoMatchFound(templateRef, contentReferences);
    }
}

private getTemplateRef(): HTMLElement | null {
  return this.options.templateRef?.nativeElement || null;
}

private getContentReferences(templateRef: HTMLElement | null): NodeListOf<Element> {
  if (templateRef) {
    return templateRef.querySelectorAll(`#${this.options.contentReference}`);
  }
  const page = document.querySelectorAll(".operId_" + PsCommonSettings.oper_ID);
  return page.length ? page[page.length - 1].querySelectorAll(`#${this.options.contentReference}`) : document.querySelectorAll(`#${this.options.contentReference}`);
}

private highlightContentReferences(contentReferences: NodeListOf<Element>, templateRef: HTMLElement | null): boolean {
  let noMatchFound = true;

  contentReferences.forEach((contentRef: Element) => {
    let contentMatchFound = false;

    if (this.options.actionType === 'filter') {
      contentRef.classList.remove('hide-list-card');
    }

    const elements = contentRef.querySelectorAll(this.options.selectors);
    elements.forEach((el: HTMLElement, index) => {
      if (!el.textContent?.length) return;

      const regex = new RegExp(`${this.word}`, 'gi');
      const matches = el.textContent.match(regex);

      if (matches && matches.length > 0) {
        el.innerHTML = el.textContent.split(matches[0])
          .join(`<span class="highlight" id='searchable-word-${index}'>${matches[0]}</span>`);
          
        if (noMatchFound && this.options.actionType === 'search') {
          document.getElementById(`searchable-word-${index}`)?.scrollIntoView({ behavior: 'smooth' });
        }
        noMatchFound = false;
        contentMatchFound = true;
      } else {
        el.innerHTML = el.textContent;
      }
    });

    if (this.options.actionType === 'filter' && !contentMatchFound && this.word) {
      contentRef.classList.add('hide-list-card');
    }
  });

  return !noMatchFound;
}

private handleNoMatchFound(templateRef: HTMLElement | null, contentReferences: NodeListOf<Element>): void {
  if (this.options.actionType === 'filter' && templateRef && contentReferences.length !== 0) {
    templateRef.classList.add('ps-container-lookup-option-list-empty');
  }

  this.toastController.create({
    message: CommonUtils.translate('no_matching_found_key'),
    duration: 3000,
    position: 'top',
    cssClass: 'toast-report-search'
  }).then(toast => toast.present());
}

}
