import { Injectable } from '@angular/core';
import { Device } from '@ionic-native/device/ngx';
import { Diagnostic } from '@ionic-native/diagnostic/ngx';
import { IsDebug } from '@ionic-native/is-debug/ngx';
/*nabil feghali : device registration*/
import { detect } from 'detect-browser';
import { environment } from 'src/environments/environment';
import { HttpService } from '../http/http.service';
import { HybridKeyService } from '../hybridkey/hybridkey.service';
import { LoggerService } from '../logger/logger.service';
import { ConstantCommon } from '../models/common-constant';
import { CommonUtils } from '../models/common-utils';
import { PsSecurityService } from './ps-security.service';

declare let cordova: any;
declare var IRoot: any;

@Injectable({
    providedIn: 'root'
})
export class CheckAppService {
    constructor(
        private logger: LoggerService,
        private device: Device,
        private isDebug: IsDebug,
        private diagnostic: Diagnostic,
        private http: HttpService,
        private hybridkey: HybridKeyService,
        private psSecurity: PsSecurityService) {
    }

    public returnNativeUserAgent(): string {
        if (CommonUtils.isNativeMobile() && this.device) {
            return 'Native Mobile : Manufacturer ' + this.device.manufacturer
                + ' , Model ' + this.device.model
                + ' , Platform ' + this.device.platform
                + ' , Version ' + this.device.version
                + ' , Uuid ' + this.device.uuid
                + ' , isVirtual ' + this.device.isVirtual
                + ' , Serial ' + this.device.serial
                + ' , Name ' + (window as any).device.name;
        }
        else {
            return null;
        }

    }

    /**
     * nabil feghali : device registration
     * https://github.com/DamonOehlman/detect-browser
     * npm install detect-browser
     */
    public async returnDeviceDetails(): Promise<any> {
        let deviceDetailsObj = null;

        if (CommonUtils.isNativeMobile() && this.device) {
            deviceDetailsObj = {
                deviceModel: this.device.manufacturer + ' ' + (window as any).device.name,
                devicePlatform: this.device.platform,
                deviceVersion: this.device.version,
                deviceUseragent: this.returnNativeUserAgent()
            };
        } else if (navigator) {
            const nav = navigator as any;
            const browser = detect();
            
            if (browser) {
                deviceDetailsObj = {
                    deviceModel: browser.name,
                    devicePlatform: browser.os,
                    deviceVersion: browser.version,
                    deviceUseragent: nav.userAgent
                };
            }
            if (/android/i.test(deviceDetailsObj.devicePlatform)) {
                deviceDetailsObj.devicePlatform = 'Android';
                deviceDetailsObj.deviceVersion = '';
                if (nav.userAgentData?.getHighEntropyValues) {
                    try {
                        const ua = await nav.userAgentData.getHighEntropyValues([
                            'platform',
                            'platformVersion',
                            'uaFullVersion',
                        ]);
                        if (ua.mobile) { 
                            deviceDetailsObj.devicePlatform = ua.platform 
                            deviceDetailsObj.deviceVersion = ua.platformVersion.split('.')[0]
                        }  
                    } catch (err) {
                        console.warn('Failed to get high entropy user agent data', err);
                    }
                }
            }
        }
        return deviceDetailsObj;
    }

    private checkIsVirtual(): boolean {
        // TP# 955174 check if the device is real or virtual (emulator/simulator)
        if (this.device.isVirtual) {
            this.logger.log('device.isVirtual: ', this.device.isVirtual);
            this.logger.error("Security Alert: Application cannot run on virtual device");
            this.psSecurity.handleStopApp();
            return true;
        }
        return false;
    }// Added by Richie for TP# 955174 & 955171


    private checkIsRooted(): Promise<boolean> {
        // TP# 955171 check if the device is rooted/jailbroken
        return new Promise<any>((val, reject) => {
            IRoot.isRooted(async (booleanVal) => {
                this.logger.log('IRoot.isRooted success: ', booleanVal);
                let isRooted = false;

                if (CommonUtils.isAndroid()) {
                    try {
                        isRooted = await this.diagnostic.isDeviceRooted();
                    } catch (err) {
                        this.logger.error('diagnostic.isDeviceRooted error:' + err);
                        reject(err);
                    }
                }

                if (booleanVal || isRooted) {
                    this.logger.error("Security Alert: Application cannot run on rooted device");
                    this.psSecurity.handleStopApp();
                    val(true);
                }
                val(false);
            }, (err) => {
                this.logger.error('IRoot.isRooted error:' + err);
                reject(err);
            });
        });
    }
    // Added by Richie for security checkings
    public async checkAppProtection(): Promise<boolean> {
        return new Promise<any>(async (res, rej) => {
            // Modified by Richie for #BUG 1323941
            if (CommonUtils.isNativeMobile() && environment.production) {

                let packageSignature = null
                try {
                    packageSignature = await this.http.http.get(ConstantCommon.ANTI_TAMPERING_PACKAGE_FILE_NAME).toPromise();
                }
                catch (err) {
                    // if the file is not found then we consider the package as tampered and stop the app.
                    this.logger.error("Error while reading package signature details.");
                    this.logger.error(err);
                    this.psSecurity.handleStopApp();
                    res(true);
                }
                if (packageSignature) {
                    /**
                     * DN: Gilbert Al Indary
                     * Check app signing signature, if signature differs from the keystore signature (android)
                     *  or from profile signature (ios), close the app immediately
                     */
                    let isTampered = false;
                    if (CommonUtils.isAndroid()) {
                        isTampered = await this.checkAndroidAppIntegrity(packageSignature);
                        if (isTampered) {
                            this.logger.error("Security Alert: Application has been tampered due to wrong signature details.");
                            this.psSecurity.handleStopApp();
                            res(isTampered);
                        }
                    } else if (CommonUtils.isIOS()) {
                        isTampered = await this.checkIosAppIntegrity(packageSignature);
                        if (isTampered) {
                            this.logger.error("Security Alert: Application has been tampered due to wrong signature details.");
                            this.psSecurity.handleStopApp();
                            res(isTampered);
                        }
                    }
                } else {
                    // if the file has no data then we consider the package as tampered and stop the app.
                    this.logger.error("Security Alert: Application has been tampered due to none exisiting package signature details.");
                    this.psSecurity.handleStopApp();
                    res(true);
                }

                // TP# 955174 check if the device is real or virtual (emulator/simulator)
                const isVirtual = this.checkIsVirtual();
                if (isVirtual) {
                    res(true);
                } else {
                    // TP# 955171 check if the device is rooted/jailbroken
                    const isrooted = await this.checkIsRooted().catch(err => { rej(err); });
                    if (isrooted) {
                        res(true);
                    }
                    else {
                        const isDebug = await this.checkIfDebug();
                        if (isDebug) {
                            this.logger.error("Security Alert: Package is in Debug Mode.");
                            this.psSecurity.handleStopApp();
                        } else {
                            const isADBModeEnabled = await this.isADBModeEnabled();
                            res(isADBModeEnabled);
                        }
                    }
                }
            }

            res(false);
        });
    }
    // End Richie

    /**
     * @author gilbertandary
     * @description method to check if the package is in debug mode
     */
    async checkIfDebug(): Promise<boolean> {
        let isDebug = await this.isDebug.getIsDebug();
        return isDebug;
    }


    /**
     * @author gilbertandary
     * @description method to check if usb debugging is enabled in system settings
     */
    async isADBModeEnabled(): Promise<boolean> {
        let isADBEnabled = false;
        if (CommonUtils.isAndroid()) {
            try {
                isADBEnabled = await this.diagnostic.isADBModeEnabled();
                if (isADBEnabled) {
                    this.psSecurity.handleADB()
                }
            } catch (e) {
                this.logger.error(e);
            }
        }
        return isADBEnabled;
    }

    getApplicationSHA256(): Promise<string> {
        return new Promise<string>(async (res, rej) => {
            if (cordova && cordova.getSignatureFingerprint) {
                cordova.getSignatureFingerprint.getSignature((sha256) => {
                    this.logger.log("SHA256: " + sha256);
                    res(sha256);
                }, (error) => {
                    this.logger.error("ERROR: " + error);
                    rej(error);
                });
            }
        });
    }


    /**
     * @author Gilbert Al Indary
     * function that checks on the integrity of the signing certificate used to sign the apk upon build time
     * @param packageSignature is a parameter read from the file ps-package.json that is created upon build of the package to get us the signature of the signing certificate.
     */
    async checkAndroidAppIntegrity(packageSignature: any): Promise<boolean> {
        const sha256 = await this.getApplicationSHA256();
        const S256 = packageSignature.AST.S256; // Modified by Richie for #BUG 1323941
        if (S256 && sha256) {
            const key = decodeURIComponent(escape(atob(ConstantCommon.CERTIFICATE_TOKEN_KEY)));
            const iv = decodeURIComponent(escape(atob(ConstantCommon.CERTIFICATE_TOKEN_IV)));
            const decryptedMessage = this.hybridkey.decryptMessage(key, iv, S256);
            if (decryptedMessage !== sha256) {
                return true; // return true indicates app has been tampered;
            }
        } else {
            return true; // return true indicates app has been tampered;
        }
        return false;
    }

    /**
     * @author RichardZourob
     * function that checks on the integrity of the signing certificate used to sign the ipa upon build time
     * @param packageSignature is a parameter read from the file ps-package.json that is created upon build of the package to get us the signature of the signing certificate.
     */
    async checkIosAppIntegrity(packageSignature: any): Promise<boolean> {
        return new Promise<boolean>(async (res, rej) => {
            const iosSigningToken = packageSignature.IST; // Modified by Richie for #BUG 1323941
            IRoot.returnAppDetails(async (currentTokenStr) => {
                if (currentTokenStr) {
                    let currentTokenObj: any;
                    try {
                        currentTokenObj = JSON.parse(currentTokenStr);
                        if (CommonUtils.isEmptyObject(currentTokenObj)) {
                            return res(true); // return true indicates app has been tampered;
                        }
                    } catch (errorParse) {
                        this.logger.error('error while parsing package signing token: ' + errorParse);
                        return rej(true); // return true indicates app has been tampered;
                    }
                    if (iosSigningToken) {
                        const key = decodeURIComponent(escape(atob(ConstantCommon.CERTIFICATE_TOKEN_KEY)));
                        const iv = decodeURIComponent(escape(atob(ConstantCommon.CERTIFICATE_TOKEN_IV)));
                        if (iosSigningToken.TID && currentTokenObj.TeamId) {
                            const decryptedTeamId = this.hybridkey.decryptMessage(key, iv, iosSigningToken.TID);
                            if (decryptedTeamId !== currentTokenObj.TeamId) {
                                return res(true); // return true indicates app has been tampered;
                            }
                        } else {
                            return res(true); // return true indicates app has been tampered;
                        }
                        // Commented By Richie For Now
                        /* if (iosSigningToken.PUUID && currentTokenObj.UUID) {
                            const decryptedUuid = this.hybridkey.decryptMessage(key, iv, iosSigningToken.PUUID);
                            if (decryptedUuid !== currentTokenObj.UUID) {
                                return res(true); // return true indicates app has been tampered;
                            }
                        } else {
                            return res(true); // return true indicates app has been tampered;
                        } */
                    } else {
                        return res(true); // return true indicates app has been tampered;
                    }
                } else {
                    return res(true); // return true indicates app has been tampered;
                }
                return res(false);
            }, (err: any) => {
                this.logger.error('Error while checking IOS Integrity: ' + err);
                return rej(true);
            });
        });
    }
}
