import { Component, ElementRef, Input, OnInit, ViewChild } from '@angular/core';
import { PsBaseFieldComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-base-field.component';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { PsColorService } from 'src/app/commonSRC/psServices/ui-services/ps-color-service.service';
import { IOptionsPsComplexChatHistoryComponent, IOptionsPsComplexChatHistoryComponentExposed } from './ps-complex-chat-history.component.interface';
@Component({
  selector: 'ps-complex-chat-history',
  templateUrl: './ps-complex-chat-history.component.html',
})


export class PsComplexChatHistoryComponent extends PsBaseFieldComponent implements OnInit {

  @Input() options: IOptionsPsComplexChatHistoryComponentExposed;
  @ViewChild('chatHistoryWrapper', { read: ElementRef, static: false }) chatHistoryWrapper: ElementRef;

  chatHistory: IOptionsPsComplexChatHistoryComponent = {

  };


  multipleView: boolean = true;
  listOfowners;
  uniqueNumberOfowners;
  mapOfColorsByowner = {};


  steps = 5;
  colorsUsed = 0;
  colorMap

  previousLength = 0;
  lockUpdate = false;

  checkifUpdate() {
    if (this.options && this.options.listOfChat && (this.options.listOfChat.length != this.previousLength) && !this.lockUpdate) {
      this.lockUpdate = true;
      this.updateChatHistory();
      this.lockUpdate = false;
    }
    return this.chatHistory?.listOfChat?.length > 0
  }

  constructor(private commonP: PsCommonService, private colorService: PsColorService, private elRefC: ElementRef) {
    super(commonP, commonP.logger, elRefC);
  }

  ngOnInit() {
    if (this.options.messageOptions.fcName) {
      this.commonP.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MAXLENGTH, [this.options.messageOptions.fcName], 2000);
    }

    this.updateChatHistory();
  }

  updateChatHistory() {
    if (this.options.listOfChat && this.options.listOfChat.length) {
      this.chatHistory = { ...this.options };

      this.chatHistory.listOfChat.sort((a, b) => {
        try {
          const aCreationDate = a.creationDate as Date;
          const bCreationDate = b.creationDate as Date;
          return aCreationDate.getTime() - bCreationDate.getTime();
        } catch (e) {

        }
      });

      this.listOfowners = [...new Set(this.chatHistory.listOfChat.map(item => item.owner))];
      this.uniqueNumberOfowners = this.listOfowners.length + 1;

      this.colorMap = this.colorService.interpolateColor("--ps-chat-message-start-background-color", "--ps-chat-message-end-background-color",
        (this.uniqueNumberOfowners * this.steps) + 2);


      this.colorMap.shift();
      this.colorMap.pop();

      this.colorsUsed = 0;
      this.chatHistory.listOfChat.forEach((chat) => {
        this.createMessage(chat);
      });
      this.previousLength = this.chatHistory.listOfChat.length;
    }
  }

  addDays(days) {
    return CommonUtils.addDays(days);
  }


  createMessage(chat, append?: boolean) {
    if (this.chatHistory.listOfChat == undefined) {
      this.chatHistory.listOfChat = [];
    }
    if (this.multipleView && chat.owner != this.options.mainAuthor) {
      if (document.dir == 'ltr') {
        chat.dir = 'rtl';
      } else {
        chat.dir = 'ltr';
      }
      chat.switchView = true;
    }

    chat.avatar = {
      value: chat.owner.substr(0, 2)
    }

    let color = this.mapOfColorsByowner[chat.owner];
    if (!color) {
      color = this.colorMap[(this.colorsUsed * this.steps) + 1].to('srgb').toString();
      this.mapOfColorsByowner[chat.owner] = color;
      this.colorsUsed++;
    }

    chat.backgroundColor = color;
    chat.fontColor = this.colorService.contrastColors(color, CommonUtils.getCssVariableValue('--ps-chat-message-light-color'),
      CommonUtils.getCssVariableValue('--ps-chat-message-dark-color'));

    if (append) {
      this.chatHistory.listOfChat.push(chat);
    }
  }


  appendMessage() {
    const message = this.options.submitOptions.group.controls[this.options.messageOptions.fcName].value;
    if (!this.options.disableAppend) {
      const chat = {
        owner: this.options.mainAuthor,
        message,
        creationDate: new Date()
      };
      this.options.listOfChat.push(chat)
    }
    if (this.chatHistoryWrapper) {
      setTimeout(() => {
        let element = /* this.elRefC.nativeElement; */ this.chatHistoryWrapper.nativeElement;
        // element.scrollTop = element.scrollHeight;
        element.scrollTo({ top: element.scrollHeight, behavior: 'smooth' });
      }, 500);
    }
    this.options.submitOptions.group.controls[this.options.messageOptions.fcName].setValue(null);
    this.onPsChange.emit(message);
  }
}
