import { Component, Input, OnInit } from '@angular/core';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { IOptionsPsActionIcon, IOptionsPsButtonFab, IOptionsPsContainerHtmlViewer } from './../../../../../psServices/models/ps-common-interface';
import { PsBaseActionComponent } from './../../ps-base-action.component';


@Component({
    selector: 'ps-button-fab',
    templateUrl: 'ps-button-fab.component.html'

})
export class PsButtonFabComponent extends PsBaseActionComponent implements OnInit {
    @Input() options: IOptionsPsButtonFab;
    htmlViewerOptions: IOptionsPsContainerHtmlViewer;
    tooltipIcon: IOptionsPsActionIcon = {
        iconName: 'info'
    }
    ifResult = false;
    constructor(common: PsCommonService, loggerP: LoggerService) {
        super(common, loggerP);
    }

    ngOnInit() {
        super.ngOnInit();
        const result = this.options.fileName;
        if (result) {
            this.htmlViewerOptions = {
                fileName: result
            };
            this.ifResult = true;
        }
    }

    onFabButtonClicked(event: Event) {
        this.onClick.emit(event);
    }
}
