import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { PsFieldFileComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-field-file/ps-field-file.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IOptionsPsFileUploadComponent } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { OmniPullService } from '../../psServices/omni-common/omni-pull.service';

@Component({
    selector: 'ps-file-upload-bs',
    templateUrl: './ps-file-upload-bs.component.html',
    styleUrls: ['./ps-file-upload-bs.component.scss']
})

export class PsFileUploadBSComponent extends PsFieldFileComponent implements OnInit {
    @Input() options: IOptionsPsFileUploadComponent = {};
    @Output() onPsFileSelect: EventEmitter<any> = new EventEmitter<any>();
    @Output() onPsFileDelete: EventEmitter<any> = new EventEmitter<any>();
    showFile:boolean = false;

    constructor(public logger: LoggerService, public commonService: PsCommonService, private omniPull?: OmniPullService) {
        super(commonService, logger);
    }
    
    ngOnInit() {
        const preLoginResponse = this.omniPull.omniCommon.session.getValueOf(ConstantCommon.preLoginResponse);
        if (this.options.allowedFilesTypeName === null || this.options.allowedFilesTypeName === undefined ||
                this.options.attachmentAllowedSize === null || this.options.attachmentAllowedSize === undefined) {
            this.omniPull.getParamValOf(ConstantCommon.ATTACHMENTS_ALLOWED_FILE_TYPES, ConstantCommon.ATTACHMENTS_ALLOWED_FILE_SIZE).then(res => {
                this.showFile = true;
                if (this.options.allowedFilesTypeName === null || this.options.allowedFilesTypeName === undefined) {
                    if (res[ConstantCommon.ATTACHMENTS_ALLOWED_FILE_TYPES]) {
                        this.options.allowedFilesTypeName = res[ConstantCommon.ATTACHMENTS_ALLOWED_FILE_TYPES];
                    } else if (preLoginResponse && preLoginResponse.parameters[ConstantCommon.ATTACHMENTS_ALLOWED_FILE_TYPES_COMMON]
                            && preLoginResponse.parameters[ConstantCommon.ATTACHMENTS_ALLOWED_FILE_TYPES_COMMON].paramDescMap
                            && preLoginResponse.parameters[ConstantCommon.ATTACHMENTS_ALLOWED_FILE_TYPES_COMMON].paramDescMap.length > 0) {
                        this.options.allowedFilesTypeName = preLoginResponse.parameters[ConstantCommon.ATTACHMENTS_ALLOWED_FILE_TYPES_COMMON].paramDescMap.map((res) => res.itemValue).toString();
                    }
                }
                if (this.options.attachmentAllowedSize === null || this.options.attachmentAllowedSize === undefined) {
                    if (res[ConstantCommon.ATTACHMENTS_ALLOWED_FILE_SIZE]) {
                        this.options.attachmentAllowedSize = Number(res[ConstantCommon.ATTACHMENTS_ALLOWED_FILE_SIZE] * 1024);
                    } else if (preLoginResponse && preLoginResponse.parameters[ConstantCommon.ATTACHMENTS_ALLOWED_FILE_SIZE_COMMON]
                            && preLoginResponse.parameters[ConstantCommon.ATTACHMENTS_ALLOWED_FILE_SIZE_COMMON].parameterVal) {
                        this.options.attachmentAllowedSize = Number(+preLoginResponse.parameters[ConstantCommon.ATTACHMENTS_ALLOWED_FILE_SIZE_COMMON].parameterVal * 1024);
                    }
                }
            }).catch(err => this.logger.log(err));
        } else {
            this.showFile = true;
        }
    }

    onFileSelect(val) {
        this.onPsFileSelect.emit(val);
    }

    onFileDeleted(val) {
        this.onPsFileDelete.emit(val);
    }

    onFileChanged(data) {
        this.onPsChange.emit(data);
    }

}