import { ErrorHandler, Injectable } from '@angular/core';
import { PsApplicationSettings } from 'src/app/commonSRC/psServices/models/ps-app-settings';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { environment } from '../../../../environments/environment';
import { ConstantCommon } from '../models/common-constant';
import { CommonUtils } from '../models/common-utils';

@Injectable({ providedIn: 'root' })
export class ErrorHandlerService implements ErrorHandler {

  public crashlytics = null;

  public LOG_VERBOSE = 2;
  public LOG_DEBUG = 3;
  public LOG_INFO = 4;
  public LOG_WARN = 5;
  public LOG_ERROR = 6;

  public LOG_LEVEL_ALL = 'ALL';
  private logLevelInterval = PsApplicationSettings.MAIN_CONFIG.LOG_LEVEL_INTERVAL ? PsApplicationSettings.MAIN_CONFIG.LOG_LEVEL_INTERVAL : 300;
  private logLevelEnabledTime = null;
  private logLevelByUser = null;
  private userName = null;
  private filterKeys = null;

  constructor() {

  }

  initialiseCrashlytics(filterKeys) {
    // try {
    //   if (this.enableCrashLiticsDebug()) {
    //     this.crashlytics = this.firebaseCrashlytics.initialise();
    //     if (filterKeys) {
    //       this.filterKeys = filterKeys;
    //     }
    //   }
    // } catch (error) {
    //   console.error("Error initializing Crashlytics:", error);
    // }
  }

  initializeLogLevelByUser(userName: string, logLevelByUser: string) {

    this.userName = userName;
    this.logLevelByUser = logLevelByUser;
    if (this.LOG_LEVEL_ALL === logLevelByUser) {
      this.logLevelEnabledTime = new Date();
    } else {
      this.logLevelEnabledTime = null;
      this.logLevelByUser = null;
    }
  }

  resetLogLevelByUser() {

    this.logLevelEnabledTime = null;
    this.logLevelByUser = null;
    this.userName = null;
  }

  isLogLevelAllEnabled(): boolean {
    if (PsApplicationSettings?.MAIN_CONFIG?.LOG_LEVEL_ALL) {
      return true;
    }
    if (this.LOG_LEVEL_ALL === this.logLevelByUser) {
      const checkDate = new Date(this.logLevelEnabledTime.getTime() + (this.logLevelInterval * 1000));
      if (checkDate.getTime() >= new Date().getTime()) {
        return true;
      } else {
        this.logLevelEnabledTime = null;
        this.logLevelByUser = null;
        return false;
      }
    }
    return false;
  }

  addLoggedInUserTrace(): string {
    return ' [user=' + (this.userName ? this.userName : 'UNKNOWN_USER') + ', date=' + new Date().toISOString() + '] ';
  }
  /**
   * function used to handle logger error from logger.service provided from console.log(), console.error() ....
   */
  handleLoggerServiceMessage(message: any, level: number) {
    if (this.enableCrashLiticsDebug()) {
      /*in case the object is not yet initialized, then initialize it*/
      // if (this.crashlytics === undefined || this.crashlytics == null) {
      //   this.crashlytics = this.firebaseCrashlytics.initialise();
      // }

      if (this.isLogLevelAllEnabled()) {
        if (level === this.LOG_VERBOSE) {
          this.crashlytics.logException('OMNI Logger Verbose: ' + this.addLoggedInUserTrace() + ' message : ' + JSON.stringify(message));
          this.addLogFilterKeys(level);
        }

        if (level === this.LOG_DEBUG) {
          this.crashlytics.logException('OMNI Logger Debug: ' + this.addLoggedInUserTrace() + ' message : ' + JSON.stringify(message));
          this.addLogFilterKeys(level);
        }

        if (level === this.LOG_INFO) {
          this.crashlytics.logException('OMNI Logger Info: ' + this.addLoggedInUserTrace() + ' message : ' + JSON.stringify(message));
          this.addLogFilterKeys(level);
        }

        if (level === this.LOG_WARN) {
          this.crashlytics.logException('OMNI Logger Warn: ' + this.addLoggedInUserTrace() + ' message : ' + JSON.stringify(message));
          this.addLogFilterKeys(level);
        }
      }

      if (level === this.LOG_ERROR) {
        if (!this.isLogLevelAllEnabled() && this.excludeErrors(message)) {
          return;
        }
        this.crashlytics.logException('OMNI Logger Error: ' + this.addLoggedInUserTrace() + ' message : ' + JSON.stringify(message));
        this.addLogFilterKeys(level);
      }
    }
  }

  handlePerformanceTracingMessage(message: any) {
    return new Promise((resolve) => {

      // if (this.crashlytics === undefined || this.crashlytics == null) {
      //   this.crashlytics = this.firebaseCrashlytics.initialise();
      // }

      if (message && Array.isArray(message)) {
        for (let i = 0; i < message.length; i++) {
          this.crashlytics.setString('TIME_' + i +":"+ message[i]);
        }
        this.crashlytics.logException('OMNI Logger Performance Trace: ' + this.addLoggedInUserTrace());
        resolve(true);
      }
    });
  }

  addLogFilterKeys(logLevel) {
    // to add additional log messages related to the exception we can use the .log() method
    let map = {};

    const operId = PsCommonSettings.oper_ID > 0 ? PsCommonSettings.oper_ID : ConstantCommon.DEFAULT_FALLBACK_OPER;
    map["OI"] = operId;

    const isLoggedIn = PsCommonSettings.isLoggedIn;
    map["UILI"] = isLoggedIn;

    const loggedInUser = this.userName ? this.userName : 'UNKNOWN_USER';
    map["UN"] = loggedInUser;

    const language = String(PsCommonSettings.activeLanguge).toUpperCase();
    map["LG"] = language;

    let logLevelStr = '';
    switch (logLevel) {
      case 2: logLevelStr = 'VERBOSE'; break;
      case 3: logLevelStr = 'DEBUG'; break;
      case 4: logLevelStr = 'INFO '; break;
      case 5: logLevelStr = 'WARN'; break;
      case 6: logLevelStr = 'ERROR'; break;
    }

    if (logLevelStr !== '') {
      map["LL"] = logLevelStr;;
    }

    if (this.filterKeys) {
      if (this.filterKeys.udid) {
        map["UDID"] = this.filterKeys.udid;
      }
      if (this.filterKeys.compCode) {
        map["CC"] = this.filterKeys.compCode;
      }
      if (this.filterKeys.channelId) {
        map["CID"] = this.filterKeys.channelId;
      }
      if (this.filterKeys.appId) {
        map["AID"] = this.filterKeys.appId;
      }
      if (this.filterKeys.appChnlId) {
        map["ACID"] = this.filterKeys.appChnlId;
      }
      if (PsCommonSettings.appVersionNumber) {
        map["AV"] = PsCommonSettings.appVersionNumber;
      }
    }
    this.crashlytics.setString("Keys:" + JSON.stringify(map));
  }

  /**
   * function used to handle runtime error like typescript error, or any other error not provided from logger.service.
   */
  handleError(error: any) {
    if (this.enableCrashLiticsDebug()) {
      /*in case the object is not yet initialized, then initialize it*/
      // if (this.crashlytics === undefined || this.crashlytics == null) {
      //   this.crashlytics = this.firebaseCrashlytics.initialise();
      // }

      this.crashlytics.logException('OMNI Runtime Error: ' + this.addLoggedInUserTrace() + ' error.message: ' + error.message + ' error.stack: ' + error.stack);
      this.addLogFilterKeys(this.LOG_ERROR);
    } else {
      // Added by Richie to simplify the debugging of issues upon startup of the app where crashlytics are still not initialized
      if (PsApplicationSettings.MAIN_CONFIG.SHOW_ALERT_ERROR) {
        let err: any;
        if (error instanceof Error) {
          err = error;
        } else {
          err = JSON.stringify(error);
        }
        alert(err);
      }
      // end Richie
      console.error(error);
    }
  }

  /**
    * prevent submitting specific errors to crashlytics console
    */
  excludeErrors(message: any) {
    // bug #1033045
    // exclude assets not found 404
    // for example exclude the following error : HttpErrorResponse {headers: HttpHeaders, status: 404, statusText: "OK", url: "http://localhost/assets/html/EN/default_banners/housing.html" }
    if (message !== undefined && message != null && (message.length === 2 && message[1].status === 404
      && message[1].url !== undefined && message[1].url != null
      && message[1].url.includes('/assets/')) || this.commonMessageChecking(message)
    ) {
      return true;
    }
    return false;
  }

  commonMessageChecking(message) {
    let result = false;
    if (message instanceof Array) {
      for (let index = 0; index < message.length; index++) {
        let element = message[index];
        if (typeof element === 'string' || element instanceof String) {
          element = element.toLowerCase();
          if (element.includes('/assets/'.toLowerCase()) || element.includes('ExpressionChangedAfterItHasBeenCheckedError'.toLowerCase())
            || element.includes('dismissLoadingCtrl'.toLowerCase()) || element.includes('overlay does not exist'.toLowerCase())) {
            result = true;
            break;
          }
        }
      }
    }
    return result;
  }

  enableCrashLiticsDebug() {
    return (environment.production || PsApplicationSettings?.MAIN_CONFIG?.LOG_LEVEL_ALL) && CommonUtils.isNativeMobile()
  }
}
