import { Component, Input } from '@angular/core';
import { PsSelect } from 'src/app/commonSRC/psServices/models/common-type';
import { LoggerService } from './../../../../../psServices/logger/logger.service';
import { IOptionsPsSelectDropdown, IOptionsPsSelectDropdownOverlay, IPsSelect } from './../../../../../psServices/models/ps-common-interface';
import { PsNavigatorService } from './../../../../../psServices/navigator/ps-navigator.service';
import { PsCommonService } from './../../../../../psServices/ps-common/ps-common.service';
import { PsSelectDropdownComponent } from './../ps-select-dropdown/ps-select-dropdown.component';

@Component({
    selector: 'ps-select-dropdown-widget',
    templateUrl: './ps-select-dropdown-widget.component.html'
})
export class PsSelectDropdownWidget extends PsSelectDropdownComponent {
    @Input() options: IOptionsPsSelectDropdown;
    showWidget = false;
    overlayOptions: IOptionsPsSelectDropdownOverlay = {
        isWidget: true
    };
    listOfOptions: PsSelect = [];

    constructor(public commonProv: PsCommonService, public loggerP: LoggerService, private navService: PsNavigatorService) {
        super(commonProv, loggerP);
    }

    ngOnInit() {
        
        this.overlayOptions.group = this.options.group;
        this.overlayOptions.fcName = this.options.fcName;
        if(this.options.asyncURL){
            this.callService();
        }else{
            this.overlayOptions.group = this.options.group;
            this.overlayOptions.fcName = this.options.fcName;
            this.overlayOptions.notSelectable = this.options.notSelectable;
            this.overlayOptions.listOfOptions = this.options.listOfOptions;
            this.overlayOptions.totalNbRec = this.overlayOptions.listOfOptions.length;
            this.overlayOptions.enablePagination = this.options.enablePagination;
            this.showWidget = true;
        }
        this.options.componentInstance = this;
    }

    callService() {
        if (this.options.renderDropdown) {
            const serviceRequestObj: any = {
                nbRec: this.options.enablePagination ? this.numberOfRowsToShow : 0,
                recToskip: 0,
                ...this.options.serviceRequestObj
            };
            this.overlayOptions.group = this.options.group;
            this.overlayOptions.fcName = this.options.fcName;
            this.overlayOptions.notSelectable = this.options.notSelectable;
            this.commonProv.presentLoading();
            super.loadAsyncUrl(serviceRequestObj, false).then(() => {
                this.overlayOptions.listOfOptions = this.options.listOfOptions;
                this.overlayOptions.totalNbRec = this.overlayOptions.listOfOptions.length;
                this.overlayOptions.enablePagination = this.options.enablePagination;
                this.showWidget = true;
                if (this.options.requestObject && this.options.requestObject[this.options.fcName]) {
                    const filteredObject = this.options.listOfOptions.filter(element =>element.itemValue == (this.options.requestObject[this.options.fcName].itemValue ? this.options.requestObject[this.options.fcName].itemValue : this.options.requestObject[this.options.fcName]));
                    if (filteredObject.length > 0) {
                        const data: IPsSelect = {
                            itemValue: filteredObject[0].itemValue,
                            description: filteredObject[0].description,
                            iconName: filteredObject[0].iconName,
                            selectedObj: filteredObject[0].selectedObj,
                            selected: true
                        };
                        this.overlayOptions.selectDefaultItem = data;
                        this.options.group.controls[this.options.fcName].setValue(data);
                        this.onItemSelected(data);
                    }
                }
                this.commonProv.dismissLoading();
            }).catch((e) => {
                this.logger.error(e);
                // this.navService.navigateToMain(); commentted by Marina AZDB-1847; service calls errors are handled in interceptor 
            });
        }
    }

    onItemSelected(value) {
        this.onPsChange.emit(value);
    }
}