import { Component, Input } from "@angular/core";
import { ILoadingOverlayAngularComp } from "ag-grid-angular";


@Component({
  selector: 'ps-ag-grid-loading',
  templateUrl: './ps-ag-grid-loading.html',
})
export class PsAgGridLoading implements ILoadingOverlayAngularComp {
  @Input() params: any;

  agInit(params) {
    this.params = params;
  }
  refresh(params) {
    this.params = params;
    return true
  }
}