import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})

/**
 * @author G.RADWAN
 * @since 07/06/2021
 * @summary saving photos(canvas) to photos library
 */
export class Canvas2ImageService {

  constructor() { }

  /**
   * calls saveImageDataToLibrary to save canvas to library (funciton in another class)
   * @param canavs canvas to be saved
   * @param format extension for the saved photo
   */
  savePhoto(canavs, format?) {
    return new Promise<any>((resolve, reject) => {
      (window as any).canvas2ImagePlugin.saveImageDataToLibrary((success => {
        resolve(true);
      }),
        (err => {
          reject(err);
        }),
        canavs,
        (format ? format : 'png')
      );
    });
  }
}
