import { Injectable } from '@angular/core';
import { ILimitParams } from 'src/app/commonBussinessSRC/psServices/limits/limits-interfaces';
import { BehaviorSubject, Observable, Subject } from 'rxjs';

/*
Created by Ahmed Makki 04 August 2024 to be responsible for Limits Calculations
*/
@Injectable({
  providedIn: 'root'
})
export class LimitsService {
  limitsSubject: BehaviorSubject<any> = new BehaviorSubject<any>(null);


  constructor() { }

  /**
  * functions for observables for daily limit to be used with multi-template
  *
  * @param data example data = {operId:1627,imit:{remainingLimit:600,dailyLimit:1000}}
  * @author Ammar Ahmed
  */
  broadcastNewLimits(data: any) {
    this.limitsSubject.next(data);
  }

  getLimitsSubject(): BehaviorSubject<any> {
    return this.limitsSubject;
  }

  // whenever the user enters any amount I need to reset the remaining value back to the value returned from the service not the one previously calculated based on the last entered amount value
  resetRemainingValueOfLimits(arrayOfLimits) {
    arrayOfLimits.forEach(limit => limit.remaining = limit.total);
  }

  //Update the reamining limits by subtracting the amount value entered by the user
  subtractArrayOfLimits(arrayOfLimits, newAmount, decimalPoints) {
    arrayOfLimits.forEach(limit => {
      if (
        limit.updateLimitLabelOnChange &&
        limit.remaining !== undefined &&
        limit.remaining > 0 &&
        newAmount !== undefined &&
        newAmount > 0
      ) {
        let newLimit = limit.total - parseFloat(newAmount);
        limit.remaining = decimalPoints
          ? parseFloat(newLimit.toFixed(decimalPoints))
          : newLimit;
      }
    });
  }

  getDefinedLimitByAlias(arrayOfDefinedLimits, alias) {
    let filteredLimits = arrayOfDefinedLimits.filter(limit => limit.alias === alias);
    if (filteredLimits.length == 0) {
      return null;
    }
    return filteredLimits[0];
  }

  getTotalOfLimitsPerType(arrayOfDefinedLimits, type) {
    return arrayOfDefinedLimits
      .filter(limit => limit.limitType === type)
      .map(limit => limit.total);
  }

  updateRemainingAmountOfLimitByAlias(arrayOfDefinedLimits, newAmount, alias) {
    let limit = this.getDefinedLimitByAlias(arrayOfDefinedLimits, alias);
    if (limit) {
      limit.remaining = newAmount;
    }
  }



}
