import { CommonModule } from '@angular/common';
import { CUSTOM_ELEMENTS_SCHEMA, NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { AgGridModule } from 'ag-grid-angular';
import { CalendarModule, DateAdapter } from 'angular-calendar';
import { adapterFactory } from 'angular-calendar/date-adapters/date-fns';
import { RoundProgressModule } from 'angular-svg-round-progressbar';
import { QRCodeModule } from "angularx-qrcode";
import { ClickOutsideModule } from 'ng-click-outside';
import { AvatarModule } from 'ngx-avatar';
import { PsCommonDirectivesModule } from '../psDirectives/ps-common-directives.module';
import { PsCommonPipesModule } from '../psPipes/ps-common-pipes.module';
import { SharedModule } from '../shared.module';
import { MaterialModule } from './material.module';
import { psComponents } from './ps-components';



@NgModule({
    declarations: [
        ...psComponents
    ],
    imports: [
        SharedModule,
        PsCommonPipesModule,
        PsCommonDirectivesModule,
        ReactiveFormsModule,
        AvatarModule,
        RoundProgressModule,
        AgGridModule.withComponents(...psComponents),
        QRCodeModule,
        ClickOutsideModule,
        CalendarModule.forRoot({
            provide: DateAdapter,
            useFactory: adapterFactory
        })
    ],
    exports: [
        ...psComponents,
        CommonModule,
        MaterialModule,
        IonicModule,
        FormsModule,
        PsCommonPipesModule,
        ReactiveFormsModule,
        TranslateModule,
        PsCommonDirectivesModule
    ],
    providers: [],
    entryComponents: [
        ...psComponents
    ],
    schemas: [CUSTOM_ELEMENTS_SCHEMA]
})
export class PsComponentsModule { }
