import { AfterViewInit, Component, ElementRef, Input, OnChanges, OnInit, QueryList, SimpleChanges, ViewChildren } from '@angular/core';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IOptionsPsContainerSegment } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { PsBaseContainerComponent } from '../ps-base-container.component';

@Component({
  selector: 'ps-container-segment',
  templateUrl: './ps-container-segment.component.html'
})
export class PsContainerSegmentComponent extends PsBaseContainerComponent implements OnInit, OnChanges, AfterViewInit {

  @Input() options: IOptionsPsContainerSegment;
  @ViewChildren('segmentRef') segmentContents: QueryList<ElementRef>;
  public showSegments = {
    segment1: false,
    segment2: false,
    segment3: false,
    segment4: false,
    segment5: false,
    segment6: false,
    segment7: false,
    segment8: false,
    segment9: false,
    segment10: false
  };
  constructor(public commonProv: PsCommonService, public loggerP: LoggerService) {
    super(commonProv, loggerP);
  }

  ngOnInit() {
    super.ngOnInit();
    this.viewSegments(true);
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.viewSegments(false);
  }

  private viewSegments(init: boolean) {
    if (this.options.selectedSegment) {
      const fieldID = this.options.fieldName + '_' + this.options.selectedSegment;
      const custSegmentMap = this.options.custOptionsMap.get(fieldID);

      for (let i = 0; i < this.options.segments.length; i++) {
       
       if (this.options.segments[i].itemValue.toString() === this.options.selectedSegment) {
          if (custSegmentMap && !custSegmentMap.visible ) {
            this.showSegments['segment' + (i + 1)] = PsCommonSettings.custMode ? true : false ;
          } else {
            this.showSegments['segment' + (i + 1)] = init ? false : true;
          }
        } else {
          if (custSegmentMap && !custSegmentMap.visible) {
            this.showSegments['segment' + (i + 1)] = true;
          } else {
            this.showSegments['segment' + (i + 1)] = false;
          }
        }
      }
    }
  }


  ngAfterViewInit(): void {
    this.viewSegments(false);
    if (this.segmentContents !== undefined) {
      this.segmentContents.changes.subscribe((comps: QueryList<ElementRef>) => {
        this.applyChildrenValidity(comps.first.nativeElement);
      });
    }
  }

  private applyChildrenValidity(nativeElement: any) {
    const fieldID = this.options.fieldName + '_' + this.options.selectedSegment;
    const custSegmentMap = this.options.custOptionsMap.get(fieldID);
    if (custSegmentMap) {
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.options.fieldName + '_' + this.options.selectedSegment], custSegmentMap.disabled ? 1 : 0, true);
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.options.fieldName + '_' + this.options.selectedSegment], custSegmentMap.visible ? 1 : 0, true);
      //added by Marina jiraAZDB-5173; to set segment to nonmandatory again as it gets changed after applying readonly customization
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.options.fieldName + '_' + this.options.selectedSegment], 0);
    }
    this.dynScreenDisplayEmitEventSubscription = this.commonProv.dynScreenDisplayEmitEvent.subscribe(result => {
      if (result && result.id === fieldID) {
        const listOfChildrenNames = CommonUtils.extractContainerFcNames(nativeElement);
        this.updateChildrenValidity(listOfChildrenNames,
          {
            isVisible: result.screenDispVO.IS_VISIBLE === 1 ? true : false,
            isReadOnly: result.screenDispVO.IS_READONLY === 1 ? true : false,
            parentNode: this
          }
        );
      }
    });
  }

}
