import { AfterViewInit, Component, Input, OnInit } from '@angular/core';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IOptionsPsSelectToggle } from '../../../../../psServices/models/ps-common-interface';
import { LoggerService } from './../../../../../psServices/logger/logger.service';
import { IOptionsPsFieldLabel, IOptionsPsLabelInput } from './../../../../../psServices/models/ps-common-interface';
import { PsCommonService } from './../../../../../psServices/ps-common/ps-common.service';
import { PsFieldSelectComponent } from './../ps-field-select.component';


@Component({
  selector: 'ps-select-toggle',
  templateUrl: './ps-select-toggle.component.html',
  styleUrls: ['./ps-select-toggle.component.scss'],
})
export class PsSelectToggleComponent extends PsFieldSelectComponent implements OnInit, AfterViewInit {

  @Input() options: IOptionsPsSelectToggle;

  get labelOptions(): IOptionsPsLabelInput {
    return {
      labelKey: this.options.labelKey,
      required: false
    };
  }

  constructor(public commonProv: PsCommonService, public loggerP: LoggerService) {
    super(commonProv, loggerP);
  }

  //edited by Marina jira:AZDB-4742; evaluate val as string as it is returned as sting in drafts and as boolean otherwise to avoid evaluating 'false' as truthy value
  get previewValOptions(): IOptionsPsFieldLabel {
    const val = this.fcValue;
    return {
      labelKey: val === undefined ? '' : (val.toString() == 'true') ? this.commonProv.translate('yes_key') : this.commonProv.translate('no_key'),
      psClass: 'ps-value-preview'
    };
  }
  ngOnInit() {
    this.custIconOptions.component = PsSelectToggleComponent;
    this.custIconOptions.componentOptions = this.options;
    super.ngOnInit();
    this.custIconOptions.availableCustomization.IS_MANDATORY = false; //added by ghada, tp#1383050 for not showing the mandatory customization on ODAM
    this.itemOptions.hideImageAndIconIfNotPresent = true;
    this.options.revertToOldValue = false;
    this.options.allowDefaultValue = CommonUtils.isNull(this.options.allowDefaultValue) ? true : this.options.allowDefaultValue;
    //added by ghada, tp#1383050 for handling control's default value
    // if (this.screenDispElt && !this.screenDispElt.DEFAULT_VALUE &&
    //   (this.options?.group?.controls[this.options.fcName]?.value === undefined || this.options?.group?.controls[this.options.fcName]?.value === null)) {
    //   this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_DEFAULT_VALUE, [this.options.fcName], false);
    // }
  }
  //edited by Marina TP:#1533352 to consider the case of the component not having any cutomization set
  // Also, to consider component's value before customization 
  //code moved to ngAfterViewInit for the form controls values to be initiated
  ngAfterViewInit() {
    if (this.options.allowDefaultValue) {
      if ((CommonUtils.isEmptyOrNull(this.options?.group?.controls[this.options.fcName]?.value))
        && (!this.screenDispElt || CommonUtils.isNull(this.screenDispElt.DEFAULT_VALUE))) {
        this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_DEFAULT_VALUE, [this.options.fcName], false);
      }
    }
  }
}
