import { Component, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Subscription } from 'rxjs';
import { JBPMService } from 'src/app/commonBussinessSRC/psServices/integration/JBPM/jbpm.service';
import { IntegrationConstants } from 'src/app/commonBussinessSRC/psServices/integration/ps-integration-constants';
import { IOmniBpmResponse, JBPMTask } from 'src/app/commonBussinessSRC/psServices/integration/ps-integration-interfaces';
import { PsBaseFieldComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-base-field.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IOptionsPsActionIconExposed } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { PsJbpmTaskDetailsComponent } from '../ps-jbpm-task-details/ps-jbpm-task-details.component';
import { IOptionsPsJBPMTaskListComponent } from './ps-jbpm-task-list.component.interfaces';

@Component({
  selector: 'ps-jbpm-task-list',
  templateUrl: './ps-jbpm-task-list.component.html'
})
export class PsJbpmTaskListComponent extends PsBaseFieldComponent implements OnInit, OnDestroy {


  @Input() options: IOptionsPsJBPMTaskListComponent = {
    initAndfetchDataOnLoad: true
  };

  switchOptions: IOptionsPsActionIconExposed = {
    iconName: 'exchange'
  };

  refreshIconOptions: IOptionsPsActionIconExposed = {
    iconName: 'to-do-refresh-icon'
  };

  currentExpandedTask: JBPMTask;

  @ViewChild('moreItems', { static: false }) moreItems: Element;
  noData;
  private eventSub: Subscription;


  constructor(public commonProv: PsCommonService, loggerP: LoggerService, private jbpm: JBPMService, private navService: PsNavigatorService) {
    super(commonProv, loggerP);
  }


  async ngOnInit() {
    this.init();
  }

  async init(forceInit?) {
    if (this.options) {
      this.eventSub = this.common.events.subscribe('refreshJBPM', (returnedData) => {
        this.populateList(returnedData);
      });
      if (this.options.initAndfetchDataOnLoad == null) {
        this.options.initAndfetchDataOnLoad = true;
      }
      if (this.options.initAndfetchDataOnLoad || forceInit) {
        if (this.options.refreshListFunc) {
          this.options.refreshListFunc = (component) => {
            return this.returnBpmTasksList(component);
          };
        }
        await this.returnBpmTasksList();
      }
    }
  }

  async returnBpmTasksList(params?) {
    params = CommonUtils.isEmptyOrNull(params) ? {} : params;
    const result: IOmniBpmResponse = await this.jbpm.returnBpmTasksList(params);
    this.populateList(result);
    super.onInit(this.options.taskList);
  }


  async populateList(result: IOmniBpmResponse) {
    this.options.taskList = [];
    this.common.dismissLoading();
    if (result && result.gridModel) {
      result.gridModel.forEach(element => {
        element.isTaskExpanded = false;
        element.expandOptions = {
          iconName: 'jbpm-expand-button'
        };
        element.collapseOptions = {
          iconName: 'jbpm-collapse-button'
        };
        element.psContainerFlipTask = {
          hideVisibleArea: false,
          isFlipped: false,
          animationClass: 'taskFlip'
        };

        if (element.taskInfo.priority) {
          element.priorityIconName = {
            iconName: 'jbpm-priority-' + element.taskInfo.priority.toLowerCase()
          }
        }

        if (element.taskActionsArray && element.taskActionsArray.length > 0) {
          element.fabMarkUp = {
            group: this.options.group,
            mainProperties: {
              iconName: 'jbpm-fab-button',
              group: this.options.group
            },
            startFabList: []
          };
          element.iconListMarkUp = [];
          for (let i = 0; i < element.taskActionsArray.length; i++) {
            const action = element.taskActionsArray[i];
            const iconObject = {
              group: this.options.group,
              iconName: action.iconName,
              labelKey: action.labelKey,
              labelOptions: {
                labelKey: action.labelKey
              },
              handler: async () => {
                element.selectedTaskAction = action;
                if (action.webService) {
                  // this.jbpm[action.webService](element);
                }
                const isSuspend = action.action == IntegrationConstants.JBPM.ACTION.ACTION_SUSPEND;
                const isComment = action.action == IntegrationConstants.JBPM.ACTION.ACTION_COMMENT;
                const isInfo = action.action == IntegrationConstants.JBPM.ACTION.ACTION_INFO;
                const isRelease = action.action == IntegrationConstants.JBPM.ACTION.ACTION_RELEASE;
                const isClaim = action.action == IntegrationConstants.JBPM.ACTION.ACTION_CLAIM;
                const isComplete = action.action == IntegrationConstants.JBPM.ACTION.ACTION_COMPLETE;
                const isResume = action.action == IntegrationConstants.JBPM.ACTION.ACTION_RESUME;

                if (isSuspend || isComment || isInfo || isResume) {
                  this.common.modalCtrl.dismiss(null, null, 'jbpm-task-details-modal');
                  if (isSuspend || isComment || isResume) {
                    this.common.presentLoading();
                    this.jbpm.returnBpmCommentsList({ ...element.taskInfo }).then((result) => {

                      element.commentMarkup = {};
                      element.commentMarkup.disableAppend = true;
                      element.commentMarkup.listOfChat = [];

                      const userName = this.common.session.getValueOf(ConstantCommon.USERINFO).userName;
                      element.commentMarkup.mainAuthor = userName;

                      const formGroup = new FormGroup({});

                      this.populateComments(result, element);

                      element.commentMarkup.submitOptions = {
                        submitServiceUrl: isComment ? IntegrationConstants.JBPM.serviceUrl.commentBpmTask : isSuspend ? IntegrationConstants.JBPM.serviceUrl.suspendBpmTask : IntegrationConstants.JBPM.serviceUrl.resumeBpmTask,
                        group: formGroup,
                        extraParams: element.taskInfo,
                        labelKey: isComment ? 'post_comment_key' : isSuspend ? 'suspend_key' : 'resume_key',
                        saveProcedureFlow: isComment ? true : false
                      }

                      if (!isComment) {
                        element.commentMarkup.submitOptions.postCallFunction = {
                          func(returnedData) {
                            return new Promise<any>((resolve, reject) => {
                              this.executionClass.common.modalCtrl.dismiss(null, null, 'jbpm-task-details-modal');
                              resolve(this.executionClass.populateList(returnedData));
                            });
                          },
                          executionClass: this,
                          params: [this]
                        }
                      } else {
                        element.commentMarkup.submitOptions.postCallFunction = {
                          func(returnedData) {
                            return new Promise<any>((resolve, reject) => {
                              resolve(this.executionClass.refreshComments(returnedData, element));
                            });
                          },
                          executionClass: this,
                          params: [this]
                        }
                      }

                      element.commentMarkup.messageOptions = {
                        labelKey: isComment ? 'comment_key' : 'reason_key',
                        group: formGroup,
                        fcName: 'comment'
                      }

                      this.openModal(element);
                    });
                  } else {
                    this.openModal(element);
                  }

                }
                if (isRelease || isComplete || isClaim) {
                  this.common.presentLoading();
                  if (isRelease) {
                    this.jbpm.releaseBpmTask({ ...element.taskInfo }).then((result) => {
                      if (element.screenMarkup && element.screenMarkup.queryParams
                        && element.screenMarkup.queryParams && element.screenMarkup.queryParams.operId) {
                        const operID = element.screenMarkup.queryParams.operId.MAP_VALUE;
                        if (PsCommonSettings.oper_ID == operID) {
                          this.navService.navigateBackward()
                        }
                        this.populateList(result);
                      }
                    })
                  }

                  if (isComplete) {
                    this.jbpm.completeBpmTask({ ...element.taskInfo }).then((result) => {
                      this.populateList(result);
                    })
                  }

                  if (isClaim) {
                    this.jbpm.claimBpmTask({ ...element.taskInfo }).then((result) => {
                      this.populateList(result);
                    })
                  }
                }
              },
              handlerParams: element
            };
            element.fabMarkUp.startFabList.push(iconObject);
            element.iconListMarkUp.push(iconObject);
          }
        }
      });
    }
    this.options.taskList = result.gridModel;
    if (this.options.taskList && this.options.taskList.length > 0) {
      this.noData = false;
    } else {
      this.noData = true;
    }
  }

  switchContent(psContainerFlipTask) {
    psContainerFlipTask.isFlipped = !psContainerFlipTask.isFlipped;
    psContainerFlipTask.hideVisibleArea = !psContainerFlipTask.hideVisibleArea;
  }

  refreshComments(returnedData, element: JBPMTask) {
    element.commentMarkup.listOfChat.length = 0;
    if (returnedData && returnedData.gridModel) {
      this.populateComments(returnedData.gridModel, element);
    }
  }

  populateComments(result, element) {
    result.forEach((comment) => {
      element.commentMarkup.listOfChat.push({
        owner: comment.commentOwner,
        creationDate: comment.commentCreationDate,
        message: comment.comment
      })
    });
  }

  ngOnDestroy() {
    if (this.eventSub) {
      this.eventSub.unsubscribe();
    }
  }

  callWebService(functionName, param) {
    this.jbpm[functionName](param).then((preCallResult) => {
    }).catch((error) => {
      this.logger.log(error);
    });
  }

  handleTask(task: JBPMTask) {
    this.common.presentLoading();
    this.jbpm.returnBpmTaskMapping(task.taskInfo).then((result) => {
      task.screenMarkup = result.screenMarkup;
      const operID = result.screenMarkup.queryParams.operId.MAP_VALUE;
      let taskTemplate = result.screenMarkup.taskTemplate;
      if (taskTemplate) {
        taskTemplate = JSON.parse(taskTemplate);
        this.navService.openPage({
          operID: operID,
          component: 'bpm-dynamic-page',
          param: {
            JBPM: { ...task, isJBPM: true },
            taskTemplate: taskTemplate
          }
        });
        return;
      }
      if (operID) {
        this.navService.openPage({
          operID: operID,
          param: {
            JBPM: { ...task, isJBPM: true },
            postCallFunction: {
              func(returnedData) {
                return new Promise<any>((resolve, reject) => {
                  const param = { ...returnedData.taskInfo, bpmParametersMap: returnedData.bpmParametersMap };
                  this.executionClass.jbpm.completeBpmTask(param).then((result) => {
                    this.executionClass.populateList(result);
                  })
                  // revertBack resolve(this.executionClass.populateList(returnedData));
                });
              },
              executionClass: this,
              params: [this]
            }
          }
        });
      }
    }).catch((err) => {
      this.common.dismissLoading();
    });
  }

  expand(task: JBPMTask) {
    if (this.moreItems) {
      this.common.hideElementWhenAnimationEnds(this.moreItems, 'jbpm-task-action-container-hide').then(() => {
        this.currentExpandedTask.isTaskExpanded = false;
        if (this.currentExpandedTask.taskInfo.bpmTaskId !== task.taskInfo.bpmTaskId) {
          this.expandTask(task)
        }
      });
    } else {
      this.expandTask(task);
    }
  }

  expandTask(task: JBPMTask) {
    if (task.isTaskExpanded) {
      this.common.hideElementWhenAnimationEnds(this.moreItems, 'jbpm-task-action-container-hide').then(() => {
        task.isTaskExpanded = !task.isTaskExpanded;
      });
    } else {
      task.isTaskExpanded = !task.isTaskExpanded;
    }
    this.currentExpandedTask = task;
  }

  async openModal(element) {
    const chatModal = await this.common.modalCtrl.create({
      component: PsJbpmTaskDetailsComponent,
      componentProps: { options: element },
      cssClass: 'alert-modal jbpm-task-details-modal',
      animated: false,
      id: 'jbpm-task-details-modal',
    });
    await chatModal.present();
    this.common.dismissLoading();
  }
}
