import { Component, ElementRef, Input, OnInit } from '@angular/core';
import { AuthenticationMatrixService } from 'src/app/commonBussinessSRC/psServices/authentication-matrix/authentication-matrix.service';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IOptionsPsButtonReject, IOptionsPsButtonSubmit } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { PsNetworkService } from 'src/app/commonSRC/psServices/network/ps-network.service';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { IOptionsPsAlertButton, IOptionsPsComplexAlertController } from 'src/app/commonSRC/psTemplates/ps-template-alert-controller/ps-template-alert-controller.template.interfaces';
import { PsButtonSubmitComponent } from '../ps-button-submit/ps-button-submit.component';
import { Events } from 'src/app/commonSRC/psServices/Event/event.service';


@Component({
  selector: 'ps-button-reject',
  templateUrl: './ps-button-reject.component.html'
})
export class PsButtonRejectComponent extends PsButtonSubmitComponent implements OnInit {

  @Input() options: IOptionsPsButtonReject;

  defaultOptions: IOptionsPsButtonSubmit;
  isCorporate = false;
  constructor(public commonService: PsCommonService, private navService: PsNavigatorService,
    private omniAuthServ: AuthenticationMatrixService, private psNetworkServ: PsNetworkService, public events: Events, elRef?: ElementRef) {
    super(commonService, navService, omniAuthServ, psNetworkServ, events, elRef);
    if (PsCommonBusinessSettings.isCorporate) {
      this.isCorporate = true;
    }
  }

  ngOnInit() {
    this.defaultOptions = {
      labelKey: 'reject_key',
      iconName: 'trash',
      psClass: 'ps-button-reject',
      actionType: ConstantCommon.ACTION_TYPE_REJECT,
      group: this.options.group,
    };
    this.options.submitServiceUrl = this.options.submitServiceUrl ? this.options.submitServiceUrl : PsCommonSettings.serviceUrl.commonSubmitAction;
    this.commonService.copyObject(this.defaultOptions, this.options, false);
    //added by ghada, AZDB-5485 for overriding defaultOptions postcallfunction if it's sent in components options  
    this.defaultOptions.postCallFunction = this.options.postCallFunction ? this.options.postCallFunction : this.defaultOptions.postCallFunction;
    super.init();
  }

  onReject(event) {
    this.onClick.emit(event);
  }

  askForReason() {
    return new Promise<any>((resolve, reject) => {
      const alertController: IOptionsPsComplexAlertController = {
        cssClass: 'ps-button-reject',
        header: 'rejection_reason_key',
        inputs: [
          {
            type: 'keyin',
            options: {
              placeHolder: 'add_rejection_reason_key',
              labelKey: 'reason_key',
              fcName: 'rejectionReason',
              group: this.options.group,
              forceShowOnPreview: true,
              enableOnDisabledScreen:true
            }
          }
        ],
        buttons: [
          {
            role: 'cancel',
            options: {
              labelKey: 'cancel_key',
              psClass: 'ps_cancel_rejection_reason_button',
              group: this.options.group
            },
            handler: () => {
              this.options.group.removeControl('rejectionReason');
              reject(false);
            }
          }
        ]
      };
      const psButtonSave: IOptionsPsAlertButton = {
        role: 'submit',
        handler: () => {
          CommonUtils.dismissAllModals();
        }
      };

      (psButtonSave.options as IOptionsPsButtonSubmit) = {
        ...this.options,
        labelKey: 'submit_key',
        psClass: 'ps-drafts-report-submit-button',
        isSave: false,
        actionType: ConstantCommon.ACTION_TYPE_REJECT,
        postCallFunction: {
          func() {
            return new Promise<any>((resolve) => {
              resolve(this.executionClass.commonService.taskApproved.next(true));
            });
          },
          executionClass: this
        },
        preCallFunction: undefined,
        failureCallFunction: undefined,
        extraParams: {
          ...this.options.extraParams, actionType: ConstantCommon.ACTION_TYPE_REJECT,
        }
      };
      alertController.buttons.push(psButtonSave);
      this.commonService.presentPsAlert(alertController, 'ps-button-reject');

    });
  }
}
