import { HttpRequest, HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { HybridKeyService } from '../hybridkey/hybridkey.service';
import { CommonUtils } from '../models/common-utils';

const maxAge = 600000;

@Injectable({
  providedIn: 'root'
})
export class PsRequestCacheService {
  private cacheWithAge = new Map();
  private permanentMap = new Map();

  constructor(private hybrid: HybridKeyService) { }

  get(req, requestBody?: any): HttpResponse<any> | any {
    let cached = undefined;
    /* Gilbert Al Indary: Added the below for #1347894 */
    if (requestBody) {
      const key = this.generateMapKey(null, requestBody);
      let urlMapByRequestBody: Map<string, any> = this.permanentMap.get(req);
      if (urlMapByRequestBody) {
        cached = urlMapByRequestBody.get(key);
      }
      if (cached) {
        cached = this.hybrid.decryptTransportMessage(cached);
      }
      return cached;
    }


    cached = this.permanentMap.get(req);
    if (cached) {
      return cached;
    }

    const url = req.urlWithParams;
    cached = this.cacheWithAge.get(url);
    if (!cached) {
      return undefined;
    }

    const isExpired = cached.lastRead < (Date.now() - maxAge);

    // const expired = isExpired ? 'expired ' : '';
    return cached.response;
  }

  putWithAge(req: HttpRequest<any> | string, response: HttpResponse<any>): void {
    let url;
    if (req instanceof HttpRequest) {
      url = req.url;
    }
    else {
      url = req;
    }
    const entry = { url, response, lastRead: Date.now() };
    this.cacheWithAge.set(url, entry);

    const expired = Date.now() - maxAge;
    this.cacheWithAge.forEach(expiredEntry => {
      if (expiredEntry.lastRead < expired) {
        this.cacheWithAge.delete(expiredEntry.url);
      }
    });
  }

  permanentCache(req: HttpRequest<any> | string, response, requestBody?: any) {
    if (requestBody) {
      if (req instanceof HttpRequest) {
        let key = this.generateMapKey(req);
        this.permanentMap.set(key, response);
        return;
      } else {
        // Main case where most of the scenarios would occur
        const key = this.generateMapKey(null, requestBody);
        let urlMapByRequestBody: Map<string, any> = this.permanentMap.get(req);
        if (!urlMapByRequestBody) {
          urlMapByRequestBody = new Map();
        }
        const encrypted = this.hybrid.encryptTransportMessage(response);
        urlMapByRequestBody.set(key, encrypted.pathParam);
        this.permanentMap.set(req, urlMapByRequestBody);
        return;
      }
    }
    this.permanentMap.set(req, response);
  }

  private generateMapKey(req: HttpRequest<any>, requestBody?: any) {
    if (requestBody) {
      const orderedBody = JSON.stringify(CommonUtils.sortObject(requestBody));
      return orderedBody;
    }
    // Keep the below incase we need the same in the future
    const url = req.url ? req.url : req.urlWithParams;
    const body = req.body;
    const orderedBody = JSON.stringify(CommonUtils.sortObject(body));
    let key = url + orderedBody;
    return key;
  }
}
