import { Injectable } from '@angular/core';
import { BarcodeScanner } from '@ionic-native/barcode-scanner/ngx';
import jsQR from 'jsqr';
import { LoggerService } from '../logger/logger.service';
import { CommonUtils } from '../models/common-utils';


@Injectable({
  providedIn: 'root'
})
export class BarcodeScannerService {

  constructor(private barcodeScanner: BarcodeScanner, private logger: LoggerService) { }

  scan() {
    return new Promise<any>((resolve, reject) => {
      this.barcodeScanner.scan().then(result => {
        if (result && result !== undefined) {
          if (result['cancelled'] == true && result['text'] == "") {
            CommonUtils.presentInfoAlert(CommonUtils.translate("operation_has_been_canceled_key"));
            reject(false);
          } else if (result['text'] != "") {
            CommonUtils.presentSuccessAlert(CommonUtils.translate("scanning_done_successfully_key"));
            resolve(result['text']);
          }
        } else {
          CommonUtils.presentInfoAlert(CommonUtils.translate('no_data_scanned_key'));
          reject(undefined)
        }
      }).catch(err => {
        this.logger.error('Error while scanning QR Code ', err);
        CommonUtils.presentFailureAlert(CommonUtils.translate('something_went_wrong_key'));
        reject(err);
      });
    })

  }

  decodeQRFromImage(base64: string) {
    return new Promise<any>((resolve, reject) => {
      const img = new Image();
      if (CommonUtils.isNativeMobile()) {
        img.src = base64;
      } else {
        img.src = 'data:image/jpeg;base64,' + base64;
      }
      img.onload = () => {
        const canvas = document.createElement('canvas');
        canvas.width = img.width;
        canvas.height = img.height;
        const ctx = canvas.getContext('2d');
        if (!ctx) return;
        ctx.drawImage(img, 0, 0);
        const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
        const qrCode = jsQR(imageData.data, imageData.width, imageData.height);
        if (qrCode) {
          console.log('QR Code Data:', qrCode.data);
          CommonUtils.presentSuccessAlert(CommonUtils.translate("scanning_done_successfully_key"));
          resolve(qrCode.data);
        } else {
          console.log('No QR code found');
          CommonUtils.presentInfoAlert(CommonUtils.translate('no_data_scanned_key'));
          reject(undefined)
        }
      };
    }).catch(err => {
      this.logger.error('Error while scanning QR Code ', err);
      CommonUtils.presentFailureAlert(CommonUtils.translate('something_went_wrong_key'));
    });
  }
}
