import { Component, HostListener, Input, OnInit } from '@angular/core';
import { ValidatorFn } from '@angular/forms';
import { DateAdapter } from '@angular/material/core';
import { ICellRendererAngularComp } from 'ag-grid-angular';
import { PsDateFormatPipe } from 'src/app/commonSRC/psPipes/ps-date-format/ps-date-format.pipe';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IOptionsPsFieldLabel, IOptionsPsKeyinDate } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { IOptionsPsActionIcon, IOptionsPsLabelInput } from '../../../../../psServices/models/ps-common-interface';
import { PsFieldKeyinComponent } from '../ps-field-keyin.component';
import { Events } from 'src/app/commonSRC/psServices/Event/event.service';
import { Subscription } from 'rxjs';
import moment from 'moment';
import { formatDate } from '@angular/common';


@Component({
  selector: 'ps-keyin-date',
  templateUrl: './ps-keyin-date.component.html',
  styleUrls: ['./ps-keyin-date.component.scss'],
})
export class PsKeyinDateComponent extends PsFieldKeyinComponent implements OnInit, ICellRendererAngularComp {

  @Input() public options: IOptionsPsKeyinDate;
  eventSub: Subscription;
  get labelOptions(): IOptionsPsLabelInput {
    return {
      required: this.required,
      labelKey: this.options.labelKey,
      previewMode: this.previewMode,
      psClass: 'ps-keyin-date-label'
    };
  }
  previewLblOptions: IOptionsPsFieldLabel;
  iconOptions: IOptionsPsActionIcon = {
    iconName: 'calendar'
  };
  isMobile = true;
  excludeTimeValidator = true;
  private datetime = undefined;
  /** Updated by Hisham.Omar TP#1136182 start
   *  Add additional condition that checks if the date should
   *  be set as formatted or as full date.
   *  Bind 'dateValue' variable to ngModel of the date. 
   */

  //edited by Marina TP:#1477922 to aviod setting date value as Unix Epoch date which can't be compared to date format of min and max dates
  get dateValue() {
    let v = super.returnValue();
    if (v != this.oldValue) {
      this.datetime = v;
      if (!this.isMobile && (typeof (v) == 'string' || typeof (v) == 'number')) {
        //added by Michel TP:#1578731 for parsing the date (for dates that contain WITHOUT_TIME_ZONE_YN = 'Y' customiztion field)
        // modified by ghada, AZDB-1489 for handling the case of time component as passing time to new Date() will get invalid Date as it's not valid format
        if (this.screenDispElt && this.screenDispElt.WITHOUT_TIME_ZONE_YN == 'Y' || this.options.isTimeComponent) {  //added by Michel TP:#1578731 for parsing the date (for dates that contain WITHOUT_TIME_ZONE_YN = 'Y' customiztion field)
          this.datetime = new PsDateFormatPipe().parse(v, this.options.displayFormat);
        }
        else {
          // modified by Mustafa, AZDB-1489 for handling date cases with ISO format or else
          if (!moment(v, moment.ISO_8601, true).isValid() && (typeof (v) == 'string')) {
            this.datetime = new PsDateFormatPipe().parse(v, this.options.displayFormat);
            v = new PsDateFormatPipe().parse(v, this.options.displayFormat);
          } else {
            this.datetime = new Date(v);
            v = new Date(v);
          }
        }
      } else if (this.isMobile && this.datetime && !CommonUtils.isDateObject(this.datetime)) {
        this.datetime = new PsDateFormatPipe().parse(this.datetime, this.options.displayFormat);
      }
    }

    if (v != this.oldValue && !this.options.group.controls[this.options.fcName][ConstantCommon.CONTROLLER_CHANGE_EVENT_STARTED]) {
      if (this.options && this.options.displayFormat && v && this.screenDispElt && this.screenDispElt.WITHOUT_TIME_ZONE_YN == 'Y') {
        v = new PsDateFormatPipe().parse(v, this.options.displayFormat);
        let dateString = new PsDateFormatPipe().transform(v, this.options.displayFormat, null, false);
        if (dateString != new PsDateFormatPipe().transform(this.oldValue, this.options.displayFormat, null, false)) {
          const fcDateValue = this.isMobile ? v : dateString;
          this.options.group.controls[this.options.fcName].setValue(v, { emitEvent: false });
          this.options.group.controls[this.options.fcName][ConstantCommon.OLD_VALUE] = fcDateValue;
          this.options.group.controls.formData.value[this.options.fcName] = fcDateValue;      
        }
      }
      else {
        if (this.oldValue != v) {
          // ADDED BY MICHEL IN ORDER TO CONVERT THE DATE TO ISO STRING FORMAT IF THE DATE FORMAT RETURNED IS NOT ISO STRING FOR DATE WITH WITHOUT_TIME_ZONE_YN = 'N'
          if(this.isMobile && !moment(v, moment.ISO_8601, true).isValid()){
           v = new PsDateFormatPipe().parse(v, this.options.displayFormat);
          }
          this.options.group.controls[this.options.fcName][ConstantCommon.OLD_VALUE] = v;
          if (!this.isMobile && (typeof (v) == 'string' || typeof (v) == 'number') && this.options.isTimeComponent) {
            v = new PsDateFormatPipe().parse(v, this.options.displayFormat);
          }
          this.options.group.controls[this.options.fcName].setValue(v, { emitEvent: false });
          this.options.group.controls.formData.value[this.options.fcName] = v;
        }
      }
    }
    return this.datetime;
  }

  getPlaceHolder() {
    if (this.value) {
      return '';
    } else {
      return this.options.placeHolder
    }
  }

  /** Updated by Hisham.Omar TP#1136182 end */

  /** Added by Hisham.Omar TP#1136182 start
   *  Create a getter 'formattedValue' that always format the date
   *  choosen whenever a display format exist.
   *  Use this getter to display the resulted value in the field.
   */
  get formattedValue() {
    const tempVal = super.returnValue();
    let dateObj = tempVal;
    if (this.options && this.options.displayFormat && tempVal) {
      const datepipe = new PsDateFormatPipe();
      if ((this.screenDispElt && this.screenDispElt.WITHOUT_TIME_ZONE_YN == 'Y') || !moment(tempVal, moment.ISO_8601, true).isValid()) {
        dateObj = datepipe.parse(tempVal, this.options.displayFormat);
      }
      dateObj = datepipe.transform(dateObj, this.options.displayFormat);
    }
    return dateObj;
  }

  // set formattedValue(val) {
  //   if (this.options && this.options.displayFormat && CommonUtils.isDateObject(val)) {
  //     super.writeValue(new PsDateFormatPipe().transform(val, this.options.displayFormat));
  //   } else {
  //     super.writeValue(val);
  //   }
  // }
  /** Added by Hisham.Omar TP#1136182 end */

  get min() {
    let val;
    const dateValidation = this.commonProv.getElementValidations(this.options.fcName);
    if (dateValidation && dateValidation.MIN_VALUE_DATE) {
      val = new Date(dateValidation.MIN_VALUE_DATE);
    } else if (this.options.min) {
      try {
        if (typeof this.options.min === 'string') {
          // Added By Omar Talaat ABEOI230044 Date component is processing the format in wrong way
          val = new PsDateFormatPipe().parse(this.options.min, this.options.displayFormat);
        } else {
          val = new Date(this.options.min)
        }
      } catch (e) {
        this.logger.error(e);
        return null;
      }
    }
    if (val) {
      if (this.isMobile) {
        return new PsDateFormatPipe().transform(val, ConstantCommon.PREV_DATE_FORMAT);
      } else {
        return val;
      }
    }
    return null;
  }

  get max() {
    let val;
    const dateValidation = this.commonProv.getElementValidations(this.options.fcName);
    if (dateValidation && dateValidation.MAX_VALUE_DATE) {
      val = new Date(dateValidation.MAX_VALUE_DATE);
    } else if (this.options.max) {
      try {
        if (typeof this.options.max === 'string') {
          val = new PsDateFormatPipe().parse(this.options.max, this.options.displayFormat);
        } else {
          val = new Date(this.options.max)
        }
      } catch (e) {
        this.logger.error(e);
        return null;
      }
    }
    if (val) {
      if (this.isMobile) {
        return new PsDateFormatPipe().transform(val, ConstantCommon.PREV_DATE_FORMAT);
      } else {
        return val;
      }
    }
    return null;
  }

  get previewValOptions(): IOptionsPsFieldLabel {
    let prevFormat = ConstantCommon.PREV_DATE_FORMAT;
    let fcValueFormat = null;
    if (this.options && this.options.displayFormat) {
      prevFormat = this.options.displayFormat;//.toLowerCase();
      //  if (prevFormat.indexOf('mmmmm') > -1) {
      //    prevFormat = prevFormat.replace('mmmmm', 'MMMMM');
      // } else if (prevFormat.indexOf('mmmm') > -1) {
      //   prevFormat = prevFormat.replace('mmmm', 'MMMM');
      // } else if (prevFormat.indexOf('mmm') > -1) {
      //   prevFormat = prevFormat.replace('mmm', 'MMM');
      // } else if (prevFormat.indexOf('mm') > -1) {
      //   prevFormat = prevFormat.replace('mm', 'MM');
      //  }
    }
    if(!CommonUtils.isDate(this.fcValue,moment.ISO_8601)){
      fcValueFormat = prevFormat;
      }

    return {
      labelKey: new PsDateFormatPipe().transform(this.fcValue, prevFormat,fcValueFormat),
      psClass: 'ps-value-preview',
      previewMode: true
    };
  }

  constructor(public commonProv: PsCommonService, loggerP: LoggerService, events: Events, private dateAdapter?: DateAdapter<any>) {
    super(commonProv, loggerP);
    // Added by Marina jira-AZDB-1260; to change the date value from moment object to formated string before sending the request
    let stringDateValue;
    this.eventSub = events.subscribe('onSubmit', (formGroup) => {
      const dateValidations = this.commonProv.getElementValidations(this.options.fcName)
      const dateValue = formGroup.controls[this.options?.fcName].value;
      if (dateValue) {
        if (dateValidations && dateValidations.WITHOUT_TIME_ZONE_YN == 'N') {
          stringDateValue = CommonUtils.toIsoString(dateValue);
        }
        else {
          stringDateValue = this.formatDateString(dateValue)
        }
        this.options.group.controls.formData.value[this.options.fcName] = stringDateValue;
      }
    })
  }
  private formatDateString(dateValue) {
    const dateFormat = this.options.group.controls[this.options.fcName][ConstantCommon.CONTROLLER_DATE_FORMAT];
        if (typeof dateValue === 'string' && moment(dateValue, dateFormat, true).isValid()) {
          // Check if the string is a valid date in the expected format
          return dateValue;
        } else if ((dateValue instanceof Date && !isNaN(dateValue.getTime())) || moment(dateValue, moment.ISO_8601, true).isValid()) {
          // Check if it's a valid Date object or ISOstring
          return moment(dateValue).format(dateFormat);
        } else {
          console.log('Invalid date input');
          this.logger.error('Invalid date input');
          return null;
        }
      }
  ngOnInit() {
    this.dateAdapter.setLocale(String(PsCommonSettings.activeLanguge).toLocaleLowerCase());
    this.custIconOptions.availableCustomization.WITHOUT_TIME_ZONE_YN = true;
    this.custIconOptions.availableCustomization.MAX_VALUE_DATE = true;
    this.custIconOptions.availableCustomization.MIN_VALUE_DATE = true;
    this.custIconOptions.component = PsKeyinDateComponent;
    super.ngOnInit();
    this.options.group.controls[this.options.fcName][ConstantCommon.CONTROLLER_NATIVE_DATETIME] = true;
    if (this.options.isTimeComponent) {
      this.options.group.controls[this.options.fcName][ConstantCommon.CONTROLLER_NATIVE_TIME] = true;
    }

    // remove the default_value cust from date fields as per Joseph's request since there's no business value in defining static value to be set on screens
    this.custIconOptions.availableCustomization.DEFAULT_VALUE = false;
    this.custIconOptions.componentOptions = this.options;
    this.isMobile = this.commonProv.isMobileLayout();
    // if the max is not defined then add the max of 100 years in the future since ion-datetime has the max by default as the end of the current year
    // if the min is not defined then subtract the min of 100 years in the past same as the default min of ion-datetime
    // let min_year = new Date().setFullYear( new Date().getFullYear() - 100);
    // let max_year = new Date().setFullYear( new Date().getFullYear() + 100);

    // for (let diff = min_year; diff < max_year; diff++) {
    //   this.yearValues.push(diff.toString());
    // }

    this.excludeTimeValidator = (!this.options.isTimeComponent && !this.isMobile);
    if (!this.options.min && (!this.options.isTimeComponent || this.isMobile)) {
      this.options.min = new Date();
      this.options.min.setFullYear(this.options.min.getFullYear() - 100);
    }

    if (!this.options.max && (!this.options.isTimeComponent || this.isMobile)) {
      this.options.max = new Date();
      this.options.max.setFullYear(this.options.max.getFullYear() + 100);
    }

    if (this.options.labelKey !== undefined) {
      this.labelOptions.labelKey = this.options.labelKey;
    }
    this.previewLblOptions = {
      labelKey: this.options.labelKey,
      psClass: 'ps-lbl-preview',
      previewMode: true
    };
    this.labelOptions.required = this.required;
    this.itemOptions.iconOptions = this.iconOptions;

    // Added by Richie for #BUG 1147159
    if (this.options.displayFormat) {
      this.options.group.controls[this.options.fcName][ConstantCommon.CONTROLLER_DATE_FORMAT] = this.options.displayFormat;
    }

    const minMaxValidators: ValidatorFn[] = [];
    if (this.options.min) {
      minMaxValidators.push(this.commonProv.minMaxDateValidator(this.options.min, true, this.excludeTimeValidator));
    }
    if (this.options.max) {
      minMaxValidators.push(this.commonProv.minMaxDateValidator(this.options.max, false, this.excludeTimeValidator));
    }
    this.commonProv.addComponentValidator(this.options.group, this.options.fcName, this.commonProv.prepareValidation(false, null, null, null, null, null, null, null, minMaxValidators));
    // End Richie
  }

  dateChanged(value) {
    // this.options.placeHolder = null;
  }
  mobiledateChanged(value) {
    if (value) {
      this.options.group.controls[this.options.fcName].setValue(value.target.value);
    }
  }
  timeChangeHandler(value) {
    // this.options.placeHolder = null;
  }
  agInit(params: any) {
    this.agInitBase(params, this);
    if (params.colDef.cellEditorParams.onPsChange !== undefined) {
      this.onPsChange.subscribe((res) => {
        res.rowIndex = params.rowIndex;
        // if (res.oldValue !== undefined) {// Commented by Richie for #BUG 1340366; in order to call the change event on first change
        params.colDef.cellEditorParams.onPsChange(res);
        // }
      });
    }
  }
  refresh(params: any) {
    this.agRefreshBase(params, this);
    return true;
  }
  ngOnDestroy(): void {
    this.eventSub.unsubscribe();
  }

}
