import { Injectable } from '@angular/core';
import { Platform } from '@ionic/angular';
import { HttpService } from '../http/http.service';
import { HybridKeyService } from '../hybridkey/hybridkey.service';
import { LoggerService } from '../logger/logger.service';
import { ConstantCommon } from '../models/common-constant';
import { CommonUtils } from '../models/common-utils';
import { PsApplicationSettings } from '../models/ps-app-settings';
import { PsCommonSettings } from '../models/ps-common.settings';
import { PlayIntegrityToken } from '@awesome-cordova-plugins/play-integrity-token/ngx';


@Injectable({
    providedIn: 'root'
})
export class PsSecurityService {
    constructor(private platform: Platform, private logger: LoggerService, private http: HttpService, private hybridkey: HybridKeyService, private playIntegrityToken: PlayIntegrityToken) {
    }

    public handleStopApp() {
        if (CommonUtils.isAndroid() && navigator['app']) {
            navigator['app'].exitApp();
        } else {
            CommonUtils.presentFatalAlert(null, { title: 'Security Alert', message: 'Cannot Open Application!', noButtons: true, psClass: 'sec-alert' });
        }
    }

    getSSLPinningMethod(): 'default' | 'legacy' | 'nocheck' | 'pinned' {
        return "pinned";
    }

    /**
     * function responsible for decrypting the encrypted hashed js file 
     * and call the function within to load all files of the package and generate the hash code 
     * then compare it with the manifest generated at build time in order to detect any tampering in the code.
     * @param params parameters holding the keys to decrypt the js file
     * @author RichardZourob
     * #BUG 1287746
     */
    public async checkAntiTampering(params: any) {
        const pathPsContent = await this.http.http.get(ConstantCommon.ANTI_TAMPERING_ENCRYPTED_FILE_NAME).toPromise();
        if (pathPsContent) {
            try {
                const decrypted = this.hybridkey.decryptMessage(params[ConstantCommon.ANTI_TAMPERING_AES_KEY], params[ConstantCommon.ANTI_TAMPERING_AES_IV], pathPsContent[ConstantCommon.ANTI_TAMPERING_ENCRYPTED_FILE]);
                if (decrypted) {

                    var myWorker = new Worker('ps.8cadbffda2d00723b86d.js');
                    myWorker.onmessage = function (e) {
                        if (e.data && e.data.error && e.data.error == '1') {
                            document.getElementsByTagName('html')[0].innerHTML = null;
                            throw new Error('ERROR: Tampered file is detected: ' + e.data.desc);
                        }
                        else if (e.data && e.data.error && e.data.error == '0') {
                            myWorker.terminate();
                            myWorker = undefined;
                        }
                    }
                    myWorker.postMessage(decrypted);
                    //window['eval'](decrypted);
                } else {
                    this.logger.error("Security Alert: Application has been tampered due to none exisiting Anti-tampering details.");
                    this.handleStopApp();
                }
            } catch (err) {
                this.logger.error('Error while preparing Anti-tampering details');
                this.logger.error(err);
                this.handleStopApp();
            }
        } else {
            this.logger.error("Error while reading Anti-tampering details.");
            this.handleStopApp();
        }
    }

    /**
     * @author Mustafa
     * @description method to check if android device is rooted by contacting google play integrity API 
    */
    rootDeviceDetection(projectNumber: string, hashedNonce: string): Promise<string> {
        if (CommonUtils.isNotEmptyString(projectNumber)) {
            return new Promise<string>((resolve, reject) => {
                this.platform.ready().then(() => {
                    const timeoutId = setTimeout(() => {
                        if (PsApplicationSettings.MAIN_CONFIG.LOG_LEVEL_ALL) {
                            CommonUtils.presentInfoAlert('Timeout occurred while detecting root device', {title: 'Tip'});
                        }
                        reject(new Error('Timeout occurred while detecting root device'));
                    }, PsApplicationSettings.MAIN_CONFIG.PITime);
   
                    this.playIntegrityToken.integToken(projectNumber, hashedNonce).then(integrityToken => {
                        clearTimeout(timeoutId);
                        if (integrityToken) {
                            resolve(integrityToken);
                        } else {
                            resolve(null);
                        }
                    }).catch(error => {
                        clearTimeout(timeoutId);
                        this.logger.error(`$$**error**$$: ${error}`);
                        if (PsApplicationSettings.MAIN_CONFIG.LOG_LEVEL_ALL) {
                            CommonUtils.presentInfoAlert(`rootDeviceDetection error: ${error}`, {title: 'Tip'});
                        }
                        reject(error);
                    });
                });
            });
        } else {
            CommonUtils.presentInfoAlert(`Play integrity: Project number not exist, please contact administrator`, {title: 'Tip'});
            return Promise.reject(new Error('Play integrity: Project number not exist, please contact administrator'));
        }
    }

    parseJWS(jws) {
        try {
            return JSON.parse(atob(jws.split('.')[1]));
        } catch (err) {
            this.logger.error(err);
            return null;
        }
    }

    checkAppProtection(val) {
        return val;
    }

    handleADB() {
        CommonUtils.presentFatalAlert(null,
            {
                title: 'Security Alert',
                message: 'App cannot be launched with USB debugging turned on. Please disable USB debugging and relauch the app.',
                noButtons: true,
                psClass: 'sec-alert'
            });
    }
}