import { ChangeDetectorRef, Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { IAmountFromToCurrencyRequest } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-interfaces';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { PsBaseFieldComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-base-field.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IOptionsPsActionIconExposed, IOptionsPsLabel } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { CommonUtils } from './../../../../commonSRC/psServices/models/common-utils';
import { IOptionsPsComplexExchnageExposed } from './ps-complex-exchange.component.interfaces';



/**
 * @author Aftab.Ali
 * @since 26/12/2019
 *
 * <p> PsComplexExchangeComponent -- </p>
 */
@Component({
  selector: 'ps-complex-exchange',
  templateUrl: './ps-complex-exchange.component.html',
  styleUrls: ['./ps-complex-exchange.component.scss'],
})
export class PsComplexExchangeComponent extends PsBaseFieldComponent implements OnInit {

  @Input() options: IOptionsPsComplexExchnageExposed;
  @Output() onFromCurrencyChanged = new EventEmitter<string>();
  @ViewChild('fromCurrRef') fromCurrRef;
  @ViewChild('toCurrRef') toCurrRef;
  public fromCurrencyCode = '';
  public toCurrencyCode = '';
  public exchangeRate = '0.00';
  public amountInToAccountAmount = '0.00';
  public excnageRateIconOptions: IOptionsPsActionIconExposed = {};
  public toCurrenciesWarning = true;
  public toCurrenciesWarningText: any;
  public amountEntered: string;
  public exchangeRatelabelOptions: IOptionsPsLabel = {
    labelKey: 'exchange_rate_key',
    previewMode: false
  };
  exchangeRateValueOptions: IOptionsPsLabel = {
    labelKey: '0.00',
    previewMode: true
  };

  constructor(
    private commonService: PsCommonService,
    logger: LoggerService,
    private omniPull: OmniPullService,
    private cdRef?: ChangeDetectorRef
  ) {
    super(commonService, logger);
  }

  ngOnInit() {
    super.ngOnInit();
    this.excnageRateIconOptions = {
      iconName: 'exchange-icon'
    };
    //Added by ghada, AZDB-4500 for setting components amount if it's value exist in the vo
    //in the to do cases "save draft & check approve "pre inistialze both currency codes so whenever the actual controls on tyhe html are filled with 
    //their respective values we are sure that both currency codes will b having their values because the exchange calculation code checks and only runs if both currency codes have value
    if (CommonUtils.isNotEmptyObject(this.options.requestObject)) {
      this.options.fromAmountOptions.currencyCode = this.options.requestObject[this.options.fromAmountOptions.currenciesOptions.fcName]
      this.options.toAmountOptions.currencyCode = this.options.requestObject[this.options.toAmountOptions.currenciesOptions.fcName]
    }
  }



  /**
   *
   * @param selectedValue
   */
  public onamountInFromAccountChange(selectedValue) {
    if (this.options.toAmountOptions && this.options.editableMode === true && (this.options.toAmountOptions.currencyCode === null || this.options.toAmountOptions.currencyCode === undefined)) {
      this.toCurrenciesWarning = true;
      this.toCurrenciesWarningText = this.omniPull.omniCommon.common.translate('please_select_to_currency_key');
      return;
    }
    if (selectedValue === null || selectedValue === undefined) {
      this.toCurrenciesWarning = true;
      this.toCurrenciesWarningText = this.omniPull.omniCommon.common.translate('please_enter_from_amount_key');
      return;
    }
    else if (selectedValue !== null && selectedValue !== undefined && (selectedValue.amount === null || selectedValue.amount === undefined || selectedValue.amount === '' || selectedValue.amount === 0)) {
      this.toCurrenciesWarning = true;
      this.commonProv.setValInsideNestedObj(this.options.fromAmountOptions.amountOptions.fcName, '', this.options.requestObject);
      this.toCurrenciesWarningText = this.omniPull.omniCommon.common.translate('please_enter_from_amount_key');
      return;
    }
    this.setExchangeAmount(selectedValue, 'from');
  }

  public onamountInToAccountChange(selectedValue) {
    if (this.options.enableTwoWayExchange) {
      if (this.options.fromAmountOptions && this.options.editableMode === true && (this.options.fromAmountOptions.currencyCode === null || this.options.fromAmountOptions.currencyCode === undefined)) {
        this.toCurrenciesWarning = true;
        this.toCurrenciesWarningText = this.omniPull.omniCommon.common.translate('please_select_from_currency_key');
        return;
      }
      if (selectedValue === null || selectedValue === undefined) {
        this.toCurrenciesWarning = true;
        this.toCurrenciesWarningText = this.omniPull.omniCommon.common.translate('please_enter_to_amount_key');
        return;
      }
      else if (selectedValue !== null && selectedValue !== undefined && (selectedValue.amount === null || selectedValue.amount === undefined || selectedValue.amount === '' || selectedValue.amount === 0)) {
        this.toCurrenciesWarning = true;
        this.commonProv.setValInsideNestedObj(this.options.toAmountOptions.amountOptions.fcName, '', this.options.requestObject);
        this.toCurrenciesWarningText = this.omniPull.omniCommon.common.translate('please_enter_to_amount_key');
        return;
      }
      this.setExchangeAmount(selectedValue, 'to')
    }
  }

  setExchangeAmount(userInputAmount, fromTo) {
    if (userInputAmount?.amount != this.amountInToAccountAmount && this.options.fromAmountOptions?.currenciesOptions?.fcName && this.options.toAmountOptions?.currenciesOptions?.fcName) {
      this.toCurrenciesWarning = false;
      this.amountEntered = userInputAmount.amount;
      let exchangedAmountCompFcname = '';
      let userInputCurrCode = '';

      if (fromTo === 'from') {
        exchangedAmountCompFcname = this.options.toAmountOptions.amountOptions.fcName
        userInputCurrCode = this.options.fromAmountOptions.currencyObj.currencyCode
      } else {
        exchangedAmountCompFcname = this.options.fromAmountOptions.amountOptions.fcName;
        userInputCurrCode = this.options.toAmountOptions.currencyObj.currencyCode
      }
      if (this.options.fromAmountOptions.currencyCode && this.options.toAmountOptions.currencyCode && this.options.fromAmountOptions.currencyCode !== this.options.toAmountOptions.currencyCode) {
        //Added by ghada, AZDB-4500 for keeping track of which amount user entered to detrimine by which amount the exchange service will be called
        if (!this.options.requestObject['dataSaveId'] || (this.options.requestObject['dataSaveId'] && this.options.requestObject['userInputCurrency'] == userInputCurrCode)) {
          this.common.presentLoading();
          let paramData: IAmountFromToCurrencyRequest = {
            fromCurrency: this.options.fromAmountOptions.currencyObj || { currencyCode: this.options.requestObject[this.options.fromAmountOptions.currenciesOptions.fcName] },
            toCurrency: this.options.toAmountOptions.currencyObj || { currencyCode: this.options.requestObject[this.options.toAmountOptions.currenciesOptions.fcName] },
            operId: this.options.operId ? this.options.operId : null,
          };
          if (fromTo === 'from') {
            paramData.fromAmount = userInputAmount.amount;
            this.options.requestObject['userInputCurrency'] = paramData.fromCurrency.currencyCode;
          } else {
            paramData.toAmount = userInputAmount.amount;
            this.options.requestObject['userInputCurrency'] = paramData.toCurrency.currencyCode;
          }
          this.omniPull.returnAmountFromToCurrency(paramData).then((result) => {
            // result = { outputCode: 0, toAmount: paramData?.fromAmount * 1.5, exchangeRate: 1.5 }; // for testing in case no result from service
            if (result && result.outputCode == 0) {
              this.amountInToAccountAmount = (fromTo === 'from' ? result.toAmount : result.fromAmount).toString();
              this.exchangeRate = result.exchangeRate.toString();
              this.exchangeRateValueOptions.labelKey = result.exchangeRate.toString();
              this.options.group.controls[exchangedAmountCompFcname].setValue(this.amountInToAccountAmount);
            } else {
              this.options.group.controls[exchangedAmountCompFcname].setValue('');
            }
            this.common.dismissLoading();
          }).catch(err => {
            this.logger.error(err);
            this.common.dismissLoading();
          });
        }
      } else if (this.options.requestObject) {
        this.commonProv.setValInsideNestedObj(exchangedAmountCompFcname, userInputAmount?.amount, this.options.requestObject);
        this.options.requestObject['userInputCurrency'] = this.options.fromAmountOptions.currencyCode;
      }
    }
  }


  public fromCurrencyChange(event) {
    //this.options.fromAmountOptions.showLimitLabel = true; commented by Marina TP:#1583818 as it is handled in ps-complex-amount component to be true in case no value is passed in options and setting it here to true overrides any passed value
    if (event !== undefined && event.selectedObj !== undefined) {
      this.amountInToAccountAmount = '0.00';
      this.options.fromAmountOptions.currencyCode = event.selectedObj.currencyCode;
      this.options.fromAmountOptions.currency = event.selectedObj.description;
      this.options.fromAmountOptions.currencyObj = event.selectedObj;
      this.options.fromAmountOptions.amountOptions.decimalPoints = event.selectedObj.decimalPoint;
      this.options.fromCurrency = event;
      if (this.options.showToCurrencyOptions !== undefined && this.options.showToCurrencyOptions === true) {
        this.showToCurrency();
      }
      //Modified by ghada, AZDB-4500 for properly handling the case of both way exchange in case to do
      if ((this.options.toAmountOptions.currencyObj || this.options.requestObject[this.options.toAmountOptions.currenciesOptions.fcName]) && this.checkAmount(this.options.fromAmountOptions)) {
        const selectedVal = {
          amount: this.amountEntered
        };
        this.onamountInFromAccountChange(selectedVal);
      }
      this.onFromCurrencyChanged.emit(event);
    }
  }

  public toCurrencyChange(event) {
    this.options.toAmountOptions.showLimitLabel = false;
    this.toCurrenciesWarning = false;
    if (event !== undefined && event.selectedObj !== undefined) {
      this.amountInToAccountAmount = '0.00';
      this.options.toAmountOptions.currencyCode = event.selectedObj.currencyCode;
      this.options.toAmountOptions.currency = event.selectedObj.description;
      this.options.toAmountOptions.currencyObj = event.selectedObj;
      this.options.toAmountOptions.amountOptions.decimalPoints = event.selectedObj.decimalPoint;
      if (this.options.showToCurrencyOptions !== undefined && this.options.showToCurrencyOptions === true) {
        this.showToCurrency();
      }
      //Modified by ghada, AZDB-4500 for properly handling the case of both way exchange in case to do
      const userInputAmountOptions = this.options.requestObject['dataSaveId'] ? this.options.toAmountOptions : this.options.fromAmountOptions
      if ((this.options.fromAmountOptions.currencyObj || this.options.requestObject[this.options.fromAmountOptions.currenciesOptions.fcName]) && this.checkAmount(userInputAmountOptions)) {
        const selectedVal = {
          amount: this.amountEntered
        };
        if (this.options.requestObject['dataSaveId']) {
          this.onamountInToAccountChange(selectedVal);
        } else {
          this.onamountInFromAccountChange(selectedVal);
        }
      }
    }
  }

  public showToCurrency() {
    if (this.options.fromAmountOptions !== undefined && this.options.toAmountOptions !== undefined && this.options.fromAmountOptions.currencyCode !== undefined && this.options.toAmountOptions.currencyCode !== undefined && this.options.fromAmountOptions.currencyCode !== this.options.toAmountOptions.currencyCode) {
      this.options.editableMode = true;
    } else {
      this.options.editableMode = false;
    }
  }

  //Modified by ghada, AZDB-4500 for handling the case of both way exchange
  private checkAmount(amountOptions): boolean {
    if (amountOptions.amountOptions.group.controls[amountOptions.amountOptions.fcName].value > 0) {
      this.amountEntered = amountOptions.amountOptions.group.controls[amountOptions.amountOptions.fcName].value;
    }
    if (this.amountEntered !== null && this.amountEntered !== undefined && this.amountEntered !== '') {
      return true;
    } else {
      return false;
    }
  }

  reset() {
    this.fromCurrRef.reset();
    this.toCurrRef.reset();
  }

}
