    /**
     * @author RichardZourob
     * #BUG 1258583
     * function to validate on form fields upon clicking next button.
     * in customization mode it should always return true in order not to stop the user from moving to next step even if there's some fields invalid.
     */
    public static validateFields(params: any) {
        const commonService: PsCommonService = CommonUtils.injectionHandler(PsCommonService);
        const result = { valid: true, numberOfInvalidControls: params.numberOfInvalidControls };
        if (params.validateEachFormControl) {
            commonService.validateFormController(params.control);
        }
        if (params.control.invalid) {
            result.valid = false;
            result.numberOfInvalidControls = params.numberOfInvalidControls + 1;
            params.listOfInvalidControls.push(params.control);
            params.invalidControlsDomElementsArray.push(params.element);
            params.mapOfInvalidControls[params.fcName] = params.control;
        }
        return result;
    }

