    /**
     * @author Mustafa
     * #US 1338932
     * catch the values of the dynamic labels from customization
     * @param commonService : Common Service
     * @param customizationRequest : object that contains mappingID and all the items that contains this mappingID
     */
    public static async dynamicLabels(commonService: any, customizationRequest: any): Promise<any> {
        return new Promise(async (resolve, reject) => {
            const sessionVars = commonService.session.getFilteredSessionObject();
            Object.assign(customizationRequest.commonParametersList, sessionVars);
            const result: any = await commonService.commonServiceCallForCustomization(customizationRequest).catch((error) => {
                if (error.data.outputType === 'U') {
                    resolve({errorMsg: 'erro_loading_data_refresh_list_key'});
                } else {
                    reject(error.data.outputNotification);
                }
            });
            if (result && result.data && result.data.commonResponseList) {
                resolve({data: result.data.commonResponseList});
            } else {
                reject('erro_loading_data_refresh_list_key');
            }
        });
    }
