import { Component, ElementRef, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Subscription } from 'rxjs';
import { PsBaseFieldComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-base-field.component';
import { Events } from 'src/app/commonSRC/psServices/Event/event.service';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IOptionsPsActionIcon, IOptionsPsActionIconExposed, IOptionsPsActionImage, IOptionsPsContainerPanel, IPageCommon } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { CommonBussinessConstant } from '../../psServices/models/ps-common-bussiness-constant';
import { PsCommonBusinessSettings } from '../../psServices/models/ps-commonBusiness.settings';
import { OmniPullService } from '../../psServices/omni-common/omni-pull.service';
import { OmniPushService } from '../../psServices/omni-common/omni-push.service';
import { PsJbpmTaskListComponent } from '../ps-integration/JBPM/ps-jbpm-task-list/ps-jbpm-task-list.component';
import { IOptionsPsJBPMTaskListComponent } from '../ps-integration/JBPM/ps-jbpm-task-list/ps-jbpm-task-list.component.interfaces';
import { IOptionsPsDraftsReportComponent } from './ps-drafts-report.component.interfaces';


@Component({
  selector: 'ps-drafts-report',
  templateUrl: './ps-drafts-report.component.html',
  styleUrls: ['./ps-drafts-report.component.scss'],
})
export class PsDraftsReportComponent extends PsBaseFieldComponent implements OnInit, OnDestroy {

  @Input() options: IOptionsPsDraftsReportComponent;
  //Added By omar Talaat Bug:AZDB-1606 OnClick Function to invoke the subscribtion to open or hide the components that created
  @Output("parentFun") parentFun: EventEmitter<any> = new EventEmitter();


  reportArray: Array<any>;
  totalProgress;
  mandatoryControls: string[];
  isCorporateUser = false;
  filterCriteria: [];
  filterValue: [];
  deleteReportIcon: IOptionsPsActionIconExposed = {
    iconName: 'trash' // deleteReportIcon
  };
  infoButton: IOptionsPsActionIcon = {
    iconName: 'info',
    allowCust: true,
    labelOptions: {
      labelKey: 'info_key',
    }
  };
  filterByOper = true;

  availableTypes: any = {

  };
  refreshIconOptions: IOptionsPsActionIconExposed = {
    iconName: 'to-do-refresh-icon'
  };
  dataWasLoaded:boolean = false;
  actionImageOptions: IOptionsPsActionImage = {
    imageName: CommonUtils.getCssVariableValue('--ps-loader-image-name')
  };
  showpanel:boolean = false
  private eventSub: Subscription; // Added by Richie #TP 1105083 due to migration to ionic 5 since Event service was removed and replaced with observable
  private tasksSub: Subscription;
  draftPanelOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'saved_drafts_key',
    iconName: 'bookmark',
    expanded: true
  };
  pendingUpdatesOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'pending_updates_key',
    iconName: 'document',
    expanded: true
  };
  jbpmPanelOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'jbpm_task_list_key',
    iconName: 'document',
    expanded: true
  };

  pendingPanelOption: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'pending_task_list_key',
    iconName: 'document',
    expanded: true
  };
  pendingIsAvailable = false;
  draftIsAvailable = false;
  pendingUpdatesIsAvailable = false;

  jbpmOptions: IOptionsPsJBPMTaskListComponent = {
    initAndfetchDataOnLoad: false
  };
  @ViewChild('jbpm', { static: false }) jbpm: PsJbpmTaskListComponent;

  formGroup = new FormGroup({});
  constructor(public commonProv: PsCommonService, private navService: PsNavigatorService, loggerP: LoggerService
    , public hostElement: ElementRef, private omniPull: OmniPullService, private omniPush: OmniPushService,
    public events: Events) {
    super(commonProv, loggerP);
  }

  ngOnInit() {
    if (!this.options || this.options?.filterByOper == false) {
      this.filterByOper = false;
    }
    // modified by Richie #TP 1105083 due to migration to ionic 5 since Event service was removed and replaced with observable with one argument
    this.eventSub = this.events.subscribe('draft:refresh', () => {
      this.refreshReport();
    });
    if (PsCommonBusinessSettings.isCorporate) {
      this.isCorporateUser = true;
    }
    this.loadDrafts();
    // this.tasksSub = this.commonProv.taskApproved.subscribe((status)=>{
    //   if(status === true){
    //      this.refreshReport();
    //   }
    // }) this subscription removed because it is a  duplciate  with another subscription in the submit button so it causd duplicate record display IDBU240010 
  }
  goToInfoReport(data) {
    let infoReportData = { ...data };
    infoReportData.operId = CommonBussinessConstant.TRANSACTION_INFO_REPORT_OPER_ID;
    const page: IPageCommon = {
      operID: CommonBussinessConstant.TRANSACTION_INFO_REPORT_OPER_ID,
      title: 'info_report_key',
      icon: 'report',
      component: 'report-page',
      param: infoReportData
    };
    this.navService.openPage(page);
    //Added By omar Talaat Bug:AZDB-1606
    this.parentFun.emit();
  }
  loadDrafts() {
    this.totalProgress = 0;
    this.mandatoryControls = [];
    if (this.options && this.options.group) {
      Object.keys(this.options.group.controls).forEach(key => {
        const keyValidations = this.commonProv.getElementValidations(key);
        if (keyValidations.IS_MANDATORY === 1 && key !== 'formData') {
          this.mandatoryControls.push(key);
          this.totalProgress++;
        }
      });
    }
    this.refreshReport();
  }

  async refreshReport(event?) {
    event?.stopPropagation();
    if (!this.options?.openedFromLanding) {
      this.common.presentLoading()
    }
    this.reportArray = [];
    this.availableTypes = {};
    if(!this.options || !this.options.loadDataFor){
    await this.loadData('P');
    await this.loadData('DR');
    if (PsCommonBusinessSettings.isCorporate) {
      await this.loadData(ConstantCommon.PENDING_UPDATES_REPORT_STATUS);
    }
    await this.loadJBPM();
    if (this.reportArray && this.reportArray.length === 0 && (this.jbpm && this.jbpmOptions.taskList.length === 0)) {
      CommonUtils.presentInfoAlert(this.commonProv.translate('no_data_found_key'));
      this.onPsChange.emit(null);
    }
   }else{
    await this.loadData(this.options.loadDataFor);
   }
  }

  async loadData(statusParam: string) {
    const result = await this.omniPull.returnSubmitDataList({ filterByOper: this.filterByOper, status: statusParam });
    const data = result.gridModel;
    if (data.length > 0) {
      for (const report of data) {
        if (this.filterByOper) {
          let myProgress = 0;
          Object.keys(report.submitFieldValueMap).forEach((attributeName) => {
            if (this.mandatoryControls.indexOf(attributeName) !== -1) {
              const controlValidations = this.commonProv.getElementValidations(attributeName);
              if (controlValidations.IS_MANDATORY === 1) {
                myProgress++;
              }
            }
          });
          report.progress = myProgress / this.totalProgress;
          report.percentage = parseInt((myProgress / this.totalProgress) * 100 + '');
        }
        report.requestObject = {};
        this.commonProv.copyObject(report.requestObject, report.submitFieldValueMap, true, false);
        if (statusParam === 'P') {
          report.operName = this.getPageByOperId(report.operId) !== undefined ? this.getPageByOperId(report.operId).OPER_NAME : '';
          this.availableTypes.T = true; this.availableTypes.TS = true; this.availableTypes.TP = true;
          this.pendingIsAvailable = true;
        } else if (statusParam === 'DR') {
          this.draftIsAvailable = true;
        }
        report.avatar = {
          value: report.status
        };
        report.pendingDeletion = false;
        this.availableTypes[statusParam] = true;
        //check if status of pending for updates
        if (statusParam === ConstantCommon.PENDING_UPDATES_REPORT_STATUS) {
          //check if maker
          if (this.isMaker(report.operId)) {
            this.reportArray.push(report);
          }
        } else {
          this.reportArray.push(report);
        }
      }
      if (statusParam === ConstantCommon.PENDING_UPDATES_REPORT_STATUS
        && this.reportArray.filter(c => c.status == ConstantCommon.PENDING_UPDATES_REPORT_STATUS).length > 0) {
        this.pendingUpdatesIsAvailable = true;
      }
      this.common.dismissLoading();
    }
    if(this.options && this.options.loadDataFor){
     this.dataWasLoaded = true
    }
  }
  isMaker(operId) {
    return this.commonProv.getPageByOperId(operId)?.MAKER_YN === 'Y' ? true : false;
  }
  getPageByOperId(operId) {
    return this.common.getPageByOperId(operId);
  }

  selectReport(report, event: Event) {
    event.preventDefault();
    event.stopPropagation();
    if(this.options && this.options.loadDataFor){
      CommonUtils.dismissAllModals()
    }
    if (this.options && this.options.stepper) {
      this.options.stepper.selectedIndex = 0;
      this.options.stepper.steps.toArray()[0].select();
      this.events.publish('draft:widgets', report.requestObject);
    }
    if (this.options == undefined || this.options?.filterByOper == false) {
      const activePage = this.getPageByOperId(report.operId);
      if (activePage != undefined) {
        report.requestObject.dataSaveId = report.dataSaveId;
        report.requestObject.status = report.status;
        report.requestObject.sendBackReason = report.sendBackReason;
        report.requestObject.operId = report.operId;
        report.requestObject.makerOcUserID = report.ocUserId;
        if (PsCommonSettings.oper_ID == activePage.OPER_ID) {
          this.events.publish('draft:navigation', report.requestObject);
          this.common.dismissLoading();
        } else {
          this.navService.openPage({

            operID: activePage.TYPE_DESC == "REP" ? CommonBussinessConstant.TODO_APPROVE_REJECT_OPER_ID : activePage.OPER_ID,
            title: report.status == "T" ? "pending_deletion_key" : '',
            param: activePage.PARAM ? {
              ...JSON.parse(activePage.PARAM), //added by Marina TP:#1542363 for operation params to be available in the opened report page
              ScreenVO: report.requestObject,
              //readOnlypage: activePage.MAKER_YN !== 'Y' && activePage.CHECKER_YN === 'Y'
            } :
              {
                ScreenVO: report.requestObject,
                //readOnlypage: activePage.MAKER_YN !== 'Y' && activePage.CHECKER_YN === 'Y'
              }
          });

        }
      } else {
        CommonUtils.presentFailureAlert(this.commonProv.translate('access_denied_key'), { autoHide: true });
      }

    } else {
      this.commonProv.copyObject(this.options.requestObject, report.requestObject, true, false);
      this.options.requestObject.dataSaveId = report.dataSaveId;
    }
    this.onPsChange.emit(report);
    if (this.options && this.options.requestObject && this.options.requestObject[ConstantCommon.LAST_ACTIVE_STEP]) {
      setTimeout(() => {
        this.commonProv.navigateStepperToIndex(this.options.stepper, parseInt(this.options.requestObject[ConstantCommon.LAST_ACTIVE_STEP]), this.options.group, this.options.requestObject);
      }, 100)
    }
    //Added By omar Talaat Bug:AZDB-1606
    this.parentFun.emit();
  }


  deleteReport(report, event: Event, reportHtmlId) {
    event.preventDefault();
    event.stopPropagation();
    if (report && report.dataSaveId) {
      report.pendingDeletion = true;
      CommonUtils.presentInfoAlert(this.commonProv.translate('are_you_sure_you_want_to_delete_this_item_key'), {
        buttonsArray: [{
          group: this.formGroup,
          labelKey: 'ok_key',
          handler: () => {
            CommonUtils.dismissAllModals();
            this.omniPush.deleteSubmitData({ dataSaveId: report.dataSaveId, actionType: 'saveDelete' }).then((result) => {
              const index = this.reportArray.findIndex(reportDetail => reportDetail.dataSaveId === report.dataSaveId);

              if (index >= 0) {
                document.getElementById(reportHtmlId).classList.remove('draftReportInAnimation');
                document.getElementById(reportHtmlId).classList.add('draftReportOutAnimation');
                const animationDuration = parseFloat(CommonUtils.getElementStyle(document.getElementById(reportHtmlId), 'animationDuration')) * 1000;
                setTimeout(() => {
                  this.reportArray.splice(index, 1);
                }, animationDuration);
              }
              if (this.reportArray.length === 0) {
                this.onPsChange.emit(null);
              }
            }).catch((error) => {
              this.logger.error(error);
              report.pendingDeletion = false;
            });

          }
        }, {
          group: this.formGroup,
          type: 'reset',
          labelKey: 'cancel_key',
          handler: () => {
            CommonUtils.dismissAllModals();
            report.pendingDeletion = false;
          }
        },]
      });

      event.stopPropagation();
      event.preventDefault();
    }
  }

  ngOnDestroy() {
    // modified by Richie #TP 1105083
    if (this.eventSub) {
      this.eventSub.unsubscribe();
    }
    if (this.tasksSub) {
      this.tasksSub.unsubscribe();
    }
  }


  async loadJBPM() {
    await this.jbpm.init(true);
  }
}
