import { Component, ElementRef, Input, OnInit } from '@angular/core';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IFileFormat, IOmniCommonFileRequest, IOmniSendReportEmailRequest, IOptionsActionSheet, IOptionsPsActionButton, IOptionsPsButtonSendEmail } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { PsActionButtonComponent } from '../ps-action-button.component';



@Component({
  selector: 'ps-button-send-email',
  templateUrl: './ps-button-send-email.component.html',
  styleUrls: ['./ps-button-send-email.component.scss'],
})
export class PsButtonSendEmailComponent extends PsActionButtonComponent implements OnInit {
  @Input() options: IOptionsPsButtonSendEmail;
  sendOptions: IOptionsPsActionButton;
  cifInfo: any;
  mailSubject: string;
  toMailId: string;
  attachmentReport: any;
  reportdata: any;
  constructor(private commonService: PsCommonService, elRef?: ElementRef) {
    super(commonService, commonService.logger, elRef);
  }

  ngOnInit() {
    this.sendOptions = {
      labelKey: this.options.labelKey ? this.options.labelKey : 'send_to_self_key',
      type: 'button',
      iconName: 'button-send-mail',
      psClass: 'ps-button-export',
      group: this.options.group,

    };
    this.cifInfo = this.commonService.session.getValueOf(ConstantCommon.USERINFO);
  }
  onSendClicked(event) {
    const actionSheetOptions: IOptionsActionSheet = {
      header: this.common.translate('select_an_option_key'),
      buttons: [{
        text: this.common.translate('pdf_key'),
        handler: () => {
          this.sendToSelfEmail(IFileFormat.PDF);
        }
      },
      {
        text: this.common.translate('excel_key'),
        handler: () => {
          this.sendToSelfEmail(IFileFormat.EXCEL);
        }
      },
      {
        text: this.common.translate('cancel_key'),
        role: 'cancel'
      }]
    };
    this.commonService.presentActionSheet(actionSheetOptions);
  }

  private async sendToSelfEmail(fileFormat) {
    const reportRequest: IOmniCommonFileRequest = {
      reportFormat: fileFormat,
      reportType: ConstantCommon.EXPORT_REPORT_TYPE,
      reportParametersList: this.options.reportParametersList
    };
    this.common.presentLoading();
    this.common.returnReportDetails(reportRequest).then(result => {
      if (result && result['data']['base64Data']) {
        this.common.dismissLoading();
        const fileEx = result['data']['reportFormat'] === IFileFormat.PDF ? 'PDF' : 'XLS';
        this.sendReportViaEmail(fileEx, result['data']['base64Data'])
      }
    }).catch((error) => {
      this.common.dismissLoading();
      this.common.logger.error('Error: in returnReportDetails() in ps-button-send-to-email');
      this.common.logger.error(error);
    });

  }

  async sendReportViaEmail(fileExtention, base64Data) {
    let fromDate;
    let toDate;
    if (this.options.fromDate != undefined && this.options.todate != undefined) {
      fromDate = this.options.fromDate.toDateString();
      toDate = this.options.todate.toDateString();
    }
    let nameDetails = [CommonUtils.translate(this.common.getPageByOperId(PsCommonSettings.oper_ID).OPER_NAME),
    new Date().toDateString(),
    (fromDate ? CommonUtils.translate('from_key') + ': ' + fromDate + ' ' + CommonUtils.translate('to_key') + ': ' + toDate : undefined)
    ]

    const params: IOmniSendReportEmailRequest = {
      name: nameDetails.filter(x => x !== undefined).join(' - '),
      format: fileExtention,
      content: base64Data
    };
    const reportRequest: IOmniCommonFileRequest = {
      reportParametersList: params
    };
    const sendToSelfEmail = await this.common.sendReportViaEmail(reportRequest).catch(error => {
      this.logger.error('Error: While calling in ps-button-send-email : ', error);
    });
  }

}
