import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IbusinessProfilesCO, IOperDef } from 'src/app/pages/omni-login/omni-login.interfaces';

/**
 * @author Malak 
 * business common functions
 */
export class OmniCommonUtils {

    constructor(/** no object should be injected here this a global classs and should not depend on any obejct from angular injector */) {
        throw new Error('Error: Instantiation failed: Use CommonUtils.methodName(...) instead of new.');
    }
   
	//TP 1328141
	// used to generate business profile map
    public static prepareBusinessProfileMap(list: IbusinessProfilesCO[],currentBusinessProfile: IOperDef[]){
        for (let i = 0; i < list.length; i++) {
            const row = list[i];
            const busProfile = row.operVO;
            CommonUtils.copyObject(busProfile, row.operAppChnlReportVO, false, false);
            CommonUtils.copyObject(busProfile, row.businessProfileOperVO, false, false);
            CommonUtils.copyObject(busProfile, row.operAppChnlVO, false, false);
            currentBusinessProfile.push(busProfile);
            if (row.children) {
                this.prepareBusinessProfileMap(row.children,currentBusinessProfile);
            }
        }
        return currentBusinessProfile;
    }
}

