    public static async dismissAllModals(data?: any, role?: string, id?: string) {
        const modalController: ModalController = CommonUtils.injectionHandler(ModalController);
        //Added by Richie to have proper removal of the corresponding modal if the its id was provided and not remove the first modal it selects.
        let modalQuerySelector = 'ion-modal';
        if (id) {
            modalQuerySelector = modalQuerySelector + '[id="' + id + '"]';
        }
        //end Richie
        let modals = document.querySelector(modalQuerySelector);
        let counter = 10;//max 10 to prevent infinit loop if any
        while (modals && counter > 0) {
            counter = counter - 1;
            await modalController.dismiss(data, role, id);
            modals = document.querySelector(modalQuerySelector);
        }
        return true;
    }


    static async presentPopUpMessage(options: IOptionsAlert) {

        const modalController: ModalController = CommonUtils.injectionHandler(ModalController);
        // modalController.dismiss(null, null, options.id);
        window.dispatchEvent(new Event('forceCloseDropdowns'));//Makki - AZDB #10200 - Broadcast a universal event to close all dropdowns before displaying a modal.
        this.dismissAllModals(null, null, options.id);
        options.displayImageOrIcon = options.displayImageOrIcon != null && options.displayImageOrIcon != undefined ? options.displayImageOrIcon : true;
        options.autoHide = options.autoHide != null && options.autoHide != undefined ? options.autoHide : false;
        const modal = await modalController.create({
            component: PsTemplatePopupMessagePage,
            cssClass: 'alert-modal',
            componentProps: { alertOptions: options },
            backdropDismiss: false,
            animated: false,
            id: options.id
        });
        return await modal.present();
    }

    static async presentWidgetModal(options: IoptionsPsContainerWidgetCard) {
        const modalController: ModalController = CommonUtils.injectionHandler(ModalController);
        const modalId= 'dashboard-widget-container-modal'
        this.dismissAllModals(null, null, modalId);
        options = {
            ...options,
            isModal:true
        }
        const modal = await modalController.create({
            component: PsContainerWidgetCardComponent,
            cssClass: 'alert-modal',
            componentProps: { options: options },
            backdropDismiss: false,
            animated: false,
            id: modalId
        });
        return await modal.present();
    }



