    //Modified by ghada, AZDB-151 for fixing the issue of presented loader being dismissed by timeout even though it's not the same loader that fires the timeout
    public static dismissLoading(id?: string | number, fromTimeout?) {
        document.getElementsByTagName('html')[0].classList.remove('open-loader');
        if (id) {
            if (this.activeSpinnerId && (this.activeSpinnerId === id || fromTimeout)) {
                return CommonUtils.dismissLoadingCtrl();
            }
        } else {
            return CommonUtils.dismissLoadingCtrl();
        }

    }


