    /**
     * Function to validate the date object if it is a valid date or it gives (invalid date)
     * #BUG 1192538
     * @author RichardZourob
     * @param date a date object
     * @returns true in case the date was valid and false in case it wasnt valid
     */
    public static isValidDate(date) {
        if (this.isDateObject(date)) {
            // it is a date
            if (isNaN(date.getTime())) {
                // date is not valid
                return false;
            } else {
                // date is valid
                return true;
            }
        } else {
            // not a date
            return false;
        }
    }

    /**
     * function to compare two date objects
     * #BUG 1147159
     * @author RichardZourob
     * @param date1 first date to compare
     * @param date2 second date to compare
     * @param excludeTime if sent true, then exclude the time from dates to compare. for example if date1 & date2 are on same day but of different time
     * @returns 0 if date1 = date2, -1 if date1 < date2, 1 if date1 > date2
     */
    public static compareDates(date1: Date, date2: Date, excludeTime?: boolean): number {
        const d1 = date1;
        const d2 = date2;
        if (excludeTime) {
            d1.setHours(0, 0, 0, 0);
            d2.setHours(0, 0, 0, 0);
        }
        const d1Time = d1.getTime();
        const d2Time = d2.getTime();
        return (d1Time < d2Time ? -1 : (d1Time === d2Time ? 0 : 1));
    }

    public static executeWithTimeOut(methodToBeExecutedAfterTimeOut: IFunctionDefinition, startTime, msg): NodeJS.Timeout {
        if (startTime !== undefined) {
            return this.showToast(methodToBeExecutedAfterTimeOut, startTime, msg);
        }
    }

    public static showToast(methodToBeExecutedAfterTimeOut: IFunctionDefinition, startTime, message): NodeJS.Timeout {
        const toastController = CommonUtils.injectionHandler(ToastController);
        let interval = setInterval(() => {
            if (startTime <= 0 || this.stopTimer) {
                if (this.toastInterval) {
                    let index = this.toastInterval.indexOf(interval);
                    clearInterval(interval);
                    if (index >= 0) {
                        this.toastInterval.splice(index, 1);
                    }
                }
                this.stopTimer = false;
                toastController.dismiss();
                if (methodToBeExecutedAfterTimeOut) {
                    const params = this.isEmptyObject(methodToBeExecutedAfterTimeOut.params) ? [{}] : methodToBeExecutedAfterTimeOut.params;
                    methodToBeExecutedAfterTimeOut.func(...params);
                }
            } else {
                startTime -= 1;

                toastController.dismiss();
                const toast = toastController.create({
                    message: message + ' ' + startTime + ' ' + CommonUtils.translate('seconds_key'),
                    duration: 1000,
                    position: 'top'
                });

                toast.then((result) => {
                    result.present();
                });
            }
        }, 1000);
        this.toastInterval.push(interval);
        return interval;
    }

    public static dismissAllToast(interval?: NodeJS.Timeout) {
        if (interval) {
            let index = this.toastInterval.indexOf(interval);
            clearInterval(interval);
            if (index >= 0) {
                this.toastInterval.splice(index, 1);
            }
        } else
            if (this.toastInterval) {
                for (let index = 0; index < this.toastInterval.length; index++) {
                    const element = this.toastInterval[index];
                    clearInterval(element);
                    this.toastInterval.splice(index, 1);
                }
            }
        const toastController = CommonUtils.injectionHandler(ToastController);
        toastController.dismiss();
    }

    public static createBPMParams(mainParams, screenVO, session, type: 'in' | 'out'): any {
        const newParams = {};
        Object.keys(mainParams).forEach((key) => {
            const MAP_VALUE = mainParams[key].MAP_VALUE;
            const MAP_TYPE = JSON.parse(mainParams[key].MAP_TYPE);
            const ARG_NAME = mainParams[key].ARG_NAME;
            let value;
            if (ARG_NAME && MAP_VALUE && MAP_TYPE) {
                if (MAP_TYPE == 1) { // screen VO
                    if (type == 'out') {
                        if (screenVO) {
                            value = screenVO.commonParametersList && screenVO.commonParametersList[MAP_VALUE] ? screenVO.commonParametersList[MAP_VALUE] : screenVO[MAP_VALUE];
                        }
                    } else {
                        value = MAP_VALUE;
                    }

                }

                if (MAP_TYPE == 2) { // Session
                    value = session.getValueOf(MAP_VALUE);
                }

                if (MAP_TYPE == 3) { // Constant
                    value = MAP_VALUE;
                }

                if (CommonUtils.isNotNull(value)) {
                    if (value) {
                        try {
                            value = JSON.parse(value);
                        } catch (err) {
                            value = value;
                        }
                    }
                    newParams[ARG_NAME] = value;
                }
            }
        });
        return newParams;
    }

    public static maskInput(str: string) {
        let masked = '';
        const iterations: number = Math.floor(Math.random() * 3) + 5;
        for (let i = 0; i < iterations; i++, masked += String.fromCharCode(9679)) { }
        str = str[0] + masked + str[str.length - 1];
        return str;
    }

    public static isMaskedInput(str: string) {
        //the masked input should contains at least 3 characters : first character , mask charachter String.fromCharCode(9679) , and last character
        if (str && str.length >= 3) {
            let maskCharacter = String.fromCharCode(9679);
            for (let i = 1; i <= str.length - 2; i++) {
                if (str.charAt(i) != maskCharacter) {
                    return false;
                }
            }
            return true;

        }
        else {
            return false;
        }
    }

    //Edited by ghada for tp#1304247 for adding region code to implement regions localizations
    public static loadMapScript(mapWebApiKey, regionIsoCode?) {
        return new Promise((resolve, reject) => {
            const activeLanguage = String(PsCommonSettings.activeLanguge).toLocaleLowerCase();
            if (!this.previousLanguage) {
                this.previousLanguage = activeLanguage;
            }
            try {
                if (activeLanguage !== this.previousLanguage && typeof google !== 'undefined' && google.maps) {
                    // delete google.maps; commented by ghada, AZDB-6475 for fixing the issue of accessing undefined object on reinitilizing the map on language change
                    this.previousLanguage = activeLanguage;
                }
            } catch (error) {
                reject(error);
            }
            if (!mapWebApiKey) {
                reject(
                    {
                        display: true,
                        message: CommonUtils.translate('no_api_key_key')
                    });
            }
            regionIsoCode = regionIsoCode ? '&&region=' + regionIsoCode : "";
            CommonUtils.addJSScript('https://maps.googleapis.com/maps/api/js?key='
                + mapWebApiKey + regionIsoCode + '&&language=' + activeLanguage + '&&libraries=geometry',
                { id: ConstantCommon.GOOGLE_MAPS_WEB_SCRIPT_ID, async: true, checkSRCIfPresent: true, removeIDIfPresent: true }).then(() => resolve(null));
        });
    }

