    /**
     * @author RichardZourob
     * #TP 1210739
     * function to extract the names of the properties inside the selectedObj of the options of the dropdowns.
     * in other words, it will return the structure of the extra details found under the selected option of a certain field as nested string properties.
     * so that we can use those in service mapping & conditional customization.
     */
    public static extractPropertyNames(obj: IPsSelect, prefix = ''): string[] {
        if (obj) {
            return Object.keys(obj).reduce((res, el) => {
                if (Array.isArray(obj[el])) {
                    return res;
                } else if (typeof obj[el] === 'object' && obj[el] !== null) {
                    return [...res, ...this.extractPropertyNames(obj[el], prefix + el + '.')];
                } else {
                    return [...res, prefix + el];
                }
            }, []);
        }
    }

    public static checkScreenFieldsDependency(expression: string) {
        let listOfIndexes = [];
        let listOfDependentFcNames = [];
        let indexOccurence = expression.indexOf(ConstantCommon.SCREEN_VAR_PREFIX);
        while (indexOccurence > -1) {
            listOfIndexes.push(indexOccurence);
            indexOccurence = expression.indexOf(ConstantCommon.SCREEN_VAR_PREFIX, indexOccurence + 1);
        }
        if (listOfIndexes.length > 0) {
            for (let eachIndex of listOfIndexes) {
                const paranthesisIndex = expression.indexOf(ConstantCommon.RIGHT_PARANTHESIS, eachIndex);
                // Added by Richie for #BUG 1407243; for case of mapping on a variable inside the selectedObj of a dropdown
                const exprVariable = expression.substring(eachIndex + 7, paranthesisIndex);
                const exprVariableArr = exprVariable.split(".");
                if (exprVariableArr.length > 1) {
                    listOfDependentFcNames.push(exprVariableArr[0]);
                } else {
                    listOfDependentFcNames.push(exprVariable);
                }
                // End Richie
            }
        }
        return listOfDependentFcNames;
    }

    public static toIsoString(date) {
        var tzo = -date.getTimezoneOffset(),
            dif = tzo >= 0 ? '+' : '-',
            pad = function (num) {
                var norm = Math.floor(Math.abs(num));
                return (norm < 10 ? '0' : '') + norm;
            };

        return date.getFullYear() +
            '-' + pad(date.getMonth() + 1) +
            '-' + pad(date.getDate()) +
            'T' + pad(date.getHours()) +
            ':' + pad(date.getMinutes()) +
            ':' + pad(date.getSeconds()) +
            dif + pad(tzo / 60) +
            ':' + pad(tzo % 60);
    }

    //return 1 if date1 time greater than date2, -1 if less, 0 if equals
    public static compareTime(datetime1, datetime2) {
        if (!CommonUtils.isDateObject(datetime1)) {
            datetime1 = new Date(datetime1);
        }
        if (!CommonUtils.isDateObject(datetime2)) {
            datetime2 = new Date(datetime2);
        }
        if (datetime1.getHours() > datetime2.getHours()) {
            return 1;
        } else if (datetime1.getHours() == datetime2.getHours()) {
            if (datetime1.getMinutes() > datetime2.getMinutes()) {
                return 1;
            } else if (datetime1.getMinutes() == datetime2.getMinutes()) {
                return 0;
            } return -1;
        }
        return -1;

    }

