    /**
     * checks the current requestTimeout of either URL or Oper
     * @param url 
     * @param theParams 
     * @param isSubmit 
     */
    public static getCurrentRequestTimeOut(url, theParams, isSubmit?: boolean) {
        let timeOut = PsCommonSettings.requestTimeOut;
        const CUSTOM_TIMEOUT_SERVICE_LIST = PsCommonSettings.CUSTOM_TIMEOUT_SERVICE_LIST;
        const CUSTOM_TIMEOUT_OPER_LIST = PsCommonSettings.CUSTOM_TIMEOUT_OPER_LIST;

        if (CUSTOM_TIMEOUT_SERVICE_LIST || CUSTOM_TIMEOUT_OPER_LIST) {
            if (CUSTOM_TIMEOUT_OPER_LIST[PsCommonSettings.oper_ID] && ((theParams && theParams[PsCommonSettings.SUBMIT_REQUEST_FLOW]) || isSubmit)) {
                timeOut = CUSTOM_TIMEOUT_OPER_LIST[PsCommonSettings.oper_ID];
                theParams[PsCommonSettings.CUSTOM_TIMEOUT_REQUEST] = true;
            }

            if (CUSTOM_TIMEOUT_SERVICE_LIST[url]) {
                timeOut = CUSTOM_TIMEOUT_SERVICE_LIST[url];
                theParams[PsCommonSettings.CUSTOM_TIMEOUT_REQUEST] = true;
            }
        }
        PsCommonSettings.currentRequestTimeOut = timeOut;
        return timeOut;
    }

