    /**
     * function that deletes a certain property from a JSON object and can be in a nested format.
     * 
     * @author RichardZourob
     * @param obj JSON object that you want to delete the property from
     * @param propertyName the name of the property you want to delete from the object and can be nested for example myObject.myProperty
     * @returns the modified object without the propertyName
     */
    public static deletePropFromObj(obj: any, propertyName: string) {
        if (obj) {
            const index = propertyName.indexOf('.');
            if (index === -1) {
                delete obj[propertyName];
            } else {
                const innerProp = propertyName.substring(index + 1);
                const outerProp = propertyName.substring(0, index);
                return this.deletePropFromObj(obj[outerProp], innerProp);
            }
        }
        return obj;
    }



