    public static cacheParameterIfNotCached<T>(parameter, session, options?: { value?: any, class?: any, methodToCall?: (...param: any) => any, params?: any }) {
        if (parameter) {
            if (PsCommonSettings.cachedParameters[parameter]) {
                return PsCommonSettings.cachedParameters[parameter];
            }
            if (session) {
                PsCommonSettings.cachedParameters[parameter] = session.getValueOf(parameter);
            } else
                if (options.value) {
                    PsCommonSettings.cachedParameters[parameter] = options.value;
                } else
                    if (options.methodToCall) {
                        const params = options.params ? options.params : [{}];
                        const value = options.methodToCall(...params);
                        PsCommonSettings.cachedParameters[parameter] = value;
                    }

            return PsCommonSettings.cachedParameters[parameter];
        }
    }

