    /**
   * function that is used to check for any attribute with indexed name and replace them with one attribute from the specified rowindex.
   * for example:
   * let result = {
                    "vo": {
                      "firstName-1": "F1",
                      "firstName-2": "F2",
                      "firstName-3": "F3",
                      "lastName-1": "L1",
                      "lastName-2": "L2",
                      "lastName-3": "L3"
                    },
                    "test": "x"
                  };
    this.removeIndexedAttributes(result, 2);

    console.log(result);

    this will print the below JSON object:
    {
      "vo": {
          "firstName": "F2",
          "lastName": "L2"
      },
      "test": "x"
    }

    * @param obj the JSON object that we need to search for indexted attributes in
    * @param rowIndex the rowindex that we will use to replace the data from and remove all other attributes of different indexes
    * @author RichardZourob
    */
    static removeIndexedAttributes(obj: Object, rowIndex: number) {
        if (obj) {
            Object.keys(obj).forEach((propName) => {
                const propNameParts = propName.split("-");
                if (propNameParts.length > 1) {
                    if (propNameParts[1] === rowIndex.toString()) {
                        obj[propNameParts[0]] = obj[propName];
                    }
                    delete obj[propName];
                } else if (typeof obj[propName] === "object") {
                    this.removeIndexedAttributes(obj[propName], rowIndex);
                }
            });
        }
    }

    /**
     * a function that checks if the field name has a rowIndex then return the field name without the rowIndex IFF the rowIndex parameter was sent. 
     * otherwise return the name as is.
     * @param fcName a name that might contain index or not for example balance-1 or balance
     * @param rowIndex a row index if sent and the field name has an index then we return the name without the index.
     * @author RichardZourob
     */
    static returnNameWithoutIndex(fcName: string, rowIndex: number): string {
        if (rowIndex || rowIndex === 0) {
            return fcName.split("-")[0];
        } else {
            return fcName;
        }
    }

    static objectsAreEqual(object1: Object, object2: Object): boolean {
        if (this.isObject(object1) && this.isObject(object2)) {
            const objectKeys1 = Object.keys(object1);
            const objectKeys2 = Object.keys(object2);
            if (objectKeys1.length !== objectKeys2.length) {
                return false;
            }
            for (const key of objectKeys1) {
                const value1 = object1[key];
                const value2 = object2[key];
                const isbothAreObjects = this.isObject(value1) && this.isObject(value2);
                if ((isbothAreObjects && !this.objectsAreEqual(value1, value2)) || (!isbothAreObjects && value1 !== value2)) {
                    return false;
                }
            }
            return true;
        } else if (this.isEmptyObject(object1) && this.isEmptyObject(object2)) {
            return true;
        }
        else {
            return false;
        }
    }

    /**
     * @author Mustafa
     * #US 1338932
     * check each element of the object if existing in the namesList after removing the rowIndex
     * @param object
     * @param namesList
    */
    public static generateNamesList(object: {}, namesList: {}) {
        let newNamesList = { ...namesList };
        Object.keys(object).forEach((element) => {
            let name = '';
            const index = element.lastIndexOf('-');
            if (index !== -1) {
                name = element.slice(0, index);
            } else {
                name = element;
            }
            if (!namesList.hasOwnProperty(name) && name !== 'formData') {
                newNamesList[element] = "";
            }
        });
        return newNamesList;
    }

    /**
     * @author Mustafa
     * #bug https://azentio.atlassian.net/browse/AZDB-151
     * add suffix string to the each key of an object
     * @param obj
     * @param suffix
    */
    public static addSuffixToKeys(obj, suffix) {
        const newObj = {};
        for (const key in obj) {
            if (obj.hasOwnProperty(key)) {
                newObj[key + suffix] = obj[key];
            }
        }
        return newObj;
    }
