import { Injectable } from '@angular/core';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { ErrorHandlerService } from 'src/app/commonSRC/psServices/errorhandler/errorhandler.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IOmniCommonCO } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { SessionService } from 'src/app/commonSRC/psServices/session/session.service';
import { CommonUtils } from './../../../commonSRC/psServices/models/common-utils';
import { PsCommonService } from './../../../commonSRC/psServices/ps-common/ps-common.service';
import { IOmniCommonServiceResponse } from './../../../pages/omni-base/omni-base.interfaces';
import { ILoginRequest, ILoginResponse } from './../../../pages/omni-login/omni-login.interfaces';
import { OmniCommonService } from './omni-common.service';


@Injectable({
  providedIn: 'root'
})
export class OmniPushService {

  constructor(public commonService: PsCommonService, private session: SessionService,
    private ommniCommon: OmniCommonService, private errorHandlerService: ErrorHandlerService) { }

  login(loginRequest: ILoginRequest): Promise<ILoginResponse> {
    return new Promise<ILoginResponse>((resolve) => {
      this.commonService.presentLoading();
      this.commonService.http.commonRequestAjax(PsCommonBusinessSettings.serviceUrl.login, loginRequest).then(result => {
        if (result.data && result.data.omniUserVO) {
          if (result.data.appId === 4) {
            PsCommonBusinessSettings.isAgent = true;
          }
          this.session.append(ConstantCommon.USERINFO, result.data);

          //check if user return default theme and apply 
          let preLoginResponse = this.session.getValueOf(ConstantCommon.preLoginResponse);
          if (result.data.omniUserVO && result.data.omniUserVO[ConstantCommon.THEME_CODE] && preLoginResponse[ConstantCommon.ALLOWED_THEMES]) {
            localStorage.setItem('defaultTheme', result.data.omniUserVO[ConstantCommon.THEME_CODE]);
            this.commonService.applyChoosenTheme(result.data.omniUserVO[ConstantCommon.THEME_CODE], preLoginResponse[ConstantCommon.ALLOWED_THEMES])
          }
          this.ommniCommon.initializeLogin(result.data);
          let encryptedUserName = this.commonService.returnEncryptedValue(result.data.omniUserVO.NAME);
          this.errorHandlerService.initializeLogLevelByUser(encryptedUserName, result.data.omniUserVO.LOG_LEVEL);
          /* FIX issue #1120252
          if (this.ommniCommon.common.isNativeMobile()) {
            this.ommniCommon.common.notificationService.createNotificationDeviceToken();
          }*/
          resolve(result.success);
        }
        else {
          CommonUtils.presentFailureAlert("Something went wrong", { autoHide: true });
        }
      }).catch(error => this.commonService.logger.log(error))
        .finally(() => this.commonService.dismissLoading());

    });
  }

  deleteSubmitData(request: any): Promise<IOmniCommonCO> {
    return new Promise<IOmniCommonCO>((resolve) => {
      this.commonService.http.commonRequestAjax(PsCommonBusinessSettings.serviceUrl.deleteSubmitData, request).then(result => {
        resolve(result);
      }).catch(error => this.commonService.logger.log(error));
    });
  }

  redeemPoints(request): Promise<IOmniCommonServiceResponse> {
    return new Promise<IOmniCommonServiceResponse>((resolve, reject) => {
      this.commonService.http.commonRequestAjax(PsCommonBusinessSettings.serviceUrl.redeemPoints, request).then(result => {
        if (result && result.data) {
          resolve(result);
        } else {
          reject(result);
        }
      }).catch(error => {
        reject(error);
        this.commonService.logger.log(error);
      });
    });
  }
}
