/**
 * @author khaledaltimany
 * all request made from the application will be intercepted here and then
 * the header will be updated as necesary to includxe token or any other things
 */
import { HttpErrorResponse, HttpHandler, HttpHeaderResponse, HttpInterceptor, HttpProgressEvent, HttpRequest, HttpResponse, HttpSentEvent, HttpUserEvent } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { HTTP } from '@ionic-native/http/ngx';
import { from, Observable, of, throwError } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';
import { CommonCustUtils } from 'src/app/commonSRC/customization/psServices/util/common-cust-utils';
import { PsSecurityService } from 'src/app/commonSRC/psServices/checkApp/ps-security.service';
import { PsApplicationSettings } from 'src/app/commonSRC/psServices/models/ps-app-settings';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsRequestCacheService } from 'src/app/commonSRC/psServices/ps-request-cache/ps-request-cache.service';
import { IOptionsPsComplexAlertController } from 'src/app/commonSRC/psTemplates/ps-template-alert-controller/ps-template-alert-controller.template.interfaces';
import { CheckAppService } from '../../../commonSRC/psServices/checkApp/checkApp.service';
import { HybridKeyService } from '../../../commonSRC/psServices/hybridkey/hybridkey.service';
import { ConstantCommon } from '../../../commonSRC/psServices/models/common-constant';
import { CommonUtils } from '../../../commonSRC/psServices/models/common-utils';
import { IKeyGeneratorResult } from '../../../commonSRC/psServices/models/hybridkey-interface';
import { CommonBussinessConstant } from '../models/ps-common-bussiness-constant';
import { OmniCommonService } from '../omni-common/omni-common.service';


/* nabil feghali - OMNI common security  */
@Injectable({
    providedIn: 'root'
})
export class InterceptorService implements HttpInterceptor {

    wasSSLPinned = false;

    constructor(private omniCommonService: OmniCommonService,
        private hybridkey: HybridKeyService,
        private cache: PsRequestCacheService,
        private checkAppService: CheckAppService,
        private nativeHttp: HTTP,
        private psSecurity: PsSecurityService) { }


    addToken(req: HttpRequest<any>): { plainRequest: any, request: HttpRequest<any> } {
        const timestamp = req.body && req.body["URequestID"] ? req.body["URequestID"] : '';
        const USER_IS_LOGGED_IN = PsCommonSettings.isLoggedIn;
        const sessionInstance = this.omniCommonService.common.session;
        const udid = CommonUtils.cacheParameterIfNotCached(ConstantCommon.UUID, sessionInstance);
        const authenticationTypesVal = this.omniCommonService.common.getAuthValidationsByOper() || {};
        req.body.udid = udid; /* nabil feghali - OMNI common security  */
        const sessionId = this.omniCommonService.common.session.getValueOf('sessionId');
        const agentAllowedCifNo = this.omniCommonService.common.session.getValueOf('agentAllowedCifNo');
        const listAllAllowedCifs = this.omniCommonService.common.session.getValueOf(ConstantCommon.listAllowedCifsAsString);
        const compCode = this.omniCommonService.returnCompCode();
        const channelId = this.omniCommonService.returnChnlID();
        const sessionAppId = CommonUtils.cacheParameterIfNotCached(ConstantCommon.APP_ID, sessionInstance);
        const appId = sessionAppId ? sessionAppId : this.omniCommonService.returnAppID();
        const custMode = PsCommonSettings.custMode ? PsCommonSettings.custMode : false;
        const userInfo = this.omniCommonService.common.session.getValueOf(ConstantCommon.USERINFO);
        const ocUserId = userInfo ? userInfo.ocUserId : null;
        const deviceDetails = this.omniCommonService.common.session.getValueOf(ConstantCommon.DEVICE_DETAILS);
        let storedUserName = this.omniCommonService.common.session.getValueOf(ConstantCommon.USER_NAME);
        const userType = this.omniCommonService.common.session.getValueOf(ConstantCommon.USR_TYPE);
        const appChnlId = CommonUtils.cacheParameterIfNotCached("appChnlId", null, {
            methodToCall() {
                let val = this.class.omniCommonService.returnAppChnlID();
                return val;
            },
            class: this
        });
        const type = PsCommonSettings.submitAfterSave ? 'S' : PsCommonSettings.savedraft ? 'D' : 'A';
        const language = String(PsCommonSettings.activeLanguge).toUpperCase();
        const cifBranchCode = userInfo?.customerInfoCO?.branchCode; //H.H- ABEOI230191	Logged in cif branch code should be available in all requests //this.omniCommonService.common.session.getValueOf(ConstantCommon.CUSTOMER_CIF_BRANCH);
        const appCustomerName = this.omniCommonService.common.session.getValueOf(ConstantCommon.CORPORATE_ID);
        const autoLogin = this.omniCommonService.common.session.getValueOf(ConstantCommon.AUTO_LOGIN);
        const userID = this.omniCommonService.common.session.getValueOf(ConstantCommon.userID);
        const userAgent = CommonUtils.cacheParameterIfNotCached("userAgent", null, {
            methodToCall() {
                let val = this.class.checkAppService.returnNativeUserAgent();
                return val;
            },
            class: this
        });
        const paymentCurrency = PsApplicationSettings.MAIN_CONFIG.PAYMENT_CURRENCY;
        let cif;
        let wcif;
        let newBody: any = {};
        let theBody: any = {};
        const SERVER_PUBLIC_KEY = this.omniCommonService.common.session.getValueOf(ConstantCommon.SERVER_PUBLIC_KEY);
        if (userInfo && userInfo.omniUserVO) {
            cif = userInfo.omniUserVO.CIF_NO;
            /* nabil feghali - OMNI common security  */
            if (storedUserName === undefined || storedUserName == null || storedUserName === '') {
                storedUserName = userInfo.omniUserVO.NAME;
            }
        }
        const workingCif = this.omniCommonService.common.session.getValueOf('WORKINGCIF');
        if (workingCif !== undefined) {
            wcif = workingCif;
        } else {
            wcif = cif;
        }
        const enableEcryption = CommonUtils.cacheParameterIfNotCached(PsCommonSettings.ENCRYPT_PARAMS, sessionInstance);
        newBody = {
            sessionId,
            compCode,
            channelId,
            appId,
            custMode,
            ocUserId,
            appChnlId,
            transferType: type,
            language: language ? language : 'EN',
            userType,
            userCifNo: cif,
            workingCif: wcif,
            localIp: PsCommonSettings.deviceLocalIp,
            publicIp: PsCommonSettings.devicePublicIp,
            userAgent,
            paymentCurrency,
            requestTimeout: PsCommonSettings.requestTimeOut,
            userIpInfo: !!PsCommonSettings.userIpInfo ? PsCommonSettings.userIpInfo : null,
            cifBranchCode,
            appCustomerName: appCustomerName ? appCustomerName : null,
            autoLogin: autoLogin ? autoLogin : 0,
            cifName: userInfo && userInfo['customerInfoCO'] ? userInfo['customerInfoCO']['shortName'] : null,
            userCifType: userInfo && userInfo['customerInfoCO'] ? userInfo['customerInfoCO']['cifType'] : null,
            userID,
            ...authenticationTypesVal,
            ...deviceDetails,
            ...req.body,
        };
        this.omniCommonService.common.removeEmpty(newBody);
        // Moved here by Richie for #BUG 934127 in order to have the oper_id sent from prelogin screens as well.
        if (req.body && !req.body.operId) {
            newBody.operId = PsCommonSettings.oper_ID > 0 ? PsCommonSettings.oper_ID : CommonBussinessConstant.DEFAULT_FALLBACK_OPER;
        }
        if (req.body && req.body.userName) {
            newBody.userName = req.body.userName;
        } else {
            newBody.userName = storedUserName;
        }
        if (USER_IS_LOGGED_IN) {
            if (PsCommonSettings.savedraft) {
                if (CommonUtils.isObject(PsCommonSettings.savedraftParam)) {
                    const keys = Object.keys(PsCommonSettings.savedraftParam);
                    if (CommonUtils.isNotEmptyArray(keys)) {
                        newBody = { ...newBody, ...PsCommonSettings.savedraftParam };
                    }
                }
            }
            if (!newBody.agentAllowedCifNo && (agentAllowedCifNo != null && agentAllowedCifNo !== undefined)) {
                newBody = { ...newBody, agentAllowedCifNo };
            }
            if (!newBody.listAllAllowedCifs && (listAllAllowedCifs != null && listAllAllowedCifs !== undefined)) {
                newBody = { ...newBody, listAllAllowedCifs };
            }
            if (this.omniCommonService.isSocialFb()) {
                if (!newBody.appIdentifier) {
                    newBody = {
                        ...newBody,
                        appIdentifier: this.omniCommonService.common.session.getValueOf(ConstantCommon.FACEBOOK_SOCIAL_ID)
                    };
                }
            }
            const currPage = this.omniCommonService.getInfoByOperId(newBody.operId);
            const operType = !!currPage ? currPage.operVO.TYPE_DESC : null;
            newBody.operType = operType;
            // Added here by Richie in order to encrypt all the params (including the default ones added here)
            this.omniCommonService.common.logger.log(new Date() + ` %c request ` + timestamp + ` : [${req.url.split('/').pop()}]`, 'background: #F49025; color: #000', newBody);
        } else {
            if (req.url.includes(ConstantCommon.LOGIN_END_POINT)) {
                if (this.omniCommonService.isSocialFb()) {
                    if (!newBody.appIdentifier) {
                        newBody = {
                            ...newBody,
                            appIdentifier: this.omniCommonService.common.session.getValueOf(ConstantCommon.FACEBOOK_SOCIAL_ID)
                        };
                    }
                }
            } else {
                newBody.sessionId = 'PRE_LOGIN';
            }
            this.omniCommonService.common.logger.log(new Date() + ` %c request ` + timestamp + ` : [${req.url.split('/').pop()}]`, 'background: #F49025; color: #000', newBody);
        }
        if (enableEcryption) {
            // Added by Richie for #TP 997475: opening omni cust war
            CommonCustUtils.putReqBody(newBody);
            // End Richie
            const result = this.encrypt(newBody);
            theBody = {
                pathParam: result.message,
                fromIonic: 1,
                key: result.key,
                iv: result.iv,
                udid, /* nabil feghali - OMNI common security  */
                appId,
                channelId,
                compCode: this.omniCommonService.returnCompCode(),
                serverPublicKeyToReceive: this.hybridkey.hashcode(SERVER_PUBLIC_KEY),
                userName: storedUserName
            };
            // Added by Richie for #TP 1150216: in order to get the private/public keys for all app/chnls under 1 prelogin
            CommonCustUtils.adjustReqParams(theBody);
            // End Richie

            /* nabil feghali - OMNI common security  */

            theBody = this.hybridkey.encryptTransportMessage(theBody);

            const theHeader: any = {
                'Content-Type': 'application/json;charset=utf-8'
            };

            const loginToken = this.omniCommonService.common.session.getValueOf(ConstantCommon.SERVER_LOGIN_TOKEN);
            if (loginToken != null) {
                theHeader.loginToken = loginToken.token;
            }

            const authToken = this.omniCommonService.common.session.getValueOf(ConstantCommon.SERVER_AUTH_TOKEN);
            if (authToken !== undefined && authToken != null) {
                if (req.url.includes(authToken.lastServiceToCall)) {
                    theHeader.authToken = authToken.token;
                    this.omniCommonService.common.session.remove(ConstantCommon.SERVER_AUTH_TOKEN);
                } else {
                    const servicesToCallList = authToken.servicesToCallList;
                    if (servicesToCallList !== undefined && servicesToCallList != null) {
                        for (const serverUrl of servicesToCallList) {
                            if (req.url.includes(serverUrl)) {
                                theHeader.authToken = authToken.token;
                                break;
                            }
                        }
                    }
                }
            }

            /* nabil feghali - OMNI common security - end */

            const R: HttpRequest<any> = req.clone({
                setHeaders: theHeader, body: theBody
            });
            return { plainRequest: newBody, request: R };
        } else {
            theBody = newBody;
            const R: HttpRequest<any> = req.clone({
                setHeaders: {
                    'Content-Type': 'application/json;charset=utf-8',
                }, body: theBody
            });
            return { plainRequest: newBody, request: R };
        }
    }

    intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpSentEvent | HttpHeaderResponse | HttpProgressEvent | HttpResponse<any> | HttpUserEvent<any>> {
        if (PsCommonSettings.isCertificateExpired) {
            CommonUtils.returnExcludedURLs().forEach(element => {
                if (!req.url.includes(element)) {
                    return;
                }
            });
        }
        // Modified by Richie for #BUG 1323941
        if (req.url.includes(PsCommonSettings.serviceUrl.returnIntegrityVerdicts) || req.url.includes(PsCommonSettings.serviceUrl.returnVersionDetails) || req.url.includes(ConstantCommon.PRE_LOGIN_SERVICE_URL) || req.url.includes('api.aladhan.com') ||
            req.url.includes('ip-api.io') || req.url.includes('assets/') || req.url.includes('ps-config.json') || req.url.includes('ps-default-config.json') || req.url.includes('mockData.json')
            || req.url.includes(ConstantCommon.ANTI_TAMPERING_ENCRYPTED_FILE_NAME) || req.url.includes(ConstantCommon.ANTI_TAMPERING_PACKAGE_FILE_NAME) || req.url.includes(ConstantCommon.CERTIFICATE_INFO_FILE_NAME)) {
            let timeStamp = req.body && req.body["URequestID"] ? req.body["URequestID"] : '';
            this.omniCommonService.common.logger.log(new Date() + ` %c Request ` + timeStamp + `: [${req.url.split('/').pop()}]`, 'background: #42ebf4; color: #000', req);

            /* nabil feghali - OMNI common security  */
            if (req.url.includes(PsCommonSettings.serviceUrl.returnIntegrityVerdicts) || req.url.includes(PsCommonSettings.serviceUrl.returnVersionDetails) || req.url.includes(ConstantCommon.PRE_LOGIN_SERVICE_URL)) {
                const newBody = this.hybridkey.encryptTransportMessage(req.body);

                const R = req.clone({
                    setHeaders: {
                        'Content-Type': 'application/json;charset=utf-8',
                    }, body: newBody
                });

                if (!CommonUtils.isNativeMobile() || PsCommonSettings.isCertificateExpired) {
                    return next.handle(R).pipe(
                        catchError(error => {
                            return this.handleHttpError(error, R, next);
                        })
                    );
                } else {
                    return from(this.handleNativeRequest(R, next)).pipe(
                        catchError(error => {
                            return this.handleHttpError(error, R, next);
                        })
                    );
                }
            } else {/* nabil feghali - OMNI common security - end */
                const cachedResponse = this.cache.get(req);
                if (cachedResponse) {
                    return of(cachedResponse);
                } else {
                    return next.handle(req).pipe(
                        tap(event => {
                            if (event instanceof HttpResponse && req.url.includes('assets/')) {
                                this.cache.putWithAge(req, event);
                            }
                        }),
                        catchError(error => {
                            return this.handleHttpError(error, req, next);
                        })
                    );
                }
            }
        } else {
            const mainRequest = this.addToken(req);
            const R = mainRequest.request;
            const timestamp = req.body && req.body["URequestID"] ? req.body["URequestID"] : '';
            this.omniCommonService.common.logger.log(new Date() + ` %c Request ` + timestamp + ` : [${R.url.split('/').pop()}]`, 'background: #42ebf4; color: #000', R);

            // Added by Richie for #TP 997475: opening omni cust war
            const mockReq = CommonCustUtils.callMockService(req);
            if (mockReq) {
                return CommonCustUtils.returnResponse(mockReq, req);
            }
            // End Richie
            if (mainRequest.plainRequest && mainRequest.plainRequest.requestToBeCached) {
                let response = this.cache.get(R.url, mainRequest.plainRequest);
                if (response) {
                    let parsedResponse = JSON.parse(response);
                    parsedResponse = JSON.parse(parsedResponse);
                    const finalResponse = new HttpResponse({
                        body: { ...parsedResponse, wasReadFromCache: true },
                        headers: R.headers,
                        url: R.url,
                    });
                    return from(Promise.resolve(finalResponse));
                }
            }
            // GI: Added SSl pinning functionality using ionic native http provider
            if (!CommonUtils.isNativeMobile() || PsCommonSettings.isCertificateExpired) {
                return next.handle(R).pipe(tap(response => {
                    if (mainRequest.plainRequest.requestToBeCached && response instanceof HttpResponse) {
                        // response.body.request = R;
                        response.body.plainRequest = mainRequest.plainRequest;
                    }
                }), catchError(error => {
                    return this.handleHttpError(error, R, next);
                })
                );
            } else {
                return from(this.handleNativeRequest(R, next)).pipe(tap(response => {
                    if (mainRequest.plainRequest.requestToBeCached && response instanceof HttpResponse) {
                        // response.body.request = R;
                        response.body.plainRequest = mainRequest.plainRequest;
                    }
                }), catchError(error => {
                    return this.handleHttpError(error, R, next);
                })
                );
            }
        }

    }

    /**
     * This method will handle native device REST requests in order to prevent SSL pinning.
     * @param R http request sent to server
     * @param next next handler
     */
    private async handleNativeRequest(R: HttpRequest<any>, next): Promise<HttpResponse<any>> {
        try {
            if (!this.wasSSLPinned) {
                this.wasSSLPinned = true;
                this.nativeHttp.setServerTrustMode(this.psSecurity.getSSLPinningMethod()).then(() => {
                    this.omniCommonService.common.logger.log("SSL pinning is now Set.");
                }).catch(() => {
                    this.omniCommonService.common.logger.error("SSL pinning failed.");
                });
            }

            this.nativeHttp.setDataSerializer('json');

            let headers = {};
            let keys = R.headers.keys();
            if (keys.length > 0) {
                R.headers.keys().forEach((key) => {
                    let val = R.headers.get(key);
                    headers[key] = String(val);
                });
            } else {
                headers = null;
            }

            // Added by Richie to log the time of each request
            if (PsApplicationSettings.MAIN_CONFIG.TIME_LOG_ENABLED) {
                PsCommonSettings.timeLogs.push('request for ' + R.url + ' before nativeHttp.sendRequest with start time: ' + (new Date()).toISOString());
            }
            // end Richie
            const nativeHttpResponse = await this.nativeHttp.sendRequest(R.url, {
                method: 'post',
                data: R.body,
                headers: headers,
                serializer: 'json',
            });
            // Added by Richie to log the time of each request
            if (PsApplicationSettings.MAIN_CONFIG.TIME_LOG_ENABLED) {
                PsCommonSettings.timeLogs.push('request for ' + R.url + ' after nativeHttp.sendRequest with end time: ' + (new Date()).toISOString());
            }
            // end Richie
            let finalResponse = {};

            try {
                finalResponse = JSON.parse(nativeHttpResponse.data);
            } catch (error) {
                finalResponse = { response: nativeHttpResponse.data };
            }

            const response = new HttpResponse({
                body: finalResponse,
                status: nativeHttpResponse.status,
                headers: R.headers,
                url: nativeHttpResponse.url,
            });

            return Promise.resolve(response);
        }
        catch (error) {
            return this.handleNativeError(R, error);
        }
    }

    handleNativeError(R: HttpRequest<any>, error) {
        if (!error.status) { return Promise.reject(error); }
        if (error && (error.status === -2 || error.status === '-2')) {
            this.psSecurity.handleStopApp(); // If SSL Security error is encountered, force stop the app
        } else {
            let resError: any = {};
            try {
                resError = JSON.parse(error);
            } catch (e) {
                resError = error ? error : {
                    error: 'REST error',
                    status: -99,
                    headers: R.headers,
                    url: R.url
                };

            }
            const response = new HttpResponse({
                body: resError.error,
                status: resError.status,
                headers: resError.headers,
                url: R.url,
            });
            return Promise.reject(response);
        }
    }

    handleHttpError(error, req, next): Observable<any> {
        this.omniCommonService.common.logger.error('<----- PATH REQUEST ERROR HANDLER ----->', error);
        if (error instanceof HttpErrorResponse) {
            switch ((error as HttpErrorResponse).status) {
                case 500:
                    this.omniCommonService.common.logger.warn('%c [' + String.fromCharCode(9888) + ']' + ' Something Went Wrong On The Server!', 'background: #eeff05; color: #000');
                    return throwError(new HttpErrorResponse(error));
                // return next.handle(req);
                case 400:
                    return this.handle400Error(req, error);
                case 403:
                    return this.handle403Error(error);
                case 0:
                    if (error.statusText === "Unknown Error" && error.ok === false) {
                        this.confirmErrorAlert();
                    }
                default:
                    return throwError(new HttpErrorResponse(error));
                // return next.handle(req);

            }
        } else {
            return throwError(new HttpErrorResponse(error));
        }
    }

    handle400Error(req, error) {
        if (req.url.toString().startsWith(PsApplicationSettings.MAIN_CONFIG.SERVICE_URL)) {
            this.confirmErrorAlert();
        } else {
            return throwError(error);
        }
    }

    handle403Error(error) {
        return throwError(new HttpErrorResponse(error));
    }

    /* nabil feghali - OMNI common security  */

    encrypt(message): IKeyGeneratorResult {
        const pKey = this.omniCommonService.common.session.getValueOf(ConstantCommon.SERVER_PUBLIC_KEY);

        let result: IKeyGeneratorResult = {};
        let ocUserId;
        const compCode = PsApplicationSettings.MAIN_CONFIG.COMP_CODE;

        if (message.hasOwnProperty('ocUserId')) {
            ocUserId = this.hybridkey.encryptKey(message.ocUserId, pKey);
        }

        result = this.hybridkey.encryptMessage(message, pKey);

        result.compCode = compCode;
        if (message.hasOwnProperty('ocUserId')) {
            result.ocUserId = parseInt(ocUserId);
        }
        return result;
    }

    /* nabil feghali - OMNI common security - end */
    initBMPFlow(body) {
        const completeParams = CommonUtils.valFromNestedObj('screenMarkup.completeParams', body.JBPM);
        const newParams = CommonUtils.createBPMParams(completeParams, body, this.omniCommonService.common.session, 'out');
        body.bpmParametersMap = { ...newParams };
        delete body.JBPM;
    }

    confirmErrorAlert() {

        let modalID = 'user-confirmation-popup';
        let formGroup = new FormGroup({})
        const alertController: IOptionsPsComplexAlertController = {
            cssClass: modalID,
            header: 'error_connecting_to_server_key',
            buttons: [
                {
                    role: 'cancel',
                    options: {
                        labelKey: 'cancel_key',
                        psClass: 'user-confirmation-popup-cancel-button',
                        group: formGroup
                    },
                    handler: () => {
                        CommonUtils.dismissAllModals(null, null, modalID)
                    }
                },
                {
                    options: {
                        labelKey: 'logout_key',
                        psClass: 'user-confirmation-popup-submit-button',
                        group: formGroup
                    },
                    handler: () => {
                        CommonUtils.dismissLoading();
                        this.omniCommonService.logoutProcess();
                        CommonUtils.dismissAllModals(null, null, modalID);
                    }
                }
            ]
        };
        this.omniCommonService.common.presentPsAlert(alertController, modalID);

    }
}
