import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { IonImg } from '@ionic/angular';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { PsApplicationSettings } from 'src/app/commonSRC/psServices/models/ps-app-settings';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { IOptionsPsActionImage } from '../../../../psServices/models/ps-common-interface';
import { PsBaseActionComponent } from '../ps-base-action.component';
import { ConstantCommon } from './../../../../psServices/models/common-constant';


/**
 * @author Zunair.Zakir
 * @since 28/10/2019
 *
 * <p> PsActionImageComponent is a simple component base on ion-image and can display image by it's name and base64 url also supported</p>
 */
@Component({
  selector: 'ps-action-image',
  templateUrl: './ps-action-image.component.html',
  styleUrls: ['./ps-action-image.component.scss'],
})
export class PsActionImageComponent extends PsBaseActionComponent implements OnInit {
  @Input() options: IOptionsPsActionImage;
  @ViewChild(IonImg, { static: false }) image: IonImg;
  wasInitialized = false;
  public get imageURL(): string {
    let src = null;
    if (!CommonUtils.isEmptyObject(this.image)) {
      if (CommonUtils.isNotEmptyObject(this.options.imageName) && this.options.imageName.includes('base64')) {
        this.options.imageBase64Url = this.options.imageName;
      }
      if (this.options.imageBase64Url !== undefined && this.options.imageBase64Url !== '' && this.options.imageBase64Url !== null) {
        src = this.options.imageBase64Url && this.options.imageBase64Url.includes('base64') ? this.options.imageBase64Url : 'data:image/png;base64, ' + this.options.imageBase64Url;
      } else if (this.options.imageName !== undefined && this.options.imageName !== '' && this.options.imageName !== null) {
        if (this.options.imagePath !== null && this.options.imagePath !== undefined && this.options.imagePath !== '') {
          // If the image path was not defined in the options, we should append the local image name with the pre-defined path.
          src = this.options.imagePath + this.options.imageName;
        } else {
          // If the image received from server side was Blob or online web image 
          if (this.options.imageName.includes('http') || this.options.imageName.includes('blob')) {
            src = this.options.imageName;
          } else {
            src = PsApplicationSettings.CLIENT_ASSETS_CONFIG.IMGS_FOLDER_URL + this.options.imageName;
          }
        }
      }
      if (CommonUtils.isNotEmptyObject(src)) {
        this.image.src = src;
        return src;
      }
    }
  }
  constructor(common: PsCommonService, loggerP: LoggerService) {
    super(common, loggerP);
  }

  ngOnInit() {
    if (this.options.allowCust === undefined) {
      this.options.allowCust = false;
    }
    super.ngOnInit();
    this.wasInitialized = true;
  }

  onActionImageClick(event: Event) {
    if (!this.disabled) {
      this.onClick.emit(event);
    }
  }


  onError() {
    if (this.options.fallBackImageName) {
      this.options.imageName = this.options.fallBackImageName;
    } else {
      this.options.imageName = ConstantCommon.FALLBACK_IMAGE_NAME;
    }
  }
}
