import { AfterViewInit, Component, ElementRef, EventEmitter, Input, OnInit, Output, ViewChild, ViewEncapsulation } from '@angular/core';
import { IonInput } from '@ionic/angular';
import { ICellRendererAngularComp } from 'ag-grid-angular';
import Inputmask from 'inputmask';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IOptionsPsKeyinInput, IOptionsPsLabelInput, IOptionsPsActionIcon } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { ConstantCommon } from '../../../../../psServices/models/common-constant';
import { IOptionsPsFieldLabel } from '../../../../../psServices/models/ps-common-interface';
import { PsFieldKeyinComponent } from '../ps-field-keyin.component';

@Component({
    selector: 'ps-keyin-input',
    templateUrl: 'ps-keyin-input.component.html',
    styleUrls: ['./ps-keyin-input.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class PsKeyinInputComponent extends PsFieldKeyinComponent implements OnInit, AfterViewInit, ICellRendererAngularComp {

    @Input() options: IOptionsPsKeyinInput;
    previewLblOptions: IOptionsPsFieldLabel;
    forceVirtualKeyboard:boolean;
    @Output() onPsIconClicked = new EventEmitter<void>();
    @Output() onPsImageClicked = new EventEmitter<void>();
    // eslint-disable-next-line @angular-eslint/no-output-on-prefix
    @Output() onPsKeyUp = new EventEmitter<void>();
    @ViewChild('ps_input_prev_sec', { static: false }) PreviewRef: ElementRef;
    @ViewChild('amountInputRef', { static: false }) amountInputRef: IonInput;
    @ViewChild('regularInputRef', { static: false }) regularInputRef: IonInput;

    public loaderOptions: IOptionsPsActionIcon = {
        iconName: 'loading',
        psClass: 'align-loader'
    };

    public errorOptions: IOptionsPsActionIcon = {
        iconName: 'close-circle',
        psClass: 'align-loader'
    };

    /**
     * @author RichardZourob
     * function added to return the input mode as numeric in case the type is number.
     * in order to show the numeric keyboard on ios devices since the type=number only on ios allows entering characters
     * #BUG 1197560
     */
    get inputMode() {
        if (this.options.type === 'number') {
            return 'numeric';
        } else {
            return '';
        }
    }

    get previewValOptions(): IOptionsPsFieldLabel {
        return {
            labelKey: this.fcFieldValue,
            psClass: 'ps-value-preview',
            previewMode: true
        };
    }

    get labelOptions(): IOptionsPsLabelInput {
        return {
            required: this.required,
            labelKey: this.options.labelKey
        };
    }

    /**
     * getter created in order to populate the mask options to the variable this.maskOptions
     * in order for the mask/unmask to work properly in case the mask options were changed on the run.
     */
    public get maskOption() {
        this.options.group.controls[this.options.fcName][ConstantCommon.CONTROLLER_MASK_OPTIONS] = this.options.mask;
        return this.options.mask;
    }


    constructor(commonProv: PsCommonService, loggerP: LoggerService,  private eRef?: ElementRef) {
        super(commonProv, loggerP);
    }

    ngOnInit() {
        this.custIconOptions.component = PsKeyinInputComponent;
        this.custIconOptions.componentOptions = this.options;

        super.ngOnInit();
        // set the available customizations
        if (this.options.type === 'number' || this.options.type === 'amount') {
            this.custIconOptions.availableCustomization.MAX_VALUE = true;
            this.custIconOptions.availableCustomization.MIN_VALUE = true;
        } else {
            this.custIconOptions.availableCustomization.MIN_LENGTH = true;
            this.custIconOptions.availableCustomization.MAX_LENGTH = true;
            this.custIconOptions.availableCustomization.PATTERN = true;
        }


        // handling specific case of label fields inside container-lookup-option
        if (this.options.shortcutLabel?.type === ConstantCommon.CARD_LABEL_TYPE) {
            this.custIconOptions.availableCustomization.IS_READONLY = false;
            this.custIconOptions.availableCustomization.PLACEHOLDER_LABEL_ID = false;
            this.custIconOptions.availableCustomization.IS_MANDATORY = false;
            this.custIconOptions.availableCustomization.DEFAULT_VALUE = false;
            this.custIconOptions.availableCustomization.SERVICE_MAPPING = false;
            this.custIconOptions.availableCustomization.MIN_LENGTH = false;
            this.custIconOptions.availableCustomization.MAX_LENGTH = false;
            this.custIconOptions.availableCustomization.PATTERN = false;
        }
        if (this.options.shortcutLabel?.type === ConstantCommon.CARD_LABEL_TYPE &&
            this.options.shortcutLabel?.custData.businessRelatedYN === 'N') {
            this.custIconOptions.availableCustomization.KEY_LABEL_ID = false;
        }
        // end setting the available customizations
        if(this.options.group.controls[this.options.fcName][ConstantCommon.CONTROLLER_SCREEN_DISPLAY_VO]?.KEY_LABEL_CODE != null &&
           this.options.group.controls[this.options.fcName][ConstantCommon.CONTROLLER_SCREEN_DISPLAY_VO]?.KEY_LABEL_CODE !== 'undefined')
        {
            this.options.labelKey = this.options.forcePreviewMode ? this.options.group.controls[this.options.fcName][ConstantCommon.CONTROLLER_SCREEN_DISPLAY_VO].KEY_LABEL_DESC : this.options.group.controls[this.options.fcName][ConstantCommon.CONTROLLER_SCREEN_DISPLAY_VO].KEY_LABEL_CODE
        }
        this.previewLblOptions = {
            labelKey: this.options.labelKey,
            psClass: 'ps-lbl-preview',
            previewMode: true
        }
        if(this.options.pendingStatus === undefined && this.previewValOptions.labelKey === undefined && this.previewLblOptions.labelKey){
         this.previewLblOptions.labelKey = this.commonProv.translate(this.previewLblOptions.labelKey);
        }
        // if (this.options.displayMode) {
        //     this.options.disablePreview = false;
        //     this.previewMode = true;
        // } else if (this.options.displayMode === false) {
        //     this.options.disablePreview = true;
        //     this.previewMode = false;
        // }

        if (this.options) {
            if (this.options.iconOptions || this.options.imageOptions) {
                // this.itemOptions = { ...this.options };
                this.itemOptions = CommonUtils.copyObject(this.itemOptions, this.options);
            }


        }
        if (this.options.mask) {
            if (!this.options.mask.placeholder) {
                this.options.mask.placeholder = '';
            }
            if ((this.options.type === 'number' || this.options.type === 'amount')) {
                this.options.mask.numericInput = true;
            }
        }
        this.options.group.controls[this.options.fcName][ConstantCommon.CONTROLLER_MASK_OPTIONS] = this.options.mask;
        this.options.group.controls[this.options.fcName][ConstantCommon.CONTROLLER_MASK_APPLICABLE] = true;
        this.itemOptions.psClass = this.options.psClass;
        if (this.options.name == undefined || this.options.name == null) {
            this.options.name = this.options.fcName;
        }
        if (this.options.enableVirtualKeyboard && this.options.type != 'amount') {
            this.options.forceTriggerChange = true;
            if (!this.options.virtualKeyboardOptions) {
                this.options.virtualKeyboardOptions = {};
            }
            if(!this.options.virtualKeyboardOptions.vKeyboardOptions){
                this.options.virtualKeyboardOptions.vKeyboardOptions = { type: 'input' };
            }
            this.forceVirtualKeyboard = this.options.virtualKeyboardOptions.forceVK;
            if(this.options.virtualKeyboardOptions.forceVK){
                this.options.customPlaceHolder = ()=>{
                    return this.options.placeHolder ? this.common.translate(this.options.placeHolder) + " " + this.common.translate('UseVirtualKeyboard_key') : '';
                }
            }
            this.options.virtualKeyboardOptions.inputId = 'vkInput_' + this.options.fcName;
            this.options.virtualKeyboardOptions.ionInputFcname = this.options.fcName;
            this.options.virtualKeyboardOptions.group = this.options.group;
            this.options.virtualKeyboardOptions.parentElementRef = this.eRef;
            if (this.options.type === 'number') {
                this.options.virtualKeyboardOptions.vKeyboardOptions.layout = 'num';
            }
        }
    }
    ngAfterViewInit() {
        if (this.options && this.options.prevModeNotPrevStep) {
            this.PreviewRef.nativeElement.removeAttribute('psPreviewEngine');
        }
    }
    changeVkInput(event) {
        let vkInput = document.getElementById('vkInput_' + this.options.fcName) as HTMLInputElement;
        if (vkInput)
         vkInput.value = event.detail.value != '' ? (event.detail.value)?.split('').reverse().join('') : event.detail.value;
    }
    public onKeyDown(event: any) {
        // #800979
        super.onKeyDown(event);
        const e = event as KeyboardEvent;
        //heba.hassan bug##1185195 - fix for a known issue in firefox/IE/Safari when using HTML input type number which is accepting alphabetical
        if (this.options.type == 'number' || this.options.type == 'amount') {
            if (this.browsersWithInputNumberIssue() && this.isNonNumericalInput(e)) {
                if (e.code !== 'Delete' && e.code !== 'Backspace') {
                    event.preventDefault();
                    event.stopPropagation();
                    return false;
                }
            }
        }

        if (this.options.type === 'text' && this.options.disableSpecChars) {
            if (!this.isAlphaNumericKey(e)) {
                event.preventDefault();
                event.stopPropagation();
            }
        } else if (this.options.type === 'number' && (e.code === 'KeyE' ||
            // the below line is added for bug nb:808781
            e.code === 'Minus' || e.shiftKey === true || e.code === 'NumpadDecimal' ||
            e.code === 'NumpadAdd' || e.code === 'NumpadSubtract')) {
            event.preventDefault();
            event.stopPropagation();

        } else if (this.options.type === 'amount' && e.code !== 'NumpadDecimal' && !this.isAlphaNumericKey(e) &&
            e.code === 'KeyE') {
            event.preventDefault();
            event.stopPropagation();

            // Added by Richie for #BUG 1192327
        } else if (event.target.value !== '.' && this.options.type === 'amount' && event.target.selectionStart == event.target.selectionEnd &&
            (((e.code === 'Backspace' || e.keyCode === 229) && event.target.value.indexOf('.') === event.target.selectionStart - 1)
                || (e.code === 'Delete' && event.target.value.indexOf('.') === event.target.selectionStart))) {
            if (e.code === 'Delete') {
                event.target.selectionStart = event.target.selectionStart + 1;
            } else {
                event.target.selectionStart = event.target.selectionStart - 1;
            }
            event.preventDefault();
            event.stopPropagation();

        } else if (event.target.value !== '.' && this.options.type === 'amount' && event.target.selectionStart === event.target.selectionEnd &&
            Inputmask.format(0, this.options.mask) === Inputmask.format(event.target.value, this.options.mask) &&
            (((e.code === 'Backspace' || e.keyCode === 229) && event.target.selectionStart === 1
            )
                || (e.code === 'Delete' && event.target.selectionStart === 0))) {
            this.options.group.controls[this.options.fcName].setValue('', { emitEvent: false });
        }
        // End Richie
        return;
    }
    browsersWithInputNumberIssue() {
        if (((navigator.userAgent.indexOf("Safari") != -1)
            || (navigator.userAgent.indexOf("Firefox") != -1) ||
            (navigator.userAgent.indexOf("MSIE") != -1)) && (navigator.userAgent.indexOf("Chrome") == -1)) {
            return true;
        }
        return false;
    }
    isNonNumericalInput(e) {
        e = e || window.event;
        var charCode = (typeof e.which == "undefined") ? e.keyCode : e.which;
        var charStr = String.fromCharCode(charCode);
        if ((ConstantCommon.ALLOWED_KEY_CODES.indexOf(e.keyCode) === -1 && charStr.match(/^[0-9]+$/))
            || e.code.indexOf('Numpad') != -1 || e.key == '.') {
            return false;
        } return true;
    }
    disableDragAndDrop(event) {
        event.stopPropagation();
        event.preventDefault();
    }

    onIconClicked(event) {
        this.onPsIconClicked.emit(event);
    }

    onImageClicked() {
        this.onPsImageClicked.emit();
    }

    onKeyUp(event) {
        this.onPsKeyUp.emit(event);
    }

    onFocus(event) {
        if (event && this.commonProv.isNativeMobile()) { // added by Salah Kousaibany for bug #1578108 
            let time = 200;
            setTimeout(() => {
                event.target.scrollIntoView({ block: "end" });
            }, time);
        }
        this.onPsFocus.emit(event);
    }

    agInit(params: any) {
        this.agInitBase(params, this);
        if (params.colDef.cellEditorParams.onPsChange !== undefined) {
            this.onPsChange.subscribe((res) => {
                res.rowIndex = params.rowIndex;
                // Commented by Richie for #BUG 1340366; in order to call the change event on first change
                res.rowNode = params.node;
                // if (res.oldValue !== undefined) {
                params.colDef.cellEditorParams.onPsChange(res);
                // }
            });
        }
    }
    refresh(params: any) {
        this.agRefreshBase(params, this);
        return true;
    }
    getDomRef() {
        if (this.options.type === 'amount') {
            return this.amountInputRef;
        }
        return this.regularInputRef;
    }
}
