@Component({
  selector: 'ps-base',
  templateUrl: './ps-base.component.html',
  styleUrls: ['./ps-base.component.scss'],
})
export class PsBaseComponent implements OnInit, OnDestroy {
  @Input() options: IOptionsPsBase;
  @Input() id: string;
  @Input() class: string;

  @Input() public psIcon: string;
  @Input() public ionicIcon: string;
  @Input() public iconPosition: 'end' | 'start' = 'end';
  @Input() decPoints = 0;


  @Output() public onPsInit: EventEmitter<any> = new EventEmitter<any>();


  visibilitySubscription: Subscription;
  readonlySubscription: Subscription;
  /* Richie: to be done
  @Input() public set autoValidation(autoValidate: boolean) {
    if (autoValidate && !this._autoValidation) {
      this.checkValidationErrors();
    }
    this._autoValidation = autoValidate;
  } */
  protected _iconOptions: IOptionsPsActionIcon = null;
  public itemOptions: IOptionsPsContainerItem = {};
  currentOperID;
  constructor(protected common: PsCommonService, protected logger: LoggerService, protected elRef?: ElementRef) {
    this.currentOperID = PsCommonSettings.oper_ID;
  }

  // end Richie

  get custMode() {
    return PsCommonSettings.custMode && this.options && this.options.allowCust;
  }

  public custIconOptions: IOptionsPsIconCustomization = {
    availableCustomization: {
      IS_VISIBLE: true,
      IS_READONLY: true,
      KEY_LABEL_ID: true
    }
  };
  public busCustIconOptions: IOptionsPsIconBusinessCustomization = {};// Added by Richie for #TP 1456907
  custLabelButtonOptions: IOptionsPsActionButton = {
    type: 'button',
    iconName: 'create',
    allowCust: false,
    group: null
  };


  public delimiter = ConstantCommon.PREVIEW_COMPONENT_DELIMITER;
  public componentPreview;
  public componentOpID;
  public formReset;
  public operCustomization;
  // Modified by Richie for #BUG 1340366
  public tempPreviewMode: boolean;
  public get previewMode() {
    return this.tempPreviewMode || this.options?.forcePreviewMode;
  }
  // End Richie
  public operationID: number;
  public screenDispElt: IsysParamObj;
  public required = false;
  public readonly = false;
  public disabled = false;
  public visible = true;
  public visibleOldValue;

  // Added by Richie in order to hide the labeling button until the labeling screen is available.
  public enableLabeling = false;

  public helpType: 'hint' | 'tooltip' = 'tooltip';
  public isHelpEnabled = true;
  public helpMessage = '1231232';
  public finalValidator: IdefaultValidators = { overrideCust: true };

  ngOnInit() {
    this.init();
    this.applyCustomization(this.id);
  }

  public init() {
    if (this.custIconOptions.fieldNameId === undefined) {
      this.custIconOptions.fieldNameId = this.id;
    }
    if (this.options) {
      if (this.options.allowCust === undefined) {
        this.options.allowCust = true;
      }
      if (!this.options.psClass) {
        this.options.psClass = ConstantCommon.psComponentsDefaultClass;
      }
      if (this.options.labelKey) {
        this.custIconOptions.fieldNameDesc = this.options.labelKey;
      }
      if (this.options.overrideDefaultCust) {
        this.custIconOptions.overrideDefaultCust = this.options.overrideDefaultCust;
      }
      // Added by Richie for #TP 1456907
      if (this.options.availableBusinessCust) {
        this.busCustIconOptions.fieldNameId = this.custIconOptions.fieldNameId;
        this.busCustIconOptions.fieldNameDesc = this.custIconOptions.fieldNameDesc;
        this.busCustIconOptions.component = this.custIconOptions.component;
        this.busCustIconOptions.componentOptions = this.custIconOptions.componentOptions;
      }
      // End Richie
    }
    this.componentOpID = this.common.getOperationID().subscribe((operID) => {
      if (operID) {
        this.operationID = operID;
      }
    }
    );
  }

  ngOnDestroy() {
    this.destroy();
  }
  public destroy() {
    if (this.componentPreview) {
      this.componentPreview.unsubscribe();
    }
    if (this.componentOpID) {
      this.componentOpID.unsubscribe();
    }
    if (this.operCustomization) {
      this.operCustomization.unsubscribe();
    }
    if (this.formReset) {
      this.formReset.unsubscribe();
    }
    if (this.visibilitySubscription) {
      this.visibilitySubscription.unsubscribe();
    }
    if (this.readonlySubscription) {
      this.readonlySubscription.unsubscribe();
    }
  }

  applyCustValidators(notOverrideCust: boolean) {
    if (this.options && this.options.allowCust && this.screenDispElt) {
      if (notOverrideCust) {
        if (this.screenDispElt.IS_READONLY != null && typeof this.screenDispElt.IS_READONLY != 'undefined') {
          this.disabled = this.finalValidator.disabled = this.screenDispElt.IS_READONLY === 1;
        }
        if (this.screenDispElt.IS_VISIBLE != null && typeof this.screenDispElt.IS_VISIBLE != 'undefined') {
          this.visible = this.finalValidator.visible = this.screenDispElt.IS_VISIBLE === 2 ? true : this.screenDispElt.IS_VISIBLE === 1;
          if (this.elRef && (this.visible != this.visibleOldValue)) {
            this.visibleOldValue = this.visible;
            this.showHideElement(this.elRef.nativeElement);
            this.showHideElement(this.elRef.nativeElement ? this.elRef.nativeElement.parentElement : null);
            this.showHideElement(this.options.parentRef ? this.options.parentRef.nativeElement : null);
          }
        }
      }
      if (this.screenDispElt.KEY_LABEL_CODE != null && typeof this.screenDispElt.KEY_LABEL_CODE != 'undefined') {
        this.options.labelKey = this.screenDispElt.KEY_LABEL_CODE;
      }
    }
  }

  applyCustomization(fieldIdName: string) {
    this.applyCustExpressions(fieldIdName);
    this.operCustomization = this.common.returnOperCustomizationObserv().subscribe(operCust => {
      const myScreenOperCust = operCust; // Modified by Richie for #BUG 1259353
      if (myScreenOperCust) {
        if (myScreenOperCust.get(fieldIdName)) {
          this.screenDispElt = myScreenOperCust.get(fieldIdName);
        }
        this.applyCustValidators(true);
      }
    });
  }

  applyCustExpressions(fieldIdName) {
    const myScreenOperCust = this.common.returnOperCustomization(); // Modified by Richie for #BUG 1259353
    if (myScreenOperCust) {
      //Added by Richie for #TP 1338932
      let fieldCustName = fieldIdName;
      if (this.options) {
        fieldCustName = CommonUtils.returnNameWithoutIndex(fieldIdName, this.options['rowIndex']); // Added by Richie to handle indexed fields
      }
      let fieldCustomizations;
      if (myScreenOperCust.get(fieldIdName)) {
        fieldCustomizations = myScreenOperCust.get(fieldIdName);
      } else if (fieldCustName !== fieldIdName && myScreenOperCust.get(fieldCustName)) {
        fieldCustomizations = myScreenOperCust.get(fieldCustName);
      }

      if (fieldCustomizations) {
        // const fieldCustomizations = myScreenOperCust.get(fieldIdName);
        //End Richie
        if (fieldCustomizations.IS_VISIBLE === 2 && fieldCustomizations.VISIBILITY_EXPR) {
          const dependencies = CommonUtils.checkScreenFieldsDependency(fieldCustomizations.VISIBILITY_EXPR);
          const visibilityBehaviorSubject: BehaviorSubject<any> = new BehaviorSubject<any>(null);
          this.common.updateScreenExpressionsMap(dependencies, visibilityBehaviorSubject);
          this.visibilitySubscription = visibilityBehaviorSubject.subscribe((formData) => {
            this.common.returnExpressionResult(formData, fieldCustomizations.VISIBILITY_EXPR, this.options['rowIndex'], dependencies, this.options["group"]).then(result => {
              if (result !== undefined && this.screenDispElt) {
                this.common.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [fieldIdName], result);
              }
            });
          });
        }
        if (fieldCustomizations.IS_READONLY === 2 && fieldCustomizations.READONLY_EXPR) {
          const readonlyBehaviorSubject: BehaviorSubject<any> = new BehaviorSubject<any>(null);
          const dependencies = CommonUtils.checkScreenFieldsDependency(fieldCustomizations.READONLY_EXPR);
          this.common.updateScreenExpressionsMap(dependencies, readonlyBehaviorSubject);
          this.readonlySubscription = readonlyBehaviorSubject.subscribe((formData) => {
            this.common.returnExpressionResult(formData, fieldCustomizations.READONLY_EXPR, this.options['rowIndex'], dependencies, this.options["group"]).then(result => {
              if (result !== undefined && this.screenDispElt) {
                this.common.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [fieldIdName], result);
              }
            });
          });
        }
      }
    }
    // const oper
  }

  openCustLabel(event) {
  }

  async openSpecialCustomization(myEvent, type) {
    /*// Richie: to be done
     let fieldNameID = '';
    if (!(this.fcName || this.id)) {
      const msgTitle = this.common.translate('cannot_proceed_key');
      const msgText = this.common.translate('elemIdAndNameChecking_key');
      const okBtn = this.common.translate('ok_key');
      return;
    } else {
      fieldNameID = this.fcName ? this.fcName : this.id;
    }

    const popover = await this.common.popoverCtrl.create({
      component: CustPopoverPage,
      componentProps: {
        operId: this.operationID,
        fieldName: fieldNameID,
        fieldType: type
      }
    });
    // {
    //   ev: myEvent
    // }
    await popover.present();//.catch((err) => { this.logger.error(err); }); */
  }

  initPreview(group: FormGroup) {
    this.componentPreview = this.common.getMode().subscribe((subject) => {
      if (subject && group && group === subject.formGroup) {
        this.tempPreviewMode = subject.preview; // Modified by Richie for #BUG 1340366
      }
    });

  }

  /**
   * function used in wrapper components to trigger the init event of the child component.
   *
   * @author RichardZourob
   * TP# 1082495
   */
  onInit(event) {
    this.onPsInit.emit(event);
  }

  /**
   * function to hide/show element
   *
   * @author GilbertAndary
   * TP# 1039642
   */
  showHideElement(element) {
    if (element && !this.custMode) {
      if (this.visible) {
        element.style.display = '';
      } else {
        element.style.display = 'none';
      }
    }
  }

  // Modified by Richie for #BUG 1340366
  public agGridCellRendererParams: ICellRendererParams;

  agInitBase(params: any, componentObj: any) {
    this.agGridCellRendererParams = params;
    componentObj.options = params.colDef.cellEditorParams.componentOptions;
    if (params.colDef.cellEditorParams.componentOptions.fcName) {
      if (params.colDef.cellEditorParams.componentOptions.group.controls[params.colDef.cellEditorParams.componentOptions.fcName]) {
        params.colDef.cellEditorParams.componentOptions.group.removeControl(params.colDef.cellEditorParams.componentOptions.fcName);
      }
      // componentObj.ngOnInit(); // Commented by Richie for #BUG 1377651
      setTimeout(() => {
        let paramData = params.data
        paramData = this.common.valFromNestedObj(params.colDef.field, paramData);
        let val = params.colDef.cellEditorParams.type === 'date' ? new Date(CommonUtils.isEmptyString(paramData) ? new Date() : paramData) : paramData;
        if (val instanceof Object && val.itemValue !== undefined) {
          val = val.itemValue;
        }
        componentObj.writeValue(val);
        componentObj.options.group.controls[componentObj.options.fcName].setValue(val, { emitEvent: false });
      }, 100);
    }
  }
  agRefreshBase(params: any, componentObj: any) {
    componentObj.options = params.colDef.cellEditorParams.componentOptions;
    if (params.colDef.cellEditorParams.componentOptions.fcName) {
      if (!params.colDef.cellEditorParams.componentOptions.group.controls[params.colDef.cellEditorParams.componentOptions.fcName]) {
        componentObj.ngOnInit();
      }
      setTimeout(() => {
        let paramData = params.data
        paramData = this.common.valFromNestedObj(params.colDef.field, paramData);
        let val = params.colDef.cellEditorParams.type === 'date' ? new Date(CommonUtils.isEmptyString(paramData) ? new Date() : paramData) : paramData;
        if (val instanceof Object && val.itemValue !== undefined) {
          val = val.itemValue;
        }
        componentObj.writeValue(val);
        componentObj.options.group.controls[componentObj.options.fcName].setValue(val);
      }, 100);
    }
  }
  // End Richie
}
