import { Component, Input, OnInit } from '@angular/core';
import { File } from '@ionic-native/file/ngx';
import { SocialSharing } from '@ionic-native/social-sharing/ngx';
import { FileService } from 'src/app/commonSRC/psServices/Files/file.Service';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IFileFormat, IOmniCommonFileRequest, IOptionsActionSheet, IOptionsPsActionButton, IOptionsPsButtonShare } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { PsActionButtonComponent } from '../ps-action-button.component';

@Component({
  selector: 'ps-button-share',
  templateUrl: './ps-button-share.component.html',
  styleUrls: ['./ps-button-share.component.scss'],
})
export class PsButtonShareComponent extends PsActionButtonComponent implements OnInit {
  @Input() options: IOptionsPsButtonShare;
  sendOptions: IOptionsPsActionButton;
  cifInfo: any;
  constructor(common: PsCommonService, loggerP: LoggerService, private socialSharing: SocialSharing,
    private fileService: FileService, private file: File) {
    super(common, loggerP);

  }

  ngOnInit() {
    this.sendOptions = {
      labelKey: this.options.labelKey ? this.options.labelKey : 'share_key',
      type: 'button',
      iconName: 'share',
      psClass: 'ps-button-export',
      group: this.options.group
    };
    this.cifInfo = this.common.session.getValueOf(ConstantCommon.USERINFO);
  }

  onShareClicked(event) {
    const actionSheetOptions: IOptionsActionSheet = {
      header: this.common.translate('select_an_option_key'),
      buttons: [{
        text: this.common.translate('pdf_key'),
        handler: () => {
          this.shareReportFile(IFileFormat.PDF);
        }
      },
      {
        text: this.common.translate('excel_key'),
        handler: () => {
          this.shareReportFile(IFileFormat.EXCEL);
        }
      },
      {
        text: this.common.translate('cancel_key'),
        role: 'cancel'
      }]
    };
    this.common.presentActionSheet(actionSheetOptions);
  }
  shareReportFile(fileFormat: IFileFormat) {
    const reportRequest: IOmniCommonFileRequest = {
      reportFormat: fileFormat,
      reportType: ConstantCommon.EXPORT_REPORT_TYPE,
      reportParametersList: this.options.reportParametersList
    };
    this.common.presentLoading();
    this.common.returnReportDetails(reportRequest).then(result => {
      if (result && result['data']['base64Data']) {
        this.common.dismissLoading();
        const mimeType = result['data']['reportFormat'] === IFileFormat.PDF ? 'application/pdf' : 'application/vnd.ms-excel';
        const fileEx = result['data']['reportFormat'] === IFileFormat.PDF ? '.pdf' : '.xls';
        const blobData = this.fileService.base64toBlob(result['data']['base64Data'], mimeType);
        this.shareFile(blobData, fileEx);
      }
    }).catch((error) => {
      this.common.dismissLoading();
      this.common.logger.error('Error: in returnReportDetails() in ps-button-share');
      this.common.logger.error(error);
    });
  }
  shareFile(blobObj, extension) {
    if (this.file.checkFile(this.file.cacheDirectory, this.cifInfo.customerInfoCO.longName + extension)) {
      this.file.writeExistingFile(this.file.cacheDirectory, this.cifInfo.customerInfoCO.longName + extension, blobObj)
        .then(() => this.logger.info('overwrite file is done in ps-button-share'))
        .catch(err => {
          this.logger.error('Error: in writeExistingFile() in ps-button-share')
          this.logger.error(err);
        });
    } else {
      this.file.writeFile(this.file.cacheDirectory, this.cifInfo.customerInfoCO.longName + extension, blobObj)
        .then(_ => this.logger.info('file write successful'))
        .catch(err => {
          this.logger.error('Error: write file failed in ps-button-share');
          this.logger.error(err);
        });
    }
    this.socialSharing.share('report', null, this.file.cacheDirectory + this.cifInfo.customerInfoCO.longName + extension, null).then((suc) => {
      this.logger.info(suc);
    }).catch((error) => {
      this.logger.error('Error: in socialSharing.share() in ps-button-share');
      this.logger.error(error);
    });
  }

}
