import { Injectable } from '@angular/core';
import { DocumentReaderApi, Response } from '@regulaforensics/document-reader-webclient';
import { DocumentReader, DocumentReaderCompletion, Enum } from '@regulaforensics/ionic-native-document-reader/ngx';
import { FaceSDK, ImageType, LivenessResponse, MatchFacesImage, MatchFacesRequest, MatchFacesResponse } from '@regulaforensics/ionic-native-face-api/ngx';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { PsApplicationSettings } from 'src/app/commonSRC/psServices/models/ps-app-settings';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';



@Injectable({
    providedIn: 'root'
})
export class RegulaService {

    constructor(private commonProv: PsCommonService, private logger: LoggerService, private faceSDK: FaceSDK, private docReader: DocumentReader) { }

    openRegulaMobileDocReader(images?: Array<string>) {
        return new Promise<DocumentReaderCompletion>((resolve, reject) => {
            this.commonProv.readFile("www/assets", "regula.license").then((license) => {
                this.docReader.prepareDatabase(PsApplicationSettings.CLIENT_ASSETS_CONFIG.UI_CONFIGURATION.REGULA_DOC_TYPE).subscribe(dbResponse => {
                    this.docReader.initializeReader({ license: license }).then((initResult) => {
                        this.docReader.setConfig({
                            functionality: {
                                cameraFrame: Enum.DocReaderFrame.MAX,
                                showTorchButton: true,
                                showCloseButton: true,
                                showCaptureButton: true,
                                showCaptureButtonDelayFromDetect: 1,
                                showCaptureButtonDelayFromStart: 1,
                                showChangeFrameButton: true,
                                showSkipNextPageButton: true,
                                showCameraSwitchButton: true
                            }, processParams: {
                                scenario: PsApplicationSettings.CLIENT_ASSETS_CONFIG.UI_CONFIGURATION.REGULA_PROCESS,
                                checkHologram: true,
                                timeout: 60.0,
                                timeoutFromFirstDetect: 30,
                                measureSystem: Enum.RGLMeasureSystem.METRIC,
                                logs: true
                            },
                            customization: {
                                cameraFrameShapeType: Enum.FrameShapeType.CORNER
                            }
                        });
                        if (images && images.length > 0) {
                            this.docReader.recognizeImages(images).subscribe((m) => {
                                let completion = DocumentReaderCompletion.fromJson(JSON.parse(m));
                                let results = completion.results;
                                if (results) {
                                    resolve(completion);
                                }
                            });
                        } else {
                            this.docReader.showScanner().subscribe((m) => {
                                let completion = DocumentReaderCompletion.fromJson(JSON.parse(m));
                                let results = completion.results;
                                if (results) {
                                    resolve(completion);
                                }
                            });
                        }
                    }).catch((e) => {
                        this.logger.error(e);
                        reject(e);
                    });
                });
            }).catch((e) => {
                this.logger.error(e);
                reject(e);
            });
        });
    }

    liveDetection() {
        return new Promise<LivenessResponse>((resolve, reject) => {
            this.faceSDK.startLiveness().then(livenessResponse => {
                const response = LivenessResponse.fromJson(JSON.parse(livenessResponse));
                const exception = response.exception;
                if (exception && (exception.errorCode || exception.message)) {
                    reject(exception);
                } else {
                    resolve(response);
                }
            });
        });
    }

    matchFaces(image1Base64, image2Base64) {
        return new Promise<MatchFacesResponse>((resolve, reject) => {
            if (image1Base64 && image2Base64) {
                const firstImage = new MatchFacesImage();
                firstImage.imageType = ImageType.PRINTED;
                firstImage.bitmap = image1Base64;

                const secondImage = new MatchFacesImage();
                secondImage.imageType = ImageType.LIVE;
                secondImage.bitmap = image2Base64;

                const request = new MatchFacesRequest();
                request.images = [firstImage, secondImage];

                this.faceSDK.matchFaces(JSON.stringify(request)).then(matchFacesResponse => {
                    const response = MatchFacesResponse.fromJson(JSON.parse(matchFacesResponse));
                    resolve(response);
                }).catch((err) => {
                    this.logger.error(err);
                    reject(err);
                });
            }
        });
    }

    async parseDocumentThroughApi(imageAsBase64String: string): Promise<Response> {
        const api = new DocumentReaderApi(/* null, "http://localhost:8100" */);
        const result = await api.process({ images: [imageAsBase64String], processParam: null });
        return result;
        // if (result && result.text && result.text.fieldList) {
        //     return result.text.fieldList;
        // }
        // return [];
    }
}
