/* eslint-disable no-console */
import { Directive, ElementRef, Host, Input, Optional, SkipSelf, TemplateRef, ViewContainerRef } from '@angular/core';
import { FormGroupDirective } from '@angular/forms';
import { PsCommonSettings } from '../../psServices/models/ps-common.settings';


@Directive({
  selector: '[psStep]'
})
export class PsStepDirective {

  @Input() set psStep(value: any) {
    let oper = PsCommonSettings.oper_ID;
    if (!PsCommonSettings.currentListOfSteps[oper]) {
      PsCommonSettings.currentListOfSteps[oper] = {};
    }
    PsCommonSettings.currentListOfSteps[oper][value] = {
      templateRef: this.templateRef,
      view: this.view,
      step: value,
      elementRef: this.elementRef,
      controlContainer: this.controlContainer
    }
  }
  constructor(
    @Optional() @Host() @SkipSelf()
    private controlContainer: FormGroupDirective,
    private elementRef: ElementRef,
    private templateRef: TemplateRef<any>,
    private view: ViewContainerRef
  ) { }
}
