import { Component, Input, OnInit } from '@angular/core';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { PsApplicationSettings } from 'src/app/commonSRC/psServices/models/ps-app-settings';
import { IOptionsPsActionIcon, IOptionsPsActionImage } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { PsBaseActionComponent } from '../ps-base-action.component';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';


@Component({
  selector: 'ps-action-icon',
  templateUrl: './ps-action-icon.component.html',
  styleUrls: ['./ps-action-icon.component.scss'],
})
export class PsActionIconComponent extends PsBaseActionComponent implements OnInit {
  @Input() options: IOptionsPsActionIcon = {};
  imageIsRequired;
  imageOptions: IOptionsPsActionImage = {};

  flipRtl;
  get url() {
    let tempUrl = null;
    this.flipRtl = false;
    if (this.options && this.options.iconName) {
      if (!this.options.disableFlipRTL) {
        if (this.common.language.currentLanguage && this.common.language.currentLanguage.direction === 'rtl') {
          this.flipRtl = true;
        }
      }

      if (Object.keys(PsApplicationSettings.commonVariables.svgsFileContent).length > 0) {
        if (this.options.iconName.indexOf(".png") >= 0) {
          this.options.iconName = this.options.iconName.replace('.png', '')
        }
        
		// read first the icon by operation to give implementation team flexibility to use different cotent for same icon name by operation, 
		// if the icon_name by oper not exist get the main icon_name 
		
        const screenCustomizedIcon = this.options.iconName + '_' + PsCommonSettings.oper_ID;
        let base64Content: string = this.options.iconName.indexOf(";base64,") >= 0 ? this.options.iconName : PsApplicationSettings.commonVariables.svgsFileContent[screenCustomizedIcon] || PsApplicationSettings.commonVariables.svgsFileContent[this.options.iconName];
        this.options.iconName = PsApplicationSettings.commonVariables.svgsFileContent[screenCustomizedIcon] ? screenCustomizedIcon : this.options.iconName;

        if (!this.options.fallBackIconName && this.options.iconName.indexOf(".svg") < 0) {
          this.options.fallBackIconName = 'help-circle';
        }
        
		// in case the icon name not found use the fallbackicon base64 instead and keep the same icon name in the CSS binding 
		// so that implementation team can inspect the icon that is missing and add it to the SV file
        
        base64Content = base64Content ? base64Content : PsApplicationSettings.commonVariables.svgsFileContent[this.options.fallBackIconName];
        if (base64Content) {
          if (base64Content.startsWith("<")) {
            this.imageIsRequired = true;
            this.imageOptions.imagePath = PsApplicationSettings.CLIENT_ASSETS_CONFIG.SVG_URL;
            this.imageOptions.imageName = this.options.iconName + "." + base64Content.replace('<', '');
            return true;
          } else if (base64Content.indexOf('svg+xml') >= 0) {
            this.imageIsRequired = false;
            return base64Content;
          } else {
            this.imageIsRequired = true;
            this.imageOptions.imageBase64Url = base64Content;
            return true;
          }
        } else if (this.options.fallBackIconName) {
          this.options.iconName = this.options.fallBackIconName;
        }
      }
      tempUrl = PsApplicationSettings.CLIENT_ASSETS_CONFIG.SVG_URL + this.options.iconName;

      if (this.options.iconName.indexOf(".svg") < 0) {
        tempUrl += tempUrl + '.svg';
      }
    }
    return tempUrl;
  }

  constructor(common: PsCommonService, public logger: LoggerService) {
    super(common, logger);
  }

  onIconClicked(event) {
    if (!this.disabled) {
      this.preCallChecking(event);
    }
  }

  preCallChecking(event) {
    if (this.options.preCallFunction && this.options.preCallFunction.executionClass) {
      this.preCallFunctionCall().then(() => {
        this.handleClickEvent(event);
      }).catch((error) => {
        this.handleClickEvent(event);
      });
    } else {
      this.handleClickEvent(event);
    }
  }

  handleClickEvent(event) {
    if (this.options) {
      this.onClick.emit(event)
    }
  }

  ngOnInit() {
    if (this.options.allowCust === undefined) {
      this.options.allowCust = false;
    }
    if (this.options.labelKey === undefined && this.options.labelOptions && this.options.labelOptions.labelKey) {
      this.options.labelKey = this.options.labelOptions.labelKey;
    }
    super.ngOnInit();
  }
}
