import { ChangeDetectorRef, Component, ElementRef, Input, ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';
import SignaturePad from 'signature_pad';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IFileUploadObject, IOptionsPsActionButton, IOptionsPsFieldLabel, IOptionsPsFieldSignaturePadComponent } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { PsFieldFileComponent } from '../ps-field-file/ps-field-file.component';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';


@Component({
  selector: 'ps-field-signature-pad',
  templateUrl: './ps-field-signature-pad.component.html'
})
export class PsFieldSignaturePadComponent extends PsFieldFileComponent /* implements OnInit */ {

  @Input() options: IOptionsPsFieldSignaturePadComponent;

  signaturePad: SignaturePad;
  @ViewChild('canvas', { read: ElementRef, static: false }) signaturePadElement;
  @ViewChild('containerItem', { read: ElementRef, static: false }) containerItem;

  canvas;

  formGroup: FormGroup = new FormGroup({});

  previewLblOptions: IOptionsPsFieldLabel = {
    labelKey: 'andaryyyyy'
  };

  undoOptions: IOptionsPsActionButton = {
    group: this.formGroup,
    psClass: 'ps-button-back ps-undo-pad-button',
    labelKey: 'undo_key'
  };
  saveOptions: IOptionsPsActionButton = {
    group: this.formGroup,
    psClass: 'ps-button-save ps-save-pad-button',
    labelKey: 'save_key'
  };
  clearOptions: IOptionsPsActionButton = {
    group: this.formGroup,
    psClass: 'ps-button-cancel ps-clear-pad-button',
    labelKey: 'clear_key'
  };

  currentValue;
  currentWidth;
  currentHeight;


  minWidth;
  minHeight;

  constructor(public commonServices: PsCommonService, logger: LoggerService, private cdRef?: ChangeDetectorRef) {
    super(commonServices, logger);
  }

  ngOnInit() {
    super.init();
    this.init();
  }

  public ngAfterViewInit(): void {
    this.afterInit();
    this.getCurrentValue();
  }

  init() {
    this.itemOptions = CommonUtils.copyObject(this.itemOptions, this.options);
    this.itemOptions.hideImageAndIconIfNotPresent = true;
    this.minWidth = parseInt(CommonUtils.getCssVariableValue('--ps-pad-min-width'));
    this.minHeight = parseInt(CommonUtils.getCssVariableValue('--ps-pad-min-height'));
  }

  afterInit() {
    if (this.signaturePadElement && this.containerItem) {
      this.canvas = this.signaturePadElement.nativeElement;
      if (this.canvas) {
        this.signaturePad = new SignaturePad(this.canvas);
        this.signaturePad.penColor = CommonUtils.getCssVariableValue('--ps-signature-pen-color');
        if (CommonUtils.isEmptyOrNull(this.currentValue)) {
          this.signaturePad.clear();
        }
      }
    }
  }

  save(): void {
    const img = this.signaturePad.toDataURL();
    if (this.currentValue != img) {
      this.compWriteValue(img);
    }
  }

  isCanvasBlank(): boolean {
    if (this.signaturePad) {
      return this.signaturePad.isEmpty() ? true : false;
    }
  }

  clear() {
    this.signaturePad.clear();
    this.compWriteValue('');
    // this.onPsEmpty.emit('')
  }

  undo() {
    const data = this.signaturePad.toData();
    if (data.length > 1) {
      data.pop(); // remove the last dot or line
      this.signaturePad.fromData(data);
    } else {
      this.clear();
    }
  }

  getCurrentValue() {
    if (this.canvas) {
      if (this.containerItem) {
        this.currentWidth = (this.containerItem.nativeElement.clientWidth ? this.containerItem.nativeElement.clientWidth : 50) - 50;
        this.currentHeight = (this.containerItem.nativeElement.clientHeight ? this.containerItem.nativeElement.clientHeight : 50) - 50;
        this.currentWidth = this.currentWidth > this.minWidth ? this.currentWidth : this.minWidth;
        this.currentHeight = this.currentHeight > this.minHeight ? this.currentHeight : this.minHeight;
        if (this.currentWidth != this.canvas.width || this.canvas.height != this.currentHeight) {
          this.canvas.width = this.currentWidth;
          this.canvas.height = this.currentHeight;
          this.afterInit();
        }
      }
      this.drawCanvasFromControls();
      let tempValue = super.returnValue();// this.options.group.controls[this.options.fcName].value;
      if (this.currentValue!= tempValue) {
        this.currentValue = tempValue;
        //check  if tempValue is defined
        if(tempValue['0'])
          this.signaturePad.fromDataURL(tempValue['0'].imageUrl);
        this.compWriteValue(tempValue)
        // const data = this.signaturePad.toData();
        // this.signaturePad.fromData(data);
      } 
    }
  }

  compWriteValue(value) {
    if (value) {
      let file: IFileUploadObject[]
      //to check if the value is file or string (incase it had a saved value from the screen's saved data)
      if (typeof value === 'string' || value instanceof String && value.split(',')[1]){
        file = [{
          status: 'N',
          file: value.split(',')[1],
          fileName: this.options.fcName + '.png',
          fileExt: 'png',
          isImage: true,
          imageUrl: value,
          imagePoints:this.signaturePad.toData()
          }];
      }else{
        file=value;
      }
      super.setFileValController(file);
      this.currentValue = file;
      this.onPsChange.emit(file);
    } else {
      super.resetFileValController();
      this.onPsChange.emit([]);//Makki: AZDB-5092 Force emit an empty array to override existing array
    }
  }
  //to set the pointGroup of drawn signature inside the signature pad after we fetch them from the saved data
  setSavedPoints(savedValue){
    const pointGroupLength=this.signaturePad.toData().length;
    // to check if canvas is not empty (has value from saved data) but pointGroup array of drawn image is empty
    if(!this.isCanvasBlank() && pointGroupLength==0){
      const savedpointsLength=savedValue.imagePoints.length;
      for(let i=0;i<savedpointsLength;i++){
        this.signaturePad.toData()[i]={...savedValue.imagePoints[i]};
      }
    }
  }
   //used to draw and initialize the signature in canvas after fetching value from controls if it already had a saved value
   drawCanvasFromControls(){
    let specVal=this.options.group.controls[ConstantCommon.CONTROLLER_FORM_DATA].value[this.options.fcName];
    if(specVal && this.isCanvasBlank()){
        this.signaturePad.fromDataURL(specVal['0'].imageUrl);
        this.setSavedPoints(specVal['0']);
      }
  }
}