import { DOCUMENT } from '@angular/common';
import { Component, ElementRef, Inject, OnInit, ViewChild } from '@angular/core';
import ResizeObserver from 'resize-observer-polyfill';
import { Subscription } from 'rxjs';
import { PsBaseComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IOptionsPsLabel } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';

import { INewsFeedResponse, IOmniNewsFeedRequest } from '../../psServices/models/ps-common-bussiness-interfaces';
import { OmniPullService } from '../../psServices/omni-common/omni-pull.service';
import { IPsNewsSliderOptions } from './ps-news-slider.component.interface';

@Component({
  selector: "ps-news-slider",
  templateUrl: "./ps-news-slider.component.html",
  styleUrls: ["./ps-news-slider.component.scss"],
})
export class PsNewsSliderComponent extends PsBaseComponent implements OnInit {
  options: IPsNewsSliderOptions = {};
  @ViewChild("scrolling_text", { static: true }) contentRef: ElementRef;
  @ViewChild("container", { static: true }) containerRef: ElementRef;

  constructor(
    private commonService: PsCommonService,
    private commonProv: PsCommonService,
    private omniPull: OmniPullService,
    public loggerP: LoggerService,
    public nav: PsNavigatorService,
    @Inject(DOCUMENT) private document: Document
  ) {
    super(commonService, loggerP);
  }
  public listOfNews: any[];
  private newsFeedRequest: IOmniNewsFeedRequest;
  public languageSub: Subscription;
  public direction: string;
  private tickerObserver: ResizeObserver;
  translateX: number;
  animationId: number;
  containerWidth: number;

  ngOnInit() {
    this.options.listOfOptions = [];
    if (this.nav.isUserLoggedIn === true) {
      this.newsFeedRequest = {
        location: "2",
      };
    } else {
      this.newsFeedRequest = {
        location: "1",
      };
    }
    this.getTickerLangDir();
  }

  ngAfterViewInit() {
    if (this.containerRef && this.containerRef.nativeElement) {
      this.tickerObserver = new ResizeObserver(() => {
        const width = this.containerRef.nativeElement.offsetWidth;

        this.containerWidth = width;
      });

      this.tickerObserver.observe(this.containerRef.nativeElement);
    }
  }
  private animate() {
    const contentWidth = this.contentRef.nativeElement.scrollWidth;

    if (contentWidth) {
      if (this.direction == "rtl") {
        --this.translateX;
        if (this.translateX < -contentWidth)
          this.translateX = this.containerWidth;
      }

      if (this.direction == "ltr") {
        ++this.translateX;
        if (this.translateX >= this.containerWidth)
          this.translateX = -contentWidth;
      }
    }
    this.animationId = requestAnimationFrame(() => {
      this.animate();
    });
  }
  getItem(item: INewsFeedResponse): IOptionsPsLabel {
    const tickerOptions: IOptionsPsLabel = {
      translate: false,
      labelKey: item.newsdisplayed,
    };
    return tickerOptions;
  }
  getTickerLangDir() {
    this.languageSub = this.commonProv.languageChanged.subscribe(() => {
      const width = this.containerRef.nativeElement.offsetWidth;
      const contentWidth = this.contentRef.nativeElement.scrollWidth;
      if (this.animationId) {
        cancelAnimationFrame(this.animationId);
      }

      this.returnNewsFeed(this.newsFeedRequest);

      if (
        this.document.documentElement.dir &&
        this.document.documentElement.dir.toLocaleLowerCase() === "rtl"
      ) {
        this.direction = "ltr";
        this.translateX = -contentWidth;
      } else {
        this.direction = "rtl";
        this.translateX = width;
      }

      
      
    });
  }

  private async returnNewsFeed(newsFeedRequest: IOmniNewsFeedRequest) {
    const result = await this.omniPull
      .returnNewsFeedList(newsFeedRequest)
      .catch((error) => {
        this.options.listOfOptions = [];
        this.logger.error(
          "Error: While fetching News Feed in PsNewsSliderComponent :",
          error
        );
      });

    if (result && result.gridModel.length > 0) {
      this.options.listOfOptions = this.populateNewsFeed(result.gridModel);
      if(this.options.listOfOptions){
        this.animate();
      }
    } else {
      this.options.listOfOptions = [];
    }
  }

  private populateNewsFeed(newsFeed: Array<any>): INewsFeedResponse[] {
    this.listOfNews = [];
    for (const iterator of newsFeed) {
      const newsFeed: INewsFeedResponse = {
        newsdisplayed:
        " | " + iterator.title + ": " + iterator.description + " | ",
      };
      this.listOfNews.push(newsFeed);
    }

    return this.listOfNews;
  }

  ngOnDestroy() {
    cancelAnimationFrame(this.animationId);
    this.tickerObserver.disconnect();
  }
}
