import { Component, ElementRef, Input, OnInit } from '@angular/core';
import { AuthenticationMatrixService } from 'src/app/commonBussinessSRC/psServices/authentication-matrix/authentication-matrix.service';
import { Events } from 'src/app/commonSRC/psServices/Event/event.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IOptionsPsButtonSendBack, IOptionsPsButtonSubmit } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { PsNetworkService } from 'src/app/commonSRC/psServices/network/ps-network.service';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { IOptionsPsAlertButton, IOptionsPsComplexAlertController } from 'src/app/commonSRC/psTemplates/ps-template-alert-controller/ps-template-alert-controller.template.interfaces';
import { PsButtonSubmitComponent } from '../ps-button-submit/ps-button-submit.component';


@Component({
  selector: 'ps-button-send-back',
  templateUrl: './ps-button-send-back.component.html'
})
export class PsButtonSendBackComponent extends PsButtonSubmitComponent implements OnInit {

  @Input() options: IOptionsPsButtonSendBack;

  defaultOptions: IOptionsPsButtonSubmit;

  constructor(public commonService: PsCommonService, private navService: PsNavigatorService,
    private authService: AuthenticationMatrixService, private psNetworkServiceChild: PsNetworkService, public events: Events, elRef?: ElementRef) {
    super(commonService, navService, authService, psNetworkServiceChild, events, elRef);
  }


  ngOnInit() {
    super.init();
    this.defaultOptions = {
      labelKey: 'send_back_key',
      iconName: 'send-back-icon',
      psClass: 'ps-button-send-back',
      group: this.options.group,
      actionType: ConstantCommon.ACTION_TYPE_SENT_BACK,
    };
  }


  askForReason() {
    return new Promise<any>((resolve, reject) => {
      const alertController: IOptionsPsComplexAlertController = {
        cssClass: 'ps-button-sent-back',
        header: 'send_back_reason_key',
        inputs: [
          {
            type: 'keyin',
            options: {
              placeHolder: 'add_send_back_rej_reason_key',
              labelKey: 'reason_key',
              fcName: 'sendBackReason',
              group: this.options.group,
              enableOnDisabledScreen:true,
              forceShowOnPreview: true
            }
          }
        ],
        buttons: [
          {
            role: 'cancel',
            options: {
              labelKey: 'cancel_key',
              psClass: 'ps_cancel_rejection_reason_button',
              group: this.options.group
            },
            handler: () => {
              this.options.group.removeControl('sendBackReason');
              reject(false);
            }
          }
        ]
      };
      const psButtonSave: IOptionsPsAlertButton = {
        role: 'submit',
        handler: () => {
          CommonUtils.dismissAllModals();
        }
      };

      (psButtonSave.options as IOptionsPsButtonSubmit) = {
        ...this.options,
        labelKey: 'submit_key',
        psClass: 'ps-drafts-report-submit-button',
        isSave: false,
        actionType: ConstantCommon.ACTION_TYPE_SENT_BACK,
        postCallFunction: this.options.postCallFunction,
        preCallFunction: this.options.preCallFunction,
        failureCallFunction: this.options.failureCallFunction,
        extraParams: {
          ...this.options.extraParams, actionType: ConstantCommon.ACTION_TYPE_SENT_BACK,
        }
      };
      alertController.buttons.push(psButtonSave);
      this.commonService.presentPsAlert(alertController, 'ps-send-back-button');

    });
  }

}
