import { Injectable } from '@angular/core';
import Color from 'colorjs.io/color';

@Injectable({
  providedIn: 'root'
})

/**
 * 
 * @author Gilbert Al Indary
 * For reference check https://colorjs.io/docs/
 * 
 */


export class PsColorService {

  constructor() { }

  public newColor(color, options?) {
    return new Color(color);
  }

  /**
   * Create a gradient color from 2 colors
   * @param color1 
   * @param color2 
   */
  public createRangeOfColors(color1, color2, options?) {
    return Color.range(color1, color2);
  }

  /**
   *  Create a list of variant colors from 2 colors based on number of steps
   * @param color1 
   * @param color2 
   * @param steps 
   * @param options 
   */
  public interpolateColor(color1, color2, steps, options?): Array<any> {
    let r = Color.range(color1, color2);
    return Color.steps(r, { steps: steps });
  }

  /**
   * Return what color to use in contrast to the color that is sent (as RGB); by default it returns either black or white
   * @param color 
   * @param lightColor 
   * @param darkColor 
   */
  public contrastColors(color, lightColor?, darkColor?) {
    const map = this.returnRGBColorSeperated(color);
    if (map != null) {
      const red = map.r;
      const green = map.g;
      const blue = map.b;
      if ((red * 0.299 + green * 0.587 + blue * 0.114) > 186) {
        return darkColor ? darkColor : '#000000';
      } else {
        return lightColor ? lightColor : '#ffffff';
      }
    }
  }


  /**
   * return map consisting of rgb elements seperated ;; {r: number, g: number, b: number}
   * @param color 
   */
  public returnRGBColorSeperated(color: string): {
    r: number, g: number, b: number
  } {
    if (color) {
      let tempColor = color;
      let rgbMap = {
        r: null,
        g: null,
        b: null
      };
      let tempMap = [];
      const convertedColor = this.convertColorAsString(color, 'srgb');
      tempColor = convertedColor ? convertedColor : color;
      tempColor = tempColor.replace('rgb', '');
      tempColor = tempColor.replace('(', '');
      tempColor = tempColor.replace(')', '');
      tempColor = tempColor.replace(/%/g, '');
      if (tempColor.indexOf(',') != -1) {
        tempMap = tempColor.split(',');
      } else {
        tempMap = tempColor.split(' ');
      }
      if (tempMap && tempMap.length == 3) {
        rgbMap.r = parseFloat(tempMap[0]);
        rgbMap.g = parseFloat(tempMap[1]);
        rgbMap.b = parseFloat(tempMap[2]);
      }
      if (rgbMap.r && rgbMap.g && rgbMap.b) {
        return rgbMap;
      }
      return null;
    }
    return null;
  }


  /**
   * convert color from a color space to another and return as Color object; ex from hex to rgb
   * @param color 
   * @param colorSPace 
   */
  public convertColor(color, colorSPace: 'hex' | 'lch' | 'srgb' | 'p3' | 'a98rgb' | 'rec2020' | 'prophoto' | 'hsl' | 'hwb' | 'hsv' | 'jzazbz' | 'jzczhz' | 'ictcp' | 'oklab') {
    if (color && colorSPace) {
      try {
        let result;
        let tempColor;
        if (this.isHexColor(color)) {
          color = this.hexToRgb(color, true, true);
          tempColor = new Color('srgb', color);
        } else {
          tempColor = new Color(color);
        }

        if (colorSPace == 'hex') {
          result = tempColor.toHex();
        }

        result = tempColor.to(colorSPace);
        return result;
      } catch (err) {
        return null;
      }
    }
  }

  /**
   * convert color from a color space to another and return as String; ex from hex to rgb
   * @param color 
   * @param colorSPace 
   */
  public convertColorAsString(color, colorSPace: 'hex' | 'lch' | 'srgb' | 'p3' | 'a98rgb' | 'rec2020' | 'prophoto' | 'hsl' | 'hwb' | 'hsv' | 'jzazbz' | 'jzczhz' | 'ictcp' | 'oklab') {
    const tempColor = this.convertColor(color, colorSPace);
    if (colorSPace === 'srgb') {
      return this.hexToRgb(tempColor.toHex());
    }
    return tempColor ? tempColor.toString() : '';
  }

  isHexColor(hex) {
    const regexp = new RegExp(/^#([A-Fa-f0-9]{3}$)|([A-Fa-f0-9]{6}$)/, 'gi');
    return typeof hex === 'string' && hex.match(regexp);
  }

  hexToRgb(hex, asArray?: boolean, zeroToOneRange?: boolean) {
    let result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
    if (result) {
      let r = parseInt(result[1], 16);
      let g = parseInt(result[2], 16);
      let b = parseInt(result[3], 16);
      if (zeroToOneRange) {
        r = r / 255;
        g = g / 255;
        b = b / 255;
      }
      return asArray ? [r, g, b] : `rgb(${r},${g},${b})`;
    }
    return null;
  }
}
