import { Component } from '@angular/core';
import { ICellRendererAngularComp } from 'ag-grid-angular';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IOptionsPsActionIcon } from 'src/app/commonSRC/psServices/models/ps-common-interface';

@Component({
  selector: 'ps-ag-grid-action-renderer',
  templateUrl: './ps-ag-grid-action-renderer.html'
})

export class PsAgGridActionRendererComponent implements ICellRendererAngularComp {

  params;
  editModeOn = false;
  public editIconOptions: IOptionsPsActionIcon = {
    iconName: 'create'
  };
  public submitIconOptions: IOptionsPsActionIcon = {
    iconName: 'save'
  };

  agInit(params): void {
    this.params = params;
  }

  refresh(params?: any): boolean {
    return true;
  }

  onEditClick() {
    this.params.editModeOn = true;
    if (this.params.onEditClick instanceof Function) {
      this.params.onEditClick(this.params);
    }
  }
  onSubmitClick() {
    this.params.editModeOn = false;
    if (this.params.onSubmitClick instanceof Function) {
      this.params.onSubmitClick(this.params);
    }
  }
}