import { Injectable } from '@angular/core';
import { NetworkInterface } from '@awesome-cordova-plugins/network-interface/ngx';
import { CommonUtils } from '../models/common-utils';

@Injectable({
  providedIn: 'root'
})
export class PsNetworkInterfaceService {

  constructor(private networkInterface: NetworkInterface) { }

  private async getWiFiIPAddress() {
    if (CommonUtils.isNativeMobile()) {
      return await this.networkInterface.getWiFiIPAddress();
    }
  }

  private async getCarrierIPAddress() {
    if (CommonUtils.isNativeMobile()) {
      return await this.networkInterface.getCarrierIPAddress();
    }
  }

  async getHttpProxyInformation(url) {
    if (CommonUtils.isNativeMobile()) {
      return await this.networkInterface.getHttpProxyInformation(url);
    }
  }

  async getFullWIFIDetails() {
    if (CommonUtils.isNativeMobile()) {
      let wifiIP = await this.getWiFiIPAddress();
      let carrieriIP = await this.getCarrierIPAddress();

      return {
        wifiIPAddress: wifiIP,
        carrieriIPAddress: carrieriIP
      }
    }
  }
}
