import { Component, Input, OnInit } from "@angular/core";
import { LoggerService } from './../../../../../../psServices/logger/logger.service';
import { ConstantCommon } from './../../../../../../psServices/models/common-constant';
import { PsSelect } from './../../../../../../psServices/models/common-type';
import { IOptionsPsKeyinTextarea, IOptionsPsTextareaAutocomplete } from './../../../../../../psServices/models/ps-common-interface';
import { PsCommonService } from './../../../../../../psServices/ps-common/ps-common.service';
import { PsKeyinTextareaComponent } from './../ps-keyin-textarea.component';

@Component({
    selector: 'ps-textarea-autocomplete',
    templateUrl: './ps-textarea-autocomplete.component.html'
})
export class PsTextareaAutocompleteComponent extends PsKeyinTextareaComponent implements OnInit {
    @Input() options: IOptionsPsTextareaAutocomplete = {};
    textareaOptions: IOptionsPsKeyinTextarea = {
        rows: ConstantCommon.TEXTAREA_DEFAULT_NUMBER_OF_ROWS,
        cols: ConstantCommon.TEXTAREA_DEFAULT_NUMBER_OF_COLS
    };

    showAutoCompleteList = false;
    filteredList: PsSelect = [];
    cursorIndex = -1;
    elemetToBeReplacedPosition;
    numberOfCharsToBeRemoved;

    constructor(commonProv: PsCommonService, loggerP: LoggerService) {
        super(commonProv, loggerP);
    }

    ngOnInit() {
        super.ngOnInit();
        this.commonProv.copyObject(this.textareaOptions, this.options, true, true);
        this.filteredList = [...this.options.autoCompleteList];
    }

    onKeydownClicked(event: Event) {
        this.cursorIndex = event.target['selectionStart'];
        if (this.options.triggerKeyDown) {
            const keyboardKey = event as KeyboardEvent;
            if (keyboardKey.code === this.options.keyboardCode) {
                this.filteredList = [...this.options.autoCompleteList];
                this.showAutoCompleteList = true;
            }
        }
    }

    onElementSelected(element) {
        if (this.options.group.controls[this.options.fcName].value) {
            let currentValue = this.options.group.controls[this.options.fcName].value as String;
            if (this.elemetToBeReplacedPosition) {
                const firstSec = currentValue.substring(0, this.elemetToBeReplacedPosition);
                const secondSec = currentValue.substring(this.elemetToBeReplacedPosition + this.numberOfCharsToBeRemoved);
                currentValue = firstSec + ' ' + secondSec;
                this.cursorIndex = this.cursorIndex - this.numberOfCharsToBeRemoved;
            } else {
                currentValue = '';
            }
            const firstPart = currentValue.substring(0, this.cursorIndex);
            const secondPart = currentValue.substring(this.cursorIndex);
            this.options.group.controls[this.options.fcName].setValue(firstPart + ' ' + element.description + ' ' + secondPart, { emitEvent: true });
        } else {
            this.options.group.controls[this.options.fcName].setValue(element.description, { emitEvent: true });
        }
        this.showAutoCompleteList = false;
    }

    onCharacterEntered(event) {
        if (event['data']) {
            if (event['data'] === ConstantCommon.SPACE_CHARACTER) {
                this.filteredList = [...this.options.autoCompleteList];
            } else {
                let searchElement;
                if (event['target']['defaultValue'] === '') {
                    searchElement = event['data'];
                } else {
                    const inputData = event['target']['defaultValue'] as String;
                    const searchableData = inputData.substring(0, this.cursorIndex);
                    const spaceIndex = searchableData.lastIndexOf(ConstantCommon.SPACE_CHARACTER);
                    if (spaceIndex === -1) {
                        searchElement = searchableData.slice(0) + event['data'];
                    } else if (spaceIndex === searchableData.length - 1) {
                        searchElement = event['data'];
                    } else {
                        searchElement = searchableData.slice(spaceIndex + 1) + event['data']
                    }
                    this.elemetToBeReplacedPosition = spaceIndex === -1 ? undefined : spaceIndex;
                    this.numberOfCharsToBeRemoved = searchElement.length;
                }
                this.filteredList = this.options.autoCompleteList.filter((element) => element.description.startsWith(searchElement));
            }
            this.showAutoCompleteList = true;
        } else {
            this.showAutoCompleteList = false;
        }
    }

    onClickOutside(event) {
        this.showAutoCompleteList = false;
    }
}