import { Component, EventEmitter, Input, Output } from '@angular/core';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IOptionsPsActionIconExposed, IOptionsPsContainerItem } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { PsBaseContainerComponent } from '../ps-base-container.component';


@Component({
  selector: 'ps-container-item',
  templateUrl: './ps-container-item.component.html',
  styleUrls: ['./ps-container-item.component.scss'],
})
export class PsContainerItemComponent extends PsBaseContainerComponent {

  @Input() options: IOptionsPsContainerItem = {};
  @Output() onPsImageClicked = new EventEmitter<void>();
  @Output() onPsIconClicked = new EventEmitter<void>();

  constructor(commonProv: PsCommonService, loggerP: LoggerService) {
    super(commonProv, loggerP);
  }
  ngOnInit() {
  }

  onIconClicked(event) {
    this.onPsIconClicked.emit(event);
  }

  onImageClicked() {
    this.onPsImageClicked.emit();
  }

  get serviceMappingIconOptions(): IOptionsPsActionIconExposed {
    let iconOpt = {};
    if (this.options && this.options.serviceMappingIconOptions) {
      iconOpt = {
        ... this.options.serviceMappingIconOptions
      };
    }
    return iconOpt;
  }

  public get showError() {
    if (this.options && this.options.componentOptions) {
      const errorMsgs = this.options.componentOptions.group.controls[this.options.componentOptions.fcName] ? this.options.componentOptions.group.controls[this.options.componentOptions.fcName][ConstantCommon.CONTROLLER_ERROR_MSGS] : undefined;
      return (errorMsgs && this.options.componentOptions.group.get(this.options.componentOptions.fcName) && this.options.componentOptions.group.get(this.options.componentOptions.fcName).touched) || (errorMsgs && this.options.componentOptions.group.controls[this.options.componentOptions.fcName] && this.options.componentOptions.group.controls[this.options.componentOptions.fcName].touched);
    } else {
      return false;
    }
  }

  public get errorMsgs() {
    if (this.options && this.options.componentOptions) {
      return this.options.componentOptions.group.controls[this.options.componentOptions.fcName][ConstantCommon.CONTROLLER_ERROR_MSGS];
    } else {
      return null;
    }
  }

  /**
   * @author RichardZourob
   * #BUG 1245198
   * getter to return the invalid value entered as a prefix for the error msg to be shown
   */
  public get invalidValueEntered() {
    if (this.options && this.options.componentOptions && this.options.componentOptions.group && !(this.options.componentOptions.hideValueOnError)
      && !CommonUtils.isEmptyOrNull(this.options.componentOptions.group.controls[this.options.componentOptions.fcName][ConstantCommon.CONTROLLER_INVALID_VALUE_ENTERED])) {
      return this.common.translate('value_key') + ' ' + this.options.componentOptions.group.controls[this.options.componentOptions.fcName][ConstantCommon.CONTROLLER_INVALID_VALUE_ENTERED];
    } else {
      return null;
    }
  }

}
