  /**
     logoutType values :
     case 0: Manual Logout 
   case 1: Force Logout
   case 2: Session Timeout Logout  
   case 3: Account Deactivation Logout
   case 4: User Blocked Maximum trials reached Logout
   case 5: device deactivated
   case 6: session terminated from admin console or refresh token expired - related to security errorcode = -25 (keycloak)
  case 7: Onboarding Complete
   */
  logout(logoutType: number, activityLogEnabled?: boolean, dependencies?): Promise<any> {
    /* nabil feghali - OMNI common security */
    return new Promise<any>((resolve, reject) => {
      this.common.presentLoading();
      const userInfo = this.common.session.getValueOf(ConstantCommon.USERINFO);
      const sessionAppId = this.common.session.getValueOf(ConstantCommon.APP_ID);


      const loginToken = this.common.session.getValueOf(ConstantCommon.SERVER_LOGIN_TOKEN);
      let refreshToken = null;
      if (loginToken != null) {
        refreshToken = loginToken.refreshToken;
      }

      const parameter = {
        userName: userInfo ? userInfo.omniUserVO.NAME : null,
        operId: 1559,
        sessionId: this.common.session.getValueOf(ConstantCommon.SESSION_ID),
        compCode: this.returnCompCode(),
        channelId: this.returnChnlID(),
        appId: sessionAppId ? sessionAppId : this.returnAppID(),
        ocUserId: userInfo ? userInfo.ocUserId : null,
        logoutType: logoutType ? logoutType : 0,
        refreshToken: refreshToken
      };
      // added by salah for AZDB-904 ABEOI230131 Error when logging out from corporate user// 
      // to navigate to the dummy screen before navigating to the activity log screen
      // since on logout no setting for the Dummyscreen operation was found
      this.navService.navigateToDummyPage();

      this.common.http.commonRequestAjax('rest/omniCommon/logoutUser', parameter
      ).then
        ((result) => {
          this.common.session.append(ConstantCommon.SERVER_PUBLIC_KEY, result.data.serverPublicKey);
          this.prepareCommonParameters(result.data.parameters);
          this.logoutProcess(false, activityLogEnabled, dependencies).then(() => {
            resolve(true);
          }).catch((err) => {
            reject(err);
          });

        }).catch((error) => {
          this.common.dismissLoading();
          this.logoutProcess(true).then(() => {
            resolve(true);
          }).catch((err) => {
            reject(err);
          })
          this.common.logger.error(error); reject(error);
        });

    });

    /* nabil feghali - OMNI common security - end  */
  }

  //Modified by ghada, tp1592133 for fixing the issue of customization request being sent with the wrong operId due to the naviagtion to login screen then to activity report before login being initalized on logout
  //sending activityLogRepEnabled, dependencies as params to handel the navigation to activity report to be only from logout button and to avoid injecting code dependencies  
  logoutProcess(keepCache?: boolean, activityLogRepEnabled?: boolean, dependencies?) {
    return new Promise<any>(async (resolve, reject) => {
      const userInfo = this.session.getValueOf(ConstantCommon.USERINFO);
      let page: IPageCommon = {};

      //in case activity log report is enabled, the navigation should be directly to the report on logout
      if (activityLogRepEnabled) {
        let queryParams = (await this.returnActivityLogReportQueryParams(userInfo, dependencies)).queryParams;
        page = {
          icon: 'activity-log',
          title: 'activity_log_key',
          operID: CommonBussinessConstant.ACTIVITY_LOG_OPER_ID_LOGOUT,
          param: queryParams
        }
      } else {
        page = {
          operID: ConstantCommon.LOGIN_OPER_ID,
          param: { logout: true }
        }
      }
      if (!keepCache) {
        this.common.screenDisplayParams.clear(); // Modified by Richie for #BUG 1259353
        this.common.initialScreenDisplayParams.clear();
        this.dynamicButtonsService.actionButtonsMap.clear();
        this.common.clearDynamicScreenMap();// Added by Richie for #BUG 1347900 in order to clear the cached dynamic screens
        //TP#1319153 - Empty the pending dynamic screen map
        this.common.dynamicLoadersCountMap.clear();
        //TP#1320717 - Empty the common param screen map
        this.common.initialScreenParamVals.clear();
        PsCommonSettings.currentListOfSteps = {};
      }
      if (PsCommonSettings.custMode) {
        PsCommonSettings.custMode = false;
      }
      //the full logout process should be done before navigating to activity report including removing all login data so that report services won't crash due of missing data previously removed on calling logout service like login Token
      if (activityLogRepEnabled) {
        this.doLogoutReseting(CommonBussinessConstant.ACTIVITY_LOG_OPER_ID_LOGOUT);
      }
      //instead of calling navigateToMain directly, openPage is the standard way to navigate takeing in consideration page title if it has, then calling navigateToMain with the right url and options
      this.navService.openPage(page, true).then(() => {
        this.doLogoutReseting();
        this.common.dismissLoading();
        resolve(true);
      }).catch((err) => {
        this.common.dismissLoading();
        this.common.logger.error(err); reject(err);
      });
    })
  }

  doLogoutReseting(operID?) {
    document.querySelector('html').classList.remove('userIsLoggedIn');
    document.querySelector('html').classList.add('userIsNotLoggedIn');
    this.navService.isLoggedIn.next(false);
    this.common.clearResponse();
    this.common.session.clearUserSession();
    PsCommonSettings.oper_ID = operID ? operID : ConstantCommon.LOGIN_OPER_ID;
    this.errorHandlerService.resetLogLevelByUser();
  }

  sLYAoMHUew91pvuA(length) {
    const array = new Uint8Array(length / 2);
    window.crypto.getRandomValues(array);
    return Array.from(array, byte => ('0' + byte.toString(16)).slice(-2)).join('');
  }

  async returnActivityLogReportQueryParams(userInfo, dependencies) {
    let sessionActivityCloseTime: any;
    const sessionId = this.session.getValueOf(ConstantCommon.SESSION_ID);
    const result = await dependencies.getParamValOf(CommonBussinessConstant.REPORT_CLOSE_TIME);
    if (result && result !== -1) {
      sessionActivityCloseTime = result.SessionActivityCloseTime;
    }

    const navigationExtras: PSNavigationOptions = {
      psPage: {
        icon: 'activity-log',
        title: 'activity_log_key',
      },
      queryParams: {
        operId: CommonBussinessConstant.ACTIVITY_LOG_OPER_ID_LOGOUT,
        filterSessionId: sessionId,
        ocUserId: userInfo?.ocUserId,
        timeOutCount: sessionActivityCloseTime,
        function: {
          func() {
            return new Promise<any>((resolve, reject) => {
              this.executionClass.navService.navigateToMain(['./omni'], { queryParams: { logout: true } });
              resolve(true);
            });
          },
          executionClass: this,
          params: []
        },
        message: CommonUtils.translate('report_will_close_after_key'),
        logout: true
      }
    };
    return navigationExtras
  }

