  endedByOtherActiveSession(errorCode?: number) {
    const userIsLoggedIn = this.session.getValueOf(ConstantCommon.USER_IS_LOGGED_IN);
    const userForceLogout = this.session.getValueOf(ConstantCommon.USER_FORCE_LOGOUT);
    if (userIsLoggedIn && errorCode === -3
      && (typeof userForceLogout === 'undefined' || userForceLogout === undefined || userForceLogout == null)) {
      this.session.append(ConstantCommon.USER_FORCE_LOGOUT, true);
      CommonUtils.dismissAllModals().then(res => {
        if (res == true) {
          this.logout(1).then(() => {
            this.presentInfoAlert(this.common.translate('force_logout_key'));
          });
        }
      })

    } else if (userIsLoggedIn && errorCode === -20 && (typeof userForceLogout === 'undefined' || userForceLogout === undefined || userForceLogout == null)) {
      this.session.append(ConstantCommon.USER_FORCE_LOGOUT, true);
      CommonUtils.dismissAllModals().then(res => {
        if (res == true) {
          this.logout(1).then(() => {
            this.presentInfoAlert(this.common.translate('access_time_reached_key'));
          });
        }
      })

    }
    else if (errorCode === -7 && !this.common.isNativeMobile() && !this.sessionReloaded) {
      this.confirmSessionReload();
    }
    else {
      CommonUtils.dismissAllModals().then(res => {
        if (res == true) {
          if (errorCode === -3) {
            this.presentInfoAlert(this.common.translate('force_logout_key'));
          } else if (errorCode === -20) {
            this.presentInfoAlert(this.common.translate('access_time_reached_key'));
          } else {
            this.presentInfoAlert(this.common.translate('security_alert_key'));
          }
        }
      });

    }
  }
  /*nabil feghali : device registration*/
  deviceRegistrationErrorHandler(errorCode?: number) {
    CommonUtils.dismissAllModals();
    /*-21 error code generated from security interceptor in case the device status is inactive, that means not trusted or registered by any oc user*/
    if (errorCode === -21) {
      CommonUtils.dismissAllModals().then(res => {
        if (res == true) {
          this.logout(5).then(() => {
            this.presentInfoAlert(this.common.translate('device_inactive_key'));
          });
        }
      })

    }
    /*-22 error code generated from security interceptor in case the device status is blocked, like for example stolen*/
    else if (errorCode === -22) {
      this.presentFailureAlert(this.common.translate('device_blocked_key'), { autoHide: false, noButtons: true });
    }
  }

