  branchSelectionAndMapControl(visible: number, fcNamesAndMandatories: { fcName: string, mandatory?: 0 | 1 }[], emitEvent = false) {
    fcNamesAndMandatories.forEach(element => {
      if (!element.mandatory) {
        element.mandatory = 0;
      }
      if (!visible) {
        this.common.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [element.fcName], 0, emitEvent);
      } else {
        const custVisible = this.common.getInitialState(element.fcName);
        if (custVisible && custVisible.IS_VISIBLE === 1) {
          this.common.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [element.fcName], 1, emitEvent);
          this.common.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [element.fcName], element.mandatory);
        } else if (custVisible && custVisible.IS_VISIBLE === 0) {
          this.common.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [element.fcName], 0, emitEvent);
        } else {
          this.common.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [element.fcName], 1, emitEvent);
          this.common.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [element.fcName], element.mandatory);
        }
      }
    });
  }

  exportData(fileFormat: IFileFormat, reportParamsList?: any, operId?: number, reportName?: string) {
    const reportRequest: IOmniCommonFileRequest = {
      operId: operId,
      reportFormat: fileFormat === IFileFormat.PDF ? IFileFormat.PDF : IFileFormat.EXCEL,
      reportType: ConstantCommon.EXPORT_REPORT_TYPE,
      reportParametersList: reportParamsList
    };
    this.common.presentLoading(); // Temporary loader added.
    const psDatePipe = new PsDateFormatPipe();
    const param = this.session.getValueOf(ConstantCommon.preLoginResponse).parameters;
    const displayFormat = param.DefaultDateTimeFormat ? param.DefaultDateTimeFormat.valueDesc : ConstantCommon.DEFAULT_DATE_FORMAT;
    let operationDetails = this.common.getOperationDetails(PsCommonSettings.oper_ID);
    this.common.returnReportDetails(reportRequest).then(result => {
      if (result) {
        this.common.dismissLoading();
        const mimeType = fileFormat === IFileFormat.PDF ? 'application/pdf' : 'application/vnd.ms-excel';
        // const blobData = this.fileService.base64toBlob(result['data']['base64Data'], mimeType);
        const operName = operationDetails.DESCRIPTION || CommonUtils.translate(operationDetails.OPER_NAME)
        this.common.downloadFile(result['data']['base64Data'], (reportName ? reportName : operationDetails ? operName : '' + '-')
          + '-' + psDatePipe.transform(new Date(), displayFormat) + '.' + (result['data']['reportFormat']).toLocaleLowerCase(), result['data']['reportFormat']);
      }
    }).catch((error) => {
      this.common.dismissLoading();
      this.common.logger.log(error);
    });
  }

  getParentDocumentsList(OperId: number) {
    const businessProfileMap = this.common.session.getValueOf(ConstantCommon.BUSINESS_PROFILE_MAP);
    let listOptions: PsSelect = [];
    if (businessProfileMap) {
      const parentReportChildren = businessProfileMap.filter(element => (element.OPER_P_ID == OperId && element.TYPE_DESC == 'REP' && element.SHOW_DOCUMENT_YN == 'Y'));
      parentReportChildren.forEach(element => {
        const title = (element.OPER_NAME ? CommonUtils.translate(element.OPER_NAME) : element.DESCRIPTION);
        const item: IPsSelect = {
          itemValue: element.OPER_ID,
          description: title,
          additionalDescList: [],
          additionActionsList: []
        };
        listOptions.push(item);
      });
    }
    return listOptions;
  }

