  updateSessionParametersAsPerNewLanguage(language: string) {
    //Jira #7097 - Refresh The All CIF parameters related to new language at the session storage
    let cifInfo = this.session.getValueOf(ConstantCommon.USERINFO);
    if (cifInfo?.customerInfoCO) {
      //Jira #7097 - A fallback was needed in case branchBriefDescEnglish,branchBriefDescArabic wasn't available in any other env
      cifInfo.customerInfoCO.branchBriefDesc = language.toLowerCase() == "ar" ? cifInfo.customerInfoCO.branchBriefDescArabic ?? cifInfo.customerInfoCO.branchBriefDesc : cifInfo.customerInfoCO.branchBriefDescEnglish ?? cifInfo.customerInfoCO.branchBriefDesc
    }
    this.session.append(ConstantCommon.USERINFO, cifInfo);
  }

  /**
   * this function is needed to check on the label key translation and override it with the static one since we had an issue that the prelogin was returned before reading those keys and thus it was overriding the correct translations
   * @param key label key to check and set the correct translation
  */
  async checkLabelTrans(key: string): Promise<string> {
    const labelsObj = this.labelKeysTransList.get(PsCommonSettings.activeLanguge.toLocaleLowerCase());
    const labelTrans = await this.translate.get(key).toPromise();
    const initialTrans = labelsObj.labels[key];
    if (labelTrans && labelTrans !== initialTrans) {
      const obj = {};
      obj[key] = initialTrans;
      this.common.language.addAndUseTranstlation(PsCommonSettings.activeLanguge.toLocaleLowerCase(), obj);
    }
    return this.translate.get(key).toPromise();
  }

