    commonJBPMServiceCall(url, param): Promise<IOmniBpmResponse> {
        return new Promise<IOmniBpmResponse>(async (resolve, reject) => {
            this.common.http.commonRequestAjax(url, param).then((response) => {
                resolve(response.data);
            }).catch((err) => {
                reject(err);
            });
        });
    }


