import defaultSettings from 'ps-default-config.json';
import { environment } from 'src/environments/environment';
import { CommonUtils } from './common-utils';

interface ASSETS_STRUCTURE {
    ASSETS_URL?: string;
    MAIN_CSS_FILE_NAME?: string;
    IMGS_FOLDER_URL?: string;
    CSS_FOLDER_URL?: string;
    FONTS_URL?: string;
    ICONS_URL?: string;
    HTML_URL?: string;
    FONT_FAMILY?: string;
    SVG_URL?: string;
    BANNERS_FILE_URL?: string;
    BULK_PAYMENT_TEMPLATES?: string;
    UI_CONFIGURATION?: {
        ENABLE_VITUAL_KEYBOARD?: boolean;
        LANDING_UI_TYPE?: number;
        DISPLAY_SLIDER_ON_LOGIN?: boolean;
        MENU_TREE?: boolean;
        ENHANCED_CONTACT_US?: boolean;
        LANDING_PAGE_OPER?: number;
        MAX_NUMBER_OF_IMAGES_IN_MEDIA_CAPTURE?: number;
        CUSTOM_TIMEOUT_SERVICE_LIST?: {};
        CUSTOM_TIMEOUT_OPER_LIST?: {};
        SUB_MENU_PAGE?: boolean;
        BOTTOM_QUICK_MENU?: boolean;
        DefaultZoomBranchPercentage?: number;
        REGULA_ENABLED?: boolean;
        REGULA_PROCESS?: string;
        REGULA_DOC_TYPE?: string;
    };
}

export interface PsApplicationSettings {
    MAIN_CONFIG?: {
        MAIN_PATH?: string;
        PACKAGE_NAME?: string;
        COMP_CODE?: number;
        APP_VERSION?: string;
        SERVICE_URL?: string;
        APP_ID?: number;
        APP_STORE_ID?: string;
        LOG_LEVEL_INTERVAL?: number;
        MINIFIED_DEFAULT_CSS_FILE_NAME?: string;
        MINIFIED_CLIENT_CSS_FILE_NAME?: string;
        APP_NAME?: string;
        REQUEST_TIMEOUT?: number;
        LOG_LEVEL_ALL?: boolean;
        PITime?: number;
        CLIENT_COUNTRY_ISO_CODE?: string; // Edited by ghada for tp#1304247 for adding region code to implement regions localizations on the map
        ALLOW_REACHMENU_FLIP_WITHOUT_RENDER ?: boolean;
        IST?: {
            TID?: string;
            CSI?: string;
            PUUID?: string;
        },
        AST?: {
            S256?: string;
        },
        SHOW_ALERT_ERROR: boolean;
        TIME_LOG_ENABLED: boolean; // Added by Richie to log the time taken for each action upon startup of the app and upon each service request.
        DEFAULT_QUICK_LINKS_MENU?: any; // Edited by neethu for userstory #1304580 to hold default quicklink values
        REQUESTS_TO_CACHE?: {} // Added By GI for #1347894
        EXPIRY_DATE?: string;
        previewTimeOut?:number; //flag added for #1583589 to wait a period of time in the preview step before displaying the summary fields
        DDT?: boolean; // flag to disable dev tool. if true, the dev tool will be disabled
        ENABLE_REQUEST_ID?: boolean; // flag to add unique request id to each service url in dev mode
        PAYMENT_CURRENCY?: string;
        PAYMENT_AMOUNT?: any;
        PAYMENT_AMOUNT_T?: any;
        SCREEN_TRANSLATION_OPER?: any; // Property to hold the screen oper Ids for fetching the Screen Operation Translation - AZDB-7529
        OLD_APP_ID?: any;
        WEBVIEW_URLS?:{
            WEBVIEW_ONBOARDING_URL?: string;// To add the webview onboarding link -  //UCBU250064 - Megha
        }
    };
    DEFAULT_ASSETS_CONFIG?: ASSETS_STRUCTURE;
    CLIENT_ASSETS_CONFIG?: ASSETS_STRUCTURE;
    GOOGLE_API_KEYS?: {
        RECAPTCHA_KEY?: string;
        RECAPTCHA_NATIVE_KEY?: string;
        MAP_WEB_API_KEY?: string;
    };
    REGULA_API_KEYS?: {
        REGULA_LICENSE?: string;
        REGULA_API_URL?: string;
    };
    functions?: {
        __SET(variable?: PsApplicationSettings, isClientConf?: boolean);
        _REFRESH();
    };
    commonVariables?: {
        svgsFileContent?: any;
        countriesFileContent?: any;
        currenciesFileContent?: any;
    }
}


export let PsApplicationSettings: PsApplicationSettings = {
    functions: {
        __SET(variable: PsApplicationSettings, isClientConf?: boolean) {
            if (variable) {
                CommonUtils.copyObject(PsApplicationSettings, variable, false, false, isClientConf);
                CommonUtils.copyObject(PsApplicationSettings.CLIENT_ASSETS_CONFIG, PsApplicationSettings.DEFAULT_ASSETS_CONFIG, false, true, true);
                if (!PsApplicationSettings.CLIENT_ASSETS_CONFIG) {
                    PsApplicationSettings.CLIENT_ASSETS_CONFIG = {};
                }
            }
        },
        _REFRESH() {
            PsApplicationSettings.MAIN_CONFIG.SERVICE_URL = PsApplicationSettings.MAIN_CONFIG.MAIN_PATH + PsApplicationSettings.MAIN_CONFIG.PACKAGE_NAME + '/pathservices/';
            if (window && environment.production && !(PsApplicationSettings.MAIN_CONFIG.LOG_LEVEL_ALL)) {
                window.console.log = () => { };
                window.console.warn = () => { };
                window.console.error = () => { };
                window.console.info = () => { };
                window.console.debug = () => { };
                window.console.trace = () => { };
            }
        },
    },
    ...defaultSettings
};
