  /**
   * GI: #1349742 - run returnVersionDetails in parallel with appflow update request
   * this method will check if we have an update in appflow
   */
  async checkAppflowforUpdate() {
    // Added by Richie to log the time of of the specific action
    if (PsApplicationSettings.MAIN_CONFIG.TIME_LOG_ENABLED) {
      PsCommonSettings.timeLogs.push('inside ps-template-landing-screen, before checkForUpdate() with start time: ' + (new Date()).toISOString());
    }
    return new Promise<string>(async (resolve, reject) => {
      const update = await this.deploy.checkForUpdate();
      // Added by Richie to log the time of of the specific action
      if (PsApplicationSettings.MAIN_CONFIG.TIME_LOG_ENABLED) {
        PsCommonSettings.timeLogs.push('inside ps-template-landing-screen, after checkForUpdate() with start time: ' + (new Date()).toISOString());
      }
      PsCommonSettings.appflowUpdateRequestWasSent = true;
      PsCommonSettings.appflowUpdateAvailable = update.available;
      this.commonUpdateChecking().then((res) => {
        resolve(res);
      }).catch((err) => {
        reject(err)
      });
    })
  }

  /**
   *  GI: #1349742 - run returnVersionDetails in parallel with appflow update request
   *  this method will check if we have an update in appflow || database update
   */
  async commonUpdateChecking() {
    return new Promise<string>(async (resolve, reject) => {
      if (PsCommonSettings.mandatoryUpdate == '1' || (PsCommonSettings.appflowUpdateRequestWasSent && PsCommonSettings.dbUpdateRequestWasSent)) {
        if (PsCommonSettings.dbUpdateAvailable && (PsCommonSettings.appflowUpdateAvailable || PsCommonSettings.mandatoryUpdate == '1')) {
          const msg = this.common.translate('store_update_required_key');
          // this.common.logger.warn(warningMsg);
          const alertOptions: IOptionsAlert = { title: 'update_key', message: msg, buttonsArray: [] };
          const updatetBtnOption: IOptionsAlertButton = {
            group: null,
            labelKey: 'update_key',
            handler: async () => {
              this.common.modalCtrl.dismiss();
              if (PsCommonSettings.mandatoryUpdate === '1') {
                let appId;          
                if (CommonUtils.isAndroid()) {
                  appId = await this.appVersion.getPackageName();
                } else if (CommonUtils.isIOS) {
                  appId = PsApplicationSettings.MAIN_CONFIG.APP_STORE_ID;
                }
                try {    
                  this.launchReview.launch(appId);
                  this.common.logger.log("[LaunchReview] Successfully opened App Store for Update");
                  resolve('storeUpdate');
                } catch (err) {
                  this.common.logger.error('Error: Failed to open app on store for app update, ', err);
                }
                return;
              } else {
                await this.performManualUpdate();
              }
              resolve('success');
            }
          };
          alertOptions.buttonsArray.push(updatetBtnOption);
          if ('0' === PsCommonSettings.mandatoryUpdate) {
            // alertOptions.message = this.common.translate('update_available_key');
            alertOptions.message = await this.omniCommon.checkLabelTrans('update_available_key');
            // add the cancel the button
            const cancelBtnOption: IOptionsAlertButton = {
              group: null,
              labelKey: 'cancel_key',
              handler: async () => {
                this.common.modalCtrl.dismiss();
                resolve('noUpdate');
              }
            };
            alertOptions.buttonsArray.push(cancelBtnOption);
          }
          CommonUtils.presentInfoAlert(null, alertOptions);
        } else {
          resolve('noUpdate');
        }
      } else {
        resolve('pendingRequest');
      }
    });
  }

  async performManualUpdate() {
    try {
      this.common.logger.log('Manual Update Started');
      this.loadingKey = 'updating_key';
      // Added by Richie to log the time of of the specific action
      if (PsApplicationSettings.MAIN_CONFIG.TIME_LOG_ENABLED) {
        PsCommonSettings.timeLogs.push('inside ps-template-landing-screen, before deploy.downloadUpdate with start time: ' + (new Date()).toISOString());
      }
      // end Richie
      await this.deploy.downloadUpdate((downProgress) => {
        if (this.loadingKey !== 'downloading_key') {
          this.loadingKey = 'downloading_key';
        }
        this.progress = downProgress;
      });
      // Added by Richie to log the time of of the specific action
      if (PsApplicationSettings.MAIN_CONFIG.TIME_LOG_ENABLED) {
        PsCommonSettings.timeLogs.push('inside ps-template-landing-screen, after deploy.downloadUpdate and before deploy.extractUpdate with end time: ' + (new Date()).toISOString());
      }
      // end Richie
      await this.deploy.extractUpdate((extractProgress) => {
        if (this.loadingKey !== 'extracting_key') {
          this.loadingKey = 'extracting_key';
        }
        this.progress = extractProgress;
      });
      // Added by Richie to log the time of of the specific action
      if (PsApplicationSettings.MAIN_CONFIG.TIME_LOG_ENABLED) {
        PsCommonSettings.timeLogs.push('inside ps-template-landing-screen, after deploy.extractUpdate and before deploy.reloadApp with end time: ' + (new Date()).toISOString());
      }
      // end Richie
      this.loadingKey = 'reloading_key';
      await this.deploy.reloadApp();
      this.common.logger.log('Manual Update Finished');
    } catch (err) {
      this.common.logger.error(err);
      this.loadingKey = 'Connection Error Occured During Update, Please Restart Your Application.';
      this.common.modalCtrl.dismiss();
    }
  }
  async performAutomaticUpdate(): Promise<string> {
    return new Promise<string>(async (resolve, reject) => {
      try {
        const currentVersion = await this.deploy.getCurrentVersion();
        // const config: IDeployConfig = {channel: 'Richie'};
        // await this.deploy.configure(config);
        this.common.logger.log('Automatic Update Started');
        this.loadingKey = 'updating_key';
        const resp = await this.deploy.sync({ updateMethod: 'auto' }, percentDone => {
          this.progress = percentDone;
        });
        if (!currentVersion || currentVersion.versionId !== resp.versionId) {
          // We found an update, and are in process of redirecting you since you put auto!
          this.common.logger.log('Automatic Update finished');
          resolve('success');
        } else {
          // No update available
          resolve('noUpdate');
        }
      } catch (err) {
        this.common.logger.error(err);
        reject('error');
      }
    });
  }

